import { useContext } from 'react';
import { ControllerContext } from "./index";

/**
 * 获得 Controller 实例，使用 `useControllerStatic` hook 的组件，不会随着编辑器内容的变化而发生重渲
 * @returns 
 */
export default function useControllerStatic() {
  const context = useContext(ControllerContext);
  if (!context) {
    throw new Error(
      "The `useControllerStatic` hook must be used inside the <Cangjie.Provider> component's context.",
    );
  }
  return context.controller;
}
