import React from 'react';
import { ShadowRoot } from '../shadowDom';
import { Selector } from '../../constants';

type SelectionSlotProps = React.PropsWithChildren<{
  // 属于哪个节点的 selection
  layerFor: string;
}>;

const slotStyle: React.CSSProperties = {
  position: 'absolute',
  zIndex: 10,
  // 相对于顶部定位，避免折叠/展开等操作引起的选区定位不稳定
  top: 0,
};

export const SelectionSlot = React.forwardRef<HTMLDivElement, SelectionSlotProps>((props, slotRef) => {
  return (
    <ShadowRoot attributes={{
      [Selector.selectionLayer]: props.layerFor,
      'data-testid': 'cangjie-selection-layer',
      style: { position: 'absolute', left: 0, top: 0 }
    }}>
      <div ref={slotRef} style={slotStyle} />
      {props.children}
    </ShadowRoot>
  );
});
