import * as React from 'react';
import ReactDOM from 'react-dom';

import Selection from '../../models/selection';
import { EdgePoint, TextPoint } from '../../models/point';
import { useZoomContainer } from '../../hooks/useZoom';
import useController from '../../controller/useController';
import findDOMNode from '../../utils/dom/findDOMNode';
import { getPopupPosition } from './popupPosition';
import { findCaretPosition } from '../../utils/dom/findCaretPosition';
import { Selector } from '../../constants';

const WIDTH = 160;
const HEIGHT = 128;
const ZOOM = 1.8;
const POPUP_EXTRA_TOP = -4;
const POPUP_EXTRA_BOTTOM = 52;

const rootStyle: React.CSSProperties = {
  position: 'fixed',
  overflow: 'hidden',
  left: '50px',
  top: '50px',
  zIndex: 10000,
  background: 'white',
  width: WIDTH,
  height: HEIGHT,
  border: '1px solid rgba(17,31,44,0.12)',
  boxShadow:
    '0 4px 8px 0 rgba(0,0,0,0.08), inset 0 10px 20px 0 rgba(0,0,0,0.08)',
  borderRadius: '8px',
  opacity: 0,
};

const blockContainerStyle: React.CSSProperties = {
  backfaceVisibility: 'hidden',
  transformOrigin: 'top left',
};

type ZoomerProps = {
  point: TextPoint | EdgePoint;
  isHanging: boolean;
};

// remove all the *cangjie* attributes
function cleanNode(node: Element) {
  Array.from(node.attributes).forEach((attr) => {
    if (attr.name.indexOf('cangjie') >= 0) {
      node.removeAttribute(attr.name);
    }
  });
  Array.from(node.children).forEach((child) => {
    cleanNode(child);
  });
}

function clearNode(node?: Element) {
  while (node && node.lastChild) {
    node.removeChild(node.lastChild);
  }
}

function Zoomer(pros: ZoomerProps) {
  const controller = useController();
  const container = useZoomContainer();
  const { point, isHanging } = pros;
  const [cachedBlocks] = React.useState(new WeakMap<Element, Element>());
  const blockRef = React.useRef<HTMLDivElement>(null);
  const rootRef = React.useRef<HTMLDivElement>(null);
  React.useEffect(() => {
    const blockNode = blockRef.current;
    const { document, selection } = controller.value;
    const { start } = selection.sort(document);
    const preferredEdge = point === start ? 'start' : 'end';
    const pointPosition = findCaretPosition(
      point,
      preferredEdge,
      container || window,
    );
    if (!blockNode || !pointPosition) {
      return;
    }

    // Get the DOM elements
    // eslint-disable-next-line react/no-find-dom-node

    const ancestor = document.getAncestors(point.key)[0];
    const dom = findDOMNode(ancestor.key);
    const maybePrevDom = dom.previousElementSibling as HTMLElement;
    const prevDom =
      maybePrevDom && !maybePrevDom.getAttribute('data-cangjie-hidden')
        ? maybePrevDom
        : null;
    const nextDom: HTMLElement = dom.nextElementSibling as HTMLElement;

    // Clone the DOM elements and cache them if necessary
    const prevArticleBlock = prevDom
      ? cachedBlocks.get(prevDom) || (prevDom.cloneNode(true) as Element)
      : null;
    const nextArticleBlock = nextDom
      ? cachedBlocks.get(nextDom) || (nextDom.cloneNode(true) as Element)
      : null;
    if (prevDom && prevArticleBlock && !cachedBlocks.get(prevDom)) {
      cleanNode(prevArticleBlock);
      cachedBlocks.set(prevDom, prevArticleBlock);
    }
    if (nextDom && nextArticleBlock && !cachedBlocks.get(nextDom)) {
      cleanNode(nextArticleBlock);
      cachedBlocks.set(nextDom, nextArticleBlock);
    }

    // We never cache the focused DOM element
    const articleBlock = dom.cloneNode(true) as Element;
    cleanNode(articleBlock);

    // Calculate the offset positions
    const domRect = dom.getBoundingClientRect();
    const marginTop = parseInt(window.getComputedStyle(dom).marginTop, 10) || 0;
    const offsetX = pointPosition.clientLeft - domRect.left - WIDTH / ZOOM / 2;
    let offsetY =
      pointPosition.clientTop +
      pointPosition.height / 2 -
      domRect.top +
      marginTop -
      HEIGHT / ZOOM / 2;
    if (prevDom && prevArticleBlock) {
      const prevStyle = window.getComputedStyle(prevDom);
      const prevMarginTop = parseInt(prevStyle.marginTop, 10) || 0;
      const prevMarginBottom = parseInt(prevStyle.marginBottom, 10) || 0;
      offsetY +=
        prevDom.getBoundingClientRect().height +
        prevMarginTop +
        prevMarginBottom;
    }

    const focusedSelection = Selection.create({
      anchor: point,
      focus: point,
    }).setHanging(isHanging);
    const popupPosition = getPopupPosition(
      focusedSelection,
      controller,
      WIDTH,
      HEIGHT,
    );

    // Attach the DOM elements and apply all positions
    clearNode(blockNode);
    if (prevArticleBlock) {
      blockNode.appendChild(prevArticleBlock);
    }
    blockNode.appendChild(articleBlock);
    if (nextArticleBlock) {
      blockNode.appendChild(nextArticleBlock);
    }
    if (rootRef.current && popupPosition) {
      const { isTop, x, y } = popupPosition;
      const extraOffset = isTop ? POPUP_EXTRA_TOP : POPUP_EXTRA_BOTTOM;
      rootRef.current.style.left = `${x}px`;
      rootRef.current.style.top = `${y + extraOffset}px`;
      rootRef.current.style.opacity = '1';
    }
    blockNode.style.width = `${domRect.width}px`;
    blockNode.style.transform = `translate(${-offsetX * ZOOM}px, ${
      -offsetY * ZOOM
    }px) scale(${ZOOM})`;
  }, [cachedBlocks, container, controller, point]);

  return (
    <div data-cangjie-mobile-zoomer style={rootStyle} ref={rootRef}>
      <div style={blockContainerStyle} ref={blockRef} />
    </div>
  );
}

export default (props: ZoomerProps) => {
  const container = useZoomContainer();
  const attachNode = container?.querySelector('[data-cangjie-zoomer]');
  if (!attachNode) {
    return null;
  }
  return ReactDOM.createPortal(<Zoomer {...props} />, attachNode);
};
