import * as React from 'react';
import equal from 'fast-deep-equal';
import { Rect } from './utils/antiAliasingRects';
import { CANGJIE_SELECTION_INACTIVE_COLOR, Selector } from '../../constants';
import Selection from '../../models/selection';
import Controller from '../../controller';

interface RangeProps {
  rects: Rect[];
  isFocused: boolean;
  isRemote: boolean;
  selection: Selection;
  controller: Controller;
}

const attrbiute = {
  [Selector.nonSelect]: true,
};

const style: React.CSSProperties = {
  pointerEvents: 'none',
};

const isComponentPropsEqual = (prevProps: RangeProps, nextProps: RangeProps) => {
  return (
    prevProps.isFocused === nextProps.isFocused
    && prevProps.isRemote === nextProps.isRemote
    && equal(prevProps.rects, nextProps.rects)
    && equal(prevProps.selection, nextProps.selection)
  );
};

/**
 * 负责渲染 expanded selection
 */
const Range = React.memo((props: RangeProps) => {
  const { rects, controller, isFocused, isRemote, selection } = props;

  const color = React.useMemo(() => controller.query(
    'selectionColor',
    selection,
    !isRemote,
  // isFocused 在 query 内部会消费掉
  // eslint-disable-next-line react-hooks/exhaustive-deps
  ), [controller, selection, isRemote, isFocused]);

  return (
    <div data-cangjie-selection={selection.viewKey} style={style} data-testid="cangjie-range" className='cangjie-range'>
      {rects.map((rect) => {
        const position = 'absolute';
        const { left, top, width, height } = rect;
        const key = `${left}-${top}-${width}-${height}`;
        const style: React.CSSProperties = {
          position,
          left,
          top,
          width,
          height,
          backgroundColor: color || CANGJIE_SELECTION_INACTIVE_COLOR,
        };
        return <div key={key} {...attrbiute} style={style} />;
      })}
    </div>
  );
}, isComponentPropsEqual);

export default Range;
