import { match, P } from 'ts-pattern';
import Document from '../../models/document';
import Element from '../../models/element';
import Operation, {
  InsertNodeOperation,
  OperationType,
  RemoveNodeOperation,
  SetNodeOperation,
} from '../../models/operation';
import { UUIDConfig, UUIDMap, UUIDSDiff } from './types';

type OPAdjuster<T extends Operation> = (
  op: T,
  document: Document,
) => { op: T; diff: UUIDSDiff };

const createMapOperations = (
  gen: UUIDConfig['gen'],
  uuidKey: string,
  getUUIDs: () => UUIDMap,
) => {
  function generateUUID(node: Element, diff: UUIDSDiff) {
    const uuids = getUUIDs();
    const uuid = node.data[uuidKey];
    // 如果无 UUID，或者 UUID 重复，则需要重新创建 UUID
    if (!uuid || uuids[uuid]) {
      const newUUID = gen();
      uuids[newUUID] = node.key;
      diff.adds.push({ uuid: newUUID, key: node.key });
      return node.merge({
        data: {
          ...node.data,
          [uuidKey]: newUUID,
        },
      });
    }
    return node;
  }

  const genUUIDForTree: (root: Element, diff: UUIDSDiff) => Element = (
    root,
    diff,
  ) => {
    const newRoot = root.mapDescendants((n) => {
      if (n.isElement()) {
        return generateUUID(n, diff);
      }
      return n;
    });
    return generateUUID(newRoot, diff);
  };

  const adjustSetNodeOperation: OPAdjuster<SetNodeOperation> = (
    op,
    document,
  ) => {
    const uuids = getUUIDs();
    const { properties, prevProperties, path } = op;
    const node = document.assertElementByPath(path);
    /* istanbul ignore next */
    const data = properties.data || {};
    /* istanbul ignore next */
    const prevData = prevProperties.data || {};
    const theUUID = data[uuidKey] as string | undefined;
    const thePrevUUID = prevData[uuidKey] as string | undefined;
    return (
      match({
        uuid: theUUID,
        prevUUID: thePrevUUID,
        reset: theUUID !== thePrevUUID,
      })
        .with(
          // 若设置节点时，丢失 UUID，则写回 UUID
          { uuid: P.nullish, prevUUID: P.not(P.nullish) },
          ({ prevUUID }) => ({
            op: {
              ...op,
              properties: {
                ...properties,
                data: {
                  ...data,
                  [uuidKey]: prevUUID,
                },
              },
            },
            diff: {
              adds: [],
              removes: [],
              updates: [],
            },
          }),
        )
        .with(
          // 若设置节点时，UUID 不重复，则允许生成
          {
            uuid: P.when((id) => id && !uuids[id]),
            prevUUID: P.not(P.nullish),
            reset: true,
          },
          ({ uuid, prevUUID }) => {
            return {
              op,
              diff: {
                adds: [],
                removes: [],
                updates: [{ oldUUID: prevUUID, newUUID: uuid }],
              },
            };
          },
        )
        .with(
          // 若设置节点时，UUID 重复，则重新生成 UUID
          {
            uuid: P.when((id) => id && uuids[id] !== node.key),
            prevUUID: P.not(P.nullish),
            reset: true,
          },
          ({ prevUUID }) => {
            const newUUID = gen();
            return {
              op: {
                ...op,
                properties: {
                  ...properties,
                  data: {
                    ...data,
                    [uuidKey]: newUUID,
                  },
                },
              },
              diff: {
                adds: [],
                removes: [],
                updates: [{ oldUUID: prevUUID, newUUID }],
              },
            };
          },
        )
        .with(
          // 设置不含有 UUID 的节点时，若新的属性缺失 UUID，则生成 UUID
          { uuid: P.nullish, prevUUID: P.nullish },
          // 设置不含有 UUID 的节点时，若新的属性有 UUID，但重复，则生成 UUIID
          {
            uuid: P.when((id) => uuids[id] && uuids[id] !== node.key),
            prevUUID: P.nullish,
          },
          () => {
            const newUUID = gen();
            return {
              op: {
                ...op,
                properties: {
                  ...properties,
                  data: {
                    ...data,
                    [uuidKey]: newUUID,
                  },
                },
              },
              diff: {
                adds: [{ uuid: newUUID, key: node.key }],
                removes: [],
                updates: [],
              },
            };
          },
        )
        .with(
          // 设置不含有 UUID 的节点时，若新的属性中含有不重复的 UUID，允许生成
          { uuid: P.when((id) => id && !uuids[id]), prevUUID: P.nullish },
          ({ uuid }) => {
            return {
              op,
              diff: {
                adds: [{ uuid, key: node.key }],
                removes: [],
                updates: [],
              },
            };
          },
        )
        /* istanbul ignore next */
        .otherwise(() => ({ op, diff: { adds: [], removes: [], updates: [] } }))
    );
  };

  const adjustRemoveNodeOperation: OPAdjuster<RemoveNodeOperation> = (op) => {
    const { node } = op;
    if (node.isElement() && node.data[uuidKey]) {
      const uuid = node.data[uuidKey];
      return { op, diff: { adds: [], removes: [uuid], updates: [] } };
    }
    return { op, diff: { adds: [], removes: [], updates: [] } };
  };

  const adjustInsertNodeOperation: OPAdjuster<InsertNodeOperation> = (op) => {
    const { node } = op;
    const diff = { adds: [], removes: [], updates: [] };
    if (node.isElement()) {
      return { op: { ...op, node: genUUIDForTree(node, diff) }, diff };
    }
    return { op, diff };
  };

  return function mapOperation(
    operation: Operation,
    document: Document,
  ): { op: Operation; diff: UUIDSDiff } {
    return match(operation)
      .with({ type: OperationType.RemoveNode }, (op) =>
        adjustRemoveNodeOperation(op, document))
      .with({ type: OperationType.SetNode }, (op) =>
        adjustSetNodeOperation(op, document))
      .with({ type: OperationType.InsertNode }, (op) =>
        adjustInsertNodeOperation(op, document))
      .otherwise((op) => ({
        op,
        diff: { adds: [], removes: [], updates: [] },
      }));
  };
};

export default createMapOperations;
