import equal from 'fast-deep-equal';
import {
  jsonMLGetAttributes,
  jsonMLGetLastChild,
  jsonMLGetTagName,
  jsonMLGetText,
  jsonMLIsText,
} from '../../utils/jsonml';
import type {
  JsonML,
} from '../../type/jsonml';

import {
  MS_OFFICES,
  REMAIN_STYLE_SOURCES,
  MoState as State,
  MoAttributes as Attributes,
  HTMLToJsonMLRule,
  createTextWithString,
  TextMark,
  LeafRule,
} from '../../index';
import { omit, isEmpty } from 'lodash-es';
import { normalizeText as normalizeTextUtil } from '../../../utils/textUtils'

/**
 * 一般使用的有两种空白符，空格敲击的空白符（Simple space）码点是 32，Non-breaking space 的码点是 160
 * 在 Word 的规范中，32 码点的字符会被裁剪掉，160 码点会保留，裁剪规则如下：
 * - 文字头端的所有连续 32 码点空白符会被全部裁剪（同时也包括了 \n 符号）；
 * - 文字中端和尾端的所有连续 32 码点空白符会合并（Collapse）成一个空白符；
 * @see https://stackoverflow.com/questions/11984029/difference-between-32-and-nbsp
 */
const SIMPLE_SPACE_CODE = 32;

// \n 符号
const BREAK_CODE = 10;

// 需要被替换为\n的字符
const BR_REPLACE_BREAK_MATCH = [/\n\r/g, /\r\n/g, /\r/g];

const HEAD_TAG_REG = /^h\d$/i;

// TODO: 兼容原 text 插件逻辑，待重构
const paragraphTags: Record<string, string> = {
  h1: 'h1',
  h2: 'h2',
  h3: 'h3',
  h4: 'h4',
  h5: 'h5',
  h6: 'h6',
  p: 'p',
  div: 'p',
  section: 'p',
};

// 解析非文档 html, 使用仓颉默认的字体大小等样式
function normalizeMarks(tagName: string, marks: Record<string, any>) {
  if (HEAD_TAG_REG.test(tagName)) {
    return omit(marks, ['vertAlign', 'sz', 'szUnit']);
  }
  return marks;
}

/**
 * Normalize 一行文本，剔除文本前面的空白符
 * @note 注意 Word 的规范中只会对 text/html 中的空白符进行裁剪，text/plain 中的空白符会保留
 * @param text
 */
function normalizeText(text: string): string {
  let index = 0;
  for (; index < text.length; index++) {
    const code = text.charCodeAt(index);
    if (
      code === SIMPLE_SPACE_CODE || // 空格符号过滤
      code === BREAK_CODE // 断行符号过滤
    ) {
      continue;
    }
    break;
  }
  // 裁剪头端所有的空格符和断行符
  text = text.slice(index, text.length);
  // 合并中端和尾端的空格符
  text = text.replace(/ +/g, String.fromCharCode(SIMPLE_SPACE_CODE));

  return normalizeTextUtil(text);
}

export function createHTMLToJsonML(htmlToAttrs: Array<LeafRule['htmlToJsonMLAttrs']>) {
  const enableBr = false; // FIX: 禁用对 br 的处理

  const converts = htmlToAttrs.filter((f): f is NonNullable<LeafRule['htmlToJsonMLAttrs']> => !!f);

  function generateMarks(tag: string, attrs: Attributes): TextMark | null {
    let marks: TextMark = converts.reduce((prev, convert) => {
      return convert(tag, { ...attrs }, { ...prev })
    }, {});

    return isEmpty(marks) ? null : marks;
  }

  return function (pasteFrom): HTMLToJsonMLRule {
    return {
      name: 'text',

      onOpenTag: (
        state: State,
        name: string,
        attrs: Attributes,
        id: string,
      ) => {
        const marks = generateMarks(name, attrs);
        if (marks) {
          state.addMarks({ id, value: marks });
          // fall through
        }
        return false;
      },

      onText: (state: State, text: string): boolean => {
        let texts = [text];
        // ['p']
        let parent = state.peek();
        // ['span', { 'data-type': 'text' }]
        let prevSibling = jsonMLGetLastChild(parent);
        const parentTagName = jsonMLGetTagName(parent);
        // TODO 待改造
        if (
          !paragraphTags[parentTagName] &&
          parentTagName !== 'a' &&
          parentTagName !== 'inlineCode'
        ) {
          if (!prevSibling || !(prevSibling as any).isPhantom) {
            const phantom: JsonML = ['p'];
            (phantom as any).isPhantom = true;
            parent = phantom;
            prevSibling = null;
            state.append(phantom);
          } else {
            parent = prevSibling;
            prevSibling = jsonMLGetLastChild(parent);
          }
        }
        // \n 只有出现在 a 及 p 中才被转换
        if (
          enableBr &&
          (paragraphTags[parentTagName] || parentTagName === 'a')
        ) {
          const breakStr = String.fromCharCode(BREAK_CODE);
          // 先统一处理成 \n
          const onlyBreakText = BR_REPLACE_BREAK_MATCH.reduce(
            (result, match) => result.replace(match, breakStr),
            text,
          );
          texts = onlyBreakText.split(breakStr).filter((a) => !!a);
        } else if (MS_OFFICES.includes(pasteFrom)) {
          // office 中的 \n 没有包含在 white-space: pre 中，统一作空格字符串使用
          texts = texts.map((txt) => txt.replace(/\n/g, ' '));
        }

        let marks = state.getMarks();
        const parentType = parent[0];
        if (
          HEAD_TAG_REG.test(parentType) &&
          !REMAIN_STYLE_SOURCES.includes(pasteFrom)
        ) {
          marks = normalizeMarks(parentType, marks);
        }

        const leafAttrs = {
          ...marks,
          'data-type': 'leaf',
        };

        // 如果 p 下面没有 text 节点
        if (!prevSibling || !jsonMLIsText(prevSibling)) {
          prevSibling = ['span', { 'data-type': 'text' }];
          parent.push(prevSibling);
        }

        texts.forEach((txt, index) => {
          if (enableBr && index > 0) {
            parent.push([
              'br',
              { type: 'textWrapping', data: {} },
              createTextWithString(''),
            ]);
            prevSibling = ['span', { 'data-type': 'text' }];
            parent.push(prevSibling);
          }
          const prevLeaf =
            enableBr && prevSibling![0] === 'br'
              ? null
              : jsonMLGetLastChild(prevSibling!);

          // 如果是空段落，则 normalize text
          if (!prevLeaf || jsonMLGetText(parent) === '') {
            txt = normalizeText(txt);
          }

          const leaf = ['span', leafAttrs, txt];

          // 如果是空 text 或者与上一个 leaf 属性不匹配，则 push
          if (!prevLeaf || !equal(leafAttrs, jsonMLGetAttributes(prevLeaf))) {
            prevSibling!.push(leaf);
          } else {
            // 相同属性的 leaf 则 concat
            prevLeaf[prevLeaf.length - 1] += txt;
          }
        });

        return false;
      },

      onCloseTag: (
        state: State,
        name: string,
        attrs: Attributes,
        id: string,
      ) => {
        state.removeMarks({ id });
        // fall through
        return false;
      },
    };
  };
}
