/**
 * @packageDocumentation
 * @module Events
 * @description Prevent clipboard data from being destroyed when asynchronous retrieval of data
 */
interface PasteInputDetail {
  clipboardData?: DataTransfer
}

type PasteInputEvent = CustomEvent<PasteInputDetail>;

function PasteInputEvent(detail: PasteInputDetail) {
  return new CustomEvent('pasteInput', { detail });
}

export default PasteInputEvent;
