import { uniqueId } from 'lodash-es';
import Controller from '../../../controller';
import Selection from '../../../models/selection';
import { CANGJIE_SELECTION_EMPTY_WIDTH, Selector } from '../../../constants';
import { findDOMNodeSafely } from '../../../utils/dom/findDOMNode';
import Path from '../../../models/path';
import Document from '../../../models/document';
import { getRectsFromLeaf } from './getRectsFromLeaf';
import { traverseDOMForest, TraverseNodeHandler, traverseDOMTree, TraverseNodeContext } from './traverse';
import { antiAliasingRects, DOMRectWithLine, Rect } from './antiAliasingRects';
import { getDOMPath } from './dom';

const isEdgeSelectable = (dom: HTMLElement) => dom.getAttribute(Selector.edgeSelectable) === 'true';
const isLeaf = (dom: HTMLElement) => dom.getAttribute(Selector.leaf) === 'true';
const isVoid = (dom: HTMLElement) => dom.getAttribute(Selector.void) === 'true';
const isFloatInlineBox = (dom: HTMLElement) => dom.getAttribute('data-type') === 'float-inline-box';
const isFloatBlockBox = (dom: HTMLElement) => dom.getAttribute('data-type') === 'float-block-box';
const isLine = (dom: HTMLElement) => dom.getAttribute('data-type') === 'line';
// const isGroupBlock = (dom: HTMLElement) => dom.getAttribute(Selector.groupBlock) === 'true';
const isPage = (dom: HTMLElement) => dom.getAttribute('data-type') === 'page';
const isTableCell = (dom: HTMLElement) => dom.getAttribute('data-type') === 'table-cell';
const isFullyContained = (key: string, voidElement: boolean, selection: Selection, document: Document) => {
  const { start, end } = selection.sort(document);
  const startPath = document.getPath(start.key);
  const endPath = document.getPath(end.key);
  const targetPath = document.getPath(key);
  if (!startPath || !endPath || !targetPath) {
    return false;
  }

  if (Path.isAncestor(targetPath, startPath) || Path.isAncestor(targetPath, endPath)) {
    // eg. 目标节点为 [0, 1]，start/end 为 [0, 1, 0]
    // => 目标节点若为 void 元素，则选区包含了目标
    // => 目标节点若非 void，则选区未完全包含目标
    return voidElement;
  }

  return document.isNodeInRange(key, selection);
};

export const GlobalGroupKey = 'global-group';
export const DefaultGapLabel = 'default-gap-label';

const getGapLabel = (decesdant: HTMLElement) => {
  const conditions = [
    // isGroupBlock, // TODO FIXME: groupNodes 隔开的上下的全局段落，会被分组到一起做空隙填充，需要再对每个连续的全局区块单独命名
    isPage,
    isTableCell,
    isFloatInlineBox, // 浮动元素单独为一组，不参与普通 Rect 的间隙填充逻辑
    isFloatBlockBox, // eg. 首字下沉
  ];
  const shouldGap = conditions.find((cond) => cond(decesdant));
  return shouldGap ? (decesdant.getAttribute(Selector.key) || DefaultGapLabel) : undefined;
};

/**
 * 判断当前节点的子节点中是否仅有一个 leaf/void 节点
 * @param decesdant
 * @returns
 */
const hasOnlyLeafChild = (decesdant: HTMLElement) => {
  let count = 0;
  const len = decesdant.children.length;
  for (let i = 0; i < len; i++) {
    if (isLeaf(decesdant.children[i] as HTMLElement) || isVoid(decesdant.children[i] as HTMLElement)) {
      count++;
    }
    // perf: 找到超过 1 个符合要求的，就提前 return
    if (count > 1) {
      return false;
    }
  }
  return count === 1;
}

const addLineIdToRect = (rect: DOMRect, lineId?: string): DOMRectWithLine => {
  return { ...(rect.toJSON ? rect.toJSON() : rect), lineId };
}

const addToRectGroupWithGap = (rects: DOMRectWithLine[], target: RectGroupWithGap, context: TraverseNodeContext = {}) => {
  const key = context.scrollableKey || GlobalGroupKey;
  const label = context.gapLabel || DefaultGapLabel;
  if (!target[key]) {
    target[key] = {};
  }
  if (!target[key][label]) {
    target[key][label] = [];
  }
  target[key][label].push(...rects);
};

export type RectGroupWithGap = Record<string, Record<string, DOMRect[]>>;
export type RectGroup = Record<string, DOMRect[]>;

export function getRectsFromSelection(selection: Selection, contentDOM: HTMLElement, controller: Controller) {
  const { document } = controller.value;
  const { start, end } = selection.sort(document);
  const startBlock = start.isEdgePoint() ? document.getNode(start.key) : document.getClosestBlock(start.key);
  const endBlock = end.isEdgePoint() ? document.getNode(end.key) : document.getClosestBlock(end.key);
  const startPath = document.getPath(start.key);
  const endPath = document.getPath(end.key);
  const startBlockPath = startBlock && document.getPath(startBlock.key);
  const endBlockPath = endBlock && document.getPath(endBlock.key);
  if (!startBlock || !endBlock || !startPath || !endPath || !startBlockPath || !endBlockPath) {
    throw new Error('start/end block or path is empty');
  }

  const selectionRects: RectGroupWithGap = {};
  const startRects: RectGroupWithGap = {};
  const endRects: RectGroupWithGap = {};

  // 遍历过程中的 DOM 节点处理逻辑
  const traverseHandler: TraverseNodeHandler = (decesdant, context = {}) => {
    const domKey = decesdant.getAttribute(Selector.key);
    if (!domKey) {
      return { stopTraverse: false };
    }

    const voidDOM = isVoid(decesdant);
    const leafDOM = isLeaf(decesdant);
    const lineDOM = isLine(decesdant);
    const edgeSelectableDOM = isEdgeSelectable(decesdant);

    // 记录 start/end 所在 block 的所有 rects，用于后续修正选区边界的 rect
    if (voidDOM || leafDOM) {
      const path = document.getPath(domKey.split(':')[0]) || [];
      if (Path.isAncestor(startBlockPath, path)) {
        addToRectGroupWithGap(
          Array.from(decesdant.getClientRects()).map((v) => addLineIdToRect(v, context.lineId)),
          startRects,
          context
        );
      }
      if (Path.isAncestor(endBlockPath, path)) {
        addToRectGroupWithGap(
          Array.from(decesdant.getClientRects()).map((v) => addLineIdToRect(v, context.lineId)),
          endRects,
          context
        );
      }
    }

    if ((edgeSelectableDOM || voidDOM)) {
      if (isFullyContained(domKey!, voidDOM, selection, document)) {
        const rect = decesdant.getBoundingClientRect();
        addToRectGroupWithGap([addLineIdToRect(rect, context.lineId)], selectionRects, context);
        return { stopTraverse: true }; // 不再继续递归
      }
      if (voidDOM) {
        return { stopTraverse: true };
      }
    }

    if (leafDOM) {
      let newRects = getRectsFromLeaf(decesdant, selection, document);
      if (context.onlyLeafChild && newRects.length === 1 && newRects[0].width < 1) {
        const { left, top, height } = newRects[0];
        const width = CANGJIE_SELECTION_EMPTY_WIDTH;
        newRects = [{ ...newRects[0].toJSON(), left, top, width, height }];
      }

      addToRectGroupWithGap(newRects.map((v) => addLineIdToRect(v, context.lineId)), selectionRects, context);

      return { stopTraverse: true };
    }

    const gapLabel = getGapLabel(decesdant) || context.gapLabel;
    return { stopTraverse: false, context: { gapLabel, lineId: lineDOM ? domKey : undefined, onlyLeafChild: hasOnlyLeafChild(decesdant) } };
  };

  const commonAncestorNode = document.getNodeByPath(Path.common(startPath, endPath));
  if (!commonAncestorNode) {
    throw new Error('commonAncestorNode is empty');
  }
  const commonAncestor = (commonAncestorNode.isElement() ? commonAncestorNode : document.getClosestBlock(commonAncestorNode.key)) || document;

  // 由于 view 或 实际 DOM 树与 document 结构并非一致，所以通过查找 DOM 树上需遍历的子树根结点来做
  let startBlockDOM = findDOMNodeSafely(startBlock.key, contentDOM);
  let endBlockDOM = findDOMNodeSafely(endBlock.key, contentDOM);

  // 分页模式下存在虚拟化，若找不到 DOM 则全量遍历已有 DOM
  if (!startBlockDOM || !endBlockDOM) {
    const commonAncestorDOM = findDOMNodeSafely(commonAncestor.key, contentDOM);
    startBlockDOM = startBlockDOM || commonAncestorDOM?.firstElementChild as HTMLElement;
    endBlockDOM = endBlockDOM || commonAncestorDOM?.lastElementChild as HTMLElement;
  }
  // 仍找不到则报错
  if (!startBlockDOM || !endBlockDOM) {
    throw new Error('start/end block DOM is empty');
  }

  const startDOMPath = getDOMPath(startBlockDOM, contentDOM);
  const endDOMPath = getDOMPath(endBlockDOM, contentDOM);

  // 以遍历森林的方式遍历 [startDOMPath, endDOMPath] 之间的节点
  traverseDOMForest(startDOMPath, endDOMPath, traverseHandler);

  const res: Rect[] = [];
  Object.keys(selectionRects).forEach((groupKey) => {
    // res[groupKey] = [];
    Object.keys(selectionRects[groupKey]).forEach((gapLabel) => {
      // TO BE SOLVED: 1. 存在上下 margin 的 block 元素在跨 block 选区时高度会不稳定
      const lines = antiAliasingRects(selectionRects[groupKey][gapLabel], startRects[groupKey]?.[gapLabel], endRects[groupKey]?.[gapLabel]);
      res.push(...lines);
    });
  });

  return res;
}
