import Text from '../../../models/text';
import type { ValueToTextRule } from './../../type/rules';

const valueToText: ValueToTextRule = {
  name: 'text',

  match(node) {
    return Text.isText(node);
  },

  convert(node) {
    return node.text;
  },
};

export function createValueToText() {
  return valueToText;
}
