import * as React from 'react';
import memoizeOne from 'memoize-one';
import equal from 'fast-deep-equal';
import warning from 'tiny-warning';
import NodeProps from '../interfaces/nodeProps';
import Controller from '../controller/index';
import Path from '../models/path';
import Value from '../models/value';
import Node from '../models/node';
import Element from '../models/element';
import Document from '../models/document';
import Block from '../models/block';
import Text from '../models/text';
import Leaf from '../models/leaf';
import Mark from '../models/mark';
import Selection from '../models/selection';
import { getChildrenIsSelected } from '../utils/getChildrenOverlaps';
import useCangjieKeyBindingRef from '../hooks/useCangjieKeyBindingRef';
import { CANGJIE_EMPTY, Selector } from '../constants';
import VoidWrapper from './voidWrapper';
import LeafComponent from './leaf';
import { useInputPending, PendingElements } from '../hooks/useInputPending';
import { InputData } from '../interfaces/input';
import { PendingType } from '../interfaces/controller';
import { useNodeStyle } from '../hooks/useStyle';
import useView from '../hooks/useView';
import GroupWrapper from './groupWrapper';
import { isGroupCollapsed, isGroupEmpty } from '../plugins/group/utils';
import useGroupData from '../hooks/useGroupData';
import { useUserData } from '../hooks/useUserData';

function isComposingMark({ type }: Mark) {
  return type === 'cangjieComposing';
}

function isNotComposingMark(mark: Mark) {
  return !isComposingMark(mark);
}

const checkRerenderNodesBySelectionChange = memoizeOne((
  prevSelection: Selection, 
  selection: Selection,
  prevDocument: Document,
  document: Document,
) => {
  const shouldRenderNodeKeys = new Set<string>();
  const { start, end } = selection.convertToTextPoints(document);
  const { start: prevStart, end: prevEnd } = prevSelection.convertToTextPoints(document);
  if (selection === prevSelection) {
    return shouldRenderNodeKeys;
  }

  if (start !== prevStart) {
    shouldRenderNodeKeys.add(start.key);
    const startAnchestors = document.getAncestors(start.key);
    startAnchestors.forEach((anchestor) => {
      shouldRenderNodeKeys.add(anchestor.key);
    });
    shouldRenderNodeKeys.add(prevStart.key);
    const prevStartAnchestors = document.getAncestors(prevStart.key);
    prevStartAnchestors.forEach((anchestor) => {
      shouldRenderNodeKeys.add(anchestor.key);
    });
  }

  if (end !== prevEnd) {
    shouldRenderNodeKeys.add(end.key);
    const endAnchestors = document.getAncestors(end.key);
    endAnchestors.forEach((anchestor) => {
      shouldRenderNodeKeys.add(anchestor.key);
    });
    shouldRenderNodeKeys.add(prevEnd.key);
    const prevEndAnchestors = document.getAncestors(prevEnd.key);
    prevEndAnchestors.forEach((anchestor) => {
      shouldRenderNodeKeys.add(anchestor.key);
    });
  }

  return shouldRenderNodeKeys;
});

// TODO: 等 selectionLayer 提到 content，修改这里的判断逻辑
function shouldComponentUpdate(prevProps: NodeProps, nextProps: NodeProps) {
  const { controller } = prevProps;

  const shouldUpdate = controller.run(
    'shouldNodeComponentUpdate',
    prevProps,
    nextProps,
  );
  const n = nextProps;
  const p = prevProps;

  if (shouldUpdate !== null) {
    if (shouldUpdate) {
      return true;
    }

    if (process.env.NODE_ENV === 'development') {
      warning(
        shouldUpdate !== false,
        "Returning false in `shouldNodeComponentUpdate` does not disable Cangjie's internal `shouldComponentUpdate` logic. If you want to prevent updates, use React's `shouldComponentUpdate` instead.",
      );
    }
  }

  if (n.node !== p.node) {
    return true;
  }

  if (n.visible !== p.visible) {
    return true;
  }

  const isSelectionPending = controller.pendingType === PendingType.selection;
  // 当存在 Hots & Pending 时，节点的重渲会存在两个诱因：
  // 1. 外部 Props 重渲
  // 2. 内部受 Hots & Pending 的控制重渲
  //
  // 假定节点树为：
  // ```tsx
  //   <value>
  //   <document>
  //     <block type="paragrah" key="p0">
  //       <block type="paragraph" key="hello">
  //         <text key="hello-text">Hello</text>
  //       </block>
  //       <block type="paragraph" key="world">
  //         <text key="world-text">World</text>
  //         <cursor />
  //       </block>
  //     </block>
  //     <block type="paragraph" key="p1">
  //       <block type="paragraph" key="cangjie">
  //         <text key="cangjie-text">Cangjie</text>
  //       </block>
  //     </block>
  //   </document>
  // </value>
  //```
  //
  // 1. 当光标从 `world-text` 移动到 `hello-text`：计算热区为 ['world', 'hello']，且开始选区 Pending
  //   1.1 Hots 驱动 `world` 组件树重渲：`world` 未选中
  //     * `world-text` 因为 props 变更而重渲 `prevProps.isSelected !== nextProps.isSelected`
  //   1.2 Hots 驱动 `hello` 组件树重渲：`hello` 选中
  //     * `hello-text` 因为 props 变更而重渲 `prevProps.isSelected !== nextProps.isSelected`
  //
  // 2. 当光标从 `hello-text` 移动到 `cangjie-text`：计算热区为 ['p0', 'p1']
  //    2.1 Hots 驱动 `p0` 组件树重渲：`p0` 节点树取消选中
  //      * `hello` 重渲，且 `prevProps.isSelected === nextProps.isSelectd === false`，但由于 `hello` 在 pending 期间被选中，因此 `hello` 应重渲以取消选中态
  //      * `hello` 组件树重渲...
  //    2.2 Hots 驱动 `p1` 组件树重渲：`p1` 节点树选中
  //      * `cangjie` 重渲，且 `prevProps.isSelectd !== nextProps.isSelected`，因此 `cangjie 重渲`
  //      * `cangjie` 组件树重渲..
  //
  if (isSelectionPending) {
    const pendingSelected = controller.getIsSelected(n.node.key);
    if (p.isSelected === n.isSelected) {
      return p.isSelected !== pendingSelected;
    }
  }

  // 1. 考虑节点树及其选中态为：
  // ```tsx
  // <value>
  //   <document>
  //     <block key="container" type="container" isSelected={true}>
  //       <block key="hello" type="paragraph" isSelected={true}>Hello</block>
  //       <block key="world" type="paragraph" isSelected={false}>World</block>
  //       <block key="cangjie" type="paragprah">Cangjie</block>
  //     </block>
  //   </document>
  // </value>
  // ```
  // 当选区从 `hello` 移动到 `world` 时，节点重渲的顺序为
  //   * `container` 因为选中态而重渲：为了保证其内部发生的选区变化也能响应，因此重渲 `container`
  //   * `hello` 选中态变化，重渲 `hello`
  //   * `world` 选中态变化，重渲 `world`
  //   * `cangjie` 选中态未变化，不重渲
  // ```tsx
  // <value>
  //   <document>
  //     <block key="container" type="container" isSelected={true}>
  //       <block key="hello" type="paragraph" isSelected={true}>Hello</block>
  //       <block key="world" type="paragraph">World</block>
  //       <block key="cangjie" type="paragraph">Cangjie</block>
  //     </block>
  //   </document>
  // </value>
  // ```
  // 
  // 2. 考虑节点树及其选中态为：
  // ```tsx
  // <value>
  //   <document>
  //     <block key="container1" type="container" isSelected={true}>
  //       <block key="hello" type="paragraph" isSelected={true}>Hello</block>
  //       <block key="world" type="paragraph"}>World</block>
  //     </block>
  //     <block key="container2" type="container">
  //       <block key="cangjie" type="paragraph">Cangjie</block>
  //     </block>
  //   </document>
  // </value>
  // ```
  // 当选区从 `container1` 移动到 `cangjie` 时，节点重渲的顺序为
  //   * `container1` 因为要取消选中而重渲：为了保证取消选中后，其内部节点的选中态也能取消，因此重渲 `container1`
  //     * `hello` 选中态变化，重渲 `hello`
  //     * `world` 选中态未变化，不重渲 `world`
  //   * `container2` 因为选中态变化重渲：为了保证其内部发生的选区变化也能响应，因此重渲 `container`
  //     * `cangjie` 因为选中态变化而重渲
  // ```tsx
  // <value>
  //   <document>
  //     <block key="container" type="container" isSelected={true}>
  //       <block key="hello" type="paragraph" isSelected={true}>Hello</block>
  //       <block key="world" type="paragraph" isSelected={false}>World</block>
  //       <block key="cangjie" type="paragprah">Cangjie</block>
  //     </block>
  //   </document>
  // </value>
  // ```
  if (n.prevValue?.selection) {
    const shouldRenderSets = checkRerenderNodesBySelectionChange(n.prevValue.selection, n.value.selection, p.document, n.document);
    if (shouldRenderSets.has(n.node.key)) {
      return true;
    }
  }
  if (n.prevValue?.isFocused !== n.value.isFocused) {
    return n.isSelected !== p.isSelected || n.isSelected || p.isSelected;
  }
  return n.isSelected !== p.isSelected;
}

function renderLeaves(parent: Node, node: Text, controller: Controller) {
  const { value } = controller;
  const { composing } = value;
  const { leaves } = node;
  const ls: React.ReactNode[] = [];

  let nextStartOffset = 0;
  for (let i = 0; i < leaves.length; i += 1) {
    const leaf = leaves[i];
    const startOffset = nextStartOffset;
    nextStartOffset += leaf.text.length;
    const hasComposingMark = leaf.marks.some(isComposingMark);
    const text = leaf.text || CANGJIE_EMPTY;
    const children: React.ReactNode[] = [
      hasComposingMark ? <u data-cangjie-composing>{text}</u> : text,
    ];
    if (composing) {
      const marks = leaf.marks.filter(isNotComposingMark);
      while (i + 1 < leaves.length) {
        const nextLeaf = leaves[i + 1];
        const nextHasComposingMark = nextLeaf.marks.some(isComposingMark);
        const marksWithoutComposing = nextHasComposingMark
          ? nextLeaf.marks.filter(isNotComposingMark)
          : nextLeaf.marks;
        if (!equal(marks, marksWithoutComposing)) {
          break;
        }
        i += 1;
        nextStartOffset += nextLeaf.text.length;
        children.push(
          nextHasComposingMark ? (
            <u data-cangjie-composing>{nextLeaf.text}</u>
          ) : (
            nextLeaf.text
          ),
        );
      }
    }
    ls.push(
      renderLeaf(
        controller,
        parent,
        node,
        leaf,
        <React.Fragment>{children}</React.Fragment>,
        startOffset,
      ),
    );
  }

  return ls;
}

const NodeComponent = React.memo(
  (props: NodeProps): JSX.Element | null => {
    const { controller, parent, path, composing, prevValue, visible = true } = props;
    const { value, enablePending, virtualizeManager } = controller;
    const { selection } = value;
    let { node, document } = props;
    const ref = useCangjieKeyBindingRef<HTMLDivElement>(node.key);
    const [pendingElements, updatePendingElements] =
      React.useState<PendingElements>({ document, node });

    // @ts-ignore
    const fold = Block.isBlock(node) ? (useUserData(node, 'fold') || false): false;

    const hidden = useGroupData<boolean>(
      node,
      (groups) => {
        return groups.some(
          (group) => isGroupCollapsed(group) && group.key !== node.key,
        );
      },
      fold,
    );

    const finalVisible = visible !== false && !hidden;
    const {
      node: view,
      document: viewDocument,
      isSelected,
    } = useView(controller, node, document, props.isSelected);
    const { inputData$ } = controller;

    // 监听输入缓冲数据流
    useInputPending<InputData>(
      { node, document },
      path,
      controller,
      enablePending.input,
      inputData$,
      updatePendingElements,
    );

    if (controller.enableVirtualize && virtualizeManager) {
      const { nodeManager: virtualizeNodeManager } = virtualizeManager;
      const path = document.getPath(node.key);
      if (path) {
        const topPath = path.slice(0, 1);
        const topBlock = document.getNodeByPath(topPath) as Block;
        // 是顶层节点
        if (node === topBlock) {
          const virtualizePersistState = virtualizeNodeManager.getVirtualizePersistState(topBlock.key);
          virtualizePersistState.forEach((childNodeKey) => {
            const childNode = document.getNode(childNodeKey);
            if (topBlock.hasNode(childNodeKey) && childNode && controller.query('shouldPersistState', { node: childNode })) {
              virtualizeNodeManager.updateVirtualizePersistState(childNodeKey, topBlock.key, true);
            } else {
              virtualizeNodeManager.updateVirtualizePersistState(childNodeKey, topBlock.key, false);
            }
          });
        }
        const shouldPersistState = controller.query('shouldPersistState', { node });
        if (shouldPersistState) {
          virtualizeNodeManager.updateVirtualizePersistState(node.key, topBlock.key, true);
        }
      }
    }

    // 输入缓冲开始，使用 pending node
    if (
      Block.isLeafBlock(props.node) &&
      controller.isPending &&
      controller.pendingType === PendingType.input
    ) {
      node = pendingElements.node;
      document = pendingElements.document;
    }

    // 如果使用热区技术，使用热区重算的模型
    if (controller.enableHots) {
      node = view;
      document = viewDocument;
    }

    const blockStyle = useNodeStyle(controller, node, finalVisible);

    const children = () => {
      const n = node.toSubType();
      if (n.isElement()) {
        const childrenIsSelected = getChildrenIsSelected(
          document,
          n,
          selection,
        );
        const renderedChildren = n.nodes.map((n: Node, i: number) =>
          renderNode(
            controller,
            value,
            document,
            node,
            n,
            path.concat(i),
            childrenIsSelected[i],
            finalVisible,
            prevValue,
          ),
        );
        return renderedChildren;
      }
      return renderLeaves(parent, n, controller);
    };

    if (Text.isText(node)) {
      if (process.env.NODE_ENV === 'test') {
        // 服务于 Test 环境下对 text 渲染的监听
        controller.run('renderText', { node });
      }
      return <>{children()}</>;
    }

    const isVoid = controller.query('isVoid', node);
    const isNonEmptyBlock = Block.isBlock(node) && !!node.nodes.length;
    const isLeafBlock = isNonEmptyBlock && Block.isLeafBlock(node);
    const isSelectable =
      isNonEmptyBlock && controller.query('isEdgeSelectable', node);
    const binding = {
      ref,
      [Selector.key]: node.key,
    };
    const attributes = {
      key: node.key,
    };
    const attributesWithBinding = { ...attributes, ...binding };

    let nonVoidAttributes: Record<string, any> = attributesWithBinding;
    if (isLeafBlock) {
      nonVoidAttributes = {
        ...nonVoidAttributes,
        style: blockStyle,
        [Selector.leafBlock]: true,
        [Selector.uuid]: controller.query('anchor', node as Element),
      };
    }
    if (isSelectable) {
      nonVoidAttributes = {
        ...nonVoidAttributes,
        style: blockStyle,
        [Selector.uuid]: controller.query('anchor', node as Element),
        [Selector.edgeSelectable]: true,
      };
    }
    const element = controller.run('renderNode', {
      controller,
      attributes: isVoid ? attributes : nonVoidAttributes,
      composing,
      isSelected,
      parent,
      node,
      path,
      children,
      visible: finalVisible,
    });
    if (element === null) {
      return null;
    }
    if (isVoid) {
      return (
        <VoidWrapper {...props} attributes={attributesWithBinding}>
          {element}
        </VoidWrapper>
      );
    }
    if (node.isElement()) {
      const group = controller.groupManager.getGroup(node.key);
      if (group) {
        const props = {
          controller,
          node,
          path,
          group,
          isCollapsed: false,
          isEmpty: isGroupEmpty(controller, { ...group, key: node.key }),
          attributes: {
            [Selector.groupKey]: node.key,
          } as const,
          children: () => element,
          visible: finalVisible,
        };
        const groupElement = controller.run('renderGroup', props);
        if (groupElement) {
          return <GroupWrapper node={node}>{groupElement}</GroupWrapper>;
        }
      }
    }
    return element;
  },
  (prevProps, nextProps) => !shouldComponentUpdate(prevProps, nextProps),
);

function renderLeaf(
  controller: Controller,
  parent: Node,
  text: Text,
  leaf: Leaf,
  child: React.ReactNode,
  offset: number,
) {
  const { composing } = controller.value;
  const leafKey = `${text.key}:${offset}`;
  const initialChild = (
    <LeafComponent key={leafKey} leafKey={leafKey}>
      {child}
    </LeafComponent>
  );

  const children = leaf.marks.reduce(
    (child: React.ReactNode, mark) =>
      controller.run('renderMark', {
        parent,
        node: text,
        offset,
        composing,
        mark,
        marks: leaf.marks,
        text: leaf.text,
        children: () => child,
      }),
    initialChild,
  );

  return children;
}

export function renderNode(
  controller: Controller,
  value: Value,
  document: Document,
  parent: Node,
  node: Node,
  path: Path,
  isSelected: boolean,
  visible = true,
  prevValue?: Value,
) {
  const { composing } = value;

  const comp = (
    <NodeComponent
      key={node.key}
      controller={controller}
      value={value}
      document={document}
      parent={parent}
      node={node}
      path={path}
      composing={composing}
      isSelected={isSelected}
      visible={visible}
      prevValue={prevValue}
    />
  );
  
  return comp;
}

export default NodeComponent;
