import { jsonMLAppend } from '../utils/jsonml';
import createTextWithString from '../utils/createTextWithString';
import { normalizeText } from '../../utils/textUtils';
import { MoSerializationPlugin, JsonML } from '../type';

const NEWLINE = '\n';

export default function createTextToJsonML(plugins: MoSerializationPlugin[]) {
  const rules = plugins
    .map((plugin) => plugin.textToJsonML!)
    .filter((rule) => !!rule);

  /**
   * 纯文本序列化成 ASL
   * @param text
   */
  function textToJsonML(text: string): JsonML {
    return normalizeText(text).split(NEWLINE).reduce(
      (jsonML: JsonML, line: string): JsonML => {
        const rule = rules.find((r) => r.match(line));
        let children: JsonML[];
        if (rule) {
          children = rule.convert(line) || [];
        } else {
          children = [createTextWithString(line)];
        }
        const paragraph = jsonMLAppend(['p'], ...children);
        return jsonMLAppend(jsonML, paragraph);
      },
      ['root'],
    );
  }

  return textToJsonML;
}
