/**
 * @packageDocumentation
 * @module Models
 */
import isPlainObject from 'is-plain-object';
import Decoration from './decoration';
// eslint-disable-next-line @typescript-eslint/no-unused-vars
import Element from './element';
import Node, { NodeProperties } from './node';
import Leaf, { LeafJSON } from './leaf';
import Mark, { MarkProperties } from './mark';
import { Serializable, SerializeOptions } from '../interfaces/serializable';
import keyUtils from '../utils/keyUtils';
import setUtils from '../utils/setUtils';
import listUtils from '../utils/listUtils';

const SUPRESS_NORMALIZE = { normalize: false };
const TEXTS = new WeakMap<Text, string>();

type TextChildren = Leaf[];

export interface TextProperties extends NodeProperties {
  leaves?: TextChildren;
}

export interface TextJSON {
  klass: 'text';
  key?: string;
  leaves: LeafJSON[];
}

export default class Text extends Node
  implements Serializable<TextJSON> {
  static create(properties: Text | TextProperties | string = ''): Text {
    if (Text.isText(properties)) {
      return properties;
    }

    let props = properties;
    if (typeof props === 'string') {
      props = { leaves: [Leaf.create(props)] };
    }
    if (isPlainObject(props)) {
      const { key = keyUtils.create(), leaves = [] } = props;
      return new Text({
        key,
        leaves: leaves.map(Leaf.create),
      });
    }

    throw new Error(
      `\`Text.create\` only accepts objects, strings or texts, but you passed it: ${properties}`,
    );
  }

  static fromJSON(json: TextJSON) {
    return Text.create({
      ...json,
      leaves: json.leaves.map(Leaf.fromJSON),
    });
  }

  static isText(maybeText: any): maybeText is Text {
    return maybeText instanceof Text;
  }

  static isEmptyText(maybeText: any): maybeText is Text {
    return maybeText instanceof Text && !maybeText.text;
  }

  static isTextList(maybeTextList: any): maybeTextList is Text[] {
    return Array.isArray(maybeTextList) && maybeTextList.every(Text.isText);
  }

  protected constructor(props: Required<TextProperties>, options = { normalize: true }) {
    super(props);
    const { leaves } = props;
    this.leaves = options.normalize ? Leaf.normalizeLeaves(leaves) : leaves;
    this.freeze();
  }

  public readonly leaves: TextChildren;

  /**
   * 获取对应的文本string
   */
  public get text() {
    if (process.env.NODE_ENV !== 'test' && TEXTS.has(this)) {
      return TEXTS.get(this)!;
    }
    const t = this.leaves.map(({ text }) => text).join('');
    TEXTS.set(this, t);
    return t;
  }

  /**
   * 获取所有的marks
   */
  public get marks() {
    return this.getMarks();
  }

  /**
   * 获取自身树下的所有 Text 节点
   * 如果自身是 Text，则返回只含有自身的数组
   */
  public getTexts(): Text[] {
    return [this];
  }

  public merge(props: Partial<TextProperties>) {
    return new Text({
      key: this.key,
      leaves: this.leaves,
      ...props,
    });
  }

  public set<T extends keyof TextProperties>(
    key: T,
    value: Required<TextProperties>[T],
  ) {
    return this.merge({
      [key]: value,
    });
  }

  public regenerateKey() {
    return this.set('key', keyUtils.create());
  }

  public getLeaves(decorations?: Decoration[]) {
    if (!decorations || decorations.length === 0) {
      return this.leaves;
    }
    if (this.leaves.length === 0) {
      const marks = decorations.map(({ mark }) => mark);
      const leaf = Leaf.create({ text: '', marks });
      return [leaf];
    }
    if (this.text.length === 0) {
      const firstLeaf = this.leaves[0];
      const marks = decorations.map((d) => d.mark);
      return [firstLeaf.addMarks(marks)];
    }

    const { key, text } = this;
    let { leaves } = this;
    decorations.forEach((dec) => {
      const { start, end, mark } = dec;
      const hasStart = start.key === key;
      const hasEnd = end.key === key;
      if (hasStart || hasEnd) {
        const index = hasStart ? start.offset : 0;
        const length = hasEnd ? end.offset - index : text.length - index;
        if (length < 1) {
          return;
        }
        if (index >= text.length) {
          return;
        }
        if (index !== 0 || length < text.length) {
          const [before, bundle] = Leaf.splitLeaves(leaves, index);
          const [middle, after] = Leaf.splitLeaves(bundle, length);
          leaves = before.concat(
            middle.map((l) => l.addMark(mark)),
            after,
          );
          return;
        }
      }
      leaves = leaves.map((l) => l.addMark(mark));
    });

    if (leaves === this.leaves) {
      return leaves;
    }
    return Leaf.normalizeLeaves(leaves);
  }

  /**
   * 获取所有的marks
   */
  public getMarks() {
    if (this.leaves.length === 0) {
      return [];
    }

    return this.leaves.reduce<Mark[]>(
      (marks, leaf) => setUtils.union(marks, leaf.marks),
      [],
    );
  }

  /**
   * 获取text内所有文字都有的，公共marks
   */
  public getActiveMarks() {
    if (this.leaves.length === 0) {
      return [];
    }

    const firstMarks = this.leaves[0].marks;
    if (firstMarks.length === 0) {
      return firstMarks;
    }

    return this.leaves
      .slice(1)
      .reduce(
        (marks, leaf) => setUtils.intersect(marks, leaf.marks),
        firstMarks,
      );
  }

  /**
   * 根据offset区间，获取marks
   */
  public getMarksBetweenOffsets(startOffset: number, endOffset: number) {
    if (startOffset <= 0 && endOffset >= this.text.length) {
      return this.getMarks();
    }

    if (startOffset >= endOffset) return [];
    // For empty text in a paragraph, use getActiveMarks;
    if (this.text === '') return this.getActiveMarks();

    let result: Mark[] | null = null;
    let leafEnd = 0;
    this.leaves.forEach((leaf) => {
      const leafStart = leafEnd;
      leafEnd = leafStart + leaf.text.length;

      if (leafEnd <= startOffset) return;
      if (leafStart >= endOffset) return;

      if (!result) {
        result = leaf.marks;
        return;
      }

      result = setUtils.union(result, leaf.marks);
    });

    return result || [];
  }

  /**
   * 根据offset区间，获取区间内所有文字都含有的，公共marks
   */
  public getActiveMarksBetweenOffsets(startOffset: number, endOffset: number) {
    if (startOffset <= 0 && endOffset >= this.text.length) {
      return this.getActiveMarks();
    }

    if (startOffset >= endOffset) return [];
    // For empty text in a paragraph, use getActiveMarks;
    if (this.text === '') return this.getActiveMarks();

    let result: Mark[] = [];
    let leafEnd = 0;
    let foundNoMarks = false;

    this.leaves.forEach((leaf) => {
      const leafStart = leafEnd;
      leafEnd = leafStart + leaf.text.length;

      if (leafEnd <= startOffset) return;
      if (leafStart >= endOffset) return;

      foundNoMarks = foundNoMarks || !leaf.marks.length;
      if (!result.length) {
        result = leaf.marks;
        return;
      }

      result = setUtils.intersect(result, leaf.marks);
    });

    if (foundNoMarks && result.length) {
      // aaaBBB: a has no marks, B has marks, so they have no intersected marks
      result = [];
    }

    return result || [];
  }

  /**
   * 获取第index个文字，对应的marks
   */
  public getMarksAtIndex(index: number) {
    const leafObj = Leaf.searchLeafAtOffset(this.leaves, index);
    return !leafObj ? [] : leafObj.leaf.marks;
  }

  /**
   * 拷贝出一个新的text节点，并在指定的位置添加mark
   * 如果offset不合法，则返回自身
   */
  public addMark(offset: number, length: number, mark: Mark) {
    return this.addMarks(offset, length, [mark]);
  }

  /**
   * 拷贝出一个新的text节点，并在指定的位置添加marks。
   * 如果marks是0，或者offset不合法，则返回自身
   */
  public addMarks(offset: number, length: number, marks: Mark[]) {
    if (this.text === '' && offset === 0) {
      const { leaves } = this;
      const first = leaves[0];
      if (!first) {
        return this.set('leaves', [Leaf.create({ text: '', marks })]);
      }

      const newFirst = first.addMarks(marks);
      if (newFirst === first) {
        return this;
      }
      return this.set('leaves', [newFirst]);
    }

    if (length === 0 || offset >= this.text.length) {
      return this;
    }

    const [before, bundle] = Leaf.splitLeaves(this.leaves, offset);
    const [middle, after] = Leaf.splitLeaves(bundle, length);
    return this.set('leaves', [
      ...before,
      ...middle.map((leaf) => leaf.addMarks(marks)),
      ...after,
    ]);
  }

  /**
   * 拷贝出一个新的text节点，并在指定的位置过滤掉对应的mark
   * 如果当前没有这个mark，或者offset不合法，则返回自身
   */
  public removeMark(offset: number, length: number, mark: Mark) {
    if (this.text === '' && offset === 0) {
      const { leaves } = this;
      const first = leaves[0];
      if (!first) {
        return this;
      }

      const newFirst = first.removeMark(mark);
      if (newFirst === first) {
        return this;
      }
      return this.set('leaves', [newFirst]);
    }

    if (length === 0 || offset >= this.text.length) {
      return this;
    }

    const [before, bundle] = Leaf.splitLeaves(this.leaves, offset);
    const [middle, after] = Leaf.splitLeaves(bundle, length);
    return this.set('leaves', [
      ...before,
      ...middle.map((leaf) => leaf.removeMark(mark)),
      ...after,
    ]);
  }

  /**
   * 在指定的区间，设置一个新的Mark
   * 如果指定区间不合法，或者区间内已经存在同样的mark，则返回自身
   */
  public setMark(
    offset: number,
    length: number,
    mark: Mark,
    properties: Partial<MarkProperties>,
  ) {
    const newMark = mark.merge(properties);

    if (this.text === '' && offset === 0 && length === 0) {
      const { leaves } = this;
      const first = leaves[0];
      if (!first) {
        return this;
      }

      const newFirst = first.setMark(mark, newMark);
      if (newFirst === first) {
        return this;
      }
      return this.set('leaves', [newFirst]);
    }

    if (length === 0 || offset >= this.text.length) {
      return this;
    }

    const [before, bundle] = Leaf.splitLeaves(this.leaves, offset);
    const [middle, after] = Leaf.splitLeaves(bundle, length);
    return this.set('leaves', [
      ...before,
      ...middle.map((leaf) => leaf.setMark(mark, newMark)),
      ...after,
    ]);
  }

  /**
   * 复制出一个新的text，并在指定的位置，插入对应的文字和marks
   */
  public insertText(offset: number, text: string, marks: Mark[] = []) {
    if (this.text === '') {
      return this.set('leaves', [Leaf.create({ text, marks })]);
    }
    if (text.length === 0) {
      return this;
    }

    const { leaves } = this;
    const leafObj = Leaf.searchLeafAtOffset(
      leaves,
      offset,
    );
    if (!leafObj) {
      throw new Error(`Text.insertText, could not find leaf with offset: ${offset}`);
    }
    const { index, leaf, startOffset } = leafObj;
    const delta = offset - startOffset;
    const beforeText = leaf.text.slice(0, delta);
    const afterText = leaf.text.slice(delta);

    const newLeaves = [...leaves];
    if (listUtils.hasSameMembers(leaf.marks, marks)) {
      const newLeaf = leaf.set('text', beforeText + text + afterText);
      newLeaves[index] = newLeaf;
      return this.set('leaves', newLeaves);
    }
    newLeaves.splice(
      index,
      1,
      leaf.set('text', beforeText),
      Leaf.create({ text, marks }),
      leaf.set('text', afterText),
    );
    return this.set('leaves', newLeaves);
  }

  /**
   * 在指定的位置，删掉指定的text长度的文字
   * 本杰：TODO: check whether deleted text is same as text?
   */
  public removeText(offset: number, text: string) {
    if (text.length === 0 || offset >= this.text.length) {
      return this;
    }

    // PERF: For simple backspace, we can operate directly on the leaf
    if (text.length === 1) {
      const leafObj = Leaf.searchLeafAtOffset(
        this.leaves,
        offset + 1,
      );
      if (!leafObj) {
        throw new Error(`Text.removeText, could not find leaf with offset: ${offset}`);
      }
      const { index, leaf, startOffset } = leafObj;
      const newLeaves = [...this.leaves];

      if (leaf.text.length === 1) {
        if (newLeaves.length === 1) {
          // keep marks when delete the last leaf
          newLeaves[0] = Leaf.create({
            text: '',
            marks: leaf.marks,
          });
        } else {
          newLeaves.splice(index, 1);
        }
        // PERF: Avoid normalizing twice for normalized `leaves`.
        return new Text(
          { key: this.key, leaves: newLeaves },
          SUPRESS_NORMALIZE,
        );
      }

      const delta = offset - startOffset;
      const beforeText = leaf.text.slice(0, delta);
      const afterText = leaf.text.slice(delta + text.length);
      const newText = beforeText + afterText;
      newLeaves[index] = leaf.set('text', newText);
      return new Text(
        { key: this.key, leaves: newLeaves},
        SUPRESS_NORMALIZE,
      );
    }

    const [before, bundle] = Leaf.splitLeaves(this.leaves, offset);
    const after = Leaf.splitLeaves(bundle, text.length)[1];
    // PERF: reduce `leaves` to be normalized.
    const middle = Leaf.normalizeLeaves([...before.slice(-1), ...after.slice(0, 1)]);
    const leaves = [
      ...before.slice(0, -1),
      ...middle,
      ...after.slice(1),
    ];

    if (leaves.length === 1) {
      const first = leaves[0];
      if (first.text === '') {
        return new Text(
          { key: this.key, leaves: [first.set('marks', this.getActiveMarks())] },
          SUPRESS_NORMALIZE,
        );
      }
    }

    return new Text(
      { key: this.key, leaves },
      SUPRESS_NORMALIZE,
    );
  }

  /**
   * 拷贝出一个新的text，并把传入的text的leaves添加上去
   */
  public mergeText(other: Text) {
    const leaves = this.leaves.concat(other.leaves);
    return this.set('leaves', leaves);
  }

  /**
   * 在指定的位置做一个split，返回split后的两个text
   */
  public splitText(position: number) {
    const [befores, afters] = Leaf.splitLeaves(this.leaves, position);
    const a = this.set('leaves', befores);
    const b = this.set('leaves', afters).regenerateKey();
    return [a, b];
  }

  public toJSON(options?: SerializeOptions) {
    const object: TextJSON = {
      klass: 'text',
      leaves: this.leaves.map((leaf) => leaf.toJSON()),
    };
    if (options?.preserveKeys) {
      object.key = this.key;
    }
    return object;
  }
}
