/**
 * @packageDocumentation
 * @module Models
 */
import Selection from '../models/selection';
import Data from '../interfaces/data';
import Path from './path';
import Node from './node';
import Mark from './mark';

export interface Properties {
  type?: string;
  data?: Data;
}

export enum OperationType {
  AddMark = 'add_mark',
  InsertNode = 'insert_node',
  InsertText = 'insert_text',
  MergeNode = 'merge_node',
  MoveNode = 'move_node',
  RemoveMark = 'remove_mark',
  RemoveNode = 'remove_node',
  RemoveText = 'remove_text',
  SetMark = 'set_mark',
  SetNode = 'set_node',
  SetSelection = 'set_selection',
  SplitNode = 'split_node',
}

interface OperationWithContext {
  context?: string;
}

export interface AddMarkOperation extends OperationWithContext {
  type: OperationType.AddMark;
  path: Path;
  offset: number;
  length: number;
  mark: Mark;
}

export interface InsertNodeOperation extends OperationWithContext {
  type: OperationType.InsertNode;
  path: Path;
  node: Node;
}

export interface InsertTextOperation extends OperationWithContext {
  type: OperationType.InsertText;
  path: Path;
  offset: number;
  text: string;
  marks?: Mark[];
}

export interface MergeNodeOperation extends OperationWithContext {
  type: OperationType.MergeNode;
  path: Path;
  position: number;
  properties: Properties;
}

export interface MoveNodeOperation extends OperationWithContext {
  type: OperationType.MoveNode;
  path: Path;
  newPath: Path;
}

export interface RemoveMarkOperation extends Omit<AddMarkOperation, 'type'> {
  type: OperationType.RemoveMark;
}

export interface RemoveNodeOperation extends Omit<InsertNodeOperation, 'type'> {
  type: OperationType.RemoveNode;
}

export interface RemoveTextOperation extends Omit<InsertTextOperation, 'type'> {
  type: OperationType.RemoveText;
}

export interface SetMarkOperation extends OperationWithContext {
  type: OperationType.SetMark;
  path: Path;
  offset: number;
  length: number;
  mark: Mark;
  properties: Properties;
}

export interface SetNodeOperation extends OperationWithContext {
  type: OperationType.SetNode;
  path: Path;
  properties: Properties;
  prevProperties: Properties;
}

export interface SetSelectionOperation extends OperationWithContext {
  type: OperationType.SetSelection;
  selection: Selection;
  prevSelection: Selection;
}

export interface SplitNodeOperation extends Omit<MergeNodeOperation, 'type'> {
  type: OperationType.SplitNode;
}

type Operation =
  | AddMarkOperation
  | InsertNodeOperation
  | InsertTextOperation
  | MergeNodeOperation
  | MoveNodeOperation
  | RemoveMarkOperation
  | RemoveNodeOperation
  | RemoveTextOperation
  | SetMarkOperation
  | SetNodeOperation
  | SetSelectionOperation
  | SplitNodeOperation;

export type NonSetSelectionOperation = Exclude<
  Operation,
  SetSelectionOperation
>;

/**
 * op取反
 *
 * move_node 路径矫正说明：
 *
 * Given the document tree:
 * ```txt
 *         []
 *        / \
 *     [0]   [1]
 *            |
 *          [1,0]
 *            |
 *         [1,0,0]
 *         __/ \__
 * [1,0,0,0]      [1,0,0,1]
 * ```
 * case one:
 * ```ts
 * op.path = [0]
 * op.newPath = [1,0,0,1]
 * Path.isYounger([0], [1,0,0,1])   // true
 * // when path is been moved, the new path should decrement
 * Path.decrement([1,0,0,1], 1, 0) // [0,0,0,1]
 * // final inverted op: [0,0,0,1] -> [0]
 * ```
 * case two:
 * ```ts
 * op.path = [1,0,0,1]
 * op.newPath = [1]
 * Path.isAncestor([1], [1,0,0,1])   // true
 * // when move node to it's ancestor, the new path should increment
 * Path.increment([1,0,0,1], 1, 0) // [2,0,0,1]
 * // final inverted op: [2,0,0,1] -> [1]
 * ```
 */
function invert(operation: Operation): Operation {
  const { type } = operation;
  if (type === 'add_mark') {
    return {
      ...operation,
      type: 'remove_mark',
    } as RemoveMarkOperation;
  }
  if (type === 'insert_node') {
    return {
      ...operation,
      type: 'remove_node',
    } as RemoveNodeOperation;
  }
  if (type === 'insert_text') {
    return {
      ...operation,
      type: 'remove_text',
    } as RemoveTextOperation;
  }
  if (type === 'merge_node') {
    const { path } = operation as MergeNodeOperation;
    const invertedPath = Path.decrement(path);
    return {
      ...operation,
      type: 'split_node',
      path: invertedPath,
    } as SplitNodeOperation;
  }
  if (type === 'move_node') {
    const { path, newPath } = operation as MoveNodeOperation;
    if (Path.isEqual(path, newPath)) {
      return operation;
    }

    let invertedPath = newPath;
    let invertedNewPath = path;

    const isSibling = Path.isSibling(path, newPath);
    // If the node's old position was a left sibling of an ancestor of
    // its new position, we need to adjust part of the path by -1.
    // If the node's new position is an ancestor of the old position,
    // or a left sibling of an ancestor of its old position, we need
    // to adjust part of the path by 1.
    if (!isSibling) {
      const isYounger = Path.isYounger(path, newPath);
      const isInvertedYounger = Path.isYounger(invertedPath, invertedNewPath);
      const isInvertedAncestor = Path.isAncestor(invertedPath, invertedNewPath);

      if (isYounger) {
        invertedPath = Path.decrement(newPath, 1, path.length - 1);
      } else if (isInvertedYounger || isInvertedAncestor) {
        invertedNewPath = Path.increment(path, 1, newPath.length - 1);
      }
    }

    return {
      ...operation,
      path: invertedPath,
      newPath: invertedNewPath,
    } as MoveNodeOperation;
  }
  if (type === 'remove_mark') {
    return {
      ...operation,
      type: 'add_mark',
    } as AddMarkOperation;
  }
  if (type === 'remove_node') {
    return {
      ...operation,
      type: 'insert_node',
    } as InsertNodeOperation;
  }
  if (type === 'remove_text') {
    return {
      ...operation,
      type: 'insert_text',
    } as InsertTextOperation;
  }
  if (type === 'set_mark') {
    const { mark, properties } = operation as SetMarkOperation;
    const invertedMark = mark.merge(properties);
    const invertedProperties = Object.keys(properties).reduce(
      (p, key) => ({ ...p, [key]: mark[key] }),
      {},
    );
    return {
      ...operation,
      mark: invertedMark,
      properties: invertedProperties,
    } as SetMarkOperation;
  }
  if (type === 'set_node') {
    const { properties, prevProperties } = operation as SetNodeOperation;
    return {
      ...operation,
      properties: prevProperties || {},
      prevProperties: properties,
    } as SetNodeOperation;
  }
  if (type === 'set_selection') {
    const { selection, prevSelection } = operation as SetSelectionOperation;
    return {
      ...operation,
      selection: prevSelection,
      prevSelection: selection,
    } as SetSelectionOperation;
  }
  if (type === 'split_node') {
    const { path } = operation as SplitNodeOperation;
    const invertedPath = Path.increment(path);
    return {
      ...operation,
      type: 'merge_node',
      path: invertedPath,
    } as MergeNodeOperation;
  }
  throw new Error(`Unknown operation type: "${type}".`);
}

const Operation = {
  invert,
};

// eslint-disable-next-line no-undef
export default Operation;
