import { isRectOnSameLineOfRect } from '../../../utils/dom/caretRangeFromTargetAndCoord';
import { mergeRects } from '../../../utils/dom/optimizeRectsToFitLineHeight';

export interface Rect {
  left: number;
  top: number;
  width: number;
  height: number;
}

export interface DOMRectWithLine extends DOMRect {
  lineId?: string;
}

interface LineSize {
  top: number;
  bottom: number;
}

// 复用选区定位逻辑中对同一行的判断来进行分行
const isOverlapped = (a: LineSize, b: LineSize) => {
  return isRectOnSameLineOfRect({
    ...a,
    height: a.bottom - a.top,
  }, {
    ...b,
    height: b.bottom - b.top,
  });
};

const getMaxLineSize = (a: LineSize, b: LineSize) => ({
  top: Math.min(a.top, b.top),
  bottom: Math.max(a.bottom, b.bottom),
});

// 扩展行高，填充行之间的空隙
function extendToFillBetweenLines(lines: LineSize[], startRects: LineSize[] = [], endRects: LineSize[] = []) {
  const length = lines.length;
  if (length === 0) {
    return [];
  }
  let first = lines[0];
  let last = lines[length - 1];
  // 用选区 start 所在 block 的 rects 扩展第一行的高度
  for (const rect of startRects) {
    if (rect && isOverlapped(rect, first)) {
      first = getMaxLineSize(rect, first);
    }
  }
  // 用选区 end 所在 block 的 rects 扩展最后一行的高度
  for (const rect of endRects) {
    if (rect && isOverlapped(rect, last)) {
      last = getMaxLineSize(rect, last);
    }
  }

  const filled = [first, ...lines.slice(1, length - 1), last];

  for (let i = 1; i < length; i++) {
    const prev = filled[i - 1];
    const now = filled[i];
    const margin = now.top - prev.bottom;
    prev.bottom += margin / 2;
    now.top -= margin / 2;
  }
  return filled;
}

function splitRectsIntoLines(rects: DOMRectWithLine[]): [DOMRectWithLine[][], LineSize[]] {
  // 将 rects 以从上到下、从左到右排序 - O(NlogN)
  const sortedRects = rects.filter((v) => v.width).sort((a, b) => (a.top - b.top) || (a.left - b.left));
  const lines: DOMRectWithLine[][] = [];
  const lineSizes: LineSize[] = [];
  // 将 sortedRects 按顺序归纳到每一行中，并记录每行的尺寸 - O(N)
  let currentIndex = 0;
  sortedRects.forEach((rect) => {
    if (lines.length === 0) {
      lines.push([rect]);
      lineSizes.push({ top: rect.top, bottom: rect.bottom });
      return;
    }

    // 新的一行
    if (!isOverlapped(rect, lineSizes[currentIndex]) || rect.lineId !== lines[currentIndex][0].lineId) {
      lines.push([rect]);
      lineSizes.push({ top: rect.top, bottom: rect.bottom });
      currentIndex += 1;
      return;
    }
    // 属于当前行
    lines[currentIndex].push(rect);
    lineSizes[currentIndex] = getMaxLineSize(rect, lineSizes[currentIndex]);
  });
  return [lines, lineSizes];
}

export function antiAliasingRects(rects: DOMRectWithLine[], startRects: DOMRect[] = [], endRects: DOMRect[] = []) {
  // 将 rects 以从上到下、从左到右排序，并记录每行的尺寸 - O(NlogN)（N 为 rect 个数）
  const [lines, lineSizes] = splitRectsIntoLines(rects);
  const startSizes = extendToFillBetweenLines(splitRectsIntoLines(startRects)[1]);
  const endSizes = extendToFillBetweenLines(splitRectsIntoLines(endRects)[1]);

  // 填充行之间的空隙：获得每行填充后的尺寸 - O(M)（M 为行数）
  const filledSizes = extendToFillBetweenLines(lineSizes, startSizes, endSizes);

  // 填充行之间的空隙：将 rects 更新为填充后的尺寸并合并相邻的 - ~O(N)
  const result: Rect[] = [];
  lines.forEach((lineRects, index) => {
    const top = filledSizes[index].top;
    const height = filledSizes[index].bottom - top;
    const fitted = lineRects.map((rect) => ({
      top,
      height,
      left: rect.left,
      width: rect.width,
    }));
    const merged = mergeRects(fitted);
    result.push(...merged);
  });
  return result;
}