import React from 'react';
import ReactDOM from 'react-dom';
import Controller from '../../controller';
import Selection from '../../models/selection';
import Caret from './caret';
import Range from './range';
import { CaretAppearance } from './types';
import { Rect } from './utils/antiAliasingRects';
import usePerf from '../../hooks/usePerf';

export interface Appearance {
  // 是否为远程选区
  isRemote: boolean;
  // 原始 selection
  selection: Selection;
  // selection 对应的某一部分的外观
  appearance: CaretAppearance | Rect[];
  // 本地光标颜色
  // perf: 由于本地光标颜色计算依赖于目标 dom 的背景色，所以需要在计算位置时计算
  color?: string;
}

export type SelectionAppearances = Map<
  HTMLDivElement,
  Appearance[]
>

interface SelectionRendererProps {
  appearances: SelectionAppearances;
  isFocused: boolean;
  controller: Controller;
}

const SelectionRenderer = React.memo((props: SelectionRendererProps) => {
  const { appearances, isFocused, controller } = props;
  const prevSelectionRef = React.useRef<Selection | null>(null);
  const [globalStyle, setGlobalStyle] = React.useState<string | null>(null);

  usePerf(controller, prevSelectionRef.current === controller.value.selection);

  React.useEffect(() => {
    const style = controller.query('selectionGlobalStyle');
    setGlobalStyle(style);
  }, []);

  const wrappers = Array.from(appearances.keys());
  return (
    <>
      {wrappers.map((wrapper) => {
        if (!wrapper) {
          return null;
        }
        const info = appearances.get(wrapper)!;
        let hasRemote = false;
        const content = (
          <>
            {info.map(({ appearance, isRemote, selection, color }, index) => {
              hasRemote = hasRemote || isRemote;
              return (
                Array.isArray(appearance)
                ? (
                  <Range
                    key={`range-${index}`}
                    rects={appearance}
                    isRemote={isRemote}
                    isFocused={isFocused}
                    controller={controller}
                    selection={selection}
                  />
                ) : (
                  <Caret
                    key={`caret-${index}`}
                    color={color}
                    isRemote={isRemote}
                    isFocused={isFocused}
                    controller={controller}
                    selection={selection}
                    position={appearance}
                  />
                )
              )
            })}
            {hasRemote && <style>{globalStyle}</style>}
          </>
        );
        return ReactDOM.createPortal(content, wrapper);
      })}
    </>
  )
});

export default SelectionRenderer;
