import React from 'react';
import root from 'react-shadow';

let nativeSupport = false
// 仓颉代码会执行在 node 里，这里做一下防护
if (typeof Element !== 'undefined' && typeof Element.prototype !== 'undefined') {
  nativeSupport = 'attachShadow' in Element.prototype && typeof Element.prototype.attachShadow === 'function';
}

interface ShadowDomProps {
  attributes?: Record<string, any>;
}

export function ShadowRoot(props: React.PropsWithChildren<ShadowDomProps>) {
  if (!nativeSupport) {
    return (
      <div {...props.attributes}>
        {props.children}
      </div>
    );
  }
  return (
    <root.div {...props.attributes}>
      {props.children}
    </root.div>
  );
}
