/* eslint-disable no-param-reassign */
/**
 * @packageDocumentation
 * @ignore
 */
import Controller from '../controller/index';
import Selection from '../models/selection';
import { del } from './delete';

export function deleteExpanded(controller: Controller) {
  const { value } = controller;
  const { selection } = value;

  if (selection.isExpanded) {
    controller.command(del);
  }
}

/**
 * 删除选区的内容；返回一个拷贝选区，并定位到原选区的start位置
 */
export function deleteExpandedAtRange(
  controller: Controller,
  range: Selection,
): Selection {
  const { value } = controller;
  const { document } = value;
  const { start, end } = range.sort(document);
  if (range.isExpanded) {
    controller.command('deleteAtRange', range);
  }

  const { document: newDocument } = controller.value;
  if (newDocument.getNode(start.key)) {
    range = range.merge({
      anchor: start,
      focus: start,
    });
  } else {
    const point = end.isTextPoint() ? end.set('offset', 0) : end;
    range = range.merge({
      anchor: point,
      focus: point,
    });
  }

  return range;
}
