import { debounce } from 'lodash-es';
import Controller from './index';
import { EnablePendingConfig, EnablePending, PendingType, OperationWithValue } from '../interfaces/controller';
import {
  GetPendingParams,
  Pending,
  PendingGetter,
} from '../interfaces/pending';
import Value from '../models/value';
import getPending from './pendings';

export interface PendingQueueProps {
  controller: Controller;
  debounceTime: number;
  pendingGetter?: PendingGetter<any>;
  enablePending: EnablePendingConfig;
}

export default class PendingQueue {
  public static create(props: PendingQueueProps) {
    return new PendingQueue(props);
  }

  private operations: OperationWithValue[] = [];
  private pending: Pending<any> | null = null;
  private ctx: any;
  private pendingGetter: PendingGetter<any>;
  private debouncedFlush: ReturnType<typeof debounce>;
  private controller: Controller;
  public value: Value | null = null;
  public enablePending: EnablePending;

  constructor(props: PendingQueueProps) {
    const {
      debounceTime,
      controller,
      pendingGetter = getPending,
      enablePending,
    } = props;

    this.pendingGetter = pendingGetter;
    this.controller = controller;
    this.debouncedFlush = debounce(this.flush, debounceTime);

    if (typeof enablePending === 'boolean') {
      this.enablePending = Object.values(PendingType).reduce((result, key) => {
        result[key] = enablePending;
        return result;
      }, {});
    } else {
      this.enablePending = enablePending;
    }
  }

  private get shouldDebouncedFlush() {
    // 需要周期性倾倒 pending queue 需满足以下条件之一：
    // 1. 如果驻留有 operation
    // 2. 如果驻留有堆 isFocused 的设置
    return this.operations.some(({ operation, key }) => (
      operation !== null
      || key === 'isFocused'
    ));
  }

  private run(operations: OperationWithValue[]) {
    const { effect } = this.pending!;
    this.value = this.controller.value;
    effect(operations, this.ctx);
    if (this.shouldDebouncedFlush) {
      this.debouncedFlush();
    }
  }

  private flush = () => {
    this.controller.flush();
    this.clear();
  };

  private isType(type: string) {
    return this.pending?.type === type;
  }

  public get type() {
    return this.pending?.type;
  }

  public queue(operations: OperationWithValue[]) {
    const params: GetPendingParams = {
      controller: this.controller,
      ops: operations,
    };
    const pending = this.pendingGetter(params);
    if (!pending || !this.enablePending[pending.type]) {
      // 若 operations 不可 pending，则立即倾倒
      this.flush();
      this.controller.dispatchPendingTerminal();
    } else {
      const { decorate = () => null, shouldAppend = () => true, type } = pending;
      if (this.isType(type) && shouldAppend(this.operations, operations)) {
        // 如果是同类型且可合并的 pending，则合并
        Array.prototype.push.apply(this.operations, operations);
        this.run(operations);
      } else if (!this.pending) {
        // 如果 pending queue 不存在 pending 对象，则创建
        this.pending = pending;
        this.ctx = decorate(operations);
        this.operations = operations;
        this.run(operations);
      } else {
        // 若产生了不能合并的 pending，则立即倾倒 pending queue
        this.flush();
        this.controller.dispatchPendingTerminal();
      }
    }
    return this;
  }

  public clear() {
    this.pending = null;
    this.operations = [];
    this.debouncedFlush.cancel();
    return this;
  }

  public isEmpty() {
    return this.size() === 0;
  }

  public size() {
    return this.operations.length;
  }
}
