import * as React from 'react';
import * as ReactDOM from 'react-dom';
import { ControllerContext } from '../controller';
import { CONTROLLER_TO_KEY_DOM } from '../utils/dom/getElementByCangjieKey';

export default function useCangjieKeyBindingRef<T extends HTMLElement>(key: string) {
  const ref = React.useRef<T>(null);
  const { controller } = React.useContext(ControllerContext)!;
  React.useLayoutEffect(() => {
    if (!CONTROLLER_TO_KEY_DOM.has(controller)) {
      CONTROLLER_TO_KEY_DOM.set(controller, new Map());
    }
    const KEY_DOM = CONTROLLER_TO_KEY_DOM.get(controller)!;
    if (ref.current) {
      const dom = ReactDOM.findDOMNode(ref.current) as HTMLElement;
      KEY_DOM.set(key, dom);
    } else {
      KEY_DOM.delete(key);
    }
  });
  return ref;
}
