/**
 * @packageDocumentation
 * @module Commands
 */
import Controller from '../controller/index';
import Mark from '../models/mark';
import Selection from '../models/selection';
import Text from '../models/text';
import Path from '../models/path';
import setUtils from '../utils/setUtils';
import { OperationType } from '../models/operation';

/**
 * 在当前选区，添加mark
 * 1. 如果选区是expand，会设置当前选中内容的mark
 * 2. 如果选区是collapse，会把m和当前的选区的marks，一起合并到当前selection内
 */
export function addMark(controller: Controller, m: Mark) {
  const mark = Mark.create(m);
  const { value } = controller;
  const { document, selection } = value;

  if (selection.isExpanded) {
    controller.command(addMarkAtRange, selection, mark);
  } else {
    const activeMarks =
      selection.marks || document.getActiveMarksAtRange(selection);
    const sel = selection.set('marks', setUtils.add(activeMarks, mark));
    controller.command('select', sel);
  }

  return controller;
}

/**
 * 对指定的选区内，增加mark
 * 1. 如果选区是collapse，则什么都不做
 */
export function addMarkAtRange(
  controller: Controller,
  range: Selection,
  mark: Mark,
) {
  if (range.isCollapsed) return controller;

  const { value } = controller;
  const { document } = value;
  const { start, end } = range.convertToTextPoints(document);
  const texts = document.getTextsAtRange(range);
  controller.withoutNormalizing(() => {
    texts.forEach((node) => {
      const { key } = node;
      let offset = 0;
      let { length } = node.text;

      if (key === start.key) ({ offset } = start);
      if (key === end.key) length = end.offset;
      if (key === start.key && key === end.key) {
        length = end.offset - start.offset;
      }

      controller.command(addMarkByKey, key, offset, length, mark);
    });
  });
  return controller;
}

/**
 * 对指定的选区内，增加mark
 * 1. 如果选区是collapse，则什么都不做
 */
export function addMarkByKey(
  controller: Controller,
  key: string,
  offset: number,
  length: number,
  mark: Mark,
) {
  const { value } = controller;
  const { document } = value;
  const path = document.assertPath(key);
  return controller.command(addMarkByPath, path, offset, length, mark);
}

/**
 * 对一个text节点的一部分文字，增加mark
 * 1. 如果目标无效，或者不是一个text，则什么都不做
 */
export function addMarkByPath(
  controller: Controller,
  path: Path,
  offset: number,
  length: number,
  mark: Mark,
) {
  const node = controller.value.document.getNodeByPath(path);
  if (!node || !Text.isText(node)) return controller;

  // Text start offset
  const bx = offset;
  // Text end offset
  const by = offset + length;
  // Current Leaf start offset
  let o = 0;

  node.leaves.forEach((leaf) => {
    // Leaf start offset
    const ax = o;
    // Leaf end offset
    const ay = ax + leaf.text.length;
    // 叠加偏移量
    o += leaf.text.length;

    // If the leaf doesn't overlap with the operation, continue on.
    if (ay < bx || by < ax) return;

    // If the leaf already has the mark, continue on.
    if (leaf.marks.find((m) => m.isEqual(mark))) return;

    // Otherwise, determine which offset and characters overlap.
    const start = Math.max(ax, bx);
    const end = Math.min(ay, by);

    controller.applyOperation({
      type: OperationType.AddMark,
      path,
      offset: start,
      length: end - start,
      mark,
    });
  });

  return controller;
}

/**
 * 在当前选区，添加marks
 * 1. 如果选区是expand，会设置当前选中内容的marks
 * 2. 如果选区是collapse，会把marks和当前的选区的marks，一起合并到当前selection内
 */
export function addMarks(controller: Controller, marks: Mark[]) {
  marks.forEach((mark) => controller.command(addMark, mark));
  return controller;
}

/**
 * 对指定的选区内，增加marks
 * 1. 如果选区是collapse，则什么都不做
 */
export function addMarksAtRange(
  controller: Controller,
  range: Selection,
  marks: Mark[],
) {
  marks.forEach((mark) => controller.command(addMarkAtRange, range, mark));
  return controller;
}

/**
 * 在当前的选区内，toggleMark
 * 1. 如果选区是expand，会设置到当前选中内容
 * 2. 如果选区是collapse，会设置到当前的selection
 */
export function toggleMark(controller: Controller, m: Mark) {
  const mark = Mark.create(m);
  const { value } = controller;
  const { selection, document } = value;

  if (selection.isExpanded) {
    return controller.command(toggleMarkAtRange, selection, mark);
  }
  const activeMarks =
    selection.marks || document.getActiveMarksAtRange(selection);
  const newMarks = setUtils.has(activeMarks, mark)
    ? setUtils.remove(activeMarks, mark)
    : setUtils.add(activeMarks, mark);

  const sel = selection.set('marks', newMarks);
  return controller.command('select', sel);
}

/**
 * 在指定的选区内，toggleMark
 * 1. 如果选区是collapse，什么都不做
 */
export function toggleMarkAtRange(
  controller: Controller,
  range: Selection,
  m: Mark,
) {
  if (range.isCollapsed) {
    return controller;
  }

  const mark = Mark.create(m);
  const { value } = controller;
  const { document } = value;
  const activeMarks = document.getActiveMarksAtRange(range);
  if (setUtils.has(activeMarks, mark)) {
    controller.command(removeMarkAtRange, range, mark);
  } else {
    controller.command(addMarkAtRange, range, mark);
  }
  return controller;
}

/**
 * 在当前的选区内，删除mark
 * 1. 如果选区是expand，会设删除当前选中内容的mark
 * 2. 如果选区是collapse，会删除当前的selection的mark
 */
export function removeMark(controller: Controller, m: Mark) {
  const mark = Mark.create(m);
  const { value } = controller;
  const { document, selection } = value;

  if (selection.isExpanded) {
    return controller.command(removeMarkAtRange, selection, mark);
  }
  const activeMarks =
    selection.marks || document.getActiveMarksAtRange(selection);
  const sel = selection.set('marks', setUtils.remove(activeMarks, mark));
  return controller.command('select', sel);
}

/**
 * 在指定的选区内，删除mark
 * 1. 如果选区是collapse，什么都不做
 */
export function removeMarkAtRange(
  controller: Controller,
  range: Selection,
  mark: Mark,
) {
  if (range.isCollapsed) return controller;

  const { value } = controller;
  const { document } = value;
  const { start, end } = range.convertToTextPoints(document);
  const texts = document.getTextsAtRange(range);
  controller.withoutNormalizing(() => {
    texts.forEach((node) => {
      const { key } = node;
      let offset = 0;
      let { length } = node.text;

      if (key === start.key) ({ offset } = start);
      if (key === end.key) length = end.offset;
      if (key === start.key && key === end.key) {
        length = end.offset - start.offset;
      }

      controller.command(removeMarkByKey, key, offset, length, mark);
    });
  });
  return controller;
}

/**
 * 对指定的节点，删除mark
 * 1. 如果指定的节点无效，会抛异常
 */
export function removeMarkByKey(
  controller: Controller,
  key: string,
  offset: number,
  length: number,
  mark: Mark,
) {
  const { value } = controller;
  const { document } = value;
  const path = document.assertPath(key);
  return controller.command(removeMarkByPath, path, offset, length, mark);
}

/**
 * 对指定的节点，删除mark
 * 1. 如果指定的节点无效，会抛异常
 */
export function removeMarkByPath(
  controller: Controller,
  path: Path,
  offset: number,
  length: number,
  mark: Mark,
) {
  const text = controller.value.document.getNodeByPath(path) as Text;
  let totalOffset = 0;
  text.leaves.forEach(leaf => {
    // current start offset
    const o = totalOffset;
    const leafLength = leaf.text.length;
    totalOffset += leafLength;

    if (o + leafLength < offset || o > offset + length) return;

    if (!setUtils.has(leaf.marks, mark)) return;

    const start = Math.max(offset, o);
    const end = Math.min(offset + length, o + leafLength);
    controller.applyOperation({
      type: OperationType.RemoveMark,
      path,
      offset: start,
      length: end - start,
      mark,
    });
  });
  return controller;
}

/**
 * 对指定的节点，替换或者添加mark
 * 1. 如果指定的节点无效，会抛异常
 */
export function replaceMark(
  controller: Controller,
  oldMark: Mark,
  newMark: Mark,
) {
  return controller.command(removeMark, oldMark).command(addMark, newMark);
}
