import Path from '../models/path';
import Node from '../models/node';
import Operation, { OperationType } from '../models/operation';

const PREFIX = 'cj/cmd/split_';

export const splitTpl = (node: Node, path: Path, position: number) =>
  `${PREFIX}${node.isText() ? 'text' : 'node'}(${JSON.stringify([
    path,
    position,
  ])}`;

/**
 * 是否是分裂 Element Operation 
 * @param op 
 * @returns 
 */
export const isSplitElementOp = (op: Operation) => {
  return op.type === OperationType.InsertNode && op.context?.startsWith(`${PREFIX}node`);
}
