/* eslint-disable @typescript-eslint/consistent-type-definitions */
import React from 'react';

// 移动端弹键盘时，兼容浏览器在 delay 此时间后滚动到光标位置
const FOCUS_SCROLL_DELAY = 300;
// 移动端弹键盘时，等待 visual viewport change 的最大时间
const MAX_VIEWPORT_WAIT_TIME = 3000;

type ScrollParams = {
  callback: Function;
  waitTimer: ReturnType<typeof setTimeout>;
};

/**
 * 移动端处理「点击后，滚动视口到光标」的逻辑
 * 这里需要等待键盘弹出后，才能计算跳转，因此依赖了 visual viewbox
 * 如果不支持 visual viewbox，就用 setTimeout
 */
export default function useClickToScroll() {
  const scrollerRef = React.useRef<ScrollParams | null>(null);

  // resize 事件发生。这里看如果有 scrollerRef，就开始滚动
  const handleResize = React.useCallback(() => {
    if (scrollerRef.current) {
      const { callback, waitTimer } = scrollerRef.current;
      callback();
      clearTimeout(waitTimer);
      scrollerRef.current = null;
    }
  }, []);

  // 注册 visual box 事件
  React.useEffect(() => {
    if (!window.visualViewport) {
      return undefined;
    }
    window.visualViewport.addEventListener('resize', handleResize);
    return () => {
      window.visualViewport.removeEventListener('resize', handleResize);
    };
  }, [handleResize]);

  const scheduler = React.useCallback((callback: Function) => {
    if (scrollerRef.current) {
      clearTimeout(scrollerRef.current.waitTimer);
    }
    if (window.visualViewport) {
      // 如果支持 visualViewport 就开始检测 size 变化
      const fallbackCall = () => {
        scrollerRef.current = null;
      };
      const waitTimer = setTimeout(fallbackCall, MAX_VIEWPORT_WAIT_TIME);
      scrollerRef.current = { callback, waitTimer };
    } else {
      // 不支持的话，就用 setTimeout
      setTimeout(callback, FOCUS_SCROLL_DELAY);
    }
  }, []);

  return scheduler;
}
