/**
 * @packageDocumentation
 * @ignore
 */
import { curry } from 'lodash-es';
import Controller from '../controller/index';
import Selection, { SupportPointName } from '../models/selection';
import Node from '../models/node';
import Element from '../models/element';
import Document from '../models/document';
import Path from '../models/path';
import { pointAtDistance, MoveUnit } from '../queries/selection';

export const movePointForward = curry(
  (
    controller: Controller,
    pn: SupportPointName,
    distance: number,
    unit: MoveUnit,
  ) => {
    return (selection: Selection) => {
      if (distance === 0) {
        return selection;
      }

      const { document } = controller.value;

      let pointName = pn;
      if (pointName === 'start') {
        pointName = selection.isForward(document) ? 'anchor' : 'focus';
      } else if (pointName === 'end') {
        pointName = selection.isForward(document) ? 'focus' : 'anchor';
      }
      const at = pointName === 'anchor' ? selection.anchor : selection.focus;

      const point = controller.query(pointAtDistance, at, distance, unit);
      return selection.set(pointName, point);
    };
  },
);

export const movePointBackward = curry(
  (
    controller: Controller,
    pn: SupportPointName,
    distance: number,
    unit: MoveUnit,
  ) => {
    return (selection: Selection) => {
      const mover = movePointForward(controller, pn, -distance, unit);
      return mover(selection);
    };
  },
);

export const movePointWordBackward = movePointBackward(
  curry.placeholder,
  curry.placeholder,
  1,
  'word',
);

export const movePointWordForward = movePointForward(
  curry.placeholder,
  curry.placeholder,
  1,
  'word',
);

/**
 * 选区是否包含整个 Element
 */
export const isSelectionContainsElement = (
  selection: Selection | null,
  element: Element,
) => {
  if (!selection || selection.isCollapsed) {
    return false;
  }
  const { anchor, focus } = selection;
  const anchorNode = element.getNode(anchor.key);
  if (anchorNode && anchorNode !== element) return false;
  const focusNode = element.getNode(focus.key);
  if (focusNode && focusNode !== element) return false;
  return true;
};

/**
 * 判断 `selection` 是否完全在 `node` 内部
 * @param node
 * @param document
 * @param selection
 * @returns
 */

export const isSelectionInNode = (
  node: Node,
  document: Document,
  selection: Selection,
) => {
  const { start, end } = selection.sort(document);
  if (Element.isElement(node)) {
    if (
      (start.isEdgePoint() && start.key === node.key) ||
      (end.isEdgePoint() && end.key === node.key)
    ) {
      return false;
    } else {
      const { start: startText, end: endText } =
        selection.convertToTextPoints(document);
      return node.hasNode(startText.key) && node.hasNode(endText.key);
    }
  } else {
    // Node is Text
    return (
      start.isTextPoint() &&
      end.isTextPoint() &&
      start.key === node.key &&
      end.key === node.key
    );
  }
};

function getFirstDataElementPath(
  document: Document,
  view: Document,
  viewElement: Element,
): Path {
  let start = viewElement.nodes[0];
  while (!document.hasNode(start.key)) {
    start = view.assertElement(start.key).nodes[0];
  }

  return document.assertPath(start.key);
}

function getLastDataElementPath(
  document: Document,
  view: Document,
  viewElement: Element,
): Path {
  let end = viewElement.nodes[viewElement.nodes.length - 1];
  while (!document.hasNode(end.key)) {
    const endElement = view.assertElement(end.key);
    end = endElement.nodes[endElement.nodes.length - 1];
  }

  return document.assertPath(end.key);
}

/**
 * 获得裁剪后的选区
 * @param controller
 * @param selection
 * @param visibles
 */
export function getCroppedSelection(
  controller: Controller,
  selection: Selection,
  visibles: string[],
): Selection | null {
  if (!controller.enableVirtualize) {
    return selection;
  }
  const { document } = controller.value;
  const view = controller.view;
  const validVisibles = visibles.filter(
    (visible) => document.hasNode(visible) || view.hasNode(visible),
  );

  if (!validVisibles.length) {
    return null;
  } else {
    const { start, end } = selection.convertToTextPoints(document);
    const selectionStartPath = document.getPath(start.key);
    const selectionEndPath = document.getPath(end.key);
    if (!selectionStartPath || !selectionEndPath) {
      return null;
    } 
    const visibleStartKey = validVisibles[0]!;
    const visibleEndKey = validVisibles[validVisibles.length - 1];

    let visibleStart: number;
    let visibleEnd: number;
    if (document.hasNode(visibleStartKey)) {
      visibleStart = document.assertPath(visibleStartKey)[0];
    } else {
      const viewElement = view.assertElement(visibleStartKey);
      visibleStart = getFirstDataElementPath(document, view, viewElement)[0];
    }

    if (document.hasNode(visibleEndKey)) {
      visibleEnd = document.assertPath(visibleEndKey)[0];
    } else {
      const viewElement = view.assertElement(visibleEndKey);
      visibleEnd = getLastDataElementPath(document, view, viewElement)[0];
    }

    const selectionStart = selectionStartPath[0];
    const selectionEnd = selectionEndPath[0];

    if (selectionStart > visibleEnd || selectionEnd < visibleStart) {
      return null;
    }

    let sel = selection;
    if (selectionStart < visibleStart) {
      sel = sel.moveStartToStartOfNode(
        document.assertNodeByPath([visibleStart]),
        controller,
      );
    }

    if (selectionEnd > visibleEnd) {
      sel = sel.moveEndToEndOfNode(
        document.assertNodeByPath([visibleEnd]),
        controller,
      );
    }

    return sel;
  }
}
