import { camelCase, trim } from 'lodash-es';

/**
 * @export
 * @param {NamedNodeMap} attrs
 * @description 将 Node 节点的 attributes 属性转成 JSON 对象
 */
export function nodeMapToObject(attrs: NamedNodeMap) {
  const obj: Record<string, any> = {
    styleObj: {},
  };
  for (let i = attrs.length - 1; i >= 0; i--) {
    if (attrs[i].value !== '') {
      obj[attrs[i].name] = attrs[i].value;
    }
  }
  return obj;
}

/**
 * @export
 * @param {string} [styleStr='']
 * @return {*}  {Partial<CSSStyleDeclaration>}
 * @description 将 inlineStyle 字符串转为 驼峰格式的 JSON 对象
 */
export function toStyleObject(styleStr = ''): Partial<CSSStyleDeclaration> {
  if (!styleStr) {
    return {};
  }

  const output: Partial<CSSStyleDeclaration> = {};
  const style = styleStr.split(';');
  for (let i = style.length - 1; i >= 0; i--) {
    const rule = style[i];
    if (rule) {
      const kv = rule.split(':');
      output[camelCase(trim(kv[0] || '')) as any] = trim(kv[1] || '');
    }
  }
  return output;
}

/**
 * @export
 * @param {string} str
 * @return {*}
 */
export function normalizeSpacesInHTML(str: string) {
  return str.replace(/\s*\n\s*/g, '');
}
