import * as React from 'react';
import type { JsonML, ConvertFunc } from "../../type";
import { jsonMLGetChildren, jsonMLGetTagName } from '../../utils/jsonml';

export default {
  name: 'root',

  match(node: JsonML): boolean {
    return jsonMLGetTagName(node) === 'root';
  },

  convert(node: JsonML, key: number[] = [], convert: ConvertFunc): React.ReactElement {
    const children = jsonMLGetChildren(node)
      .map((child, index: number) => convert(child, [...key, index]));
    return (
      <article className="4ever-article">{children}</article>
    );
  },
};
