import type Renderer from 'markdown-it/lib/renderer';
import type { MoSerializationPlugin, MarkdownRuleName, MarkdownToHTMLRule } from '../type';

const markdownRuleNameMap: Record<MarkdownRuleName, string[]> = {
  table: ['table'],
  code: ['code', 'fence'],
  blockquote: ['blockquote'],
  hr: ['hr'],
  list: ['list'],
  heading: ['heading', 'lheading'],
  codespan: ['backticks'],
  strikethrough: ['strikethrough'],
  emphasis: ['emphasis'],
  link: ['link', 'autolink'],
  image: ['image'],
  html: ['html_block', 'html_inline']
};

export default async function createMarkdownToHTML(plugins: MoSerializationPlugin[]) {
  const rules = plugins
    .map((plugin) => plugin.markdownToHTML!)
    .filter((rule) => !!rule);

  // default，从原 basic 插件迁移
  rules.push({
    ruleName: 'html',
    options: {
      html: true,
      xhtmlOut: true,
    },
  });

  // async loading markdown-it
  const MarkdownIt = (await import(/* webpackChunkName: 'cangjie-lib-markdown-it'*/'markdown-it')).default;
  const md = new MarkdownIt('zero');

  // 应用 rule 到 md
  const summary = {
    names: [] as string[],
    options: {},
    rewrite: {} as Record<string, { renderer?: Renderer.RenderRule }>,
    uses: [] as NonNullable<MarkdownToHTMLRule['uses']>
  };
  rules.forEach((rule) => {
    if (rule.ruleName) {
      summary.names.push(...markdownRuleNameMap[rule.ruleName]);
    }
    if (rule.options) {
      summary.options = { ...summary.options, ...rule.options };
    }
    if (rule.rewrite) {
      rule.rewrite.forEach(({ name, createRenderer }) => {
        if (createRenderer) {
          summary.rewrite[name] = { renderer: createRenderer(md) };
        }
      })
    }
    if (rule.uses) {
      summary.uses.push(...rule.uses);
    }
  });

  md.enable(summary.names)
    .set(summary.options);

  for (let name in summary.rewrite) {
    if (md.renderer.rules[name] && summary.rewrite[name].renderer) {
      md.renderer.rules[name] = summary.rewrite[name].renderer;
    }
  }

  for (let { plugin: pluginLoader, data } of summary.uses) {
    const plugin = await pluginLoader();
    const d = (typeof data === 'function') ? await data() : data;
    md.use(plugin.default, ...(d || []));
  }

  function markdownToHTML(markdown: string): string {
    const res = md.render(markdown);
    return res;
  }

  return markdownToHTML;
}
