/**
 * @packageDocumentation
 * @module domUtils
 */

/**
 * 查找 node 节点下 first/last 的 Text 节点
 * @param node 查找的节点
 * @param order 遍历的顺序 pre-前序遍历 post-后序遍历
 */
function findTextNode(node: Node, order: 'pre' | 'post' = 'pre'): Text | null {
  const childNodes =
    order === 'post' ? Array.from(node.childNodes).reverse() : node.childNodes;
  for (let i = 0; i < childNodes.length; i += 1) {
    const child = childNodes[i];
    if (child instanceof Text) {
      return child;
    }
    const text = findTextNode(child, order);
    if (text instanceof Text) {
      return text;
    }
  }
  return null;
}

/**
 * 获取 DOM 的真实选区
 * @param root
 * @description 由于编辑器层没有浏览器的选区，所以使用 mark 的方式自行构造选区
 */
export default function findCurrentDOMRange(
  root: HTMLElement | Document = window.document,
): Range | null {
  const nodes = root.querySelectorAll('[data-cangjie-range');
  if (nodes.length <= 0) {
    return null;
  }
  const range = document.createRange();
  const start = nodes[0];
  const end = nodes[nodes.length - 1];
  const anchor = findTextNode(start, 'pre');
  const focus = findTextNode(end, 'post');
  if (!anchor || !focus) {
    return null;
  }
  try {
    range.setStart(anchor, 0);
    range.setEnd(focus, Math.max(focus.length, 0));
  } catch (error) {
    return null;
  }
  return range;
}
