import type { MoSerializationPlugin } from '../type';
import type { Node } from '../../';
import Block from '../../models/block';

function createNodeToText(plugins: MoSerializationPlugin[]) {
  const rules = plugins.map((plugin) => plugin.valueToText!).filter((rule) => !!rule);

  function nodeToText(node: Node): string {
    let rule = rules.find((item) => item.match(node));

    if (!rule) {
      rule = {
        name: 'default',
        match: () => false,
        convert: (n) => {
          const { text } = n;
          // 块级元素在末尾追加换行
          if (Block.isBlock(node)) {
            return `${text }\n`;
          }
          return text;
        },
      };
    }

    const text = rule.convert(node, nodeToText);

    return text;
  }

  return nodeToText;
}

export default createNodeToText;
