/**
 * @packageDocumentation
 * @module Commands
 */
import Controller from '../controller/index';
import Path from '../models/path';
import Node from '../models/node';
import { OperationType, Properties } from '../models/operation';
import Element from '../models/element';
import Selection from '../models/selection';
import Mark, { MarkProperties } from '../models/mark';
import { replaceTextByPath } from './replace';

export function setNodeByKey(
  controller: Controller,
  key: string,
  properties: Properties,
) {
  const { value } = controller;
  const { document } = value;

  const path = document.assertPath(key);
  return controller.command(setNodeByPath, path, properties);
}

export function setNodeByPath(
  controller: Controller,
  path: Path,
  properties: Properties,
) {
  const prevNode = controller.value.document.assertNodeByPath(path);
  const prevProperties = Element.isElement(prevNode)
    ? { type: prevNode.type, data: prevNode.data }
    : {};

  return controller.applyOperation({
    type: OperationType.SetNode,
    path: [...path],
    properties: Node.createProperties(properties),
    prevProperties,
  });
}

export function setBlocks(controller: Controller, properties: Properties) {
  const { selection } = controller.value;
  return controller.command(setBlocksAtRange, selection, properties);
}

export function setBlocksAtRange(
  controller: Controller,
  selection: Selection,
  properties: Properties,
) {
  const { value } = controller;
  const { document } = value;

  const { start, end } = selection.convertToTextPoints(document);

  const hasVoidParent = document.hasVoidParent(start.key, controller);
  const startBlock = document.getClosestBlock(start.key);
  const endBlock = document.getClosestBlock(end.key);

  const startNodeKey = startBlock ? startBlock.getFirstNode().key : null;
  const endNodeKey = endBlock ? endBlock.getLastNode().key : null;

  // Check the "Hanging Selection" case for UX reasons.
  // For more detail, go read the comments in [deleteAtRange]
  const isHanging =
    selection.isCollapsed === false &&
    hasVoidParent === false &&
    start.offset === 0 &&
    end.offset === 0 &&
    start.key === startNodeKey &&
    end.key === endNodeKey &&
    start.key !== end.key;

  const allBlocks = document.getLeafBlocksAtRange(selection);
  const blocks = isHanging ? allBlocks.slice(0, -1) : allBlocks;

  return controller.withoutNormalizing(() => {
    blocks.forEach((block) => {
      controller.command('setNodeByKey', block.key, properties);
    });
  });
}

export function setInlines(controller: Controller, properties: Properties) {
  const { selection } = controller.value;
  return controller.command(setInlinesAtRange, selection, properties);
}

export function setInlinesAtRange(
  controller: Controller,
  selection: Selection,
  properties: Properties,
) {
  const { value } = controller;
  const { document } = value;

  return controller.withoutNormalizing(() => {
    const inlines = document.getLeafInlinesAtRange(selection);
    inlines.forEach((inline) => {
      controller.command('setNodeByKey', inline.key, properties);
    });
  });
}

export function setMarkByKey(
  controller: Controller,
  key: string,
  offset: number,
  length: number,
  mark: Mark | MarkProperties | string,
  properties: Properties,
) {
  const { value } = controller;
  const { document } = value;
  const path = document.assertPath(key);
  return controller.command(
    setMarkByPath,
    path,
    offset,
    length,
    mark,
    properties,
  );
}

export function setMarkByPath(
  controller: Controller,
  path: Path,
  offset: number,
  length: number,
  m: Mark | MarkProperties | string,
  properties: Properties,
) {
  const mark = Mark.create(m);

  return controller.applyOperation({
    type: OperationType.SetMark,
    path,
    offset,
    length,
    mark,
    properties,
  });
}

export function setTextByKey(
  controller: Controller,
  key: string,
  text: string,
  marks?: Mark[],
) {
  const { value } = controller;
  const { document } = value;

  const path = document.assertPath(key);
  return controller.command(setTextByPath, path, text, marks);
}

export function setTextByPath(
  controller: Controller,
  path: Path,
  text: string,
  marks?: Mark[],
) {
  const { value } = controller;
  const { document } = value;

  const node = document.assertNodeByPath(path);
  return controller.command(replaceTextByPath, path, 0, node.text, text, marks);
}
