import Element from '../../models/element';
import { UUIDMap } from './types';
import Query, { ConvertToPluginQueries } from '../../interfaces/query';

export const createUUIDQuery = (uuidKey: string) => (_, node: Element) => {
  return node.data[uuidKey];
};

export const createAnchorQuery = (uuidKey: string) => (_, node: Element) => {
  return node.data[uuidKey];
};

export function createGetNodeKeyByUUID(getUUIDS: () => UUIDMap) {
  const getNodeKeyByUUID: Query = (_, uuid: string) => {
    const uuids = getUUIDS();
    return uuids[uuid];
  };
  return getNodeKeyByUUID;
}

declare module '@ali/4ever-cangjie' {
  /** type combination for commands、queries、actions */
  interface PluginQueries
    extends ConvertToPluginQueries<{
      uuid: ReturnType<typeof createUUIDQuery>;
      anchor: ReturnType<typeof createAnchorQuery>;
      getNodeKeyByUUID: ReturnType<typeof createGetNodeKeyByUUID>;
    }> {}
}
