import Controller from '../../controller';
import Plugin from '../../interfaces/plugin';
import createMapOperation from './createMapOperation';
import { createGetNodeKeyByUUID, createUUIDQuery } from './queries';
import { UUIDMap, UUIDConfig } from './types';
import { diffUUIDS, getUUIDSFromDocument, isEmptyDiff } from './utils';
import { actions, isActions } from './actions';
import Value from '../../models/value';
import { createGenerateUUID, setNodeUUID } from './commands';
import onAction from './onAction';
import { DEFAULT_UUID_KEY } from './constants';

export default function createUUIDPlugin(config: UUIDConfig): Plugin {
  // 初始化 UUID 映射
  let uuids: UUIDMap = {};

  function getUUIDS() {
    return uuids;
  }

  const uuidKey = config.uuidKey || DEFAULT_UUID_KEY;

  function regenerateDuplicateUUIDS(controller: Controller, value: Value) {
    const befores = { ...getUUIDS() };
    const ret = getUUIDSFromDocument(value.document, uuidKey);
    // 为重复的 id 重新生成 uuid
    const newUUIDS = ret.duplications.reduce((ids, key) => {
      const element = value.document.assertElement(key);
      const uuid = config.gen();
      controller.command(setNodeUUID, element, uuid, uuidKey);
      ids[uuid] = key;
      return ids;
    }, ret.uuids);
    const diff = diffUUIDS(befores, newUUIDS);
    if (!isEmptyDiff(diff)) {
      uuids = newUUIDS;
      controller.dispatch('updateUUIDs', {
        // Immutable，防止外部改动了 uuid
        uuids: { ...newUUIDS },
        diff,
      });
    }
  }

  const mapOperation = createMapOperation(config.gen, uuidKey, getUUIDS);

  return {
    actions,
    isActions,
    commands: {
      generateUUID: createGenerateUUID(config.gen, uuidKey),
    },
    onConstruct: (controller, next) => {
      regenerateDuplicateUUIDS(controller, controller.value);
      return next();
    },
    onResetValue: (value, _, controller, next) => {
      regenerateDuplicateUUIDS(controller, value);
      return next();
    },
    onAction,
    mapOperation: (operation, controller, next) => {
      const { op, diff } = mapOperation(operation, controller.value.document);
      if (isEmptyDiff(diff)) {
        return op;
      }
      diff.removes.forEach((uuid) => {
        delete uuids[uuid];
      });
      diff.adds.forEach(({ uuid, key }) => {
        uuids[uuid] = key;
      });
      diff.updates.forEach(({ oldUUID, newUUID }) => {
        const nodeKey = uuids[oldUUID];
        uuids[newUUID] = nodeKey;
        delete uuids[oldUUID];
      });
      controller.dispatch('updateUUIDs', {
        // Immutable，防止外部改动了 uuid
        uuids: { ...uuids },
        diff,
      });
      return next(op);
    },
    queries: {
      uuid: createUUIDQuery(uuidKey),
      anchor: createUUIDQuery(uuidKey),
      getNodeKeyByUUID: createGetNodeKeyByUUID(getUUIDS),
    },
  };
}
