/**
 * @packageDocumentation
 * @module Models
 */
import equal from 'fast-deep-equal';
import isPlainObject from 'is-plain-object';
import ImmutableRecord from './immutableRecord';
import Mark, { MarkJSON } from './mark';
import { Serializable } from '../interfaces/serializable';
import listUtils from '../utils/listUtils';

function has<T>(as: T[], b: T) {
  return as.some((a) => equal(a, b));
}

function union<T>(as: T[], bs: T[]) {
  return bs.reduce((merged, b) => {
    if (has(merged, b)) {
      return merged;
    }
    return [...merged, b];
  }, as);
}

function except<T>(as: T[], bs: T[]) {
  return as.filter((a) => !has(bs, a));
}

export interface LeafProperties {
  text: string;
  marks?: Mark[];
}

export interface LeafJSON {
  klass?: 'leaf';
  key?: string;
  text: string;
  marks?: MarkJSON[];
}

export default class Leaf extends ImmutableRecord
  implements Serializable<LeafJSON> {
  static create(properties: Leaf | LeafProperties | string = ''): Leaf {
    if (Leaf.isLeaf(properties)) {
      return properties;
    }

    let props = properties;
    if (typeof props === 'string') {
      props = { text: props };
    }

    if (isPlainObject(props)) {
      const { text = '', marks = [] } = props;
      return new Leaf({
        text,
        marks: marks.map(Mark.create),
      });
    }

    throw new Error(
      `\`Leaf.create\` only accepts objects, strings or leaves, but you passed it: ${properties}`,
    );
  }

  static fromJSON(json: LeafJSON) {
    const { marks } = json;
    return Leaf.create({
      ...json,
      marks: marks ? marks.map(Mark.fromJSON) : undefined,
    });
  }

  static isLeaf(maybeLeaf: any): maybeLeaf is Leaf {
    return maybeLeaf instanceof Leaf;
  }

  /**
   * 对leaves进行normalize，并返回新Leafs
   */
  static normalizeLeaves(leaves: Leaf[]) {
    if (leaves.length === 0) {
      return [Leaf.create()];
    }
    if (leaves.length === 1) {
      return leaves;
    }

    let invalid = false;
    const normalized = leaves.reduceRight((ls: Leaf[], leaf) => {
      const next = ls[0];
      if (!next) {
        return [leaf];
      }

      if (listUtils.hasSameMembers(leaf.marks, next.marks)) {
        invalid = true;
        ls[0] = next.set('text', `${leaf.text}${next.text}`);
        return ls;
      }

      if (next.text === '') {
        invalid = true;
        ls[0] = leaf;
        return ls;
      }

      if (leaf.text === '') {
        invalid = true;
        return ls;
      }
      ls.unshift(leaf);
      return ls;
    }, []);

    if (!invalid) {
      return leaves;
    }
    return normalized;
  }

  /**
   * 对leaves执行split操作。
   * @offset text的偏移量，如果位于某一个leaf内部，则分割该leaf
   */
  static splitLeaves(leaves: Leaf[], offset: number): [Leaf[], Leaf[]] {
    if (offset < 0) {
      return [[], leaves];
    }

    if (leaves.length === 0) {
      return [[], []];
    }

    let endOffset = 0;
    let left: Leaf | undefined;
    let right: Leaf | undefined;
    const index = leaves.findIndex((leaf) => {
      const startOffset = endOffset;
      const { text } = leaf;
      endOffset += text.length;

      if (endOffset < offset) {
        return false;
      }
      if (startOffset > offset) {
        return false;
      }

      const length = offset - startOffset;
      if (typeof text.slice !== 'function') {
        throw new Error(`Unable to split leaf with text: ${text}`);
      }
      left = leaf.set('text', text.slice(0, length));
      right = leaf.set('text', text.slice(length));
      return true;
    });

    if (!left || !right) {
      // Same as `!left && !right`
      return [leaves, []];
    }

    if (left.text === '') {
      if (index === 0) {
        return [[left], leaves];
      }

      return [leaves.slice(0, index), leaves.slice(index)];
    }

    if (right.text === '') {
      if (index === leaves.length - 1) {
        return [leaves, [right]];
      }

      const splittedIndex = index + 1;
      return [leaves.slice(0, splittedIndex), leaves.slice(splittedIndex)];
    }

    return [
      [...leaves.slice(0, index), left],
      [right, ...leaves.slice(index + 1)],
    ];
  }

  /**
   * 在leaves中搜索第offset个字符对应的位置
   */
  static searchLeafAtOffset(leaves: Leaf[], offset: number) {
    let startOffset = 0;
    let endOffset = 0;
    const index = leaves.findIndex((leaf) => {
      startOffset = endOffset;
      endOffset = startOffset + leaf.text.length;
      return endOffset >= offset;
    });

    const found = index !== -1;
    if (!found) {
      return null;
    }

    return {
      index,
      leaf: leaves[index],
      startOffset,
      endOffset,
    };
  }

  protected constructor(props: Required<LeafProperties>) {
    super();
    const { text, marks } = props;
    this.text = text;
    this.marks = marks;
    this.freeze();
  }

  public readonly text: string;

  public readonly marks: Mark[];

  public merge(props: Partial<LeafProperties>) {
    return new Leaf({
      text: this.text,
      marks: this.marks,
      ...props,
    });
  }

  public set<T extends keyof LeafProperties>(
    key: T,
    value: Required<LeafProperties>[T],
  ) {
    return this.merge({
      [key]: value,
    });
  }

  /**
   * 拷贝一个新Leaf，并把Mark加上去
   */
  public addMark(mark: Mark) {
    return this.set('marks', union(this.marks, [mark]));
  }

  /**
   * 拷贝一个新Leaf，并把Marks加上去
   */
  public addMarks(marks: Mark[]) {
    return this.set('marks', union(this.marks, marks));
  }

  /**
   * 拷贝一个新Leaf，并把Mark过滤掉
   */
  public removeMark(mark: Mark) {
    return this.set('marks', except(this.marks, [mark]));
  }

  /**
   * 拷贝一个新Leaf，并把Mark替换掉
   * 如果mark不存在、mark和新mark相等，则返回当前的Leaf
   */
  public setMark(mark: Mark, newMark: Mark) {
    if (equal(mark, newMark)) {
      return this;
    }

    const { marks } = this;
    if (!has(marks, mark)) {
      return this;
    }

    const newMarks = marks.map((m) => (equal(m, mark) ? newMark : mark));
    return this.set('marks', newMarks);
  }

  public toJSON(): LeafJSON {
    return {
      klass: 'leaf',
      text: this.text,
      marks: this.marks.map((mark) => mark.toJSON()),
    };
  }
}
