import { throttle } from 'lodash-es';
import Controller from '../../controller';
import { IS_MOBILE } from '../environment';

import { getScrollableContainerInfo } from './scrollToSelection';

type StartParams = {
  scroller: HTMLElement;
  controller: Controller;
};

type ScheduleParams = {
  controller: Controller;
  isWindow: boolean;
  scroller: HTMLElement;
  left: number;
  from: number;
  isTop: boolean;
  speed: number;
};

type ScrollState = {
  timer?: any;
  isStarted: boolean;
};

const stateMap = new WeakMap<Controller, ScrollState>();

// 检测 move 事件的 Throttle
const THROTTLE_MOVE = 300;
// 用户每移动这么多 pixel，就加一个速度
const SPEED_PIXEL = 20;
// 用户最大速度（最小是 1）
const MAX_SPEED = 6;
// 底部热区大一些，提前就滚动
const BOTTOM_EXTRA_HOT_SIZE = IS_MOBILE ? 100 : 50;
// 移动端顶部热区大一些，提前滚动
const TOP_EXTRA_HOT_SIZE = IS_MOBILE ? 100 : 0;

const scheduleScroll = (params: ScheduleParams) => {
  const { controller, isWindow, scroller, left, from, speed, isTop } = params;
  const state = stateMap.get(controller);
  if (!state || !state.isStarted) {
    return;
  }
  cancelSchedule(controller);
  // step 从用户体验角度，做了指数加速，一般是：2, 6, 12, 20, 30, ... 72
  const px = speed * (speed + 1);
  const step = isTop ? -px : px;
  const top = Math.round(from + step);
  if (isWindow) {
    window.scrollTo(left, top);
  } else {
    scroller.scrollTop = top;
  }
  const newSpeed = Math.min(MAX_SPEED, speed + 0.1);
  state.timer = requestAnimationFrame(() => {
    scheduleScroll({ ...params, from: top, speed: newSpeed });
  });
};

const cancelSchedule = (controller: Controller) => {
  const state = stateMap.get(controller);
  if (state?.timer) {
    cancelAnimationFrame(state.timer);
    state.timer = undefined;
  }
};

export const startScroll = ({ scroller, controller }: StartParams) => {
  stopScroll(controller);
  stateMap.set(controller, {
    isStarted: true,
  });
  return throttle((event: MouseEvent | PointerEvent | TouchEvent) => {
    // PC Safari 下，不支持 TouchEvent
    const { clientY } = (
      typeof TouchEvent === 'function' && event instanceof TouchEvent
        ? event.touches[0]
        : event as (MouseEvent | PointerEvent)
    );
    const {
      isWindow,
      scrollerTop: top,
      height,
      xOffset,
      yOffset,
    } = getScrollableContainerInfo(scroller);
    const bottom = top + height;
    const topOffset = top - clientY + SPEED_PIXEL + TOP_EXTRA_HOT_SIZE;
    const bottomOffset = clientY - bottom + SPEED_PIXEL + BOTTOM_EXTRA_HOT_SIZE;
    if (topOffset <= 0 && bottomOffset <= 0) {
      cancelSchedule(controller);
      return;
    }
    const isTop = topOffset > 0;

    // speed 一般是：1 - 8
    const speed = Math.max(
      1,
      Math.min(
        MAX_SPEED,
        Math.round(
          isTop ? topOffset / SPEED_PIXEL : bottomOffset / SPEED_PIXEL,
        ),
      ),
    );

    scheduleScroll({
      controller,
      isWindow,
      scroller,
      left: xOffset,
      from: yOffset,
      isTop,
      speed,
    });
  }, THROTTLE_MOVE);
};

export const stopScroll = (controller: Controller) => {
  const state = stateMap.get(controller);
  if (!state || !state.isStarted) {
    return;
  }
  cancelSchedule(controller);
  state.isStarted = false;
};

export const isScrolling = (controller: Controller) => {
  const state = stateMap.get(controller);
  return Boolean(state?.isStarted && state.timer);
};
