/**
 * @packageDocumentation
 * @module Controller
 */
import * as React from 'react';
import warning from 'tiny-warning';
import { noop } from 'lodash-es';
import {
  ControllerProperties,
  ControllerOptions,
} from '../interfaces/controller';
import { focus } from '../commands/index';
import Controller, { ControllerContext } from './index';
import Value from '../models/value';
import { StyleContext, DocStyle } from '../hooks/useStyle';
import { Subject } from '../models/observable';
import { IS_DEV } from '../utils/environment';

export interface ProviderProps extends ControllerProperties {
  /**
   * 编辑器Controller引用的回调函数
   */
  controllerRef?: (controller: Controller) => void;
  /**
   * 编辑器参数
   */
  options?: ControllerOptions;
  /**
   * children
   */
  children: React.ReactNode;
  /**
   * 文档样式表
   */
  docStyle?: DocStyle;
  onFirstRender?: (controller: Controller) => void;
}

export default function Provider(props: ProviderProps) {
  const {
    biz,
    dataUid,
    plugins,
    value,
    onChange = noop,
    controllerRef = noop,
    children,
    readOnly = false,
    autoFocus = false,
    canCopy,
    options,
    flushType,
    configs,
    onBusyStateChange,
    onFirstRender,
    docStyle,
    onPending = noop,
  } = props;

  const pluginsRef = React.useRef<ProviderProps['plugins']>();
  const onChangeRef = React.useRef<ProviderProps['onChange']>();
  const prevValueRef = React.useRef<Value>(value);
  const controller = React.useMemo(() => {
    const controllerIns = Controller.create(
      {
        biz,
        dataUid,
        plugins,
        value,
        onChange,
        readOnly,
        autoFocus,
        canCopy,
        flushType,
        configs,
        onBusyStateChange,
        onPending,
      },
      options,
    );

    pluginsRef.current = plugins;
    onChangeRef.current = onChange;

    controllerIns.groupManager.generateAllGroups();

    return controllerIns;
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  React.useLayoutEffect(() => {
    if (onFirstRender) {
      onFirstRender(controller);
    }
  }, []);

  React.useEffect(() => {
    controller.showCaret = options?.showCaret;
  }, [options?.showCaret?.onBlur, options?.showCaret?.onReadOnly]);

  // 因 canCopy 而重新生成 controller 会导致后续的交互出 BUG
  React.useEffect(() => {
    if (typeof canCopy === 'boolean') {
      controller.canCopy = canCopy;
    }
  }, [controller, canCopy]);

  React.useEffect(() => {
    if (process.env.NODE_ENV === 'development') {
      warning(
        plugins === pluginsRef.current,
        "Cangjie.Provider will ignore `plugins`'s change.",
      );
      warning(
        onChange === onChangeRef.current,
        "Cangjie.Provider will ignore `onChange`'s change.",
      );
    }
  }, [plugins, onChange]);

  React.useEffect(() => {
    // uid 变化，刷新用户数据
    controller.switchUid(dataUid);
  }, [dataUid]);

  controllerRef(controller);

  if (readOnly !== controller.readOnly) {
    controller.setReadOnly(readOnly);
  }

  if (controller.enableVirtualize && controller.virtualizeManager?.viewInitialized) {
    controller.virtualizeManager.workflowOnValueChange();
  }

  // Note: This action will trigger normalize
  if (
    value !== prevValueRef.current &&
    value !== controller.value
  ) {
    controller.resetValue(value, options);
  }

  prevValueRef.current = value;

  const context = React.useMemo(() => {
    const subject = new Subject<Value>();
    return {
      controller,
      subject,
    };
  }, [controller]);

  // Note: Force re-render except first mount.
  const didMountRef = React.useRef(false);
  React.useEffect(() => {
    if (didMountRef.current) {
      context.subject.next(value);
    } else {
      didMountRef.current = true;
    }
  }, [value]);

  React.useEffect(() => {
    if (autoFocus) {
      controller.command(focus).flush();
    }
  }, [controller, autoFocus]);

  React.useEffect(() => {
    // 首次渲染完成，刷新分组信息
    controller.groupManager.notifyNewGroups(true);
    return () => {
      controller.destroy();
    };
  }, [controller]);

  React.useEffect(() => {
    if (IS_DEV) {
      const [main] = React.version.split('.');
      warning(
        Number(main) < 18,
        'React > 17 is not fully supported, editor will have some weird problems.',
      );
    }
  }, []);

  return (
    <ControllerContext.Provider value={context}>
      <StyleContext.Provider value={docStyle}>{children}</StyleContext.Provider>
    </ControllerContext.Provider>
  );
}
