/* eslint-disable max-len */
import * as React from 'react';
import * as ReactDOMServer from 'react-dom/server';

import type { MoSerializationPlugin, JsonMLToHTMLRule, ConvertFunc, JsonML } from '../type';
import { jsonMLGetChildren } from '../utils/jsonml';

interface JsonMLToHTMLProps {
  /**
   * 完整的 JsonML content
   */
  content: JsonML;
  /**
   * 序列化插件
   */
  rules: JsonMLToHTMLRule[];
}

function jsonMLToHTML(props: JsonMLToHTMLProps, node: JsonML, path: number[] = []): JSX.Element | null {
  const { rules } = props;
  let rule = rules.find((r) => r.match(node));
  if (!rule) {
    rule = {
      name: 'default',
      match: () => false,
      convert(node, key = [], convert) {
        const children = jsonMLGetChildren(node).map((child, index) => convert(child, [...key, index]));
        return (
          <>
            {children}
          </>
        );
      },
    }
  }

  const convert: ConvertFunc = (...args) => {
    return jsonMLToHTML(props, ...args);
  };

  const element = rule.convert(node, path, convert);
  if (!element) return null;

  const elementWithKey = React.isValidElement<{ 'path'?: number[] }>(element)
    ? React.cloneElement(element, { key: (element.props.path || []).toString() })
    : element;

  return elementWithKey;
}

interface HTMLProviderProps {
  content: JsonML;
  rules: JsonMLToHTMLRule[];
}

function HTMLProvider(props: HTMLProviderProps): React.ReactElement | null {
  const { content, rules, ...rest } = props;
  const elem = jsonMLToHTML(props, content);
  if (!elem) return null;
  return React.cloneElement(elem, rest);
}

export default function createJsonMLToHTML(plugins: MoSerializationPlugin[]) {
  // 遍历取出 jsonMLToHTML 插件
  const defaultRules = plugins
    .map((plugin) => plugin.jsonMLToHTML!)
    .filter((rule) => !!rule);

  /**
   * 序列化 JsonML to HTML，主要适用于富文本拷贝导出
   * @param content JsonML 内容
   * @param configs 自定义插件，如果插件名已经存在，则会覆盖原插件的方法
   */
  function contentJsonMLToHTML(content: JsonML, configs: Record<string, Partial<JsonMLToHTMLRule>> = {}): string {
    // 加载 configs 中的插件
    const rules = defaultRules.map((rule) => {
      if (rule.name in configs) {
        return {
          ...rule,
          ...configs[rule.name],
        };
      }
      return rule;
    });

    const before = plugins
      .map((p) => p.jsonMLToHTML?.hooks?.before)
      .filter((p): p is NonNullable<NonNullable<NonNullable<MoSerializationPlugin['jsonMLToHTML']>['hooks']>['before']> => !!p)
      .reduce((acc, p) => (value: JsonML) => p(acc(value)), (value: JsonML) => value);

    const elem = React.createElement(HTMLProvider, {
      content: before(content),
      rules,
    });
    return ReactDOMServer.renderToStaticMarkup(elem);
  }

  return contentJsonMLToHTML;
}
