/**
 * @packageDocumentation
 * @module Models
 */
import equal from 'fast-deep-equal';
import isPlainObject from 'is-plain-object';
import ImmutableRecord from './immutableRecord';
import Path from './path';
import Point, {
  EdgePoint,
  TextPoint,
  TextPointJSON,
  EdgePointJSON,
  PointEdge,
} from './point';
import Node from './node';
import Document from './document';
import { Serializable, SerializeOptions } from '../interfaces/serializable';
import Data from '../interfaces/data';
import Mark, { MarkJSON } from './mark';
import Controller from '../controller';
import { pointAtStartOfNode, pointAtEndOfNode } from '../queries/selection';
import textUtils from '../utils/textUtils';
import Block from './block';

/**
 * @ignore
 */
export type SupportEdge = 'start' | 'end';

export type SupportPointName = 'anchor' | 'focus' | 'start' | 'end';
/**
 * @ignore
 */
export type SupportSide = 'previous' | 'next';
/**
 * @ignore
 */
export type SupportKlass = 'block' | 'inline' | 'text';

export enum SelectionTrigger {
  Selecting = 'selecting',
  SelectStart = 'selectStart',
  SelectAll = 'selectAll',
  MoveUpward = 'moveUpward',
  MoveDownward = 'moveDownward',
  MoveBackward = 'moveBackward',
  MoveForward = 'moveForward',
  MultipleClick = 'multipleClick',
}

export interface SelectionProperties {
  data?: Data;
  anchor: TextPoint | EdgePoint;
  focus: TextPoint | EdgePoint;
  marks?: Mark[] | null;
}

export type RangeSelectionProperties = SelectionProperties;

export interface SelectionJSON {
  data?: Data;
  anchor: TextPointJSON | EdgePointJSON;
  focus: TextPointJSON | EdgePointJSON;
  marks?: MarkJSON[] | null;
}

export type RangeSelectionJSON = SelectionJSON;

type Mover = (selection: Selection) => Selection;

type PointMoverCreator = (pn: SupportPointName, n: number) => Mover;

const IS_COLLAPSED_CACHE = new WeakMap<Selection, boolean>();
const IS_FORWARD_CACHE = new WeakMap<Selection, WeakMap<Document, boolean>>();

export default class Selection extends ImmutableRecord
  implements Serializable<SelectionJSON> {
  static create(
    props: Selection | SelectionProperties,
  ): Selection {
    if (Selection.isSelection(props)) {
      return props;
    }

    if (isPlainObject(props)) {
      const { data = {}, anchor, focus, marks } = props;
      return new Selection({
        data,
        anchor: Point.create(anchor),
        focus: Point.create(focus),
        marks: marks ? marks.map(mark => Mark.create(mark)) : null,
      });
    }

    throw new Error(
      `\`Selection.create\` only accepts objects or range selections, but you passed it: ${props}`,
    );
  }

  /**
   * 调整range的开始位置和结束位置，使其"收缩"到最小的位置， 如果ignoreEnd的值为true，则忽略对结束位置的调整
   * @param document
   * @param ignoreEnd 如果ignoreEnd的值为true，则忽略对结束位置的调整
   * @example
   * before:1234<anchor/><link>56<focus/>78</link>90
   * after:1234<link><anchor/>56<focus/>78</link>90
   */
  public shrinkBoundary(document: Document, ignoreEnd?: boolean) {
    const start = this.getStart(document);
    const end = this.getEnd(document);
    if (!start.isTextPoint() || !end.isTextPoint()) {
      return this;
    }
    const isForward = this.isForward(document);
    let targetRange: Selection = this;
    if (!ignoreEnd && end.offset === 0) {
      const targetEndText = document.getPreviousText(end.key);
      if (targetEndText) {
        targetRange = targetRange.set(
          isForward ? 'focus' : 'anchor',
          TextPoint.fromJSON({
            key: targetEndText.key,
            offset: targetEndText.text.length,
          }),
        );
      }
    }

    const beforeStartText = document.getNode(start.key);
    if (beforeStartText && start.offset === beforeStartText.text.length) {
      const targetStartText = document.getNextText(start.key);
      if (targetStartText) {
        targetRange = targetRange.set(
          isForward ? 'anchor' : 'focus',
          Point.fromJSON({ key: targetStartText.key, offset: 0 }),
        );
      }
    }
    return targetRange;
  }

  static fromJSON(json: SelectionJSON) {
    const { data, anchor, focus, marks } = json;
    return Selection.create({
      data,
      anchor: Point.fromJSON(anchor),
      focus: Point.fromJSON(focus),
      marks: marks ? marks.map(mark => Mark.fromJSON(mark)) : null,
    });
  }

  static isSelection(
    maybeSelection: any,
  ): maybeSelection is Selection {
    return maybeSelection instanceof Selection;
  }

  static isRangeSelection(
    maybeSelection: any,
  ): maybeSelection is Selection {
    return Selection.isSelection(maybeSelection);
  }

  static isValidSelection(document: Document, selection: any) {
    if (!Selection.isSelection(selection)) {
      return false;
    }
    const { anchor, focus } = selection;
    const anchorNode = document.getNode(anchor.key);
    const focusNode = document.getNode(focus.key);
    if (!anchorNode || !focusNode) {
      return false;
    }
    const isAnchorValid =
      (anchor.isTextPoint() &&
        anchor.offset >= 0 &&
        anchor.offset <= anchorNode.text.length) ||
      anchor.isEdgePoint();
    const isFocusValid =
      (focus.isTextPoint() &&
        focus.offset >= 0 &&
        focus.offset <= focusNode.text.length) ||
      focus.isEdgePoint();
    return isAnchorValid && isFocusValid;
  }

  static isValidRangeSelection(document: Document, selection: any) {
    return Selection.isValidSelection(document, selection);
  }

  static selectWordAtPoint(document: Document, point: TextPoint | EdgePoint) {
    if (point.isEdgePoint()) {
      return Selection.create({
        anchor: point.set('edge', EdgePoint.BEFORE),
        focus: point.set('edge', EdgePoint.AFTER),
      });
    }
    const text = document.getNode(point.key);
    if (!text) {
      return null;
    }

    const [start, end] = textUtils.getWordRange(text.text, point.offset);
    return Selection.create({
      anchor: point.set('offset', start),
      focus: point.set('offset', end),
    });
  }

  static isEqual(rangSelection: Selection, target: Selection) {
    return (
      equal(rangSelection.anchor, target.anchor) &&
      equal(rangSelection.focus, target.focus)
    );
  }

  public readonly data: Data;

  public readonly anchor: TextPoint | EdgePoint;

  public readonly focus: TextPoint | EdgePoint;

  public readonly marks: Mark[] | null;

  protected constructor(props: Required<SelectionProperties>) {
    super();
    const { data, anchor, focus, marks = null } = props;
    this.data = data;
    this.anchor = anchor;
    this.focus = focus;
    this.marks = marks;
    this.freeze();
  }

  /**
   * 选区是否指向同一个位置（caret）
   */
  public get isCollapsed() {
    if (!IS_COLLAPSED_CACHE.has(this)) {
      const { anchor, focus } = this;
      IS_COLLAPSED_CACHE.set(this, equal(anchor, focus));
    }
    return IS_COLLAPSED_CACHE.get(this)!;
  }

  /**
   * 选区是否是扩展
   */
  public get isExpanded() {
    return !this.isCollapsed;
  }

  public get viewKey() {
    const keyStr = `${this.anchor.viewKey}_${this.focus.viewKey}`;
    try {
      return `${keyStr}_${JSON.stringify(this.data)}`;
    } catch (error) {
      return keyStr;
    }
  }

  public get plainViewKey() {
    return `${this.anchor.viewKey}_${this.focus.viewKey}`;
  }

  public merge(props: Partial<SelectionProperties>) {
    return new Selection({
      data: this.data,
      anchor: this.anchor,
      focus: this.focus,
      marks: this.marks,
      ...props,
    });
  }

  public set<T extends keyof SelectionProperties>(
    key: T,
    value: SelectionProperties[T],
  ) {
    return this.merge({
      [key]: value,
    });
  }

  private isForwardInDocument(document: Document) {
    const { anchor, focus } = this;
    // same node
    if (anchor.key === focus.key) {
      if (anchor.isEdgePoint()) {
        return anchor.edge === EdgePoint.BEFORE;
      }
      if (focus.isEdgePoint()) {
        return focus.edge === EdgePoint.AFTER;
      }
      return anchor.offset <= focus.offset;
    }

    // parent-child
    if (anchor.isEdgePoint()) {
      const anchorNode = document.getNode(anchor.key);
      if (anchorNode?.isElement() && anchorNode.getNode(focus.key)) {
        return anchor.edge === EdgePoint.BEFORE;
      }
    }
    if (focus.isEdgePoint()) {
      const focusNode = document.getNode(focus.key);
      if (focusNode?.isElement() && focusNode.getNode(anchor.key)) {
        return focus.edge === EdgePoint.AFTER;
      }
    }

    // other relationship
    const anchorPath = anchor.getPathIn(document);
    const focusPath = focus.getPathIn(document);
    return Boolean(anchorPath && focusPath && Path.isBefore(anchorPath, focusPath));
  }

  /**
   * 选区是不是正序的（anchor在前，focus在后）
   */
  public isForward(document: Document) {
    if (!IS_FORWARD_CACHE.has(this)) {
      IS_FORWARD_CACHE.set(this, new WeakMap());
    }
    const DOCUMENT_TO_IS_FORWARD_CACHE = IS_FORWARD_CACHE.get(this)!;
    if (!DOCUMENT_TO_IS_FORWARD_CACHE.has(document)) {
      DOCUMENT_TO_IS_FORWARD_CACHE.set(document, this.isForwardInDocument(document));
    }
    return DOCUMENT_TO_IS_FORWARD_CACHE.get(document)!;
  }

  /**
   * 选区是不是倒序的（anchor在后，focus在前）
   */
  public isBackward(document: Document) {
    return !this.isForward(document);
  }

  /**
   * 获取start point
   */
  public getStart(document: Document) {
    return this.isForward(document) ? this.anchor : this.focus;
  }

  /**
   * 获取end point
   */
  public getEnd(document: Document) {
    return this.isBackward(document) ? this.anchor : this.focus;
  }

  /**
   * 根据PointName，获取point
   */
  public getPoint(pointName: SupportPointName, document: Document) {
    if (pointName === 'start') {
      return this.getStart(document);
    } else if (pointName === 'end') {
      return this.getEnd(document);
    } else {
      return this[pointName];
    }
  }

  /**
   * 同时获取start point、end point
   */
  public sort(document: Document) {
    return {
      start: this.getStart(document),
      end: this.getEnd(document),
    };
  }

  /**
   * 把当前的数据选区的 Points 转换成 TextPoints。
   */
  public convertToTextPoints(document: Document) {
    let { start, end } = this.sort(document);
    if (this.isCollapsed) {
      const point = start.convertToTextPoint(document);
      return { anchor: point, focus: point, start: point, end: point };
    }
    if (start.isEdgePoint()) {
      if (start.edge === EdgePoint.BEFORE) {
        start = start.convertToTextPoint(document);
      } else {
        const startNode = document.getNextText(start.key) || document.getFirstNode();
        start = TextPoint.create({
          key: startNode.key,
          offset: 0,
        });
      }
    }
    if (end.isEdgePoint()) {
      if (end.edge === EdgePoint.AFTER) {
        end = end.convertToTextPoint(document);
      } else {
        const endNode = document.getPreviousText(end.key) || document.getLastNode();
        end = TextPoint.create({
          key: endNode.key,
          offset: endNode.text.length,
        });
      }
    }
    const isForward = this.isForward(document);
    const anchor = isForward ? start : end;
    const focus = isForward ? end : start;
    return { anchor, focus, start, end };
  }

  /**
   * 获得当前选区所处的 Node
   * @param document
   */
  public getClosestNode(document: Document) {
    const { start, end } = this.convertToTextPoints(document);
    const aPath = document.getPath(start.key);
    const bPath = document.getPath(end.key);
    if (!aPath || !bPath) {
      return null;
    }
    const node = document.getNodeByPath(Path.common(aPath, bPath));
    if (node?.isText()) {
      return document.getClosestBlock(node.key);
    }
    return node as Block | null;
  }

  /**
   * 选区是否在Hanging状态
   */
  public get isHanging() {
    return Boolean(this.data.isHanging);
  }

  public get isCollapsedAtEdge() {
    return this.isCollapsed && this.anchor.isEdgePoint();
  }

  /**
   * 拷贝出一个新的选区，并设置选区的Haning状态
   */
  public setHanging(isHanging: boolean) {
    if (this.data.isHanging !== isHanging) {
      return this.merge({
        data: {
          ...this.data,
          isHanging,
        },
      });
    }
    return this;
  }

  /**
   * 拷贝出一个新的选区，并把start point移到end point的位置
   */
  public moveToEnd(document: Document) {
    if (this.isBackward(document)) {
      return this.merge({
        focus: this.getEnd(document),
      });
    }
    return this.merge({
      anchor: this.getEnd(document),
    });
  }

  public moveStartToStartOfNode(node: Node, controller: Controller) {
    const { document } = controller.value;
    if (this.isForward(document)) {
      return this.moveAnchorToStartOfNode(node, controller);
    } else {
      return this.moveFocusToStartOfNode(node, controller);
    }
  }

  public moveStartToEndOfNode(node: Node, controller: Controller) {
    const { document } = controller.value;
    if (this.isForward(document)) {
      return this.moveAnchorToEndOfNode(node, controller);
    } else {
      return this.moveFocusToEndOfNode(node, controller);
    }
  }

  public moveEndToStartOfNode(node: Node, controller: Controller) {
    const { document } = controller.value;
    if (this.isForward(document)) {
      return this.moveFocusToStartOfNode(node, controller);
    } else {
      return this.moveAnchorToStartOfNode(node, controller);
    }
  }

  public moveEndToEndOfNode(node: Node, controller: Controller) {
    const { document } = controller.value;
    if (this.isForward(document)) {
      return this.moveFocusToEndOfNode(node, controller);
    } else {
      return this.moveAnchorToEndOfNode(node, controller);
    }
  }

  public moveAnchorToStartOfNode(node: Node, controller: Controller) {
    return this.set('anchor', controller.query(pointAtStartOfNode, node));
  }

  public moveAnchorToEndOfNode(node: Node, controller: Controller) {
    return this.set('anchor', controller.query(pointAtEndOfNode, node));
  }

  public moveFocusToStartOfNode(node: Node, controller: Controller) {
    return this.set('focus', controller.query(pointAtStartOfNode, node));
  }

  public moveFocusToEndOfNode(node: Node, controller: Controller) {
    return this.set('focus', controller.query(pointAtEndOfNode, node));
  }

  public moveAnchorTo(key: string, where: number | PointEdge) {
    return this.set('anchor', Point.createFromParams(key, where));
  }

  public moveFocusTo(key: string, where: number | PointEdge) {
    return this.set('focus', Point.createFromParams(key, where));
  }

  public moveToStartOfNode(node: Node, controller: Controller) {
    const anchor = controller.query(pointAtStartOfNode, node);
    const focus = anchor;
    return this.merge({ anchor, focus });
  }

  public moveToEndOfNode(node: Node, controller: Controller) {
    const anchor = controller.query(pointAtEndOfNode, node);
    const focus = anchor;
    return this.merge({ anchor, focus });
  }

  /**
   * 拷贝出一个新的选区，并把anchor和focus调换位置
   */
  public flip() {
    return this.merge({
      anchor: this.focus,
      focus: this.anchor,
    });
  }

  public moveAnchorBackward(n = 1, mover?: PointMoverCreator) {
    return mover
      ? mover('anchor', n)(this)
      : this.set('anchor', this.anchor.moveBackward(n));
  }

  public moveAnchorForward(n = 1, mover?: PointMoverCreator) {
    return mover
      ? mover('anchor', n)(this)
      : this.set('anchor', this.anchor.moveForward(n));
  }

  public moveFocusBackward(n = 1, mover?: PointMoverCreator) {
    return mover
      ? mover('focus', n)(this)
      : this.set('focus', this.focus.moveBackward(n));
  }

  public moveFocusForward(n = 1, mover?: PointMoverCreator) {
    return mover
      ? mover('focus', n)(this)
      : this.set('focus', this.focus.moveForward(n));
  }

  public moveToFocus() {
    return this.set('anchor', this.focus);
  }

  public moveToAnchor() {
    return this.set('focus', this.anchor);
  }

  public moveToStart(document: Document) {
    const start = this.getStart(document);
    return this.merge({
      anchor: start,
      focus: start,
    });
  }

  public moveToRangeOfNode(node: Node, controller: Controller) {
    return this.moveAnchorToStartOfNode(node, controller).moveFocusToEndOfNode(
      node,
      controller,
    );
  }

  public moveForward(n = 1, mover?: PointMoverCreator) {
    if (!mover) {
      return this.updatePoints((point: TextPoint | EdgePoint) => {
        return point.isTextPoint() ? point.moveForward(n) : point;
      });
    }
    return this.moveAnchorForward(n, mover).moveFocusForward(n, mover);
  }

  public moveStartBackward(document: Document, n: number) {
    if (this.isForward(document)) {
      return this.moveAnchorBackward(n);
    }
    return this.moveFocusBackward(n);
  }

  public moveStartForward(document: Document, n: number) {
    return this.moveStartBackward(document, -n);
  }

  public moveEndBackward(document: Document, n: number) {
    if (this.isForward(document)) {
      return this.moveFocusBackward(n);
    }
    return this.moveAnchorBackward(n);
  }

  public moveEndForward(document: Document, n: number) {
    return this.moveEndBackward(document, -n);
  }

  public moveBackward(n = 1, mover?: PointMoverCreator) {
    if (!mover) {
      return this.updatePoints((point: TextPoint | EdgePoint) => {
        return point.isTextPoint() ? point.moveBackward(n) : point;
      });
    }
    return this.moveAnchorBackward(n, mover).moveFocusBackward(n, mover);
  }

  public move(mover: Mover) {
    return mover(this);
  }

  public moveToStartOfDocument(controller: Controller) {
    return this.moveToStartOfNode(controller.value.document, controller);
  }

  public moveToEndOfDocument(controller: Controller) {
    return this.moveToEndOfNode(controller.value.document, controller);
  }

  public movePointEdgeKlass(
    controller: Controller,
    pointName: SupportPointName,
    edge: SupportEdge,
    klass: SupportKlass,
  ) {
    const { document } = controller.value;
    const PName = pointName.slice(0, 1).toUpperCase() + pointName.slice(1);
    const Edge = edge.slice(0, 1).toUpperCase() + edge.slice(1);
    const Object = klass.slice(0, 1).toUpperCase() + klass.slice(1);
    const method = `move${PName}To${Edge}OfNode`;
    const getNode = klass === 'text' ? 'getNode' : `getClosest${Object}`;
    const p = this.getPoint(pointName, document);
    const node = p.isTextPoint() ? document[getNode](p.key) : document.getNode(p.key);
    if (!node) {
      return this;
    }
    const newSelection = this[method](node, controller) as Selection;
    return newSelection;
  }

  public movePointEdgeSideKlass(
    controller: Controller,
    pointName: SupportPointName,
    edge: SupportEdge,
    side: SupportSide,
    klass: SupportKlass,
  ) {
    const { document } = controller.value;
    const PName = pointName.slice(0, 1).toUpperCase() + pointName.slice(1);
    const Edge = edge.slice(0, 1).toUpperCase() + edge.slice(1);
    const Side = side.slice(0, 1).toUpperCase() + side.slice(1);
    const Klass = klass.slice(0, 1).toUpperCase() + klass.slice(1);
    const method = `move${PName}To${Edge}OfNode`;
    const getNode = klass === 'text' ? 'getNode' : `getClosest${Klass}`;
    const getDirectionNode = `get${Side}${Klass}`;
    const p = this.getPoint(pointName, document);

    const node = document[getNode](p.key);
    if (!node) {
      return this;
    }
    const target = document[getDirectionNode](node.key);
    if (!target) {
      return this;
    }
    const newSelection = this[method](target, controller) as Selection;
    return newSelection;
  }

  public updatePoints(
    updater: (point: TextPoint | EdgePoint) => TextPoint | EdgePoint,
  ) {
    let { anchor, focus } = this;
    anchor = updater(anchor);
    focus = updater(focus);
    return this.merge({ anchor, focus });
  }

  public toJSON(options?: SerializeOptions) {
    return {
      klass: 'selection',
      anchor: this.anchor.toJSON(options),
      focus: this.focus.toJSON(options),
      marks: this.marks ? this.marks.map(m => m.toJSON()) : null,
    };
  }
}
