/**
 * @packageDocumentation
 * @module Models
 */
import isPlainObject from 'is-plain-object';
import ImmutableRecord from './immutableRecord';
import Element from './element';
import Text from './text';
import { Serializable, SerializeOptions } from '../interfaces/serializable';

export interface NodeProperties {
  key?: string;
}

export default abstract class Node extends ImmutableRecord
  implements Serializable {
  static createProperties(attrs: string | object = {}) {
    if (typeof attrs === 'string') {
      return { type: attrs };
    }

    if (isPlainObject(attrs)) {
      return attrs;
    }

    throw new Error(
      `\`Node.createProperties\` only accepts objects, strings, but you passed it: ${attrs}`,
    );
  }

  public readonly key: string;

  protected constructor(props: Required<NodeProperties>) {
    super();
    const { key } = props;
    this.key = key;
  }

  public abstract get text(): string;

  public abstract regenerateKey(): Node;

  /**
   * 获取自身树下的所有 Text 节点
   * 如果自身是 Text，则返回只含有自身的数组
   */
  public abstract getTexts(): Text[];

  /**
   * 把当前的 Node 转化成子类型（Text 或者 Element）
   */
  public toSubType(): Text | Element {
    const node: Node = this;
    if (node instanceof Text) {
      return node;
    } else if (node instanceof Element) {
      return node;
    }
    throw new Error(`${node} is not a Node`);
  }

  /**
   * 此节点是否是一个Text节点。see Text.isText
   */
  public isText(): this is Text {
    return this instanceof Text;
  }

  /**
   * 此节点是否是一个Element节点。see Element.isElement
   */
  public isElement(): this is Element {
    return this instanceof Element;
  }

  /**
   * 获取第一个叶子Node；如果自身就是一个叶子Node，则返回自身
   * 根据目前Schema规范，返回的都都是一个text；但是后续Cangjie扩展，可能返回其它类型Node
   */
  public getFirstNode(): Node {
    if (!this.isElement() || !this.nodes.length) {
      return this;
    }
    return this.nodes[0].getFirstNode();
  }

  /**
   * 获取最后一个叶子Node；如果自身就是一个叶子Node，则返回自身
   * 根据目前Schema规范，返回的都都是一个text；但是后续Cangjie扩展，可能返回其它类型Node
   */
  public getLastNode(): Node {
    if (!this.isElement() || !this.nodes.length) {
      return this;
    }
    return this.nodes[this.nodes.length - 1].getLastNode();
  }

  /**
   * 获取第一个Text
   * 根据目前Schema规范，都会返回一个text；但是后续Cangjie扩展，可能返回null
   */
  public getFirstText(): Text | null {
    if (this.isText()) {
      return this;
    }
    if (!this.isElement() || !this.nodes.length) {
      return null;
    }
    return this.nodes[0].getFirstText();
  }

  /**
   * 获取最后一个Text
   * 根据目前Schema规范，都会返回一个text；但是后续Cangjie扩展，可能返回null
   */
  public getLastText(): Text | null {
    if (this.isText()) {
      return this;
    }
    if (!this.isElement() || !this.nodes.length) {
      return null;
    }
    return this.nodes[this.nodes.length - 1].getLastText();
  }

  public abstract toJSON(options?: SerializeOptions): any;
}
