/**
 * @packageDocumentation
 * @module Hooks
 */
import React from 'react';
import Node from '../models/node';
import Block from '../models/block';
import Controller from '../controller';

interface DocStyleData {
  rPr?: Record<string, any>;
  pPr?: Record<string, any>;
}

export interface DocStyle {
  docDefaults?: {
    data: DocStyleData;
    name: string;
    type: string;
    default?: number;
  };
  tableHeader?: Record<string, any>;
}

export interface RenderNodeStyleProps {
  /**
   * 节点
   */
  node: Node;
  /**
   * 文档样式表
   */
  docStyle?: DocStyle;
}

export type RenderContentStyleProps = Omit<RenderNodeStyleProps, 'node'>;

/**
 * 样式 context
 */
export const StyleContext = React.createContext<DocStyle | undefined>(undefined);

export function useStyle() {
  return React.useContext(StyleContext);
}

/**
 * 获取节点样式
 * @returns React.CSSProperties
 */
export function useNodeStyle(
  controller: Controller,
  node: Node,
  visible = true,
) {
  const docStyle = React.useContext(StyleContext);
  // 非 block 元素不消费 style，return 以提高性能
  if (!Block.isBlock(node)) return {};
  const style: React.CSSProperties = controller.run('getNodeStyle', {
    node,
    docStyle,
  }) || {};
  if (visible === false) {
    style.display = 'none';
  }
  return style;
}

/**
 * 获取全局默认文本样式
 * @returns React.CSSProperties
 */
export function useContentStyle(controller: Controller) {
  const docStyle = React.useContext(StyleContext);
  const style: React.CSSProperties = controller.run('getContentStyle', {
    docStyle,
  }) || {};
  return style;
}
