export const CONTEXT_SPLIT = '@';

export function getContexts(context?: string) {
  return context ? context.split(CONTEXT_SPLIT) : [];
}

export function getContextMeta<T>(context?: string) {
  if (typeof context !== 'string') {
    return null;
  }

  const matched = context.match(/\((.*?)\)$/);

  if (matched) {
    try {
      return JSON.parse(matched[1]) as T;
    } catch {
      return null;
    }
  }

  return null;
}
