import Action, {
  ConvertToPluginActionCreators,
  ConvertToPluginActionPredicates,
} from '../../interfaces/action';
import Element from '../../models/element';
import { UUIDMap, UUIDSDiff } from './types';

export const UPDATE_UUIDS = Symbol('update_uuids');
export const GENERATE_UUID = Symbol('generate_uuid');
export interface UpdateUUIDsPayload {
  /** 最新的 UUID Map */
  uuids: UUIDMap;
  diff: UUIDSDiff;
}

export interface UpdateUUIDsAction {
  type: typeof UPDATE_UUIDS;
  payload: UpdateUUIDsPayload;
}

export interface GenerateUUIDPayload {
  node: Element;
}
export interface GenerateUUIDAction {
  type: typeof GENERATE_UUID;
  payload: GenerateUUIDPayload;
}

export function updateUUIDs(payload: UpdateUUIDsPayload): UpdateUUIDsAction {
  return {
    type: UPDATE_UUIDS,
    payload,
  };
}

export function isUpdateUUIDs(action: Action): action is UpdateUUIDsAction {
  return action.type === UPDATE_UUIDS;
}

export function generateUUID(payload: GenerateUUIDPayload) {
  return {
    type: GENERATE_UUID,
    payload,
  };
}

export function isGenerateUUID(action: Action): action is GenerateUUIDAction {
  return action.type === GENERATE_UUID;
}

export const actions = {
  updateUUIDs,
  generateUUID,
};

export const isActions = {
  isUpdateUUIDs,
  isGenerateUUID,
};

declare module '@ali/4ever-cangjie' {
  /** type combination for commands、queries、actions */
  interface PluginActionCreators
    extends ConvertToPluginActionCreators<typeof actions> {}
  interface PluginActionPredicates
    extends ConvertToPluginActionPredicates<typeof isActions> {}
}
