import Controller from '../../controller';
import { Selector } from '../../constants';

export const ROOT_TO_CONTROLLER = new WeakMap<Element | Document, Controller>();
export const CONTROLLER_TO_KEY_DOM = new WeakMap<Controller, Map<string, HTMLElement>>();
/**
 * 这里原先作用应该是提供缓存命中率，由于内存泄露问题，暂时中断GLOBAL_KEY_DOM的缓存
 * 不确定什么场景下CONTROLLER_TO_KEY_DOM会失效，需要用到GLOBAL_KEY_DOM
 * 如果需要继续维持的话，这里也可以通过documentKey的维度进行缓存，类似：
 * const GLOBAL_KEY_DOM = new Map<string, Map<string, HTMLElement>>();
 */
export const GLOBAL_KEY_DOM = new Map<string, HTMLElement>();

function getElementByCangjieKey(key: string, root: Element | Document): HTMLElement | null {
  let KEY_DOM;
  const controller = ROOT_TO_CONTROLLER.get(root);
  if (controller) {
    if (!CONTROLLER_TO_KEY_DOM.has(controller)) {
      CONTROLLER_TO_KEY_DOM.set(controller, new Map());
    }
    KEY_DOM = CONTROLLER_TO_KEY_DOM.get(controller)!;
  }

  if (KEY_DOM && KEY_DOM.has(key)) {
    return KEY_DOM.get(key)!;
  }
  if (root instanceof Element && key === root.getAttribute(Selector.key)) {
    return root as HTMLElement;
  }
  return root.querySelector<HTMLElement>(`[${Selector.key}="${key}"]`);
}

export default getElementByCangjieKey;
