/**
 * @packageDocumentation
 * @module Models
 */
import isPlainObject from 'is-plain-object';
import Data from '../interfaces/data';
import ImmutableRecord from './immutableRecord';
import Node from './node';
import Element from './element';

function defaultMergeData(a: Data, b: Data): Data {
  return { ...a, ...b };
}

export interface InjectionProperties<T extends Data = Data> {
  key: string;
  data: T;
  mergeData?: typeof defaultMergeData;
}

export interface InjectionJSON<T extends Data = Data> {
  key: string;
  data: T;
}

export default class Injection<D extends Data = Data> extends ImmutableRecord {
  static create<T extends Data = Data>(props: Injection<T> | InjectionProperties<T>) {
    if (Injection.isInjection(props)) {
      return props;
    }

    if (isPlainObject(props)) {
      const { key, data, mergeData } = props;
      return new Injection({
        key,
        data,
        mergeData,
      });
    }

    throw new Error(
      `\`Injection.create\` only accepts objects or injections, but you passed it: ${props}`,
    );
  }

  static isInjection(maybeInjection: any): maybeInjection is Injection {
    return maybeInjection instanceof Injection;
  }

  static injectIntoNode(injections: Injection[], node: Node) {
    if (!injections.length || !Element.isElement(node)) {
      return node;
    }
    return injections.reduce((n, injection) => {
      if (injection.key !== n.key) {
        return n;
      }
      const { mergeData } = injection;
      return n.set('data', mergeData(n.data, injection.data));
    }, node);
  }

  static fromJSON(json: InjectionJSON) {
    const { key, data } = json;
    return Injection.create({
      key, data,
    });
  }

  constructor(props: InjectionProperties<D>) {
    super();
    const { key, data, mergeData = defaultMergeData } = props;
    this.key = key;
    this.data = data;
    this.mergeData = mergeData;
    this.freeze();
  }

  public readonly key: string;

  public readonly data: D;

  public readonly mergeData: typeof defaultMergeData;

  public merge(props: Partial<InjectionProperties<D>>) {
    return new Injection({
      key: this.key,
      data: this.data,
      mergeData: this.mergeData,
      ...props,
    });
  }

  public set<T extends keyof InjectionProperties<D>>(
    key: T,
    value: InjectionProperties<D>[T],
  ) {
    return this.merge({
      [key]: value,
    });
  }

  public toJSON() {
    return {
      key: this.key,
      data: this.data,
    };
  }
}
