/* eslint-disable react/no-find-dom-node */
/**
 * @packageDocumentation
 * @ignore
 */
import * as React from 'react';
import equal from 'fast-deep-equal';
import { curry, omit } from 'lodash-es';
import Controller from '../controller/index';
import Selection, { SelectionTrigger, SupportEdge } from '../models/selection';
import { TextPoint, EdgePoint } from '../models/point';
import CangjieSelectEvent from '../events/cangjieSelect';
import caretRangeFromTargetAndCoord from '../utils/dom/caretRangeFromTargetAndCoord';
import findDOMRange from '../utils/dom/findDOMRange';
import findDOMNode from '../utils/dom/findDOMNode';
import hotKeys from '../utils/hotkeys';
import { CURSOR_WIDTH } from '../constants';
import scrollToVisible from '../utils/dom/scrollToVisible';
import isPointAtEdge, {
  findRectsInEdgePoint,
} from '../utils/dom/isPointAtEdge';
import {
  isCoordAtLeftSide,
} from '../utils/dom/caretHanging';
import {
  movePointForward,
  movePointBackward,
  movePointWordBackward,
  movePointWordForward,
} from '../utils/selection';
import {
  getUpsidePoint,
  getUpsideRange,
  getDownsidePoint,
  getDownsideRange,
  pointAtDistance,
} from '../queries/selection';
import { CHARACTERS_LENGTH_AFTER_USER_INPUT } from './useInputDetector';
import { IS_IOS, IS_MAC, IS_MOBILE, IS_SAFARI } from '../utils/environment';
import shoudStopInsideVoid from '../utils/shouldStopInsideVoid';
import { startScroll, stopScroll } from '../utils/dom/outsideScroller';
import { PerfType } from '../interfaces/perf';
import Data from '../interfaces/data';

const MULTIPLE_CLICK_DELAY = 500;
const TAP_HOLD_DELAY = 600;
// 方向键需要快速响应，目前暂未发现明显问题
const CARET_MOVE_GAP_TIME = 0;
const MULTIPLE_CLICK_RANGE = 10;

function getWordSelection(
  controller: Controller,
  target: HTMLElement,
  x: number,
  y: number,
) {
  const r = caretRangeFromTargetAndCoord(target, x, y, controller);
  if (!r) {
    return null;
  }
  const { document } = controller.value;
  return Selection.selectWordAtPoint(document, r.anchor);
}

// get a new selection at Line Start or Line End of the current selection.
function getSelectionAtLine(
  where: SupportEdge,
  controller: Controller,
  selection: Selection | null,
) {
  const { value } = controller;
  const { document } = value;
  if (!selection) {
    return null;
  }
  const isStart = where === 'start';
  const { focus, isHanging } = selection;
  if (focus.isEdgePoint()) {
    const edge = isStart ? EdgePoint.BEFORE : EdgePoint.AFTER;
    const anchor = EdgePoint.create({ key: focus.key, edge });
    return Selection.create({ anchor, focus: anchor });
  }
  let range: Range | null = null; // selection's range
  let blockDomNode: Element | null = null; // selection's closest block node
  try {
    const block = document.getClosestBlock(focus.key);
    range = findDOMRange(selection, controller);
    if (block) {
      blockDomNode = findDOMNode(block.key);
    }
  } catch (_) {
    return null;
  }
  if (!range || !blockDomNode) {
    return null;
  }

  // selection's rect
  const rects = range.getClientRects();
  if (!rects.length) {
    return null;
  }
  let rect = rects[isHanging ? rects.length - 1 : 0];
  const edgeRects = isPointAtEdge(focus) ? findRectsInEdgePoint(focus) : [];
  if (edgeRects && edgeRects.length) {
    rect = edgeRects[isHanging ? edgeRects.length - 1 : 0];
  }
  // selection's closest block's rect
  scrollToVisible(blockDomNode as HTMLElement, where, CURSOR_WIDTH);
  const blockRect = blockDomNode.getBoundingClientRect();

  // target selection's position
  const position = {
    x: where === 'start' ? blockRect.left + 1 : blockRect.right - 1,
    y: rect.top + rect.height / 2,
  };

  const newRange = caretRangeFromTargetAndCoord(
    blockDomNode,
    position.x,
    position.y,
    controller,
  );

  if (!newRange) {
    return null;
  }

  let { anchor: point } = newRange;

  // if the target point is inside a void element, take the next point
  const parent = document.getParent(point.key);
  if (parent && controller.query('isVoid', parent)) {
    point = controller.query(pointAtDistance, point, isStart ? -1 : 1);
  }
  const newSelection = Selection.create({
    anchor: point,
    focus: point,
  });
  return newSelection;
}

function shouldSelectOnRightClick(targetPoint: TextPoint | EdgePoint, controller: Controller) {
  const { selection, document } = controller.value;

  if (!selection || selection.isCollapsed) {
    return true;
  }

  const { start, end } = selection.sort(document);
  const rangeStart = Selection.create({
    anchor: targetPoint,
    focus: start,
  });
  const rangeEnd = Selection.create({
    anchor: end,
    focus: targetPoint,
  });
  const isBeforeStart = rangeStart.isExpanded && rangeStart.isForward(document);
  const isAfterEnd = rangeEnd.isExpanded && rangeEnd.isForward(document);
  return isBeforeStart || isAfterEnd;
}

export default function useSelectionCalculator(
  controller: Controller,
  textAreaRef: React.MutableRefObject<HTMLTextAreaElement | null>,
  scrollableContainerRef: React.MutableRefObject<HTMLDivElement | null>,
) {
  const isJustTapHold = React.useRef(false);

  const tapHoldTimer = React.useRef<number | null>(null);

  const outsideScoller = React.useRef<ReturnType<typeof startScroll> | null>(null);

  const lastSingleClick = React.useRef<{ time: number; x: number; y: number }>({
    time: 0,
    x: 0,
    y: 0,
  });
  const lastDoubleClick = React.useRef<{ time: number; x: number; y: number }>({
    time: 0,
    x: 0,
    y: 0,
  });

  const lastCaretMoveTime = React.useRef(0);
  const prevSelectionRef = React.useRef<Selection | null>(null);

  const onCangjieSelect = React.useCallback(
    (selection: Selection, trigger?: SelectionTrigger) => {
      const { composing, isFocused } = controller.value;
      if (composing && selection.isExpanded) {
        return;
      }
      if (!isFocused) {
        controller.run('onCangjieFocus');
      }

      const newSelection = selection.marks === null ? selection : selection.set('marks', null);
      const subtypeTriggerMap = {
        selectStart: 'focusText',
        selecting: 'selectText',
        selectAll: 'selectAll',
      };
      if (!trigger || !subtypeTriggerMap[trigger] || equal(prevSelectionRef.current, newSelection)) {
        // 需要 delay 丢弃的 perf 事件
        controller.perfDrop(PerfType.selection);
      }
      prevSelectionRef.current = newSelection;
      return controller.run(
        'onCangjieSelect',
        CangjieSelectEvent({
          selection: newSelection,
          trigger: trigger || 'userSelect',
        }),
      );
    },
    [controller],
  );

  const moveUpward = () => {
    const now = Date.now();
    const isGapTimeTooShort =
      now - lastCaretMoveTime.current < CARET_MOVE_GAP_TIME;
    if (isGapTimeTooShort) {
      return;
    }
    lastCaretMoveTime.current = now;

    const newRange = controller.query(getUpsideRange);

    if (!newRange) {
      return;
    }
    onCangjieSelect(
      newRange,
      SelectionTrigger.MoveUpward,
    );
  };

  const moveDownward = () => {
    const now = Date.now();
    const isGapTimeTooShort =
      now - lastCaretMoveTime.current < CARET_MOVE_GAP_TIME;
    if (isGapTimeTooShort) {
      return;
    }
    lastCaretMoveTime.current = now;
    const newRange = controller.query(getDownsideRange);

    if (!newRange) {
      return;
    }
    onCangjieSelect(
      newRange,
      SelectionTrigger.MoveDownward,
    );
  };

  const moveBackward = () => {
    const { document, selection } = controller.value;
    if (selection.isExpanded) {
      const isAtEdge = isPointAtEdge(selection.getStart(document));
      onCangjieSelect(
        selection.moveToStart(document).setHanging(isAtEdge),
      );
      return;
    }

    const isProceeded = shoudStopInsideVoid(controller, 'moveBackward');
    if (isProceeded) {
      return;
    }

    const isAtEdge = isPointAtEdge(selection.anchor);
    if (isAtEdge && selection.isHanging) {
      onCangjieSelect(selection.setHanging(false));
      return;
    }

    const now = Date.now();
    const isGapTimeTooShort =
      now - lastCaretMoveTime.current < CARET_MOVE_GAP_TIME;
    if (isGapTimeTooShort) {
      return;
    }
    const newSelection = selection
      .moveAnchorBackward(
        1,
        movePointBackward(
          controller,
          curry.placeholder,
          curry.placeholder,
          'offset',
        ),
      )
      .moveToAnchor();
    const isNextPointAtEdge = isPointAtEdge(newSelection.anchor);
    lastCaretMoveTime.current = now;
    onCangjieSelect(
      newSelection.setHanging(isNextPointAtEdge),
      SelectionTrigger.MoveBackward,
    );
  };

  const moveForward = () => {
    const { document, selection } = controller.value;
    if (selection.isExpanded) {
      onCangjieSelect(selection.moveToEnd(document));
      return;
    }
    const isProceeded = shoudStopInsideVoid(controller, SelectionTrigger.MoveForward);
    if (isProceeded) {
      return;
    }
    const isAtEdge = isPointAtEdge(selection.anchor);
    if (isAtEdge && !selection.isHanging) {
      onCangjieSelect(selection.setHanging(true));
      return;
    }

    const now = Date.now();
    const isGapTimeTooShort =
      now - lastCaretMoveTime.current < CARET_MOVE_GAP_TIME;
    if (isGapTimeTooShort) {
      return;
    }
    lastCaretMoveTime.current = now;
    onCangjieSelect(
      selection
        .moveAnchorForward(
          1,
          movePointForward(
            controller,
            curry.placeholder,
            curry.placeholder,
            'offset',
          ),
        )
        .moveToAnchor(),
      SelectionTrigger.MoveForward,
    );
  };

  const handleNativeSelectionChange = () => {
    if (!textAreaRef.current || document.activeElement !== textAreaRef.current) {
      return;
    }

    // iOS - Voice Input will try to select all words when finished
    // ref: https://aone.alibaba-inc.com/v2/project/585561/bug/32431619
    if (textAreaRef.current.selectionStart !== textAreaRef.current.selectionEnd) {
      return;
    }
    // Detect users move selection by external keyboard arrow keys under iOS@<13
    // Ref: https://bugs.webkit.org/show_bug.cgi?id=149054
    const defaultCaretPosition = textAreaRef.current.value.length - CHARACTERS_LENGTH_AFTER_USER_INPUT;
    const newCaretPosition = textAreaRef.current.selectionStart;

    if (IS_IOS) {
      // Mobile 3rd IME will delete to beginning when choosing english suggestion
      // In this case both `newCaretPosition` and `defaultCaretPosition` is 0
      // We handle this case by adding `defaultCaretPosition !== 0`
      if (newCaretPosition === 0 && defaultCaretPosition !== 0) {
        moveUpward();
      } else if (newCaretPosition === textAreaRef.current.value.length) {
        moveDownward();
      } else if (newCaretPosition - defaultCaretPosition < 0) {
        moveBackward();
      } else if (newCaretPosition - defaultCaretPosition > 0) {
        moveForward();
      }
    }

    // Reset selection to end of textarea while users change it.
    // e.g. Users move selection by 3D touch.
    // Note: selectionStart is offset of characters and works fine with emoji.
    if (textAreaRef.current.selectionStart !== defaultCaretPosition) {
      textAreaRef.current.selectionStart = defaultCaretPosition;
    }
    if (textAreaRef.current.selectionEnd !== defaultCaretPosition) {
      textAreaRef.current.selectionEnd = defaultCaretPosition;
    }
  };
  React.useEffect(() => {
    if (IS_MOBILE) {
      document.addEventListener('selectionchange', handleNativeSelectionChange);
      return () => {
        document.removeEventListener('selectionchange', handleNativeSelectionChange);
      };
    }
    return undefined;
  }, [controller]);

  React.useEffect(() => {
    return () => {
      if (outsideScoller.current) {
        outsideScoller.current.cancel();
        outsideScoller.current = null;
      }
    };
  }, [controller]);

  return React.useMemo(
    () => ({
      onKeyDown(event: React.KeyboardEvent | KeyboardEvent) {
        const { value, readOnly } = controller;
        const { document, selection } = value;
        if (!selection) {
          return;
        }

        const now = Date.now();
        const isGapTimeTooShort =
          now - lastCaretMoveTime.current < CARET_MOVE_GAP_TIME;

        if (hotKeys.isSelectAll(event)) {
          controller.perfStart({ type: PerfType.selection, subtype: 'selectAll' });
          event.preventDefault();
          onCangjieSelect(selection.moveToRangeOfNode(document, controller), SelectionTrigger.SelectAll);
          return;
        }
        if (hotKeys.isMoveBackward(event)) {
          event.preventDefault();
          moveBackward();
          return;
        }
        if (hotKeys.isMoveForward(event)) {
          event.preventDefault();
          moveForward();
          return;
        }
        if (hotKeys.isMoveUpward(event)) {
          if (readOnly && selection.isCollapsed) {
            return;
          }
          event.preventDefault();
          moveUpward();
          return;
        }
        if (hotKeys.isMoveDownward(event)) {
          if (readOnly && selection.isCollapsed) {
            return;
          }
          event.preventDefault();
          moveDownward();
          return;
        }
        if (hotKeys.isMoveWordBackward(event)) {
          event.preventDefault();
          if (isGapTimeTooShort) {
            return;
          }
          lastCaretMoveTime.current = now;
          onCangjieSelect(
            selection
              .move(movePointWordBackward(controller, 'focus'))
              .moveToFocus(),
          );
          return;
        }
        if (hotKeys.isMoveWordForward(event)) {
          event.preventDefault();
          if (isGapTimeTooShort) {
            return;
          }
          lastCaretMoveTime.current = now;
          onCangjieSelect(
            selection
              .move(movePointWordForward(controller, 'focus'))
              .moveToFocus(),
          );
          return;
        }
        if (hotKeys.isMoveLineBackward(event)) {
          event.preventDefault();
          const range = getSelectionAtLine('start', controller, selection);
          if (range) {
            const isAtEdge = isPointAtEdge(range.anchor);
            onCangjieSelect(range.setHanging(isAtEdge));
          }
          return;
        }
        if (hotKeys.isMoveLineForward(event)) {
          event.preventDefault();
          const range = getSelectionAtLine('end', controller, selection);
          if (range) {
            onCangjieSelect(range);
          }
          return;
        }
        if (hotKeys.isMoveToDocumentStart(event)) {
          event.preventDefault();
          onCangjieSelect(selection.moveToStartOfDocument(controller));
          return;
        }
        if (hotKeys.isMoveToDocumentEnd(event)) {
          event.preventDefault();
          onCangjieSelect(selection.moveToEndOfDocument(controller));
          return;
        }
        if (hotKeys.isExtendBackward(event)) {
          event.preventDefault();
          if (isGapTimeTooShort) {
            return;
          }
          lastCaretMoveTime.current = now;
          onCangjieSelect(
            selection.moveFocusBackward(
              1,
              movePointBackward(
                controller,
                curry.placeholder,
                curry.placeholder,
                'offset',
              ),
            ),
          );
          return;
        }
        if (hotKeys.isExtendForward(event)) {
          event.preventDefault();
          if (isGapTimeTooShort) {
            return;
          }
          lastCaretMoveTime.current = now;
          onCangjieSelect(
            selection.moveFocusForward(
              1,
              movePointForward(
                controller,
                curry.placeholder,
                curry.placeholder,
                'offset',
              ),
            ),
          );
          return;
        }
        if (hotKeys.isExtendUpward(event)) {
          event.preventDefault();
          if (isGapTimeTooShort || !selection) {
            return;
          }
          lastCaretMoveTime.current = now;
          const point = getUpsidePoint(
            controller,
            selection.focus,
            selection.isHanging,
          );
          const { focus, anchor } = selection;
          onCangjieSelect(
            Selection.create({
              anchor,
              focus: point || focus.moveToStartOfNode(document),
            }),
          );
          return;
        }
        if (hotKeys.isExtendDownward(event)) {
          event.preventDefault();
          if (isGapTimeTooShort || !selection) {
            return;
          }
          lastCaretMoveTime.current = now;
          const { focus, anchor, isHanging } = selection;
          const point = getDownsidePoint(controller, focus, isHanging);
          onCangjieSelect(
            Selection.create({
              anchor,
              focus: point || focus.moveToEndOfNode(document),
            }),
          );
          return;
        }
        if (hotKeys.isExtendWordBackward(event)) {
          event.preventDefault();
          if (isGapTimeTooShort) {
            return;
          }
          lastCaretMoveTime.current = now;
          onCangjieSelect(
            selection.move(movePointWordBackward(controller, 'focus')),
          );
          return;
        }
        if (hotKeys.isExtendWordForward(event)) {
          event.preventDefault();
          if (isGapTimeTooShort) {
            return;
          }
          lastCaretMoveTime.current = now;
          onCangjieSelect(
            selection.move(movePointWordForward(controller, 'focus')),
          );
          return;
        }
        if (hotKeys.isExtendLineBackward(event)) {
          event.preventDefault();
          const range = getSelectionAtLine('start', controller, selection);
          if (range) {
            onCangjieSelect(
              Selection.create({
                anchor: selection.anchor,
                focus: range.focus,
              }),
            );
          }
          return;
        }
        if (hotKeys.isExtendToDocumentStart(event)) {
          event.preventDefault();
          onCangjieSelect(selection.moveFocusToStartOfNode(document, controller));
          return;
        }
        if (hotKeys.isExtendToDocumentEnd(event)) {
          event.preventDefault();
          onCangjieSelect(selection.moveFocusToEndOfNode(document, controller));
          return;
        }
        if (hotKeys.isExtendLineForward(event)) {
          event.preventDefault();
          const range = getSelectionAtLine('end', controller, selection);
          if (range) {
            onCangjieSelect(
              Selection.create({
                anchor: selection.anchor,
                focus: range.focus,
              }),
            );
          }
        }
      },
      // eslint-disable-next-line @typescript-eslint/no-unused-vars
      onKeyUp(event: React.KeyboardEvent | KeyboardEvent) {
        lastCaretMoveTime.current = 0;
      },
      onMultipleClick(event: React.MouseEvent<Element>) {
        const now = Date.now();
        if (event.button === 2) {
          return;
        }
        if (
          now - lastDoubleClick.current.time < MULTIPLE_CLICK_DELAY &&
          Math.abs(event.clientY - lastDoubleClick.current.y) <
            MULTIPLE_CLICK_RANGE &&
          Math.abs(event.clientX - lastDoubleClick.current.x) <
            MULTIPLE_CLICK_RANGE
        ) {
          lastDoubleClick.current = { time: 0, x: 0, y: 0 };
          lastSingleClick.current = { time: 0, x: 0, y: 0 };
          const range = caretRangeFromTargetAndCoord(
            event.target,
            event.clientX,
            event.clientY,
            controller,
          );
          if (!range) {
            return;
          }
          const { value } = controller;
          const { document, selection } = value;
          const anchorBlock = document.getClosestBlock(range.anchor.key);
          if (selection && anchorBlock) {
            onCangjieSelect(
              selection!.moveToRangeOfNode(anchorBlock, controller),
              SelectionTrigger.MultipleClick,
            );
          }
          return;
        }

        if (controller.query('isQuickPreview')) {
          return;
        }

        if (
          now - lastSingleClick.current.time > MULTIPLE_CLICK_DELAY ||
          Math.abs(event.clientY - lastSingleClick.current.y) >
            MULTIPLE_CLICK_RANGE ||
          Math.abs(event.clientX - lastSingleClick.current.x) >
            MULTIPLE_CLICK_RANGE
        ) {
          lastSingleClick.current = {
            time: now,
            x: event.clientX,
            y: event.clientY,
          };
          lastDoubleClick.current = {
            time: 0,
            x: event.clientX,
            y: event.clientY,
          };
          return;
        }

        lastSingleClick.current = { time: 0, x: 0, y: 0 };
        lastDoubleClick.current = {
          time: now,
          x: event.clientX,
          y: event.clientY,
        };
        const range = getWordSelection(
          controller,
          event.target as HTMLElement,
          event.clientX,
          event.clientY,
        );
        if (range) {
          onCangjieSelect(
            range,
            SelectionTrigger.MultipleClick,
          );
        }
      },
      onSelectStart(event: React.MouseEvent) {
        let isSelecting = false;
        function onSelectUpdate(e: MouseEvent) {
          controller.perfStart({ type: PerfType.selection, subtype: 'selectText' });
          if (!scrollableContainerRef.current) {
            controller.perfDrop(PerfType.selection);
            return;
          }

          outsideScoller.current && outsideScoller.current(e);
          const { target } = e;
          const x = e.clientX;
          const y = e.clientY;
          if (!target) {
            controller.perfDrop(PerfType.selection);
            return;
          }
          const range = caretRangeFromTargetAndCoord(target, x, y, controller);
          if (!range) {
            controller.perfDrop(PerfType.selection);
            return;
          }

          const { selection } = controller.value;
          if (!selection || equal(range, selection)) {
            controller.perfDrop(PerfType.selection);
            return;
          }
          isSelecting = true;
          onCangjieSelect(selection.set('focus', range.focus), SelectionTrigger.Selecting);
        }

        function onSelectEnd() {
          if (outsideScoller.current) {
            outsideScoller.current.cancel();
            outsideScoller.current = null;
          }
          stopScroll(controller);
          if (isSelecting) {
            const { selection } = controller.value;
            onCangjieSelect(
              selection.set('data', omit(selection.data, 'trigger')),
            );
          }
          window.document.removeEventListener('mousemove', onSelectUpdate);
          window.document.removeEventListener('mouseup', onSelectEnd);
          window.document.removeEventListener('mouseleave', onSelectEnd);
        }

        controller.perfStart({ type: PerfType.selection, subtype: 'focusText' });

        if (isJustTapHold.current) {
          controller.perfDrop(PerfType.selection);
          isJustTapHold.current = false;
          return;
        }

        const rawRange = caretRangeFromTargetAndCoord(
          event.target,
          event.clientX,
          event.clientY,
          controller,
        );

        const isClickAtLeftSide = isCoordAtLeftSide(event.clientX, controller);

        if (!rawRange) {
          controller.perfDrop(PerfType.selection);
          return;
        }

        const range = rawRange.setHanging(
          isClickAtLeftSide && isPointAtEdge(rawRange.anchor),
        );

        // HACK: Safari + third-party IME + MacOS checked TapToHold option
        // sometimes mouseup event triggers before mousedown in a click progress
        // Ref: https://github.com/facebook/react/issues/20787
        if (IS_MAC && IS_SAFARI && event.buttons === 0) { // in this backgound, event.buttons is 0
          const { selection } = controller.value;
          if (event.shiftKey) {
            onCangjieSelect(selection.set('focus', range.focus), SelectionTrigger.SelectStart);
          } else {
            onCangjieSelect(range, SelectionTrigger.SelectStart);
          }
          return;
        }

        // Right button clicked.
        // Ref: https://developer.mozilla.org/en-US/docs/Web/API/MouseEvent/button
        if (event.button === 2) {
          const shouldSelect = shouldSelectOnRightClick(range.focus, controller);
          if (shouldSelect) {
            onCangjieSelect(range);
            return;
          }
          controller.perfDrop(PerfType.selection);
          if (!controller.value.isFocused) {
            controller.run('onCangjieFocus');
          }
          return;
        }

        if (scrollableContainerRef.current) {
          outsideScoller.current = startScroll({
            scroller: scrollableContainerRef.current,
            controller,
          });
        }

        window.document.addEventListener('mousemove', onSelectUpdate);
        window.document.addEventListener('mouseup', onSelectEnd);
        window.document.addEventListener('mouseleave', onSelectEnd);

        const { selection } = controller.value;
        if (event.shiftKey) {
          onCangjieSelect(selection.set('focus', range.focus), SelectionTrigger.SelectStart);
        } else {
          onCangjieSelect(range, SelectionTrigger.SelectStart);
        }
      },
      onTapHoldStart(event: React.TouchEvent) {
        if (tapHoldTimer.current) {
          clearTimeout(tapHoldTimer.current);
        }

        const { target, clientX, clientY } = event.touches[0];
        tapHoldTimer.current = window.setTimeout(() => {
          isJustTapHold.current = true;
          const range = getWordSelection(
            controller,
            target as HTMLElement,
            clientX,
            clientY,
          );
          if (range) {
            onCangjieSelect(range);
          }
        }, TAP_HOLD_DELAY);
      },
      onTapHoldEnd() {
        if (tapHoldTimer.current) {
          clearTimeout(tapHoldTimer.current);
          tapHoldTimer.current = null;
        }
      },
    }),
    [controller, scrollableContainerRef, onCangjieSelect],
  );
}
