/**
 * @packageDocumentation
 * @module Models
 */
import isPlainObject from 'is-plain-object';
import ImmutableRecord from './immutableRecord';
import Node from './node';
import Document from './document';
import Path from './path';
import { Serializable, SerializeOptions } from '../interfaces/serializable';

export interface PointProperties {
  key: string;
}

export interface PointJSON {
  klass?: 'point';
  key?: string;
}

export interface TextPointProperties extends PointProperties {
  offset: number;
}

export interface TextPointJSON extends PointJSON {
  offset: number;
}

export interface EdgePointProperties extends PointProperties {
  edge: PointEdge;
}

export interface EdgePointJSON extends PointJSON {
  edge: PointEdge;
}

export type PointEdge = 'before' | 'after';

const PATH_CACHE = new WeakMap<Point, WeakMap<Document, Path | null>>();

export default abstract class Point extends ImmutableRecord
  implements Serializable<PointJSON> {
  static createFromParams(key: string, where: number | PointEdge) {
    if (typeof where === 'number') {
      return TextPoint.create({
        key,
        offset: where,
      });
    } else {
      return EdgePoint.create({
        key,
        edge: where,
      });
    }
  }

  static create(props: PointProperties | Point) {
    if (props instanceof Point) {
      return props.toSubType();
    }
    if ('edge' in props) {
      return EdgePoint.create(props as EdgePointProperties);
    } else {
      return TextPoint.create(props as TextPointProperties);
    }
  }

  static fromJSON(json: TextPointJSON | EdgePointJSON) {
    if ('offset' in json) {
      return TextPoint.fromJSON(json as TextPointJSON);
    }
    return EdgePoint.fromJSON(json as EdgePointJSON);
  }

  public readonly key: string;

  public abstract get viewKey(): string;

  constructor(props: PointProperties) {
    super();
    const { key } = props;
    this.key = key;
  }

  /**
   * 创建一个新的 Point，并移到 node 的第一个子节点的起始位置
   */
  public moveToStartOfNode(node: Node) {
    const firstNode = node.getFirstNode();
    return TextPoint.create({
      key: firstNode.key,
      offset: 0,
    });
  }

  /**
   * 创建一个新的 Point，并移到 node 的最后一个子节点的终点位置
   */
  public moveToEndOfNode(node: Node) {
    const lastNode = node.getLastNode();
    return TextPoint.create({
      key: lastNode.key,
      offset: lastNode.text.length,
    });
  }

  /**
   * 是否在 node 的第一个节点的起始位置
   */
  public abstract isAtStartOfNode(node: Node): boolean;

  /**
   * 是否在 node 的最后一个节点的终点位置
   */
  public abstract isAtEndOfNode(node: Node): boolean;

  public isTextPoint(): this is TextPoint {
    return this instanceof TextPoint;
  }

  public isEdgePoint(): this is EdgePoint {
    return this instanceof EdgePoint;
  }

  public moveBackward(): TextPoint | EdgePoint {
    return this.toSubType();
  }

  public moveForward(): TextPoint | EdgePoint {
    return this.toSubType();
  }

  private toSubType(): TextPoint | EdgePoint {
    if (this.isTextPoint()) {
      return this;
    } else if (this.isEdgePoint()) {
      return this;
    }
    throw new Error(`Target is not a valid point: ${this}`);
  }

  public convertToTextPoint(document: Document) {
    const point = this.toSubType();
    if (point.isTextPoint()) {
      return point;
    }
    const node = document.getNode(this.key);
    let textNode: Node | null | undefined =
      point.edge === EdgePoint.BEFORE
        ? node?.getFirstText()
        : node?.getLastText();
    if (!textNode) {
      textNode =
        point.edge === EdgePoint.BEFORE
          ? document.getFirstNode()
          : document.getLastNode();
    }
    const { key } = textNode;
    const offset = point.edge === EdgePoint.BEFORE ? 0 : textNode?.text.length;
    return TextPoint.create({
      key,
      offset,
    });
  }

  /**
   * 是否在 node 内部
   */
  public isInNode(node: Node) {
    if (node.isText() && node.key === this.key) return true;
    if (node.isElement() && node.getNode(this.key)) return true;
    return false;
  }

  // NOTE: point.getPathIn(document) === document.getPath(point.key)
  // PERF: ~5ms in Huge Document story
  //       Why do not memoize `document.getPath`? Node's path will not be re-get frequently, but Point's path does.
  public getPathIn(document: Document) {
    if (!PATH_CACHE.has(this)) {
      PATH_CACHE.set(this, new WeakMap());
    }
    const DOCUMENT_TO_PATH_CACHE = PATH_CACHE.get(this)!;
    if (!DOCUMENT_TO_PATH_CACHE.has(document)) {
      DOCUMENT_TO_PATH_CACHE.set(document, document.getPath(this.key));
    }
    return DOCUMENT_TO_PATH_CACHE.get(document)!;
  }

  public abstract toJSON(options?: SerializeOptions): PointJSON;
}

export class TextPoint extends Point {
  static create(props: TextPoint | TextPointProperties): TextPoint {
    if (props instanceof TextPoint) {
      return props;
    }

    if (isPlainObject(props)) {
      const { key, offset } = props;
      return new TextPoint({
        key,
        offset,
      });
    }

    throw new Error(
      `\`Point.create\` only accepts objects or points, but you passed it: ${props}`,
    );
  }

  static fromJSON(json: TextPointJSON) {
    return TextPoint.create({
      ...json,
      key: json.key || 'NONE',
    });
  }

  public readonly offset: number;

  public get viewKey() {
    return `${this.key}-${this.offset}`;
  }

  private constructor(props: TextPointProperties) {
    super(props);
    const { offset } = props;
    this.offset = offset;
    this.freeze();
  }

  /**
   * 把 offset 加 n，并返回一个新的 Point。
   * @n 移动的个数，可以为正/负值，默认为 1；如果 n=0，则返回自身。
   */
  public moveForward(n = 1): TextPoint {
    if (n === 0) {
      return this;
    }
    if (n < 0) {
      return this.moveBackward(-n);
    }
    return this.set('offset', this.offset + n);
  }

  /**
   * 把 offset 减 n，并返回一个新的 Point。
   * @n 移动的个数，可以为正/负值，默认为 1；如果 n=0，则返回自身。
   */
  public moveBackward(n = 1): TextPoint {
    if (n === 0) {
      return this;
    }
    if (n < 0) {
      return this.moveForward(-n);
    }
    return this.set('offset', this.offset - n);
  }

  /**
   * 创建一个新的 Point
   * @offset 默认是 0
   */
  public moveTo(key: string, offset = 0) {
    return this.merge({
      key,
      offset,
    });
  }

  /**
   * 是否在 node 的第一个节点的起始位置
   */
  public isAtStartOfNode(node: Node) {
    if (this.offset !== 0) return false;

    const first = node.getFirstNode();
    return this.key === first.key;
  }

  /**
   * 是否在 node 的最后一个节点的终点位置
   */
  public isAtEndOfNode(node: Node) {
    const last = node.getLastNode();
    return this.key === last.key && this.offset === last.text.length;
  }

  public merge(props: Partial<TextPointProperties>) {
    return new TextPoint({
      key: this.key,
      offset: this.offset,
      ...props,
    });
  }

  public set<T extends keyof TextPointProperties>(
    key: T,
    value: TextPointProperties[T],
  ) {
    return this.merge({
      [key]: value,
    });
  }

  public toJSON(options?: SerializeOptions) {
    const object: TextPointJSON = {
      klass: 'point',
      offset: this.offset,
    };

    if (options?.preserveKeys) {
      object.key = this.key;
    }

    return object;
  }
}

export class EdgePoint extends Point {
  static readonly BEFORE = 'before';

  static readonly AFTER = 'after';

  static create(props: EdgePoint | EdgePointProperties): EdgePoint {
    if (props instanceof EdgePoint) {
      return props;
    }

    if (isPlainObject(props)) {
      const { key, edge } = props;
      return new EdgePoint({
        key,
        edge,
      });
    }

    throw new Error(
      `\`Point.create\` only accepts objects or points, but you passed it: ${props}`,
    );
  }

  static fromJSON(json: EdgePointJSON) {
    return EdgePoint.create({
      ...json,
      key: json.key || 'NONE',
    });
  }

  public readonly edge: PointEdge;

  public get viewKey() {
    return `${this.key}-${this.edge}`;
  }

  private constructor(props: EdgePointProperties) {
    super(props);
    const { edge } = props;
    this.edge = edge;
    this.freeze();
  }

  /**
   * 是否在 node 的第一个节点的起始位置
   */
  public isAtStartOfNode(node: Node) {
    return this.key === node.key && this.edge === EdgePoint.BEFORE;
  }

  /**
   * 是否在 node 的最后一个节点的终点位置
   */
  public isAtEndOfNode(node: Node) {
    return this.key === node.key && this.edge === EdgePoint.AFTER;
  }

  public merge(props: Partial<EdgePointProperties>) {
    return new EdgePoint({
      key: this.key,
      edge: this.edge,
      ...props,
    });
  }

  public set<T extends keyof EdgePointProperties>(
    key: T,
    value: EdgePointProperties[T],
  ) {
    return this.merge({
      [key]: value,
    });
  }

  public toJSON(options?: SerializeOptions) {
    const object: EdgePointJSON = {
      klass: 'point',
      edge: this.edge,
    };

    if (options?.preserveKeys) {
      object.key = this.key;
    }

    return object;
  }
}
