import React, { useEffect, useState } from 'react';
import Controller from '../../controller';
import { VirtualizeResult } from '../interface';


export default function useVirtualize(
  controller: Controller,
  containerRef: React.MutableRefObject<HTMLElement | null>,
  getScrollableContainer: () => Window | HTMLElement | null,
) {
  const { virtualizeManager, enableVirtualizeView } = controller;
  const [virtualizeId, setVirtualizeId] = useState(0);
  useEffect(() => {
    if (!virtualizeManager) {
      return;
    }
    if (enableVirtualizeView) {
      virtualizeManager.initView({
        scrollContainer: getScrollableContainer() as HTMLElement,
        editorContainer: containerRef.current!,
        triggerViewUpdate: setVirtualizeId,
      });
      virtualizeManager.start();
    } else {
      virtualizeManager.stop();
    }
    return;
  }, [enableVirtualizeView, virtualizeManager]);

  if (!enableVirtualizeView || !virtualizeManager) {
    return null;
  }

  const { outputManager } = virtualizeManager;

  const { beforePlaceholderHeight, afterPlaceholderHeight, visibles } = outputManager;

  const virtualizeResult: VirtualizeResult = {
    virtualizeId: typeof virtualizeManager.virtualizeId === 'number' ? virtualizeManager.virtualizeId : virtualizeId,
    beforePlaceholderHeight,
    afterPlaceholderHeight,
    visibles,
  }
  return [virtualizeResult, setVirtualizeId] as const;
}
