import Element from '../models/element';
import Node from '../models/node';
import Path from '../models/path';

export function replaceNodeIntoElement(
  origin: Element,
  path: Path | null | undefined,
  node: Node,
) {
  // invalid path
  if (!path) {
    return origin;
  }

  // path 指向自身
  if (!path.length) {
    return node;
  }

  const { nodes } = origin;
  const offset = path[0];
  let target: Node = node;
  // 递归找到目标位置
  if (path.length > 1) {
    const parent = nodes[offset];
    if (parent && parent.isElement()) {
      target = replaceNodeIntoElement(parent, path.slice(1), node);
    } else {
      target = parent;
    }
  }
  // 替换目标节点
  if (
    nodes[offset] &&
    nodes[offset] !== target
  ) {
    const next = nodes.slice(0);
    next[offset] = target;
    return origin.set('nodes', next);
  }
  return origin;
}
