export const MS_WORD = 'MS_WORD';
export const MS_EXCEL = 'MS_EXCEL';
// word、excel 以外的 office
export const MS_OFFICE = 'MS_OFFICE';
export const MS_OFFICES = [MS_WORD, MS_EXCEL, MS_OFFICE];
export const YUQUE = 'YUQUE';

// 以下复制来源，保留所有样式
export const REMAIN_STYLE_SOURCES = [MS_WORD, MS_EXCEL, MS_OFFICE, YUQUE];

export function isMicrosoftWord(html: string): boolean {
  return Boolean(
    html?.includes('xmlns:w="urn:schemas-microsoft-com:office:word"'),
  );
}

export function isMicrosoftExcel(html: string): boolean {
  return Boolean(
    html?.includes('xmlns:x="urn:schemas-microsoft-com:office:excel"'),
  );
}

export function isMicrosoftOffice(html: string): boolean {
  return Boolean(html?.includes('xmlns:x="urn:schemas-microsoft-com"'));
}

export function isYuqueDocs(html: string): boolean {
  return Boolean(
    html?.includes('<meta name="source" content="lake"/>') ||
    html?.includes('class="lake-content"'),
  );
}

export function getHtmlFrom(html: string): string {
  if (isMicrosoftWord(html)) {
    return MS_WORD;
  }
  if (isMicrosoftExcel(html)) {
    return MS_EXCEL;
  }
  if (isMicrosoftOffice(html)) {
    return MS_OFFICE;
  }
  if (isYuqueDocs(html)) {
    return YUQUE;
  }
  return 'web';
}
