import { pick, isEmpty } from 'lodash-es';
import type { MoSerializationPlugin, HTMLToMarkdownRule } from '../type';

export default async function createHtmlToMarkdown(plugins: MoSerializationPlugin[]) {
  const rules = plugins
    .map((plugin) => plugin.htmlToMarkdown!)
    .filter((rule) => !!rule);

  const Turndown = (await import(/* webpackChunkName: 'cangjie-lib-turndown'*/'turndown')).default;
  const turndownService = new Turndown({
    headingStyle: 'atx',
  });
  rules.forEach(rule => {
    const pluginRule = pick(rule, ['filter', 'replacement']);
    if (!isEmpty(pluginRule)) {
      turndownService.addRule(rule.name, pluginRule);
    }
    if (rule.use) {
      turndownService.use(rule.use);
    }
  })
  return (html: string) => {
    return turndownService.turndown(html);
  }
}
