/**
 * @packageDocumentation
 * @module Commands
 */
import Controller from '../controller/index';
import Mark from '../models/mark';
import Path from '../models/path';
import Selection from '../models/selection';
import { TextPoint } from '../models/point';
import { removeTextByPath, removeNodeByPath } from './remove';
import { insertTextByPath, insertNodeByPath } from './insert';
import setUtils from '../utils/setUtils';
import Node from '../models/node';

/**
 * 在指定的位置上，替换node
 */
export function replaceNodeByKey(
  controller: Controller,
  key: string,
  newNode: Node,
) {
  const { document } = controller.value;
  const path = document.getPath(key);
  if (!path) {
    return controller;
  }
  return controller.command(replaceNodeByPath, path, newNode);
}

/**
 * 在指定的位置上，替换node
 */
export function replaceNodeByPath(
  controller: Controller,
  path: Path,
  newNode: Node,
) {
  const index = path[path.length - 1];
  const parentPath = Path.parent(path);

  return controller.withoutNormalizing(() => {
    controller
      .command(removeNodeByPath, path)
      .command(insertNodeByPath, parentPath, index, newNode);
  });
}

/**
 * 在指定的位置上，替换文本，并附加marks
 * 1. 提过oldText长度大于可用长度，按照可用长度计算
 */
export function replaceTextByKey(
  controller: Controller,
  key: string,
  offset: number,
  oldText: string,
  text: string,
  marks?: Mark[],
) {
  const { value } = controller;
  const { document } = value;

  const path = document.assertPath(key);
  return controller.command(
    replaceTextByPath,
    path,
    offset,
    oldText,
    text,
    marks,
  );
}

/**
 * 在指定的位置上，替换文本，并附加marks
 * 1. 提过oldText长度大于可用长度，按照可用长度计算
 */
export function replaceTextByPath(
  controller: Controller,
  path: Path,
  offset: number,
  oldText: string,
  text: string,
  marks: Mark[] = [],
) {
  const { value } = controller;
  const { document } = value;

  const node = document.assertNodeByPath(path);
  const activeMarks = document.getActiveMarksAtRange(
    Selection.create({
      anchor: TextPoint.create({ key: node.key, offset }),
      focus: TextPoint.create({ key: node.key, offset: offset + oldText.length }),
    }),
  );
  const mergedMarks = setUtils.union(
    marks,
    activeMarks,
    (a, b) => a.type === b.type,
  );

  return controller.withoutNormalizing(() => {
    controller
      .command(removeTextByPath, path, offset, oldText)
      .command(
        insertTextByPath,
        path,
        offset,
        text,
        mergedMarks.map(Mark.create),
      );
  });
}
