import React from 'react';
import Controller from '../../../controller';
import Selection from '../../../models/selection';
import { splitSelectionAndGetWrapper } from './splitSelectionAndGetWrapper';
import { SelectionWrapperMap } from '../types';
import { getCroppedSelection } from '../../../utils/selection';

/**
 * 计算选区应该分布的根节点
 *
 * A. 对于单个原始 selection
 * 1. 若为 collapsed，则返回值的 key 为该 selection，value 为它应渲染目标宿主节点 + originSelection
 * 2. 若为 expanded，则返回值 key 为该 selection 按需切割的子 selection，value 为其对应渲染的目标宿主节点 + originSelection
 *
 * B. remoteSelections 会分别将每一项按上面的规则分割并聚合到一个 Map 中
 *
 * @param controller Controller
 * @param globalWrapperRef 渲染在全局的根节点
 * @returns [local, remotes]: [SelectionWrapperMap, SelectionWrapperMap]
 */
export function useSelectionWrappers(controller: Controller, globalWrapperRef: React.MutableRefObject<HTMLDivElement | null>) {
  const { remoteSelections, selection, document } = controller.value;
  const prevSelection = React.useRef<Selection | null>(null);
  const [visibles, setVisibles] = React.useState<string[]>([]);

  const getWrappersBySelection = React.useCallback((sel: Selection) => {
    const wrappers = splitSelectionAndGetWrapper(controller, sel, globalWrapperRef);
    return wrappers;
  }, [document, controller]);

  const local: SelectionWrapperMap = React.useMemo(() => {
    const isHideSelection = controller.query(
      'isHideSelection',
      { selection },
    );

    const croppedSelection = getCroppedSelection(controller, selection, controller.visibleBlocks);

    // 不显示本地选区时 or 首次渲染 globalWrapperRef 仍为空时
    if (isHideSelection || !globalWrapperRef.current || !croppedSelection) {
      return new Map();
    }

    return getWrappersBySelection(croppedSelection);
    // 首次渲染出 globalWrapperRef 后，使其重新计算一下以得到选区 appearance
  }, [selection, document, controller, globalWrapperRef.current, visibles]);

  const remotes = React.useMemo(() => {
    const map: SelectionWrapperMap = new Map();
    remoteSelections.forEach((sel) => {
      const croppedSelection = getCroppedSelection(controller, sel, controller.visibleBlocks);
      if (croppedSelection) {
        const wrappers = getWrappersBySelection(croppedSelection);
        wrappers.forEach((v, key) =>
          map.set(key, v)
        );
      }
    });
    return map;
  }, [remoteSelections, document, controller, visibles]);

  React.useEffect(() => {
    prevSelection.current = selection;
  }, [selection, document]);

  React.useEffect(() => {
    const sub = controller.visibles$.subscribe(visibles => {
      setVisibles(visibles);
    });
    return () => {
      sub.unsubscribe();
    }
  }, [controller]);

  return [local, remotes];
}
