/**
 * @packageDocumentation
 * @ignore
 */
export enum Status {
  Idle = 'Idle',
  Pending = 'Pending',
  Canceled = 'Canceled',
  Done = 'Done',
}

/**
 * WARN: Sometimes request idle callback in Safari will never be called!
 */
export type FlushType = 'microtask' | 'frametask' | 'idletask';

/**
 * 默认的 flush 类型
 */
const DEFAULT_FLUSH_TYPE: FlushType = 'microtask';

/**
 * flush 最大等待时间（100ms 是为了保证常规情况下反馈不会过长，同时大文档在连续情况下每隔 100ms 做一次渲染也能保障流畅度）
 * @description 浏览器的调度有时候会将线程长时间闲置，导致 flush 延迟过高
 * @example 一个典型的例子是当滚动区域触发一个滑动后，光标的再次点击，使浏览器的线程调度闲置（具体原因还在研究中）
 */
const FLUSH_MAX_WAIT = 100;

export interface FlushTaskOptions {
  flushType?: FlushType;
}

/**
 * 是否支持 idleCallback
 */
const IS_SUPPORT_IDLE_TASK = typeof window === 'object' &&
  'requestIdleCallback' in window && 'cancelIdleCallback' in window;

/**
 * 是否支持 animationFrame
 */
const IS_SUPPORT_FRAME_TASK = typeof window === 'object' &&
  'requestAnimationFrame' in window && 'cancelAnimationFrame' in window;

/**
 * 决议能够使用的 flushType
 * @param flushType 需要的 flushType
 */
function resolveSupportedType(flushType: FlushType): FlushType {
  if (flushType === 'microtask') {
    return 'microtask';
  }
  if (flushType === 'idletask') {
    if (IS_SUPPORT_IDLE_TASK) {
      return 'idletask';
    } else if (IS_SUPPORT_FRAME_TASK) {
      return 'frametask';
    }
  }
  if (flushType === 'frametask' && IS_SUPPORT_FRAME_TASK) {
    return 'frametask';
  }
  return 'microtask';
}

class FlushTask {
  static create(task: Function, options?: FlushTaskOptions) {
    return new FlushTask(task, options);
  }

  /**
   * 等待执行的微任务
   */
  private task: Function;

  /**
   * 当前任务的状态
   */
  private status = Status.Idle;

  /**
   * 是否已经 resolved，保障一个任务只执行一次函数
   */
  private resolved = false;

  /**
   * 原始的 flushType
   */
  private flushType: FlushType;

  /**
   * 当前支持的 flushType
   */
  private supportedFlushType: FlushType;

  private taskId = -1;

  private constructor(task: Function, options: FlushTaskOptions = {}) {
    this.task = task;
    const {
      flushType = DEFAULT_FLUSH_TYPE,
    } = options;
    this.flushType = flushType;
    this.supportedFlushType = resolveSupportedType(flushType);
  }

  start(): FlushTask {
    this.status = Status.Pending;
    if (!this.resolved) {
      this.requestTask();
      this.resolved = true;
    }
    return this;
  }

  cancel(): FlushTask {
    this.status = Status.Canceled;
    this.cancelTask();
    return this;
  }

  getStatus(): Status {
    return this.status;
  }

  private requestTask() {
    if (this.supportedFlushType === 'idletask') {
      this.taskId = window.requestIdleCallback(() => this.run(), { timeout: FLUSH_MAX_WAIT });
    } else if (this.supportedFlushType === 'frametask') {
      this.taskId = window.requestAnimationFrame(() => this.run());
    } else {
      Promise.resolve().then(() => this.run());
    }
  }

  private cancelTask() {
    if (this.supportedFlushType === 'idletask') {
      window.cancelIdleCallback(this.taskId);
    } else if (this.supportedFlushType === 'frametask') {
      window.cancelAnimationFrame(this.taskId);
    }
  }

  private run() {
    if (this.status === Status.Pending && typeof this.task === 'function') {
      this.task.call(null);
      this.status = Status.Done;
    }
  }
}

export default FlushTask;
