/**
 * @packageDocumentation
 * @module Commands
 */
import Controller from '../controller/index';
import Path from '../models/path';
import Text from '../models/text';
import { removeNodeByPath } from './remove';
import { moveNodeByPath } from './move';
import { insertTextByPath } from './insert';
import { mergeTpl } from '../contexts/merge';

/**
 * 把目标 path 的 Node，合并到其上一个 Sibling 上。
 * 1. 必须满足 Node 和上一个 Sibling 是同一种类型。
 * 2. 如果没有上一个 Sibling，直接抛异常。
 */
export function mergeNodeByKey(controller: Controller, key: string) {
  const { value } = controller;
  const { document } = value;
  const path = document.assertPath(key);
  return controller.command(mergeNodeByPath, path);
}

/**
 * 把目标 path 的 Node，合并到其上一个 Sibling 上。
 * 1. 必须满足 Node 和上一个 Sibling 是同一种类型。
 * 2. 如果没有上一个 Sibling，直接抛异常。
 */
export function mergeNodeByPath(controller: Controller, path: Path) {
  const { value } = controller;
  const { document } = value;
  const original = document.getNodeByPath(path)!;
  const previous = original ? document.getPreviousSibling(original.key) : null;

  if (!previous) {
    throw new Error(
      `Unable to merge node with path "${path}", because it has no previous sibling.`,
    );
  }
  if (previous.constructor !== original.constructor) {
    throw new Error(
      `Unable to merge two different kinds of nodes: ${previous} and ${original}`,
    );
  }

  const previousPath = document.getPath(previous.key)!;
  const previousNode = previous.toSubType();

  const position = previousNode.isText()
    ? previousNode.text.length
    : previousNode.nodes.length;

  controller.withOpContext(mergeTpl(original, path), () => {
    controller.withoutNormalizing(() => {
      if (original.isElement()) {
        const moveNodePath = [...path, 0];
        original.nodes.forEach((node, index) => {
          controller.command(
            moveNodeByPath,
            moveNodePath,
            previousPath,
            position + index,
          );
        });
        controller.command(removeNodeByPath, path);
      } else {
        const { leaves } = original as Text;
        let offset = previousNode.text.length;
        const { selection, decorations } = controller.value;
        leaves.forEach((leaf) => {
          controller.command(
            insertTextByPath,
            previousPath,
            offset,
            leaf.text,
            leaf.marks,
          );
          offset += leaf.text.length;
        });
        controller.command(removeNodeByPath, path);
        // 处理 updatePoints 的问题；具体请参考 split 中同样的处理
        const valueToUpdate = controller.value
          .set('decorations', decorations)
          .set('selection', selection);
        const updatedValue = valueToUpdate.updatePoints(
          valueToUpdate.document,
          (point) => {
            if (point.key === original.key) {
              return point.merge({
                key: previousNode.key,
                offset: point.offset + position,
              });
            }
            return point;
          },
        );
        controller.setValue(updatedValue, { clearOperations: false });
      }
    });
  });

  return controller;
}
