function isValidElement(el: Element | Node | null): el is Element {
  return (el !== null &&
  el.nodeType === Node.ELEMENT_NODE &&
  !(el as Element).getAttribute('data-cangjie-content'));
}

/**
 * Find closest ancestor
 * @see https://developer.mozilla.org/en-US/docs/Web/API/Element/closest
 * @returns
 */
export default function findClosestInContent(el: Element | Node | null, selector: string): Element | null {
  do {
    if (Element.prototype.matches.call(el, selector)) return el as Element;
    el = el!.parentElement || el!.parentNode;
  } while (
   isValidElement(el)
  );
  return null;
}
