/**
 * @packageDocumentation
 * @ignore
 */
import * as React from 'react';
import Controller from '../controller/index';
import ContextMenu from '../components/mobile/contextMenu';
import Handles from '../components/mobile/handles';
import Selection from '../models/selection';
import caretRangeFromTargetAndCoord from '../utils/dom/caretRangeFromTargetAndCoord';
import { IS_TOUCH_DEVICE, IS_MOBILE } from '../utils/environment';
import { ControllerConfigs } from '../interfaces/controller';

export const ACTION_CLEAR_MOBILE_MENU = Symbol('ACTION_CLEAR_MOBILE_MENU');


function getDraggingSelection(controller: Controller) {
  // eslint-disable-next-line no-underscore-dangle
  return controller.value.data._mobileDraggingSelection;
}

function hidePopup(controller) {
  showPopup(controller, undefined);
}

function showPopup(controller: Controller, selection?: Selection) {
  const { data } = controller.value;
  const prevRange = getPopupSelection(controller);
  const focusedSelection =
    prevRange && selection && Selection.isEqual(prevRange, selection)
      ? undefined
      : selection;
  controller.setData({
    ...data,
    _mobileFocusedSelection: focusedSelection,
  });
}

function getPopupSelection(controller: Controller) {
  // eslint-disable-next-line no-underscore-dangle
  return controller.value.data._mobileFocusedSelection;
}

/**
 * 兼容 鼠标 + 触屏 场景
 *
 * 背景：
 * touch 事件会触发 onTouchStart 和 onMouseDown
 * mouse 事件会触发 onMouseDown
 *
 * 方案：
 * touch 事件，touchCount 值重置为 2
 * mouse 事件，touchCount 值减 1
 *
 * 结果：
 * 用户使用了 mouse：touchCount <= 0
 * 用户使用了 touch：touchCount === 1
 */
let touchCount = 0;

export default function createMobilePlugin(configs?: ControllerConfigs) {
  const { enableZoomer = false } = configs?.plugins?.mobile || {};
  function onTouchStart(event: React.TouchEvent, controller: Controller, next) {
    const { selection, isFocused } = controller.value;
    const { isCollapsed } = selection;
    if (isFocused && isCollapsed) {
      const { target, clientX, clientY } = event.touches[0];
      const range = caretRangeFromTargetAndCoord(
        target,
        clientX,
        clientY,
        controller,
      );
      if (range && Selection.isEqual(selection, range)) {
        showPopup(controller, selection);
      } else if (range && getPopupSelection(controller)) {
        hidePopup(controller);
      }
    }
    touchCount = 2;
    return next();
  }

  function onMouseDown(event: React.MouseEvent, controller: Controller, next) {
    touchCount = touchCount > 0 ? touchCount - 1 : 0;
    return next();
  }

  function renderContent(_, controller: Controller, next) {
    const { value, readOnly } = controller;
    const { selection, isFocused } = value;
    const { contextData } = value.data;
    const focusedSelection = getPopupSelection(controller);
    const shouldRenderH5Controls = Boolean(
      isFocused || controller.query('shouldRenderH5Controls'),
    );
    // 鼠标 + 触屏 场景，如果是鼠标触发的，就不显示菜单
    const isDisabled = !IS_MOBILE && IS_TOUCH_DEVICE && touchCount === 0;

    const isInFocusedSelection =
      focusedSelection && Selection.isEqual(focusedSelection, selection);
    // 当处于 focusedSelection 或者 插件允许的区域，可以渲染 contextMenu
    const isInRenableSelection =
      isInFocusedSelection || controller.query('shouldRenderContextMenu');

    const shouldRenderContextMenu = Boolean(
      shouldRenderH5Controls &&
        !contextData &&
        !isDisabled &&
        isInRenableSelection,
    );

    const shouldRenderHandles =
      shouldRenderH5Controls && !contextData && !isDisabled && !controller.query('isHideSelection');
    return (
      <React.Fragment>
        {next()}
        {selection.isCollapsed && shouldRenderContextMenu && (
          <ContextMenu selection={selection} controller={controller} hidePopup={hidePopup} />
        )}
        {shouldRenderHandles && (
          <Handles
            enableZoomer={enableZoomer}
            selection={selection}
            controller={controller}
          />
        )}
        {/* {shouldRenderHoldAndMove && (
          <HoldAndMove
            setDraggingSelection={setDraggingSelection}
            setFocusedSelection={setFocusedSelection}
            getDraggingSelection={getDraggingSelection}
          />
        )} */}
      </React.Fragment>
    );
  }

  function renderCaret(props, controller: Controller, next) {
    const caret = next();
    if (!caret) {
      return null;
    }
    const isDragging = getDraggingSelection(controller);
    const mode = isDragging ? 'placeholder' : 'caret';
    return React.cloneElement(caret, { mode });
  }

  function onAction(action, controller: Controller, next) {
    if (action.type === ACTION_CLEAR_MOBILE_MENU) {
      const { data } = controller.value;
      // eslint-disable-next-line no-underscore-dangle
      if (data._mobileFocusedSelection) {
        controller.setData({
          ...data,
          _mobileFocusedSelection: undefined,
        });
      }
    }
    return next();
  }

  return {
    onAction,
    onTouchStart,
    onMouseDown,
    renderContent,
    renderCaret,
    models: [],
  };
}
