import { GROUP_COLLAPSED_KEY } from '../../constants';
import Controller from '../../controller';
import { Group, GroupWithKey } from '../../interfaces/group';
import Element from '../../models/element';

/**
 * 是否分组被折叠 
 * @param group 
 * @returns 
 */
export function isGroupCollapsed(group: Group) {
  return group.data[GROUP_COLLAPSED_KEY];
}

/**
 * 是否分组为空 
 * @param controller 
 * @param group 
 * @returns 
 */
export function isGroupEmpty(controller: Controller, group: GroupWithKey) {
  const { document } = controller.value;
  const next = document.getNextSibling(group.key);
  if (next?.isElement() && controller.query('isElementInGroup', next, group) !== false) {
    return false;
  }
  return true;
}
