import Deserializer from './Deserializer';
import { MoSerializationPlugin } from '../../type';
import { DATA_CLIPBORAD_CANGJIE } from '../../utils/constants';
import { getHtmlFrom } from './htmlFrom';
import type { MoHtmlParser, JsonML } from '../../type';

function getJsonMLFromAttr(root: Document): JsonML | null {
  const clipboardDataNode = root.querySelector(`[${DATA_CLIPBORAD_CANGJIE}]`);

  /**
   * 需要判断数据节点是否是第一个，防止一些 html 夹杂着无用的 data-clipboard-data 节点，导致误解析
   */
  if (clipboardDataNode && root.body.firstElementChild === clipboardDataNode) {
    const clipboardData = clipboardDataNode.getAttribute(
      DATA_CLIPBORAD_CANGJIE,
    );
    try {
      return JSON.parse(clipboardData as string);
    } catch (e) {
      // @ali/4ever-logger 依赖库里面有浏览器环境依赖，导致 server cp 脚本无法运行
      console.error(e);
    }
  }
  return null;
}

export default function createHtmlToJsonML(plugins: MoSerializationPlugin[], parser?: MoHtmlParser) {
  return function htmlToJsonML(html: string, configs: {} = {}) {
    const htmlFrom = getHtmlFrom(html);
    const domParser = new DOMParser();
    const parsedDocument = domParser.parseFromString(html, 'text/html');
    const domJsonML = getJsonMLFromAttr(parsedDocument);
    if (domJsonML) return domJsonML;
    const deserializer = new Deserializer({ plugins, htmlFrom, parser });
    const parsedHTML = parsedDocument.lastElementChild!;
    return deserializer.deserialize(parsedHTML.outerHTML, configs);
  };
}
