import React from 'react';
import equal from 'fast-deep-equal';
import { throttle } from 'lodash-es';
import Controller from '../../controller';
import Selection from '../../models/selection';
import Node from '../../models/node';
import { useSelectionDataAtTheStartOfNextFrame } from '../../hooks/useSelectionData';
import { useZoom } from '../../hooks/useZoom';
import { useResumePosition } from '../../hooks/useResumePosition';
import { useScrollBySelection } from '../../hooks/useScrollBySelection';
import SelectionRenderer, { SelectionAppearances } from './renderer';
import { getSelectionAppearance } from './utils/getSelectionAppearance';
import { SelectionSlot } from './slot';
import { useSelectionWrappers } from './utils/useSelectionWrappers';
import {
  useInputingHots,
  useSelectingHotsAtTheStartOfNextFrame,
} from '../../hooks/useHots';

export { SelectionSlot } from './slot';
export type { SelectionWrapperMap, CaretAppearance } from './types';

const THROTTLE_TIME_RESIZE = 300;

interface SelectionLayerProps {
  controller: Controller;
  // content 的 dom ref
  contentRef: React.MutableRefObject<HTMLDivElement | null>;
  scrollableContainerRef: React.MutableRefObject<HTMLDivElement | null>;
}

const SelectionLayer = (props: SelectionLayerProps) => {
  const { controller, contentRef, scrollableContainerRef } = props;
  const {
    composing,
    isFocused,
    selection,
    document,
    injections,
    decorations,
  } = controller.value;

  const zoom = useZoom() || 1;
  const [_, forceUpdate] = React.useReducer((c) => c + 1, 0);
  const [resizeFlag, setResizeFlag] = React.useState(0);
  const globalWrapperRef = React.useRef<HTMLDivElement | null>(null);
  const isUnmountedRef = React.useRef(false);

  // 本地选区外观
  const [local, setLocal] = React.useState<SelectionAppearances>(new Map());
  // 远程选区外观
  const [remotes, setRemotes] = React.useState<SelectionAppearances>(new Map());

  const localDepsRef = React.useRef<{
    composing: string;
    selection: Selection;
    node: Node | null;
  }>({
    composing,
    selection,
    node: null,
  });

  React.useEffect(() => {
    isUnmountedRef.current = false;
    return () => {
      isUnmountedRef.current = true;
    };
  }, []);

  React.useEffect(() => {
    if (!contentRef.current) {
      return undefined;
    }
    const resizeUpdator = throttle(() => {
      if (controller.shouldHideSelectionOnBlur() && controller.value.isBlurred) {
        return;
      }
      setResizeFlag((val) => (val + 1) % 2); // 这里无需读取真实的 size 造成 reflow，只需要可以感知到变化即可
    }, THROTTLE_TIME_RESIZE);

    if (typeof ResizeObserver !== 'undefined') {
      const observer = new ResizeObserver(resizeUpdator);
      observer.observe(contentRef.current);
      return () => {
        resizeUpdator.cancel();
        if (contentRef.current) {
          observer.unobserve(contentRef.current);
        }
      };
    }

    window.addEventListener('resize', resizeUpdator);
    return () => {
      resizeUpdator.cancel();
      window.removeEventListener('resize', resizeUpdator);
    };
  }, []);

  React.useEffect(() => {
    localDepsRef.current.node = selection.isCollapsed
      ? document.getPreviousNode(selection.anchor.key)
      : null;
  }, [selection, document]);

  const shouldForceUpdate = React.useCallback(
    (range: Selection) => {
      if (
        equal(range, selection) &&
        controller.value.composing === localDepsRef.current.composing
      ) {
        // 仅 focus 发生变化时，需要及时 rerender 选区
        if (isFocused !== controller.value.isFocused) {
          return true;
        }

        // BACKGROUND: 在文本型链接的最后 delete 时，链接文字变少了，但选区不会发生变化
        // 所以需要在这种情况下 force re-render
        if (range.isCollapsed) {
          const prevNode = controller.value.document.getPreviousNode(
            range.anchor.key,
          );
          if (!equal(prevNode, localDepsRef.current.node)) {
            return true;
          }
        }
        return false;
      }
      return true;
    },
    [controller, isFocused, selection],
  );

  const selectionUpdator = React.useCallback(
    ({ range }: { range: Selection }) => {
      if (shouldForceUpdate(range)) {
        localDepsRef.current.composing = controller.value.composing;
        localDepsRef.current.selection = range;
        forceUpdate();
      }
    },
    [controller, shouldForceUpdate],
  );

  const onHots = React.useCallback(() => {
    selectionUpdator({ range: controller.value.selection });
  }, [controller, selectionUpdator]);

  const onInputingHots = React.useCallback(() => {
    // 保证节点完成渲染之后才计算和绘制选区
    Promise.resolve().then(onHots);
  }, [onHots]);

  useSelectionDataAtTheStartOfNextFrame(controller.selectionData$, null, selectionUpdator, []);
  useSelectingHotsAtTheStartOfNextFrame(controller, onHots);
  useInputingHots(controller, onInputingHots);

  // 根据本地选区变化滚动页面
  useScrollBySelection(scrollableContainerRef, contentRef, controller);

  useResumePosition(scrollableContainerRef, contentRef, controller);

  // 计算选区渲染所分布的根节点
  const [localWrappersMap, remoteWrappersMap] = useSelectionWrappers(
    controller,
    globalWrapperRef,
  );

  // 选区需保证 node 渲染完成后再更新
  React.useLayoutEffect(() => {
    const appearance = getSelectionAppearance(
      localWrappersMap,
      zoom,
      false,
      contentRef,
      controller,
    );
    setLocal(appearance);
  }, [
    localWrappersMap,
    composing,
    zoom,
    // BACKGROUND: https://aone.alibaba-inc.com/v2/project/897543/bug/41787750#
    isFocused,
    controller,
    resizeFlag,
    injections,
    decorations,
  ]);

  React.useLayoutEffect(() => {
    const appearance = getSelectionAppearance(
      remoteWrappersMap,
      zoom,
      true,
      contentRef,
      controller,
    );
    setRemotes(appearance);
  }, [
    remoteWrappersMap,
    controller,
    zoom,
    isFocused,
    resizeFlag,
    document,
    injections,
    decorations,
  ]);

  return (
    <SelectionSlot ref={globalWrapperRef} layerFor="global">
      <SelectionRenderer
        appearances={local}
        isFocused={isFocused}
        controller={controller}
      />
      <SelectionRenderer
        appearances={remotes}
        isFocused={isFocused}
        controller={controller}
      />
    </SelectionSlot>
  );
};

export default SelectionLayer;
