export function getDOMPath(dom: HTMLElement, root = window.document.body) {
  const path: HTMLElement[] = [];
  let current: HTMLElement | null = dom;
  while (current && current !== root) {
    path.push(current);
    current = current.parentElement;
  }
  return path.reverse();
}

export function getTraverseRootsByDOMPath(
  path1: HTMLElement[],
  path2: HTMLElement[]
): [HTMLElement, HTMLElement, HTMLElement[]] {
  const length = Math.min(path1.length, path2.length);
  let current = 0;
  for (; current < length; current++) {
    if (path1[current] !== path2[current]) {
      break;
    }
  }
  if (current === length) {
    return [path1[length - 1], path1[length - 1], path1.slice(0, length)];
  }
  return [path1[current], path2[current], current > 0 ? path1.slice(0, current) : []];
}
