/* eslint-disable implicit-arrow-linebreak */
// eslint-disable-next-line import/no-extraneous-dependencies
/**
 * @packageDocumentation
 * @ignore
 */
import * as React from 'react';
import ReactDOM from 'react-dom';
import equal from 'fast-deep-equal';

import Controller from '../../controller/index';
import * as Commands from '../../commands/index';
import Selection from './../../models/selection';
import caretRangeFromTargetAndCoord from '../../utils/dom/caretRangeFromTargetAndCoord';
import ContextMenu from './contextMenu';
import { useScroll } from '../../hooks/useScroll';
import Zoomer from './Zoomer';
import { TextPoint, EdgePoint } from '../../models/point';
import findDOMNode, { findDOMNodeSafely } from '../../utils/dom/findDOMNode';
import { pointAtDistance } from '../../queries/selection';
import { useSelectionData } from '../../hooks/useSelectionData';
import {
  PointLocation,
  findCaretPosition,
} from '../../utils/dom/findCaretPosition';
import { Selector } from '../../constants';
import CangjieSelectEvent from '../../events/cangjieSelect';
import { startScroll, stopScroll } from '../../utils/dom/outsideScroller';
import { useScrollableContainer } from '../ScrollableContainer';

interface HandlesProps {
  selection: Selection;
  enableZoomer: boolean;
  controller: Controller;
}

interface HandleProps {
  style: object;
  onDrag: (type: DraggingType, event: TouchEvent) => void;
  type: DraggingType;
  onDragStart: (type: DraggingType) => void;
  onDragEnd: (type: DraggingType) => void;
  isHidden?: boolean;
}

// handle needle width
const HANDLE_WIDTH = 2;
// handle round dot size
const HANDLE_DOT_SIZE = 10;
// handle touchable rect size
const HANDLE_TOUCH_SIZE = 30;

const TYPE_START = 'start';
const TYPE_END = 'end';
type DraggingType = 'start' | 'end' | null;

const cursorStyle: React.CSSProperties = {
  position: 'fixed',
  background: '#0089ff',
  opacity: 1,
  width: `${HANDLE_WIDTH}px`,
};

const handleRectStyle: React.CSSProperties = {
  position: 'absolute',
  width: `${HANDLE_TOUCH_SIZE}px`,
  height: `${HANDLE_TOUCH_SIZE}px`,
  left: `-${HANDLE_TOUCH_SIZE / 2 - HANDLE_WIDTH / 2}px`,
  bottom: `-${HANDLE_TOUCH_SIZE / 2 + HANDLE_DOT_SIZE / 2}px`,
  display: 'flex',
  justifyContent: 'center',
  alignItems: 'center',
};

const handleRectTop: React.CSSProperties = {
  left: `-${HANDLE_TOUCH_SIZE / 2}px`,
  top: `-${HANDLE_TOUCH_SIZE / 2}px`,
  bottom: 'auto',
};

const handleRoundDotStyle: React.CSSProperties = {
  background: '#0089ff',
  width: `${HANDLE_DOT_SIZE}px`,
  height: `${HANDLE_DOT_SIZE}px`,
  borderRadius: `${HANDLE_DOT_SIZE}px`,
};

const handleRectTopStyle: React.CSSProperties = {
  ...handleRectStyle,
  ...handleRectTop,
};

const isPointVisible = ({ node, clientLeft }: PointLocation) => {
  let visible = true;
  let pElement: HTMLElement | null = node.parentElement;
  while (pElement && !pElement.getAttribute(`[${Selector.content}]`)) {
    if (pElement.scrollWidth > pElement.offsetWidth) {
      const pRect = pElement.getBoundingClientRect();
      if (clientLeft < pRect.left || clientLeft > pRect.right) {
        visible = false;
        break;
      }
    }
    pElement = pElement.parentElement;
  }
  return visible;
};

const updateHandlePosition = (node: HTMLElement, loc: PointLocation) => {
  node.style.top = `${loc.clientTop}px`;
  node.style.left = `${loc.clientLeft}px`;
  node.style.height = `${loc.height}px`;
};

const blockEvent = (e: Event | React.TouchEvent) => {
  e.preventDefault();
  e.stopPropagation();
};

// get a range from a client point (x, y)
const caretRangeFromPoint = (x: number, y: number, controller: Controller) => {
  const { key } = controller.value.document;
  const docNode = findDOMNodeSafely(key);
  const ele = docNode?.closest(`[${Selector.content}]`);
  if (!ele) {
    return null;
  }
  return caretRangeFromTargetAndCoord(ele, x, y, controller);
};

// Handle component
const Handle = React.forwardRef<HTMLElement, HandleProps>(
  ({ style, onDrag, onDragStart, onDragEnd, type, isHidden = false }, ref) => {
    // prevent page scroll when dragging
    const handleTouchStart = (e: React.TouchEvent) => {
      blockEvent(e);
      e.nativeEvent.stopImmediatePropagation();
      onDragStart(type);
    };
    const handleTouchEnd = (e: React.TouchEvent) => {
      blockEvent(e);
      e.nativeEvent.stopImmediatePropagation();
      onDragEnd(type);
    };

    const handleDrag = React.useCallback(
      (event: TouchEvent) => {
        onDrag(type, event);
      },
      [type, onDrag],
    );

    const touchMoveRef = React.useRef<HTMLDivElement>(null);
    React.useEffect(() => {
      const element = touchMoveRef ? touchMoveRef.current : null;
      if (element !== null) {
        element.addEventListener('touchmove', handleDrag, {
          passive: false,
          capture: true,
        });
      }
      return () => {
        if (element !== null) {
          element.removeEventListener('touchmove', handleDrag, {
            capture: true,
          });
        }
      };
    }, [handleDrag]);

    return (
      <span
        ref={ref}
        data-testid="mobile-range-handle"
        style={isHidden ? { ...cursorStyle, opacity: 0 } : cursorStyle}
      >
        <span
          ref={touchMoveRef}
          onTouchStart={handleTouchStart}
          onTouchEnd={handleTouchEnd}
          style={style}
        >
          <span style={handleRoundDotStyle} />
        </span>
      </span>
    );
  },
);

// Handles component, contains two handles at start and end of a selection
function Handles({ selection, controller, enableZoomer }: HandlesProps) {
  const { document } = controller.value;
  const scrollableContainer = useScrollableContainer();

  const startRef = React.useRef<HTMLSpanElement>(null);
  const endRef = React.useRef<HTMLSpanElement>(null);
  const outsideScoller = React.useRef<ReturnType<typeof startScroll> | null>(null);

  const { start, end } = selection.sort(document);

  const setNodeVisible = React.useCallback(
    (nodeRef: React.RefObject<HTMLSpanElement>, visible: boolean) => {
      const node = nodeRef.current;
      if (node) {
        node.style.visibility = visible ? 'unset' : 'hidden';
      }
    },
    [],
  );

  const updatePosition = () => {
    const root = findDOMNodeSafely(document.key);
    const posStart = findCaretPosition(start, 'start', root);
    const posEnd = findCaretPosition(end, 'end', root);
    const pairs: Array<[
      PointLocation | undefined,
      React.RefObject<HTMLSpanElement>,
    ]> = [
      [posStart, startRef],
      [posEnd, endRef],
    ];
    pairs.forEach(([position, ref]) => {
      if (position && isPointVisible(position) && ref.current) {
        setNodeVisible(ref, true);
        updateHandlePosition(ref.current, position);
      } else {
        setNodeVisible(ref, false);
      }
    });
  };

  React.useEffect(updatePosition);
  useScroll({
    onStart: () => {
      setNodeVisible(startRef, false);
      setNodeVisible(endRef, false);
    },
    onEnd: () => {
      updatePosition();
    },
  });

  const optimizeVerticalPosition = React.useCallback(
    (isStart: boolean, orginalY: number) => {
      const friendNode = isStart ? endRef.current : startRef.current;
      if (friendNode) {
        const friendRect = friendNode.getBoundingClientRect();
        const friendY = friendRect.top + friendRect.height / 2;
        return isStart
          ? Math.min(friendY, orginalY)
          : Math.max(friendY, orginalY);
      }
      return orginalY;
    },
    [],
  );

  // update the selection when the user drags the handle bar
  const handleDrag = React.useCallback(
    (type: DraggingType, event: TouchEvent) => {
      blockEvent(event);
      outsideScoller.current && outsideScoller.current(event);

      const isStart = type === TYPE_START;
      event.stopImmediatePropagation();
      const touchPoint = event.touches[0];
      const { clientX, clientY } = touchPoint;
      const x = clientX;
      const orginalY = isStart
        ? clientY + HANDLE_TOUCH_SIZE
        : clientY - HANDLE_TOUCH_SIZE;
      const y = optimizeVerticalPosition(isStart, orginalY);

      const range = caretRangeFromPoint(x, y, controller);
      const { document: doc, selection } = controller.value;
      if (!range) {
        return;
      }

      let { focus: targetPoint } = range;
      const voidParent = document.getClosestVoid(targetPoint.key, controller);
      if (voidParent) {
        // Handle Drag will skip the point inside void elements
        try {
          // eslint-disable-next-line react/no-find-dom-node
          const voidNode = findDOMNode(voidParent.key);
          const voidRect = voidNode.getBoundingClientRect();
          const voidCenterX = voidRect.x + voidRect.width / 2;
          const distance = clientX > voidCenterX ? 1 : -1;
          targetPoint = controller.query(pointAtDistance, targetPoint, distance);
        } catch (error) {
          // ignore
        }
      }

      const newSelection = selection.set('anchor', targetPoint);

      if (
        newSelection.isCollapsed ||
        newSelection.isBackward(doc) !== selection.isBackward(doc) ||
        equal(newSelection, selection)
      ) {
        return;
      }
      controller.run(
        'onCangjieSelect',
        CangjieSelectEvent({
          selection: newSelection,
          trigger: 'dragging',
        }),
      );
    },
    [controller],
  );
  const [dragging, setDragging] = React.useState<DraggingType>(null);
  const onDragStart = React.useCallback((type: DraggingType) => {
    /**
     * Some android devices scroll to the HiddenTextArea after drag, it
     * use 'anchor' point for positioning. So we make sure the user always
     * drag the 'anchor' point here.
     */
    const isBackward = selection.isBackward(document);
    // if we're dragging the 'focus' point, flip the selection
    if ((type === TYPE_START && isBackward) || (type === TYPE_END && !isBackward)) {
      const newSelection = selection.merge({
        anchor: selection.focus,
        focus: selection.anchor,
      });
      controller.run(
        'onCangjieSelect',
        CangjieSelectEvent({
          selection: newSelection,
          trigger: 'dragging',
        }),
      );
    }
    setDragging(type);
    if (scrollableContainer) {
      outsideScoller.current = startScroll({
        scroller: scrollableContainer as HTMLElement,
        controller,
      });
    }
  }, [controller, document, selection]);
  const onDragEnd = React.useCallback(() => {
    setDragging(null);
    if (outsideScoller.current) {
      outsideScoller.current.cancel();
      outsideScoller.current = null;
    }
    stopScroll(controller);
  }, []);

  React.useEffect(() => {
    return () => {
      if (outsideScoller.current) {
        outsideScoller.current.cancel();
        outsideScoller.current = null;
      }
    };
  }, [controller]);

  const dragProps = { onDragStart, onDragEnd };

  let draggingPoint: TextPoint | EdgePoint | null = null;
  if (dragging === TYPE_START) {
    draggingPoint = start;
  } else if (dragging === TYPE_END) {
    draggingPoint = end;
  }

  return (
    <React.Fragment>
      <Handle
        {...dragProps}
        type={TYPE_START}
        isHidden={enableZoomer && dragging === TYPE_END}
        onDrag={handleDrag}
        ref={startRef}
        style={handleRectTopStyle}
      />
      <Handle
        {...dragProps}
        type={TYPE_END}
        isHidden={enableZoomer && dragging === TYPE_START}
        onDrag={handleDrag}
        ref={endRef}
        style={handleRectStyle}
      />
      {enableZoomer && draggingPoint && (
        <Zoomer isHanging={dragging === TYPE_START} point={draggingPoint} />
      )}
      {!dragging && (
        <ContextMenu selection={selection} controller={controller} />
      )}
    </React.Fragment>
  );
}

export default (props: HandlesProps) => {
  const { selection: propsSelection, controller } = props;
  const [selection, setSelection] = React.useState(propsSelection);

  React.useMemo(() => {
    setSelection(propsSelection);
  }, [propsSelection]);

  useSelectionData(controller.selectionData$, null, ({ range }) => {
    setSelection(range);
  }, []);
  if (selection.isCollapsed) {
    return null;
  }
  return ReactDOM.createPortal(<Handles {...props} selection={selection} />, document.body);
};
