export const TOGGLE_GROUP_FOLD = Symbol('toggleGroupFold');
export const FOLD_GROUP = Symbol('fold');
export const EXPAND_GROUP = Symbol('expand');

export interface SetGroupFoldPayload {
  key: string;
}

/**
 * 折叠/展开分组
 * @param key
 * @returns
 */
export function toggleGroupFold(key: string) {
  return {
    type: TOGGLE_GROUP_FOLD,
    payload: { key } as SetGroupFoldPayload,
  };
}

/**
 * 折叠分组
 * @param key
 * @returns
 */
export function fold(key: string) {
  return {
    type: FOLD_GROUP,
    payload: { key } as SetGroupFoldPayload,
  };
}

/**
 * 展开分组
 * @param key
 * @returns
 */
export function expand(key: string) {
  return {
    type: EXPAND_GROUP,
    payload: { key } as SetGroupFoldPayload,
  };
}
