import PolyfillDataTransfer from '../events/dataTransfer';
import { MIME_TYPES } from '../constants';

// TODO: merge PolyfillDataTransfer & CangjieDataTransfer into one model
export default class CangjieDataTransfer extends PolyfillDataTransfer {
  static create(clipboardData?: DataTransfer) {
    return new CangjieDataTransfer(clipboardData);
  }

  private constructor(clipboardData?: DataTransfer) {
    super();
    if (!clipboardData) {
      return this;
    }

    // 仅复制剪切板里基础类型数据，防止文件过大导致内存溢出，需要消费 File、Img 类型数据直接从 nativeEvent 上获取
    for (const type of Object.values(MIME_TYPES)) {
      if (!clipboardData.types.includes(type)) {
        continue;
      }
      const data = clipboardData.getData(type);
      super.setData(type, data);
    }

    return this;
  }

  public setData(type: string, data: string | File): CangjieDataTransfer {
    super.setData(type, data);
    return Object.create(this, {
      clearData: {
        value() {
          Object.getPrototypeOf(this).clearData();
        }
      },
      getData: {
        value(t) {
          return Object.getPrototypeOf(this).getData(t);
        },
      },
    });
  }
}
