/**
 * @packageDocumentation
 * @ignore
 */
import Controller from '../../controller/index';
import findDOMNode from './findDOMNode';
import Point, { EdgePoint } from '../../models/point';
import { getAroundRects } from './isPointAtEdge';
import { getLocalCaretPos } from './localCaretPos';

/**
 * 从 from 垂直变到 to 点时，新的点是否为 isHanging
 * @param from 起始 Point
 * @param to 目标 Point
 * @returns isHanging
 */
export function shouldBeHangingByVertically(from: Point, to: Point, controller: Controller) {
  if (to.isEdgePoint()) {
    return false;
  }
  const [firstRect, secondRect] = getAroundRects(to);
  if (!firstRect || !secondRect) {
    return false;
  }

  if (from.isEdgePoint()) {
    return secondRect.left < firstRect.left && from.edge === EdgePoint.BEFORE;
  }

  const fromPos = getLocalCaretPos(controller);
  if (!fromPos || secondRect.left >= firstRect.left) {
    return false;
  }

  // to 在边界点上，且起点更靠近 secondRect.left 时，应为 isHanging
  return (fromPos.left - secondRect.left) < (firstRect.right - fromPos.left);
}

export function isCoordAtLeftSide(
  clientX: number,
  controller: Controller,
) {
  const { document } = controller.value;
  // eslint-disable-next-line react/no-find-dom-node
  const articleDom = findDOMNode(document.key);
  if (!articleDom) {
    return false;
  }
  const rect = articleDom.getBoundingClientRect();
  return clientX < rect.left + rect.width / 2;
}
