import { GROUP_COLLAPSED_KEY } from '../../constants';
import Plugin from '../../interfaces/plugin';
import {
  SetGroupFoldPayload,
  TOGGLE_GROUP_FOLD,
  FOLD_GROUP,
  EXPAND_GROUP,
} from './actions';

const onAction: Plugin['onAction'] = (action, controller, next) => {
  const { type, payload } = action;
  const { groupManager } = controller;
  if (
    type === TOGGLE_GROUP_FOLD ||
    type === FOLD_GROUP ||
    type === EXPAND_GROUP
  ) {
    const { key } = payload as SetGroupFoldPayload;
    const group = groupManager.getGroup(key);
    if (group) {
      if (type === TOGGLE_GROUP_FOLD) {
        groupManager.mergeGroupData(key, {
          [GROUP_COLLAPSED_KEY]: !group.data[GROUP_COLLAPSED_KEY],
        });
        return controller;
      } else {
        groupManager.mergeGroupData(key, {
          [GROUP_COLLAPSED_KEY]: type === FOLD_GROUP,
        });
        return controller;
      }
    }
  }
  return next();
};

export default onAction;
