/**
 * @packageDocumentation
 * @module Events
 */
interface CangjiePerfDetail {
  /** 性能统计类型 */
  type: string;
  /** 耗时 */
  duration: number;
  /** 是否掉帧 */
  dropped?: boolean;
}

interface CangjiePerfEvent extends CustomEvent<CangjiePerfDetail> {}

function CangjiePerfEvent(detail: CangjiePerfDetail) {
  return new CustomEvent('cangjiePerf', {
    detail: {
      dropped: false,
      ...detail,
    },
  });
}

export default CangjiePerfEvent;
