/**
 * @packageDocumentation
 * @ignore
 */
import equal from 'fast-deep-equal';

type Func<T> = (a: T) => boolean;

export default {
  skipUntil<T>(as: T[], func: Func<T>) {
    const index = as.findIndex((a) => func(a));
    if (index === -1) {
      return [];
    }
    return as.slice(index);
  },
  takeUntil<T>(as: T[], func: Func<T>) {
    const index = as.findIndex((a) => func(a));
    if (index === -1) {
      return as.slice();
    }
    return as.slice(0, index);
  },
  hasSameMembers<T, P>(a: T[], b: P[]) {
    if (a.length !== b.length) {
      return false;
    }
    return a.length === 0 || a.every((aElem) => !!b.find((bElem) => equal(aElem, bElem)));
  },
};
