/**
 * @packageDocumentation
 * @ignore
 */
import * as React from 'react';
import equal from 'fast-deep-equal';
import Debug from 'debug';
import Controller from '../controller/index';
import * as Commands from '../commands/index';
import Plugin from '../interfaces/plugin';
import RenderEditableProps from '../interfaces/renderEditableProps';
import Selection, { SelectionTrigger } from '../models/selection';
import { EdgePoint } from '../models/point';
import Block from '../models/block';
import CangjieDataTransfer from '../models/dataTransfer';
import { LocalCaret } from '../components/selection/caret';
import CangjieClipboardEvent from '../events/cangjieClipboard';
import CangjieInputEvent from '../events/cangjieInput';
import CangjieSelectEvent from '../events/cangjieSelect';
import createClipboardDataFromSelection from '../utils/createClipboardDataFromSelection';
import { getEventTransfer, copyFragment } from '../utils/transfer';
import hotKeys from '../utils/hotkeys';
import findNode from '../utils/dom/findNode';
import * as environment from '../utils/environment';
import { CANGJIE_SELECTION_COLOR, CANGJIE_SELECTION_INACTIVE_COLOR, MIME_TYPES } from '../constants';
import shoudStopInsideVoid from '../utils/shouldStopInsideVoid';
import { PerfType } from '../interfaces/perf';
import { perfMark } from '../utils/perf';
import { ACTION_COPY, ACTION_CUT, ACTION_COPY_FAIL, ACTION_CUT_FAIL, ACTION_PASTE_FAIL } from './contextMenu';
import Operation from '../models/operation';
import { CaretAppearance } from '../components/selection';
import { CONTROLLER_TO_KEY_DOM } from '../utils/dom/getElementByCangjieKey';
import { Group } from '../interfaces/group';

const warn = Debug('EDITOR:CANGJIE:PLUGIN');
if (typeof console !== 'undefined') {
  warn.log = console.warn.bind(console);
}

const IS_TOUCH_MOBILE = environment.IS_MOBILE || environment.IS_TOUCH_DEVICE;

export default function createCorePlugin(): Plugin {
  function decorateNode() {
    return [];
  }

  function injectToNode() {
    return [];
  }

  function onApplyOperation(op: Operation, controller: Controller) {
    return controller.applyOperation(op);
  }

  function groupNodes([ start, end ], { parent }): ReturnType<Required<Plugin>['groupNodes']> {
    const { nodes } = parent;
    return { node: nodes[start], remainedRange: [start + 1, end] };
  }

  function onCopy(event: React.ClipboardEvent, controller: Controller) {
    event.preventDefault();
    if (controller.readOnly && !controller.canCopy) {
      warn('Copy command is prohibited.');
      return;
    }
    const cangjieCopyEvent = CangjieClipboardEvent('cangjieCopy', {
      nativeEvent: event.nativeEvent,
    });
    try {
      controller.run(
        'onCangjieCopy',
        cangjieCopyEvent.setClipboardData(createClipboardDataFromSelection(controller)),
      );
    } catch (error) {
      controller.run('onAction', { type: ACTION_COPY_FAIL, payload: { error } });
      throw (error);
    }
  }

  function onCut(event: React.ClipboardEvent, controller: Controller) {
    event.preventDefault();
    if (controller.readOnly && !controller.canCopy) {
      warn('Cut command is prohibited.');
      return;
    }
    const cangjieCutEvent = CangjieClipboardEvent('cangjieCut', {
      nativeEvent: event.nativeEvent,
    });
    try {
      controller.run(
        'onCangjieCut',
        cangjieCutEvent.setClipboardData(createClipboardDataFromSelection(controller)),
      );
    } catch (error) {
      controller.run('onAction', { type: ACTION_CUT_FAIL, payload: { error } });
      throw (error);
    }
  }

  function onPaste(event: React.ClipboardEvent, controller: Controller) {
    event.preventDefault();
    const cangjiePasteEvent = CangjieClipboardEvent('cangjiePaste', {
      nativeEvent: event.nativeEvent,
    });
    try {
      controller.run('onCangjiePaste', cangjiePasteEvent.setClipboardData(CangjieDataTransfer.create(event.clipboardData)));
    } catch (error) {
      controller.run('onAction', { type: ACTION_PASTE_FAIL, payload: { isShortcut: true, error } });
      throw (error);
    }
  }

  function onCangjieFocus(controller: Controller) {
    return controller.command(Commands.focus);
  }

  function onCangjieBlur(controller: Controller) {
    return controller.command(Commands.blur);
  }

  function onCangjieInput(e: CangjieInputEvent, controller: Controller, next) {
    const { document, selection } = controller.value;
    const { detail } = e;
    const { type, data } = detail;
    if (type === 'insertText' && data) {
      // 在这里 flush 会导致 Android 联想状态回车后续的 splitBlock 无效
      // 但不 flush 会导致桌面端中文输入选词失效
      return controller.command(Commands.insertText, data);
    }
    if (type === 'compositionStart') {
      perfMark('compositionStart');
      if (selection?.isExpanded) {
        return controller.command('del');
      }
      if (
        selection?.isCollapsed
        && document.hasVoidParent(selection.anchor.key, controller)
      ) {
        return controller.command(Commands.moveToStartOfNextText);
      }
    }
    if (type === 'deleteContentBackward') {
      if (!selection) {
        return next();
      }
      const length = (data || '').length;
      return controller.command(Commands.deleteBackwardAtRange, selection, length);
    }
    if (type === 'insertParagraph') {
      if (selection?.isCollapsed && document.hasVoidParent(selection.anchor.key, controller)) {
        const parent = document.getClosestVoid(selection.anchor.key, controller);
        if (document.getLastNode().key === selection.anchor.key && parent) {
          return controller.command(Commands.insertEmptyBlock, EdgePoint.create({
            key: parent.key,
            edge: EdgePoint.AFTER,
          }));
        }
        const nextText = document.getNextText(selection.focus.key);
        if (nextText) {
          const newRange = selection
            .moveAnchorTo(nextText.key, 0)
            .moveToAnchor();
          return controller.run(
            'onCangjieSelect',
            CangjieSelectEvent({ selection: newRange }),
          );
        }
      }
      return controller.command(Commands.splitBlock);
    }
    return next();
  }

  function onCangjieSelect(event: CangjieSelectEvent, controller: Controller) {
    const { detail } = event;
    const { selection, trigger } = detail;
    const { document, selection: prevSelection } = controller.value;

    if (!Selection.isValidSelection(document, selection)) {
      const selectionJSONString = JSON.stringify(
        selection.toJSON({ preserveKeys: true }),
        null,
        2,
      );
      throw new Error(
        `Core[onCangjieSelect] receive invalid selection: ${selectionJSONString}`,
      );
    }

    // 发现selection除mark外没变化则不设置
    // 这里是行为层，不是command层，做这种处理是安全的
    if (
      equal(prevSelection.data, selection.data)
      && equal(prevSelection.anchor, selection.anchor)
      && equal(prevSelection.focus, selection.focus)
    ) {
      return controller;
    }

    if (trigger) {
      const selectionWithData = trigger === SelectionTrigger.Selecting
        ? selection.merge({ data: { ...selection.data, trigger } })
        : selection;
      return controller.command(Commands.userSelect, selectionWithData);
    }

    return controller.command(Commands.select, selection);
  }

  function onCangjieCopy(event: CangjieClipboardEvent, controller: Controller) {
    if (!controller.canCopy) {
      warn('Copy command is prohibited.');
      return;
    }
    copyFragment(event, () => {
      if (environment.IS_MOBILE || environment.IS_TOUCH_DEVICE) {
        controller.command(Commands.moveToEnd);
      }
    });
  }

  function onCangjieCut(event: CangjieClipboardEvent, controller: Controller) {
    const { selection } = controller.value;
    if (!controller.canCopy) {
      warn('Cut command is prohibited.');
      return;
    }
    copyFragment(event, () => {
      // 修复移动端点击触发 copy 后，仓颉选区 collapsed，造成无法删除（iOS 12 系列及 iOS 14.6 ）
      controller.command(Commands.select, selection).command(Commands.del);
    });
  }

  function onCangjieComposingChange(composing = '', controller: Controller) {
    if (!composing.length) {
      perfMark('compositionEnd');
    }
    controller.setComposing(composing);
  }

  function onCangjiePaste(event: CangjieClipboardEvent, controller: Controller) {
    if (!event.clipboardData) {
      throw new Error('`event` is invalid, please pass clipbaord data with event.');
    }

    const transfer = getEventTransfer(event, controller);
    if (transfer.fragment) {
      controller.command(Commands.insertFragment, transfer.fragment);
      return;
    }
    if (!transfer.text) {
      return;
    }
    const texts = transfer.text.split('\n');
    texts.forEach((line, index) => {
      if (index !== 0) {
        controller.command(Commands.splitBlock);
      }
      controller.command(Commands.insertText, line);
    });
  }

  function onClick(e: React.MouseEvent, controller: Controller, next) {
    const { value } = controller;
    const { document } = value;
    const node = findNode(e.target as Element, controller);
    if (!node) return next();
    const ancestors = document.getAncestors(node.key);
    const isVoid =
      node &&
      (controller.query('isVoid', node) ||
        ancestors!.some((a) => controller.query('isVoid', a)));
    const firstText = node.getFirstText();
    if (isVoid && firstText) {
      const selection = value.selection
        .moveAnchorTo(firstText.key, 0)
        .moveToAnchor();
      controller.run('onCangjieSelect', CangjieSelectEvent({ selection }));
    }
    return next();
  }
  function onKeyDown(event: React.KeyboardEvent, controller: Controller, next) {
    const { selection, document } = controller.value;
    if (!selection) {
      return next();
    }
    if (hotKeys.isSplitBlock(event)) {
      event.preventDefault();
      // 如果光标在一个 void 元素中，则不进行 split 操作，直接移动光标到下一个 Text 节点
      // 这个行为主要用于处理 image/mention 等 inline 元素的断行问题
      return controller.run('onCangjieInput', CangjieInputEvent({
        type: 'insertParagraph',
        data: null,
      }));
    }
    if (hotKeys.isDeleteBackward(event)) {
      if (environment.IS_MOBILE) {
        // 移动端由于删除事件不准确，这里不 preventDefault，走 onChange 路径
        return;
      }
      const isProceeded = shoudStopInsideVoid(controller, 'moveBackward');
      event.preventDefault();
      if (isProceeded) {
        return controller;
      }
      return controller.command(Commands.deleteCharBackward);
    }
    if (hotKeys.isDeleteForward(event)) {
      const isProceeded = shoudStopInsideVoid(controller, 'moveForward');
      event.preventDefault();
      if (isProceeded) {
        return controller;
      }
      return controller.command(Commands.deleteCharForward);
    }
    if (hotKeys.isDeleteLineBackward(event)) {
      event.preventDefault();
      return controller.command(Commands.deleteLineBackward);
    }
    if (hotKeys.isDeleteLineForward(event)) {
      event.preventDefault();
      return controller.command(Commands.deleteLineForward);
    }
    if (hotKeys.isDeleteWordBackward(event)) {
      event.preventDefault();
      return controller.command(Commands.deleteWordBackward);
    }
    if (hotKeys.isDeleteWordForward(event)) {
      event.preventDefault();
      return controller.command(Commands.deleteWordForward);
    }
    if (hotKeys.isMoveBlockBackward(event)) {
      event.preventDefault();
      const newSelection = selection
        .movePointEdgeKlass(controller, 'start', 'start', 'block')
        .moveToStart(document);
      return controller.command(Commands.select, newSelection);
    }
    if (hotKeys.isMoveBlockForward(event)) {
      event.preventDefault();
      const newSelection = selection
        .movePointEdgeKlass(controller, 'end', 'end', 'block')
        .moveToEnd(document);
      return controller.command(Commands.select, newSelection);
    }
    if (hotKeys.isExtendBlockBackward(event)) {
      event.preventDefault();
      const newSelection = selection
        .movePointEdgeKlass(controller, 'focus', 'start', 'block');
      return controller.command(Commands.select, newSelection);
    }
    if (hotKeys.isExtendBlockForward(event)) {
      event.preventDefault();
      const newSelection = selection
        .movePointEdgeKlass(controller, 'focus', 'end', 'block');
      return controller.command(Commands.select, newSelection);
    }
    if (hotKeys.isCopy(event) && IS_TOUCH_MOBILE) {
      controller.run('onAction', { type: ACTION_COPY });
      return controller;
    }
    if (hotKeys.isCut(event) && IS_TOUCH_MOBILE) {
      controller.run('onAction', { type: ACTION_CUT });
      return controller;
    }
    return next();
  }

  function renderEditable(props: RenderEditableProps) {
    const { children } = props;
    return children();
  }

  function renderNode(props, _, next) {
    const { attributes, node, children } = props;
    const Tag = Block.isBlock(node) ? 'div' : 'span';
    const result = next();
    if (result === null) {
      return <Tag {...attributes}>{children()}</Tag>;
    }
    return result;
  }

  function renderMark(props) {
    const { children } = props;
    return children();
  }

  function renderCaret({ position, color }: { position: CaretAppearance, color: string }, controller: Controller) {
    const { value } = controller;
    const { document, selection } = value;

    if (!selection) {
      return null;
    }
    if (document.getClosest(selection.focus.key, (node) => controller.query('isVoid', node))) {
      return null;
    }

    return (
      <LocalCaret
        data-cangjie-selection={selection.viewKey}
        controller={controller}
        selection={selection}
        position={position}
        color={color}
      />
    );
  }

  function selectionColor(controller: Controller) {
    const { isFocused } = controller.value;
    return isFocused
      ? CANGJIE_SELECTION_COLOR
      : CANGJIE_SELECTION_INACTIVE_COLOR;
  }

  function isEditorBusy(controller: Controller) {
    // OP Pending 或热区过程中
    if (controller.isPending) {
      return true;
    }

    // 输入法输入过程中
    if (controller.value.composing) {
      return true;
    }

    return false;
  }

  function onCleanup(controller, next) {
    if (CONTROLLER_TO_KEY_DOM.has(controller)) {
      // weakmap的value是map,引用到了dom
      const map = CONTROLLER_TO_KEY_DOM.get(controller);
      map && Object.keys(map).forEach((key) => {
        map?.delete(key);
      });
      CONTROLLER_TO_KEY_DOM.delete(controller);
    }
    return next();
  }

  function mapOperation(op) {
    return op;
  }

  function isElementInGroup(controller: Controller, element: Element, group: Group) {
    return true;
  }

  return {
    queries: {
      selectionColor,
      isEditorBusy,
      isElementInGroup,
    },
    decorateNode,
    injectToNode,
    groupNodes,
    onApplyOperation,
    onCangjieFocus,
    onCangjieBlur,
    onCangjieInput,
    onCangjieSelect,
    onCangjieCopy,
    onCangjieCut,
    onCangjiePaste,
    onCangjieComposingChange,
    onClick,
    onCopy,
    onCut,
    onKeyDown,
    onPaste,
    onCleanup,
    renderEditable,
    renderContent: renderEditable,
    renderNode,
    renderMark,
    renderCaret,
    mapOperation,
    models: [],
  };
}
