/**
 * @packageDocumentation
 * @ignore
 */
import Controller from '../../controller/index';
import Selection from '../../models/selection';
import Point from '../../models/point';
import findDOMRange from './findDOMRange';
import isPointAtEdge, { findRectsInEdgePoint } from './isPointAtEdge';
import findDOMNode from './findDOMNode';

export default function findRectFromPoint(
  point: Point,
  isHanging: boolean,
  controller: Controller,
) {
  if (!point.isTextPoint()) {
    // eslint-disable-next-line react/no-find-dom-node
    const node = findDOMNode(point.key);
    return node.getBoundingClientRect();
  }
  const { document } = controller.value;
  const text = document.getNode(point.key)?.text;
  const isAfterNewLine = text && text[point.offset - 1] === '\n';
  const isAtEdge = isPointAtEdge(point);
  if (isAtEdge) {
    const edgeRects = findRectsInEdgePoint(point);
    return isHanging ? edgeRects[edgeRects.length - 1] : edgeRects[0];
  }
  const domRange = findDOMRange(
    Selection.create({
      anchor: point,
      focus: point,
    }),
    controller,
  );
  if (!domRange) {
    return null;
  }
  const rangeRects = domRange.getClientRects();
  // choose last rect when the point is hanging or after \n
  return (isHanging || isAfterNewLine) ? rangeRects[rangeRects.length - 1] : rangeRects[0];
}
