/**
 * @packageDocumentation
 * @module Models
 */
import isPlainObject from 'is-plain-object';
import equal from 'fast-deep-equal';
import ImmutableRecord from './immutableRecord';
import Data from '../interfaces/data';
import { Serializable } from '../interfaces/serializable';

export interface MarkProperties<D extends Data = Data> {
  type: string;
  data?: D;
}

export interface MarkJSON<D extends Data = Data> {
  klass?: 'mark';
  type: string;
  data?: D;
}

export default class Mark<D extends Data = Data> extends ImmutableRecord
  implements Serializable<MarkJSON<D>> {
  static create<SD extends Data = Data>(properties: Mark<SD> | MarkProperties<SD> | string): Mark<SD> {
    if (Mark.isMark(properties)) {
      return properties;
    }

    let props = properties;
    if (typeof props === 'string') {
      props = { type: props };
    }
    if (isPlainObject(props)) {
      const { type, data = {} as SD } = props;

      if (typeof type !== 'string') {
        throw new Error('`Mark.fromJS` requires a `type` string.');
      }

      return new Mark<SD>({
        type,
        data,
      });
    }

    throw new Error(
      `\`Mark.create\` only accepts objects, strings or marks, but you passed it: ${properties}`,
    );
  }

  static fromJSON(json: MarkJSON) {
    return Mark.create(json);
  }

  static isMark(maybeMark: any): maybeMark is Mark {
    return maybeMark instanceof Mark;
  }

  protected constructor(props: Required<MarkProperties<D>>) {
    super();
    const { type, data } = props;
    this.type = type;
    this.data = { ...data };
    this.freeze();
  }

  public readonly type: string;

  public readonly data: D;

  public merge(props: Partial<MarkProperties<D>>) {
    return new Mark<D>({
      type: this.type,
      data: this.data,
      ...props,
    });
  }

  public set<T extends keyof MarkProperties<D>>(
    key: T,
    value: Required<MarkProperties<D>>[T],
  ) {
    return this.merge({
      [key]: value,
    });
  }

  public isEqual(mark?: Mark): boolean {
    return equal(this, mark);
  }

  public toJSON(): MarkJSON<D> {
    return {
      klass: 'mark',
      type: this.type,
      data: { ...this.data },
    };
  }
}
