import React from 'react';
import { Observable, Observer } from '../models/observable';
import { Terminal } from '../interfaces/controller';
export function usePendingTerminal(
  terminal$: Observable<Terminal | null>,
  onTerminate: Observer<Terminal | null>,
) {
  React.useEffect(() => {
    const subs = terminal$.subscribe(onTerminate);
    return subs.unsubscribe;
  }, [onTerminate, terminal$]);
}
