import { isFileMimeType } from '../utils/getClipboardData';

class CustomDataTransfer {
  private data: { dragX?: string; dragY?: string };

  private dropEffect: string;

  private effectAllowed: string;

  public files: any[];

  private img: string;

  public items: any[];

  public types: any[];

  private xOffset: number;

  private yOffset: number;

  constructor() {
    this.data = { dragX: '', dragY: '' };
    this.dropEffect = 'none';
    this.effectAllowed = 'all';
    this.files = [];
    this.img = '';
    this.items = [];
    this.types = [];
    this.xOffset = 0;
    this.yOffset = 0;
  }

  clearData() {
    this.data = {};
    this.items = [];
    this.types = [];
  }

  getData(format: string) {
    return this.data[format];
  }

  setData(format: string, data: string | File) {
    const toAdd = {
      kind: 'string',
      type: format,
    }
    if (
      !this.items.some((i) => {
        i.kind === toAdd.kind && i.type === toAdd.type;
      })
    ) {
      this.items.push(toAdd);
    }
    if (isFileMimeType(format) && data instanceof Blob) {
      this.data[format] = '';
      if (!this.types.includes('Files')) {
        this.types.push('Files');
      }
      this.files.push(data);
    } else {
      this.data[format] = data;
      if (!this.types.includes(format)) {
        this.types.push(format);
      }
    }
  }

  setDragImage(img: string, xOffset: number, yOffset: number) {
    this.img = img;
    this.xOffset = xOffset;
    this.yOffset = yOffset;
  }
}

export default CustomDataTransfer;
