import React from 'react';
import Selection from '../models/selection';
import { Observable, Observer } from '../models/observable';

export type SelectionData = {
  range: Selection;
  blocksKeys: Set<string>;
  isHideSelection: boolean;
};

export function useSelectionData(
  selectionData$: Observable<SelectionData>,
  key: string | null,
  onSelectionData: Observer<Omit<SelectionData, 'blocksKeys'> & { isSelected: boolean | null }>,
  deps: any[],
) {
  React.useEffect(() => {
    const subs = selectionData$.subscribe(({ range, blocksKeys, isHideSelection }) => {
      const isSelected = key === null ? null : blocksKeys.has(key);
      onSelectionData({
        range,
        isSelected,
        isHideSelection,
      });
    });
    return subs.unsubscribe;
  }, [key, onSelectionData, ...deps]);
}

export function useSelectionDataAtTheStartOfNextFrame(
  selectionData$: Observable<SelectionData>,
  key: string | null,
  onSelectionDataNextFrame: Observer<Omit<SelectionData, 'blocksKeys'> & { isSelected: boolean | null }>,
  deps: any[],
) {
  const animId = React.useRef<number | null>(null);
  const onSelectionData = React.useCallback(
    (pendingData) => {
      animId.current = window.requestAnimationFrame(() => {
        onSelectionDataNextFrame(pendingData);
      });
    },
    [onSelectionDataNextFrame],
  );

  React.useEffect(() => {
    return () => {
      if (animId.current) {
        window.cancelAnimationFrame(animId.current);
      }
    };
  }, []);

  useSelectionData(selectionData$, key, onSelectionData, deps);
}
