/**
 * @packageDocumentation
 * @ignore
 */
import Node from '../models/node';
import Document from '../models/document';
import Element from '../models/element';
import Decoration from '../models/decoration';
import Selection from '../models/selection';
import Point, { EdgePoint } from '../models/point';
import Path from '../models/path';

interface OrderedItem {
  order: number;
}

interface RangeStart<T> extends OrderedItem {
  start: true;
  range: T;
}

interface RangeEnd<T> extends OrderedItem {
  end: true;
  range: T;
}

interface Child extends OrderedItem {
  child: Node;
}

type OrderItem<T> = Child | RangeStart<T> | RangeEnd<T>;

function isChild<T>(item: OrderItem<T>): item is Child {
  return 'child' in item;
}

function isRangeStart<T>(item: OrderItem<T>): item is RangeStart<T> {
  return 'start' in item;
}

function isRangeEnd<T>(item: OrderItem<T>): item is RangeEnd<T> {
  return 'end' in item;
}

function isEdgeAfter(edgePoint: Point, edgePath: Path, nodePath: Path) {
  return edgePoint.isEdgePoint() &&
    edgePoint.edge === EdgePoint.AFTER &&
    (Path.isEqual(edgePath, nodePath) || Path.isAncestor(edgePath, nodePath));
}

function isEdgeBefore(edgePoint: Point, edgePath: Path, nodePath: Path) {
  return edgePoint.isEdgePoint() &&
    edgePoint.edge === EdgePoint.BEFORE &&
    (Path.isEqual(edgePath, nodePath) || Path.isAncestor(edgePath, nodePath));
}

function sort<T>(a: OrderItem<T>, b: OrderItem<T>) {
  if (a.order > b.order) {
    return 1;
  } else if (a.order === b.order) {
    return isRangeEnd(b) ? 1 : -1;
  }
  return -1;
}

export function getChildrenDecorations(
  document: Document,
  node: Element,
  decorations: Decoration[],
) {
  const childrenDecorations: Decoration[][] = [];

  // 初始化排序数组，先放入节点子孙
  const orderArray: OrderItem<Decoration>[] = node.nodes.map((child, index) => ({
    child,
    order: index,
  }));

  const nodePath = document.assertPath(node.key);
  const depth = nodePath.length;
  const last = node.nodes.length - 1;
  // 遍历 decorations，将其放入排序数组
  decorations.forEach((decoration) => {
    const { start: startPoint, end: endPoint } = decoration;
    const startPath = document.getPath(startPoint.key);
    const endPath = document.getPath(endPoint.key);

    if (!startPath || !endPath) {
      return;
    }
    if (Path.isAfter(startPath, nodePath) || Path.isBefore(endPath, nodePath)) {
      return;
    }

    // 获得 decoration 相对于 node children 的起始位置
    const start = Path.isBefore(startPath, nodePath) ? 0 : startPath[depth];
    const end = Path.isAfter(endPath, nodePath) ? last : endPath[depth];

    orderArray.push({
      start: true,
      range: decoration,
      order: start - 0.5,
    });

    orderArray.push({
      end: true,
      range: decoration,
      order: end + 0.5,
    });
  });

  const activeDecorations = new Set<Decoration>();
  orderArray.sort(sort).forEach(item => {
    if (isRangeStart(item)) {
      activeDecorations.add(item.range);
    } else if (isRangeEnd(item)) {
      activeDecorations.delete(item.range);
    } else if (isChild(item)) {
      childrenDecorations.push(Array.from(activeDecorations));
    }
  });
  return childrenDecorations;
}

export function getChildrenIsSelected(
  document: Document,
  node: Element,
  selection: Selection,
) {
  const childrenIsSelected: boolean[] = node.nodes.map(() => false);
  if (selection.isCollapsedAtEdge) {
    return childrenIsSelected;
  }

  const { start: startPoint, end: endPoint } = selection.sort(document);
  const startPath = document.getPath(startPoint.key);
  const endPath = document.getPath(endPoint.key);
  if (!startPath || !endPath) {
    return childrenIsSelected;
  }

  const nodePath = document.assertPath(node.key);
  if (
    Path.isAfter(startPath, nodePath)
      || isEdgeAfter(startPoint, startPath, nodePath)
  ) {
    return childrenIsSelected;
  }
  if (
    Path.isBefore(endPath, nodePath)
      || isEdgeBefore(endPoint, endPath, nodePath)
  ) {
    return childrenIsSelected;
  }

  const depth = nodePath.length;
  const last = node.nodes.length - 1;
  const start = Path.isBefore(startPath, nodePath) || isEdgeBefore(startPoint, startPath, nodePath)
    ? 0
    : startPath[depth];
  const end = Path.isAfter(endPath, nodePath) || isEdgeAfter(endPoint, endPath, nodePath)
    ? last
    : endPath[depth];

  for (let i = start; i <= end; i += 1) {
    childrenIsSelected[i] = true;
  }
  return childrenIsSelected;
}
