/** 
 * 插件 action 创建快捷方法，解决 action 的 typing 问题
 */

 import type { default as Action } from '../interfaces/action';
 
 // utils handler for createAction and judgeAction
 export const createActionHandlers = <P extends {
   [key: string]: any;
 } = {}>(type: symbol): [
     (payload?: P) => Action<P>,
     (action: Action<any>) => action is NonNullable<Action<P>>
   ] => {
   const createAction = (
     payload: P = {} as any,
   ): Action<P> => ({
     type,
     payload,
   });
   
   const isAction = (
     action: Action<any>,
   ): action is Action<P> => action?.type === type;
 
   return [createAction, isAction];
 };
