/**
 * @packageDocumentation
 * @module environment
 */
export const IS_IOS =
  typeof navigator !== 'undefined' &&
  typeof window !== 'undefined' &&
  /iPad|iPhone|iPod/.test(navigator.userAgent) &&
  !(window as any).MSStream;

export const IS_ANDROID =
  typeof navigator !== 'undefined' && /Android/i.test(navigator.userAgent);

export const IS_MOBILE = IS_IOS || IS_ANDROID;

export const IS_TOUCH_DEVICE =
  typeof navigator !== 'undefined' && navigator.maxTouchPoints > 0;

export const IS_IPAD =
  typeof navigator !== 'undefined' &&
  typeof window !== 'undefined' &&
  /iPad/.test(navigator.userAgent) &&
  !(window as any).MSStream;

export const IS_MAC =
  typeof navigator !== 'undefined' && /Mac OS X/i.test(navigator.userAgent);

export const IS_WINDOWS =
  typeof navigator !== 'undefined' && /Windows/i.test(navigator.userAgent);

export const IS_FIREFOX =
  typeof navigator !== 'undefined' &&
  /mozilla\/[a-z._\d]+ \((?:mobile)|(?:tablet)/i.test(navigator.userAgent);

// Mac 容器内置的 Safari 内核浏览器在 userAgent 上没有 Safari 标识...
// 这里为了兼容钉钉容器内置 Safari 的情况
export const IS_SAFARI =
  typeof navigator !== 'undefined' &&
  navigator.vendor === 'Apple Computer, Inc.';

export const IS_DEV = process.env.NODE_ENV === 'development';

export const IS_NODE = !!(
  typeof process !== 'undefined' &&
	process.versions &&
	process.versions.node
);

/**
 * 返回 Chrome 版本
 */
export const getChromeVersion = () => {
  const raw = navigator?.userAgent?.match(/Chrom(e|ium)\/([0-9]+)\./);
  return raw ? parseInt(raw[2], 10) : -1;
};
