import * as React from 'react';
import equal from 'fast-deep-equal';
import Controller from '../controller/index';
import Document from '../models/document';
import Value from '../models/value';
import Selection from '../models/selection';
import { getChildrenIsSelected } from '../utils/getChildrenOverlaps';
import useCangjieKeyBindingRef from '../hooks/useCangjieKeyBindingRef';
import { renderNode } from './node';
import { Selector } from '../constants';
import { VirtualizePlaceholder } from '../virtualize/components/VirtualizePlaceholder';
import useVirtualize from '../virtualize/hooks/useVirtualize';

export interface EditableContentProps {
  controller: Controller;
  contentRef: React.MutableRefObject<HTMLDivElement | null> | ((instance: HTMLDivElement | null) => void);
  containerRef: React.MutableRefObject<HTMLElement | null>;
  getScrollableContainer: () => Window | HTMLElement | null,
  isFocused: boolean;
  selection: Selection;
  document: Document;
}

const arePropsEqual = (prevProps: EditableContentProps, nextProps: EditableContentProps) => {
  const { selection: prevSelection, document: prevView, isFocused: prevIsFocused } = prevProps;
  const { selection: nextSelection, document: nextView, isFocused: nextIsFocused, controller } = nextProps;

  // 有热区时，不更新，内容交给节点子树自行更新
  if (controller.hasHots()) {
    return true;
  }

  return (
    equal(prevSelection, nextSelection) &&
    prevView === nextView &&
    prevIsFocused === nextIsFocused
  );
};

const EditableContent = React.memo((props) => {
  const { controller, contentRef: ref, getScrollableContainer, selection, document } = props;
  const { value } = controller;
  const prevValue = React.useRef<Value>();
  React.useEffect(() => {
    prevValue.current = value;
  }, [value]);
  const childrenIsSelected = getChildrenIsSelected(document, document, selection);

  const editableRef = useCangjieKeyBindingRef<HTMLDivElement>(props[Selector.key]);
  // HACK: use `useCombinedRefs` will break `useCangjieKeyBindingRef`.
  React.useLayoutEffect(() => {
    if (!ref) {
      return;
    }
    if (typeof ref === 'function') {
      ref(editableRef.current);
    } else {
      ref.current = editableRef.current;
    }
  });

  const useVirtualizeResult = useVirtualize(controller, editableRef, getScrollableContainer);

  const {
    virtualizeId = -1,
    beforePlaceholderHeight = 0,
    afterPlaceholderHeight = 0,
    visibles = null,
  } = useVirtualizeResult ? useVirtualizeResult[0] : {};

  const virtualizeAttr = React.useMemo(() => {
    return controller.enableVirtualize ? {
      "data-virtualize-id": virtualizeId,
    } : {};
  }, [controller.enableVirtualize, virtualizeId]);

  // 因为任何原因拿不到虚拟化结果，走非虚拟化分支
  if (useVirtualizeResult === null || virtualizeId === -1) {
    const children = document.nodes.map((child, index) =>
      renderNode(controller, value, document, document, child, [index], childrenIsSelected[index], true, prevValue.current)
    );
    return (
      <>
        <div
          ref={editableRef}
          data-cangjie-key={document.key}
          data-cangjie-editable
        >
          {children}
        </div>
      </>
    );
  }

  const { blockFinalVisibleStates } = visibles!;

  const children = document.nodes.map((child, index) => {
    const blockFinalVisibleState = blockFinalVisibleStates[index];
    const visible = blockFinalVisibleState === 'show';
    if (blockFinalVisibleState === 'destroy') {
      return null;
    }
    return renderNode(controller, value, document, document, child, [index], childrenIsSelected[index], visible, prevValue.current);
  });

  return (
    <>
      <div
        ref={editableRef}
        data-cangjie-key={document.key}
        data-cangjie-editable
        {...virtualizeAttr}
      >
        {controller.enableVirtualize ? <VirtualizePlaceholder height={beforePlaceholderHeight} /> : null}
        {children}
        {controller.enableVirtualize ? <VirtualizePlaceholder height={afterPlaceholderHeight} /> : null}
      </div>
    </>
  );
}, arePropsEqual);

export default EditableContent;
