import {
  jsonMLGetTagName,
} from '../../utils/jsonml';

import type { JsonML } from '../../type';

export default class State {
  private readonly root: JsonML = ['root'];

  private current: JsonML = this.root;

  private readonly stack: JsonML[] = [];

  private marksList: Array<Record<string, any>> = [];

  private readonly listsList: string[] = [];

  htmlDom!: Element;

  currentDom?: HTMLElement;

  currentListId: string | null = null;

  // 这里用于记录zhi渲染的list的level, TODO 需要和getListLevel统一
  currentListLevel: number | null = null;

  constructor(props: {
    htmlDom?: Element;
  }) {
    if (props.htmlDom) {
      this.htmlDom = props.htmlDom;
    }
  }

  addMarks(marks: Record<string, any>): void {
    this.marksList.push(marks);
  }

  removeMarks(marks: Record<string, any>): void {
    this.marksList = this.marksList.filter((m) => m.id !== marks.id);
  }

  getMarks(): Record<string, any> {
    return this.marksList.reduce<Record<string, any>>(
      (attrs, marks: Record<string, any>) => ({
        ...attrs,
        ...marks.value,
      }),
      {},
    );
  }

  wrapList(type: string): void {
    this.listsList.push(type);
  }

  unwrapList(): void {
    this.listsList.pop();
  }

  isParentOrderedList(): boolean {
    return this.listsList[this.listsList.length - 1] === 'ordered-list';
  }

  getListItemLevel(): number {
    return this.listsList.length - 1;
  }

  append(...nodes: JsonML[]): void {
    nodes.forEach((node: JsonML): void => {
      this.current.push(node);
    });
  }

  push(node: JsonML): void {
    this.stack.push(this.current);
    this.current = node;
  }

  pop(): JsonML {
    const node = this.current;
    this.current = this.stack.pop() || this.root;
    return node;
  }

  peek(): JsonML {
    return this.current;
  }

  closest(type: string): JsonML | null {
    if (jsonMLGetTagName(this.current) === type) {
      return this.current;
    }

    for (let i = this.stack.length - 1; i >= 0; i -= 1) {
      if (jsonMLGetTagName(this.stack[i]) === type) {
        return this.stack[i];
      }
    }
    return null;
  }

  isStackEmpty() {
    return this.stack.length === 0;
  }

  getParent(): JsonML | null {
    return this.stack.length > 0 ? this.stack[this.stack.length - 1] : null;
  }

  getJsonML(): JsonML {
    return this.root;
  }
}
