/**
 * @packageDocumentation
 * @module Models
 */
import isPlainObject from 'is-plain-object';
import Element, { ElementProperties } from './element';
import Text, { TextJSON } from './text';
import { SerializeOptions } from '../interfaces/serializable';
import Data from '../interfaces/data';
import keyUtils from '../utils/keyUtils';

/**
 * @ignore
 */
export type InlineChildren = (Inline | Text)[];

export interface InlineProperties extends ElementProperties {
  type: string;
  nodes?: InlineChildren;
}

export interface InlineJSON {
  klass: 'inline';
  key?: string;
  type: string;
  data?: Data;
  nodes?: (TextJSON | InlineJSON)[];
}

export default class Inline extends Element {
  static create(properties: Inline | InlineProperties | string): Inline {
    if (Inline.isInline(properties)) {
      return properties as Inline;
    }

    let props = properties;
    if (typeof props === 'string') {
      props = { type: props };
    }

    if (isPlainObject(props)) {
      const {
        type,
        key = keyUtils.create(),
        data = {},
        nodes = [Text.create()],
      } = props;

      if (typeof type !== 'string') {
        throw new Error('`Inline.create` requires a `type` string.');
      }

      return new Inline({
        type,
        key,
        data,
        nodes,
      });
    }

    throw new Error(
      `\`Inline.create\` only accepts objects, string or blocks, but you passed it: ${props}`,
    );
  }

  static childFromJSON(node: InlineJSON | TextJSON) {
    const { klass } = node;
    if (klass === 'inline') {
      return Inline.fromJSON(node as InlineJSON);
    }
    if (klass === 'text') {
      return Text.fromJSON(node as TextJSON);
    }

    throw new Error(
      '`Inline.childFromJSON` requires a valid `klass`(inline|text) string.',
    );
  }

  static fromJSON(json: InlineJSON): Inline {
    const { nodes } = json;
    return Inline.create({
      ...json,
      nodes: nodes ? nodes.map(Inline.childFromJSON) : undefined,
    });
  }

  static isInline(maybeInline: any): maybeInline is Inline {
    return maybeInline instanceof Inline;
  }

  /**
   * 是否是叶子inline
   */
  static isLeafInline(maybeLeafInline: any): maybeLeafInline is Inline {
    if (!this.isInline(maybeLeafInline)) {
      return false;
    }
    for (const child of maybeLeafInline.nodes) {
      if (child.isElement()) {
        return false;
      }
    }
    return true;
  }

  protected constructor(props: Required<InlineProperties>) {
    super(props);
    const { type, nodes } = props;
    this.type = type;
    this.nodes = nodes;
    this.freeze();
  }

  public readonly type: string;

  public readonly nodes: InlineChildren;

  public merge(props: Partial<InlineProperties>) {
    return new Inline({
      type: this.type,
      key: this.key,
      data: this.data,
      nodes: this.nodes,
      ...props,
    });
  }

  public set<T extends keyof InlineProperties>(
    key: T,
    value: Required<InlineProperties>[T],
  ) {
    return this.merge({
      [key]: value,
    });
  }

  public toJSON(options?: SerializeOptions) {
    const object: InlineJSON = {
      klass: 'inline',
      type: this.type,
      data: { ...this.data },
      nodes: this.nodes.map((node) => node.toJSON(options)),
    };
    if (options?.preserveKeys) {
      object.key = this.key;
    }
    return object;
  }
}
