/**
 * @packageDocumentation
 * @module domUtils
 */
import { TextPoint } from '../../models/point';
import findDOMLeaves from './findDOMLeaves';

/**
 * 根据 Point 来获取浏览器的 HTML 节点、offset。
 */
function findDOMPoint(point: TextPoint, root: Window | Element = window) {
  const leaves = findDOMLeaves(point.key, root);

  let start = 0;
  // eslint-disable-next-line no-restricted-syntax
  for (const leaf of leaves) {
    const node = leaf.firstChild;
    const length = node && node.textContent ? node.textContent.length : 0;
    const end = start + length;
    if (point.offset <= end) {
      const offset = point.offset - start;
      return { node, offset };
    }
    start = end;
  }
  return null;
}

export default findDOMPoint;
