/**
 * @packageDocumentation
 * @module Hooks
 */
import * as React from 'react';

/**
 * @ignore
 */
export const ZoomContext = React.createContext<number>(1);

/**
 * @ignore
 */
export const ExternalZoomContext = React.createContext<number>(1);

/**
 * @ignore
 */
export const ZoomContainerContext = React.createContext<HTMLElement | null>(null);

export function useZoom() {
  return React.useContext(ZoomContext);
}

/**
 * @ignore
 *
 * TODO: external zoom 是为了感知上层业务自行实现的缩放，但是这个设计是不合理的，上层业务从使
 * 用上看，没必要自行实现缩放功能，应该使用 cangjie 自带的缩放设计。后续希望移除此部分的 API。
 *
 * 目前已知的业务自行实现的缩放场景：PC 演示模式、生成长图
 * 2021-0222 新加：缩放状态下的评论
 */
export function useExternalZoom() {
  return React.useContext(ExternalZoomContext);
}

export function useZoomContainer() {
  return React.useContext(ZoomContainerContext);
}
