import { useState, useEffect, useRef } from 'react';
import useControllerStatic from '../controller/useControllerStatic';
import { GroupWithKey } from '../interfaces/group';
import Element from '../models/element';
import Node from '../models/node';
import { Subscription } from '../models/observable';

export type GroupDataSelector<T = any> = (groups: GroupWithKey[]) => T;

/**
 * 获得所在分组的数据 
 * @param node 
 * @param selector 
 * @param defaultValue 
 * @returns 
 * 
 * @example
 * ```tsx
 * const Element = props => {
 *   const hidden = useGroupData(groups => groups.some(group => group.hidden));
 * 
 *   // ...
 * }
 * ```
 */
export default function useGroupData<T>(
  node: Node,
  selector: GroupDataSelector<T>,
  defaultValue: T,
) {
  let finalNode: Node | null = node;
  const controller = useControllerStatic();

  // 兼容老 Group 的代码
  const legacyGroupIndex = node.key.indexOf('-group');
  if (legacyGroupIndex > -1) {
    const nodeKey = node.key.split('-group')[0];
    const originalNode = controller.view.getNode(nodeKey);
    if (originalNode) {
      finalNode = originalNode;
    }
  }

  const groups = controller.groupManager.getNodeGroups(finalNode as Element);

  const selectorRef = useRef(selector);
  selectorRef.current = selector;

  // default state 就要取实际值，否则数据有延迟，有延迟就对虚拟化不友好
  const defaultData = selectorRef.current(groups) || defaultValue;
  const [data, setData] = useState<T>(defaultData);

  // 响应上游的分组信息变更
  useEffect(() => {
    let sub: Subscription | null = null;
    if ((finalNode && Element.isElement(finalNode))) {
      sub = controller.groupManager.groups$.subscribe((groupsMap) => {
        const groups = controller.groupManager.getNodeGroups(finalNode as Element);
        setData(selectorRef.current(groups));
      });
    }
    return () => {
      if (sub) {
        sub.unsubscribe();
      }
    };
  }, [controller, finalNode]);

  return data;
}
