import { useCallback, useReducer, useRef } from 'react';
import { Controller, Node, Document } from '..';
import { PendingType } from '../interfaces/controller';
import Block from '../models/block';
import { isSelectionInNode } from '../utils/selection';
import useHots from './useHots';

/**
 * 开启热区时，使用热区视图模型
 * @param controller
 * @param viewNode
 * @param viewDocument
 * @returns
 */
function useView<T extends Node>(
  controller: Controller,
  viewNode: T,
  viewDocument: Document,
  isSelected,
) {
  /**************************************************************************************\
  *                                                                                      *
  *                                                                                      *
  *         Render with hots$                                  Render with props         *
  *                                                                                      *
  *           +------------+                                     +------------+          *
  *           |    hot$    |                                     |   props    |          *
  *           |  +-------+ |                                     |            |          *
  *           |  |  hot  | |                                     | +--------+ |          *
  *           |  +-------+ |                                     | |document| |          *
  *           |  +-------+ |             .-------.               | +--------+ |          *
  *           |  |  hot  | |<--observe--( <Node/> )<---inject----|            |          *
  *           |  +-------+ |             `-------'               | +--------+ |          *
  *           |  +-------+ |                                     | |  node  | |          *
  *           |  |  hot  | |                                     | +--------+ |          *
  *           |  +-------+ |                                     |            |          *
  *           +------------+                                     +------------+          *
  *                  |                                                  |                *
  *                  |                                                  |                *
  *                  v                                                  v                *
  *            .-----------.                                      .-----------.          *
  *      +----( isViewHot?  )-----+                      +---Y---(  hasHots?   )--+      *
  *      N     `-----------'      Y                      |        `-----------'   |      *
  *      |                        |                      |                        |      *
  *      v                        v                      v                        |      *
  * +--------+            +--------------+         .-----------.                  |      *
  * |        |            |              |   +----(isInsideHot? )                 |      *
  * | silent |            |recalculateView|   |     `-----------'                  |      *
  * |        |            |              |   |           |                        |      *
  * +--------+            +--------------+   |           N                        N      *
  *                               |          Y           v                        |      *
  *                               v          |      +--------+                    |      *
  *                      +------------------+|      |        |                    |      *
  *                      | {node, document} ||      | silent |                    |      *
  *                      +------------------+|      |        |                    |      *
  *                                |         |      +--------+                    |      *
  *                                v         v                                    |      *
  *                             +------------------------+                        |      *
  *                             |                        |                        |      *
  *                             | render(node, document) |<-----------------------+      *
  *                             |                        |                               *
  *                             +------------------------+                               *
  *                                                                                      *
  \**************************************************************************************/
  const [, forceUpdate] = useReducer((c) => !c, false);
  const selectedRef = useRef(isSelected);

  const onHots = useCallback(() => {
    if (controller.isViewHot(viewNode)) {
      // 若正在框选节点，其选中态未发生变化，则不重渲
      if (
        controller.pendingType === PendingType.selection &&
        controller.value.selection.isExpanded
      ) {
        const prevSelected = selectedRef.current;
        const { node, document } = controller.getView(
          viewNode,
          viewDocument,
          false,
        );
        selectedRef.current = isSelectionInNode(
          node,
          document,
          controller.value.selection,
        );
        if (prevSelected === selectedRef.current) {
          return;
        }
      }
      forceUpdate();
    }
  }, [controller, viewNode, viewDocument, isSelected, forceUpdate]);

  useHots(controller, onHots, undefined, !Block.isBlock(viewNode));

  if (!controller.hasHots()) {
    return {
      node: viewNode,
      document: viewDocument,
      isSelected,
    };
  }

  controller.removeIsSelected(viewNode.key);

  // 重渲时，若节点为热区节点，需要重算其视图模型
  if (controller.isViewHot(viewNode)) {
    const { node, document } = controller.getView(viewNode, viewDocument, true);

    let selected = false;
    if (controller.pendingType === PendingType.input) {
      selected = true;
    } else if (
      controller.pendingType === PendingType.selection &&
      controller.value.selection.isExpanded
    ) {
      // 这种情况已经计算完成，直接使用缓存
      selected = selectedRef.current;
    } else {
      selected = isSelectionInNode(node, document, controller.value.selection);
    }

    selectedRef.current = selected;

    if (selected) {
      controller.addIsSelected(viewNode.key);
    }

    return {
      node,
      document,
      isSelected: selected,
    };
  }

  // 重渲时，若节点非热区，则返回缓存的视图模型
  const { node, document } = controller.getView(viewNode, viewDocument, false);
  selectedRef.current = isSelected;
  return {
    node,
    document,
    isSelected,
  };
}

export default useView;
