/**
 * @packageDocumentation
 * @module Plugin-PlaceHolder
 */
import * as React from 'react';
import Controller from '../controller/index';
import Decoration from '../models/decoration';
import { TextPoint } from '../models/point';
import Node from '../models/node';
import Element from '../models/element';
import Text from '../models/text';
import Mark, { MarkProperties } from '../models/mark';
import RenderMarkProps from '../interfaces/renderMarkProps';
import { useSelectionData } from '../hooks/useSelectionData';
import { usePendingTerminal } from '../hooks/usePendingTerminal';
import * as environment from '../utils/environment';
import { useSelectingHots } from '../hooks/useHots';

const placeholderMarkType = 'cangjiePlaceholder';
const defaultPlaceholerSymbol = Symbol();

const placeholderStyle: React.CSSProperties = {
  pointerEvents: 'none',
  userSelect: 'none',
  WebkitUserSelect: 'none',
  position: 'relative',
};

// 用于外部宽度没有撑开的情况
const innerStyleWithWidth: React.CSSProperties = {
  width: 'fit-content',
  whiteSpace: 'nowrap',
  color: 'rgba(23,26,29, 0.4)',
};

const innerStyle: React.CSSProperties = {
  position: 'absolute',
  ...innerStyleWithWidth,
};

const ContentPlaceholderContext = React.createContext<React.MutableRefObject<string | React.ReactElement> | null>(null);
function useContentPlaceholder() {
  return React.useContext(ContentPlaceholderContext);
}

interface PlaceholderProps {
  controller: Controller;
  node: Node;
  mark: Mark;
  children: React.ReactNode;
}

/**
* 目前需要支持两种配置方式
* 1.自定义展示placeholder配置
* 2.支持从各个插件读取placeholder配置
* todo: 排版模式下不支持
*/
interface PlaceholderConfig {
  text: string | Symbol | ((controller: Controller, node: Node) => string);
  when: (controller: Controller, node: Node) => boolean;
  // 满足条件后禁用
  disable?: boolean;
  // 是否在当前行显示
  focusMode?: boolean;
  needWidth?: boolean;
}
interface PlaceholderPluginConfig {
  isPlaceholderVisible?: (controller: Controller, node: Node) => boolean;
}

function blockEvent(event: React.TouchEvent | React.MouseEvent) {
  event.preventDefault();
  event.stopPropagation();
}

const defaultPx = '14.667';
const FONTSIZE_BIGGER = environment.IS_MOBILE ? 1.06 : 1;
// 并不需要精确计算placeholder的fontsize，目前只在非排版下使用
function formatFontSize(mark?: MarkProperties) {
  if (mark?.data?.value) {
    const { value, szUnit } = mark.data;
    const unit = szUnit || 'px';
    if (unit === 'pt') {
      // word等转换的pt数据依然需要格式化
      return `${value * FONTSIZE_BIGGER}pt`
    } else if (unit === 'px' && mark.data.value === 14) {
      // 特殊处理默认字号：稍微放大至11pt(14.667px)
      return `${defaultPx}px`
    }
  }
  return '';
}

function PlaceholderProvider({ placeholder = '', children }) {
  const placeholderRef = React.useRef(placeholder);
  placeholderRef.current = placeholder;
  return (
    <ContentPlaceholderContext.Provider value={placeholderRef}>
      {children}
    </ContentPlaceholderContext.Provider>
  );
}

function Placeholder(props: PlaceholderProps) {
  const [, forceUpdate] = React.useReducer((c) => !c, false);
  const contentPlaceholderRef = useContentPlaceholder();

  // 逻辑收敛到Placeholder组件
  const { controller, node, mark, children } = props;
  const { selection, isFocused, composing } = controller.value;
  const focusKey = selection?.focus?.key;
  const { config, block } = mark.data;
  const disable = !config || config.disable || composing;
  const { text, needWidth, focusMode, when } = config as PlaceholderConfig;
  const isDefaultPlaceholder = defaultPlaceholerSymbol === text;

  const showDefaultPlaceholder = React.useMemo(() => {
    // 这里需要对默认placeholder做一个特殊的处理，默认placeholder是需要关心全文的，而不是上下文就可以了
    if (!isDefaultPlaceholder) return false;
    return when(controller, block);
  }, [controller.value, block, isDefaultPlaceholder, when]);

  const noRender = disable ||
  (
    focusMode && (!isFocused || !focusKey || node.key !== focusKey || controller.value.selection.isExpanded)
  )
;

  const innerPlaceholderStyle = React.useMemo(() => {
    const style = needWidth ? innerStyleWithWidth : innerStyle;
    const leave = (node as Text).leaves?.length === 1 ? (node as Text).leaves[0] : null;
    const size = leave?.marks?.find((m) => m.type === 'sz');
    if (size) {
      const fontSize = formatFontSize(size);
      if (fontSize) {
        return {
          ...style,
          fontSize,
        }
      }
    }
    return style;
  }, [node, needWidth]);

  const handlePendingSelectionChange = React.useCallback(() => {
    if (
      !disable &&
      focusKey !== controller.value.selection?.focus?.key &&
      (node.key === focusKey || node.key === controller.value.selection?.focus?.key)
    ) {
      forceUpdate();
    }
  }, [controller, focusKey, node, disable]);

  const handlePendingTerminate = React.useCallback(() => {
    if (
      node.key !== controller.value.selection?.focus?.key &&
      !noRender
    ) {
      forceUpdate();
    }
  }, [noRender, node, controller]);

  usePendingTerminal(controller.terminal$, handlePendingTerminate);
  useSelectionData(controller.selectionData$, null, handlePendingSelectionChange, []);
  // 当监听到热区时，也要重渲 placeholder，避免 placeholder 不消失
  useSelectingHots(controller, handlePendingSelectionChange);

  if (noRender || (isDefaultPlaceholder && !showDefaultPlaceholder)) return <>{children}</>;

  const textContent = typeof text === 'function' ? text(controller, node) : text;
  return (
    <>
      {children}
      <span
        onTouchStart={blockEvent}
        onMouseDown={blockEvent}
        style={placeholderStyle}
        data-testid="cangjie-placeholder"
        data-cangjie-placeholder
      >
        <span style={innerPlaceholderStyle}>
          {isDefaultPlaceholder ? contentPlaceholderRef?.current : textContent}
        </span>
      </span>
    </>
  );
}

export default function createPlaceholderPlugin(config: PlaceholderPluginConfig = {}) {
  const { isPlaceholderVisible } = config;

  // HACK: use `Symbol` as placeholder before getting placholder from `Cangjie.Content`.
  const contentPlaceholderConfig: PlaceholderConfig | null = isPlaceholderVisible
    ? { when: isPlaceholderVisible, text: defaultPlaceholerSymbol }
    : null;
  let placeholdersConfig: PlaceholderConfig[] | null = null;

  function decorateNode(node: Element, controller: Controller, next) {
    if (!node.isEmpty()) {
      return next();
    }

    // 读取placeholder配置
    if (!placeholdersConfig) {
      placeholdersConfig = [];
      if (contentPlaceholderConfig) {
        // 内置空白行placeholder逻辑
        placeholdersConfig.push(contentPlaceholderConfig);
      }
      // @ts-ignore
      const { placeholder } = controller.handlers;
      placeholder?.forEach((item) => {
        if (Array.isArray(item)) {
          placeholdersConfig!.push(...item);
        } else {
          // @ts-ignore
          placeholdersConfig!.push(item);
        }
      });
      placeholdersConfig = placeholdersConfig.filter(({ text, disable }) => !!(text || disable));
    }

    const match = placeholdersConfig.find(({ when }) => when(controller, node));
    if (!match) {
      return next();
    }

    const first = node.getFirstText();
    const last = node.getLastText();
    if (!first || !last) {
      return next();
    }

    const start = TextPoint.create({
      key: first.key,
      offset: 0,
    });
    const end = TextPoint.create({
      key: last.key,
      offset: last.text.length,
    });
    return [Decoration.create({
      start,
      end,
      mark: Mark.create({ type: placeholderMarkType, data: { config: match, block: node } }),
    }), ...next()];
  }

  function renderEditable(props, _, next) {
    return (
      <PlaceholderProvider {...props}>
        {next()}
      </PlaceholderProvider>
    );
  }

  function renderMark(props: RenderMarkProps, controller: Controller, next) {
    const { parent, node, mark, children } = props;
    if (mark.type !== placeholderMarkType) {
      return next();
    }
    return (
      <Placeholder
        key={`${parent.key}_placeholder`}
        controller={controller}
        node={node}
        mark={mark}
      >
        {children()}
      </Placeholder>
    );
  }

  return {
    decorateNode,
    renderEditable,
    renderMark,
  };
}
