import { last } from 'lodash-es';
import { OperationWithValue, PendingType } from '../../interfaces/controller';
import {
  ShouldAppend,
  PendingPattern,
  PendingPredicate,
  PendingGetter,
} from '../../interfaces/pending';
import Operation, {
  InsertTextOperation,
  OperationType,
  RemoveTextOperation,
  SetSelectionOperation,
} from '../../models/operation';
import { TextPoint } from '../../models/point';
import Path from '../../models/path';
import Text from '../../models/text';
import { getSelectionData } from './selection';

export interface TextInputContext {
  /** 输入作用的 text node key */
  key: string;
  /** 输入开始的 path */
  startPath: Path;
  /** 输入开始的偏移 */
  startOffset: number;
}

function isInsertTextOperation(
  op: Operation | null,
): op is InsertTextOperation {
  return op !== null && op.type === OperationType.InsertText;
}

function isRemoveTextOperation(
  op: Operation | null,
): op is RemoveTextOperation {
  return op !== null && op.type === OperationType.RemoveText;
}

function isInsertOrRemove(operation: Operation) {
  return (
    (isInsertTextOperation(operation) || isRemoveTextOperation(operation)) &&
    operation.text.length === 1
  );
}

function isSetSelectionOperation(
  op: Operation | null,
): op is SetSelectionOperation {
  return op !== null && op.type === OperationType.SetSelection;
}

function isContinuousOp(prevOp: Operation, operation: Operation) {
  // @ts-ignore
  if (!Path.isEqual(operation.path, prevOp.path)) {
    return false;
  }
  if (isInsertTextOperation(prevOp) && isInsertTextOperation(operation)) {
    return operation.offset === prevOp.offset + prevOp.text.length;
  } else if (
    isInsertTextOperation(prevOp) &&
    isRemoveTextOperation(operation)
  ) {
    return (
      operation.offset ===
      prevOp.offset + prevOp.text.length - operation.text.length
    );
  } else if (
    isRemoveTextOperation(prevOp) &&
    isInsertTextOperation(operation)
  ) {
    return operation.offset === prevOp.offset;
  } else if (
    isRemoveTextOperation(prevOp) &&
    isRemoveTextOperation(operation)
  ) {
    return operation.offset === prevOp.offset - operation.text.length;
  }
  return false;
}

function isContinuousInput(ops: OperationWithValue[]) {
  const operations = ops.filter(
    ({ operation }) => !operation || !isSetSelectionOperation(operation),
  );
  if (!operations.length) {
    return false;
  }
  const operationsWithoutComposing = operations.filter(({ key }) => key !== 'composing');
  return operationsWithoutComposing.every(({ operation }, index) => {
    if (!isInsertOrRemove(operation!)) {
      return false;
    }
    if (index === 0) {
      return true;
    }

    const { operation: prevOp } = operationsWithoutComposing[index - 1];
    return !prevOp || isContinuousOp(prevOp, operation!);
  });
}
/**
 * 是否为连续输入
 * @param params
 * @returns
 */
const isTextInput: PendingPredicate = (params) => {
  const { ops } = params;
  return isContinuousInput(ops);
};

export const shouldAppend: ShouldAppend = (prevOps, ops) => {
  // 找到最后一个非 set_selection op，做连续性比较
  const { operation: prevOp } = last(
    prevOps.filter(({ key, operation }) => key !== 'composing' && !isSetSelectionOperation(operation)),
  ) || {};
  const { operation } = ops.find(
    ({ key, operation }) => key !== 'composing' && !isSetSelectionOperation(operation),
  ) || {};

  return !prevOp || !operation || isContinuousOp(prevOp, operation);
};

const getPending: PendingGetter<TextInputContext> = ({ controller }) => ({
  type: PendingType.input,
  decorate: (ops) => {
    const { operation, value } = ops.find(
      ({ operation }) => !isSetSelectionOperation(operation),
    )!;
    if (operation === null) {
      // NOTE: operation is composing, need to get meta from controller.value
      const { selection, document } = controller.value;
      const { key, offset } = selection.focus as TextPoint;
      return {
        key,
        startOffset: offset,
        startPath: document.getPath(key)!,
      };
    }
    const { path, offset } = operation as InsertTextOperation;
    const text = value.document.getNodeByPath(path)! as Text;
    return {
      key: text.key,
      startPath: path,
      startOffset: offset,
    };
  },
  effect: (ops, ctx) => {
    ops.forEach(({ operation }) => {
      // send input op
      if (isSetSelectionOperation(operation)) {
        return;
      }
      controller.inputData$.next({
        key: ctx.key,
        startPath: ctx.startPath,
        startOffset: ctx.startOffset,
      });
    });
    controller.selectionData$.next(getSelectionData(controller));
  },
  shouldAppend,
});

const pattern: PendingPattern<TextInputContext> = [isTextInput, getPending];

export default pattern;
