import * as React from 'react';
import equal from 'fast-deep-equal';
import Controller from '../../../controller';
import Selection from '../../../models/selection';
import LocalCaret from './caret';
import { CaretAppearance } from '../types';

export { LocalCaret };

interface CaretProps {
  controller: Controller;
  selection: Selection;
  isRemote: boolean;
  isFocused: boolean;
  position: CaretAppearance;
  color?: string;
}

const isComponentPropsEqual = (prevProps: CaretProps, nextProps: CaretProps) => {
  return (
    prevProps.isFocused === nextProps.isFocused
    && prevProps.isRemote === nextProps.isRemote
    && prevProps.color === nextProps.color
    && prevProps.selection === nextProps.selection
    && equal(prevProps.position, nextProps.position)
  );
};

// eslint-disable-next-line prefer-arrow-callback
export default React.memo(function Caret(props: CaretProps) {
  const { controller, isRemote, isFocused, selection, color, position } = props;
  const { onBlur: showCaretOnBlur, onReadOnly: showCaretOnReadOnly } = controller.showCaret || {};

  if (selection.isExpanded) {
    return null;
  }

  if (
    // 大多数情况，focused && 编辑态
    (isFocused && !controller.readOnly) ||
    // blur 时，配置 showCaretOnBlur 时可以显示远程光标
    (!isFocused && isRemote && showCaretOnBlur) ||
    // 只读时，配置 showCaretOnReadOnly 时可以显示远程光标
    (controller.readOnly && isRemote && showCaretOnReadOnly)
  ) {
    return (
      <>
        {isRemote
          ? controller.run('renderRemoteCaret', { selection, position })
          : controller.run('renderCaret', { position, color: color! })
        }
      </>
    );
  }

  return null;
}, isComponentPropsEqual);
