import Controller from '../../controller/index';
import caretRangeFromTargetAndCoord from './caretRangeFromTargetAndCoord';

/**
 * 根据用户的 click 或 touch 事件获取仓颉的 Selection
 */
function findRangeByEvent(
  event: { x: number; y: number; target: EventTarget },
  controller: Controller,
) {
  const { target, x, y } = event;
  return caretRangeFromTargetAndCoord(target, x, y, controller);
}

export default findRangeByEvent;
