import type { MoSerializationPlugin } from "../../type"
import { createHTMLToJsonML } from "./createHTMLToJsonML";
import { createJsonMLToValue } from "./createJsonMLToValue";
import { createJsonMLToHTML } from "./createJsonToHtml";
import { createValueToJsonML } from "./createValueToJsonMl";
import { createValueToText } from "./createValueToText";

export const createSerializerPlugin = (plugins: MoSerializationPlugin[]): MoSerializationPlugin => {
  return {
    htmlToJsonML: createHTMLToJsonML(plugins.map(p => p.leaf?.htmlToJsonMLAttrs)),
    jsonMLToHTML: createJsonMLToHTML(plugins.map(p => p.leaf?.jsonMLAttrsToStyle)),
    jsonMLToValue: createJsonMLToValue(plugins.map(p => p.leaf?.jsonMLAttrsToMarks)),
    valueToJsonML: createValueToJsonML(plugins.map(p => p.leaf?.markToJsonMLAttrs)),
    valueToText: createValueToText(),
  }
}
