/* eslint-disable no-param-reassign */
/**
 * @packageDocumentation
 * @module Commands
 */
import { splitDescendantsByKey, splitNodeByKey } from './split';
import {
  moveToEndOfNode,
  select,
  moveToStart,
  moveToStartOfNode,
  moveBackward,
  moveToStartOfNextBlock,
} from './selection';
import Selection from '../models/selection';
import Controller from '../controller/index';
import Block, { BlockChild, BlockProperties } from '../models/block';
import Node from '../models/node';
import Path from '../models/path';
import Mark from '../models/mark';
import Document from '../models/document';
import Element from '../models/element';
import Inline, { InlineProperties } from '../models/inline';
import Text from '../models/text';
import { TextPoint, EdgePoint } from '../models/point';
import { deleteExpanded, deleteExpandedAtRange } from './utils';
import { moveNodeByKey } from './move';
import { removeNodeByKey } from './remove';
import listUtils from '../utils/listUtils';
import GraphemesBreaker from '../utils/grapheme/GraphemeBreaker';
import { insertBlockTpl, insertInlineTpl, insertTpl } from '../contexts/insert';
import { OperationType } from '../models/operation';

/**
 * 在指定位置，插入一个node
 * @key 插入目标节点
 * @index 插入后在目标节点的顺序
 */
export function insertNodeByKey(
  controller: Controller,
  key: string,
  index: number,
  node: Node,
) {
  const { value } = controller;
  const { document } = value;
  const path = document.assertPath(key);
  return controller.command(insertNodeByPath, path, index, node);
}

/**
 * 在指定位置，插入一个node
 * @path 插入目标节点
 * @index 插入后在目标节点的顺序
 */
export function insertNodeByPath(
  controller: Controller,
  path: Path,
  index: number,
  node: Node,
) {
  const insertPath = [...path, index];
  return controller.withOpContext(insertTpl(insertPath), () => {
    return controller.applyOperation({
      type: OperationType.InsertNode,
      path: insertPath,
      node,
    });
  });
}

function isBlockEmpty(controller: Controller, block: Block) {
  return (
    !controller.query('isVoid', block) &&
    controller.query('isEmpty', block) &&
    block.text === '' &&
    !block.findDescendant((n) => controller.query('isVoid', n))
  );
}

/**
 * 在当前选区插入一段文字，并附带marks。
 * 1. marks：会从参数里获取；如果参数没有提供，会尝试从当前selection获取；如果依然没有，就从当前的插入位置获取
 */
export function insertText(controller: Controller, text: string, ms?: Mark[]) {
  const { value } = controller;
  const { document, selection } = value;
  if (!selection) {
    return controller;
  }
  const marks =
    ms || selection.marks || document.getInsertMarksAtRange(selection);
  return controller.command(insertTextAtRange, selection, text, marks);
}

/**
 * 在 EdgePoint 的 before 或者 after 位置，插入一个空白的段落。
 */
export function insertEmptyBlock(controller: Controller, at: EdgePoint) {
  let { document } = controller.value;
  const block = document.getNode(at.key);
  const parent = document.getParent(at.key);
  const isAtBefore = at.edge === EdgePoint.BEFORE;
  if (!block || !parent || !block.isElement() || !block.isBlock()) {
    return controller;
  }
  const index = parent.nodes.indexOf(block);
  const newBlock = Block.create(Block.DEFAULT_TYPE);
  const insertIndex = isAtBefore ? index : index + 1;
  controller.command(insertNodeByKey, parent.key, insertIndex, newBlock);
  document = controller.value.document;
  const node = newBlock.getFirstNode();
  const anchor = TextPoint.create({ key: node.key, offset: 0 });
  const focus = anchor;
  return controller.command(select, Selection.create({ anchor, focus }));
}

/**
 * 在目标位置插入一段文字，并附带marks。
 * 1. 目标位置需要对应一个Text节点，否则会抛出异常
 * 2. marks：会从参数里获取；如果参数没有提供，会尝试从当前的插入位置获取
 */
export function insertTextAtRange(
  controller: Controller,
  r: Selection,
  text: string,
  marks?: Mark[],
) {
  if (r.isCollapsed && r.anchor.isEdgePoint()) {
    controller.command(insertEmptyBlock, r.anchor);
    const textNode = controller.value.focusText;
    if (!textNode) {
      return controller;
    }
    return controller.command(insertTextByKey, textNode.key, 0, text, marks);
  }
  const range = r.isExpanded ? deleteExpandedAtRange(controller, r) : r;
  const { value } = controller;
  const { document } = value;
  const { start } = range.convertToTextPoints(document);
  const parent = document.getParent(start.key);
  if (controller.query('isVoid', parent)) {
    return controller;
  }

  return controller.command(
    insertTextByKey,
    start.key,
    start.offset,
    text,
    marks,
  );
}

/**
 * 在目标位置插入一段文字，并附带marks。
 * 1. 目标位置需要对应一个Text节点，否则会抛出异常
 * 2. marks：会从参数里获取；如果参数没有提供，会尝试从当前的插入位置获取
 */
export function insertTextByKey(
  controller: Controller,
  key: string,
  offset: number,
  text: string,
  marks?: Mark[],
) {
  const { value } = controller;
  const { document } = value;
  const path = document.assertPath(key);
  return controller.command(insertTextByPath, path, offset, text, marks);
}

/**
 * 在目标位置插入一段文字，并附带marks。
 * 1. 目标位置需要对应一个Text节点，否则会抛出异常
 * 2. marks：会从参数里获取；如果参数没有提供，会尝试从当前的插入位置获取
 */
export function insertTextByPath(
  controller: Controller,
  path: Path,
  offset: number,
  text: string,
  ms?: Mark[],
) {
  const { value } = controller;
  const { document, decorations } = value;
  const node = document.assertNodeByPath(path);
  if (!node.isText()) {
    throw new Error(
      `Commands.insertText, target([${path.join(',')}] type: ${
        (node as Element).type
      }) is not a Text node`,
    );
  }
  const marks = ms || node.getMarksAtIndex(offset);

  let updated = false;
  const decs = decorations.filter((dec) => {
    const { start, end, mark } = dec;
    const isAtomic = controller.query('isAtomic', mark);
    if (!isAtomic) {
      return true;
    }

    const startPath = document.getPath(start.key);
    if (!startPath) {
      updated = true;
      return false;
    }
    if (!Path.isEqual(startPath, path)) {
      return true;
    }

    const endPath = document.getPath(end.key);
    if (!endPath) {
      updated = true;
      return false;
    }
    if (
      start.offset < offset &&
      (!Path.isEqual(endPath, path) || end.offset > offset)
    ) {
      updated = true;
      return false;
    }

    return true;
  });
  if (updated) {
    controller.setValue(value.set('decorations', decs), { normalize: false });
  }

  return controller.applyOperation({
    type: OperationType.InsertText,
    path,
    offset,
    text: text || '',
    marks: marks.map(Mark.create),
  });
}

/**
 * 在指定的选区的start位置，插入一个block。
 * 1. 如果start节点在开始/结束位置，或者start节点是个空block，则直接插入；否则先split，后插入
 */
export function insertBlockAtRange(
  controller: Controller,
  range: Selection,
  block: Block,
) {
  const newRange = deleteExpandedAtRange(controller, range);
  const { value } = controller;
  const { document } = value;

  const { start } = newRange.convertToTextPoints(document);
  let { key: startKey, offset: startOffset } = start;
  const startBlock = document.getClosestBlock(startKey)!;
  const startInline = document.getClosestInline(startKey);
  const parent = document.getParent(startBlock.key)!;
  const index = parent.nodes.indexOf(startBlock);

  controller.withOpContext(insertBlockTpl(), () => {
    if (newRange.anchor.isEdgePoint()) {
      // If we insert block at edge point.
      controller.command(
        insertNodeByKey,
        parent.key,
        index + (newRange.anchor.edge === 'before' ? 0 : 1),
        block,
      );
    } else if (!startInline && startBlock.text === '') {
      // if we insert block at an empty block
      controller.command(insertNodeByKey, parent.key, index + 1, block);
    } else if (start.isAtStartOfNode(startBlock)) {
      controller.command(insertNodeByKey, parent.key, index, block);
    } else if (start.isAtEndOfNode(startBlock)) {
      controller.command(insertNodeByKey, parent.key, index + 1, block);
    } else {
      // If we're in the middle of a block, we should consider split it first

      // If we're in a void inline, we should move cursor to the sibliting text of the inline before spliting
      if (startInline && controller.query('isVoid', startInline)) {
        const atEnd = start.isAtEndOfNode(startInline);
        const siblingText = atEnd
          ? document.getNextText(startKey)
          : document.getPreviousText(startKey);

        const splitRange = atEnd
          ? newRange.moveToStartOfNode(siblingText!, controller)
          : newRange.moveToEndOfNode(siblingText!, controller);

        const { anchor } = splitRange.convertToTextPoints(
          controller.value.document,
        );
        startKey = anchor.key;
        startOffset = anchor.offset;
      }

      controller.withoutNormalizing(() => {
        controller.command(
          splitDescendantsByKey,
          startBlock.key,
          startKey,
          startOffset,
        );
        controller.command(insertNodeByKey, parent.key, index + 1, block);
      });
    }
  });

  return controller;
}

/**
 * 在当前选区位置，插入一个block。
 * 1. 如果当前选区是expand，会先执行删除选区操作。
 * 2. 如果start节点在开始/结束位置，或者start节点是个空block，则直接插入；否则先split，后插入
 * @blockProps block或者block参数，会传递给Block.create来创建一个block
 */
export function insertBlock(
  controller: Controller,
  blockProps: BlockProperties | Block | string,
) {
  const { selection } = controller.value;
  const block = Block.create(blockProps);
  controller.command(
    insertBlockAtRange,
    deleteExpandedAtRange(controller, selection),
    block,
  );

  // move to end of inserted block
  const node = controller.value.document.getNode(block.key);
  if (node) {
    controller.command(moveToEndOfNode, node);
  }

  return controller;
}

/**
 * 在当前选区位置，插入一个inline。
 * 1. 如果当前选区是expand，会先执行删除选区操作。
 */
export function insertInlineAtRange(
  controller: Controller,
  range: Selection,
  inline: Inline,
) {
  controller.withOpContext(insertInlineTpl(), () => {
    controller.withoutNormalizing(() => {
      const point = deleteExpandedAtRange(controller, range).anchor;
      if (range.isCollapsed && point.isEdgePoint()) {
        controller.command(insertEmptyBlock, point);
      }

      const { document, selection } = controller.value;
      const { start } = selection.convertToTextPoints(document);
      const parent = document.getParent(start.key)!;
      const startText = document.assertNode(start.key);
      const index = parent.nodes.indexOf(startText);

      if (controller.query('isVoid', parent)) return;

      controller.command(splitNodeByKey, start.key, start.offset);
      controller.command(insertNodeByKey, parent.key, index + 1, inline);
    });
  });
  return controller;
}

/**
 * 在当前选区位置，插入一个inline。
 * 1. 如果当前选区是expand，会先执行删除选区操作。
 */
export function insertInline(
  controller: Controller,
  inlineProps: InlineProperties | Inline | string,
) {
  const { selection } = controller.value;
  const inline = Inline.create(inlineProps);

  controller.command(insertInlineAtRange, selection, inline);

  // move to end of inserted block
  const node = controller.value.document.getNode(inline.key);
  if (node) {
    controller.command(moveToEndOfNode, node);
  }

  return controller;
}

// If the fragment is wrapped by/nested in blocks, and the nesting pattern
// already exists at the range, we meld the fragment contents into the
// document’s existing nesting pattern as much as possible

// Given a `fragment`, `document`, and `startKey`, it returns a Node from the
// `fragment` to be inserted into the document. By default, the node returned
// will be the `fragment`, but if a common nesting pattern is detected, a
// sub-node of the fragment will be returned.

// The detection algorithm is as follows:
// 1. Ensure the fragment has a single child node, call it `fragmentInner`
// 2. Find the furthest document ancestor of the `startKey` matching
//    `fragmentInner.type`. Call this `documentInner`
// 3. Drill into both `documentInner` and `fragmentInner` as long as each has
//    a single child node and their types match. The stopping point for the
//    fragment is the sub-node to insert into the document.
const findInsertionNode = (
  fragment: Document,
  document: Document,
  startKey: string,
) => {
  const hasSingleNode = (object: Node): boolean => {
    if (!object || Text.isText(object)) {
      return false;
    }
    return (object as Element).nodes.length === 1;
  };

  const firstNode = (object: Element) => object && (object.nodes[0] as Element);
  let node: Element = fragment;

  if (hasSingleNode(fragment)) {
    let fragmentInner = firstNode(fragment);

    let documentInner = document.getFurthsest(startKey, (documentNode) => {
      if (document instanceof Element) {
        return (documentNode as Element).type === fragmentInner.type;
      }
      return false;
    }) as Element;

    if (documentInner === document.getParent(startKey)) {
      node = fragmentInner;
    }

    while (
      hasSingleNode(fragmentInner) &&
      hasSingleNode(documentInner as Node)
    ) {
      fragmentInner = firstNode(fragmentInner);
      documentInner = firstNode(documentInner);
      if (fragmentInner.type === documentInner.type) {
        node = fragmentInner;
      } else {
        break;
      }
    }
  }
  return node;
};

/**
 * 在目标位置插入一个fragment
 * 1. 内部遍历fragment的每一个node，然后调用Commands.insertNode
 * 2. 如果针对复制粘贴等上层场景，需要考虑key重新生成等逻辑，请使用Commands.insertFragment
 */
export function insertFragmentByKey(
  controller: Controller,
  key: string,
  index: number,
  fragment: Document,
) {
  const { value } = controller;
  const { document } = value;
  const path = document.assertPath(key);
  return controller.command(insertFragmentByPath, path, index, fragment);
}

/**
 * 在目标位置插入一个fragment
 * 1. 内部遍历fragment的每一个node，然后调用Commands.insertNode
 * 2. 如果针对复制粘贴等上层场景，需要考虑key重新生成等逻辑，请使用Commands.insertFragment
 */
export function insertFragmentByPath(
  controller: Controller,
  path: Path,
  index: number,
  fragment: Document,
) {
  fragment.nodes.forEach((node, i) => {
    controller.command(insertNodeByPath, path, index + i, node);
  });
  return controller;
}

/**
 * 在当前插入一个fragment
 * 1. 内部会处理插入点、插入后的光标行为，以符合用户直觉
 */
export function insertFragment(controller: Controller, fragment: Document) {
  if (!fragment.nodes.length) {
    return controller;
  }

  // pre-step, delete the selection is it's expanded
  deleteExpanded(controller);

  // save the values before insert
  let { value } = controller;
  let { document } = value;
  let { selection } = value;
  if (!selection) {
    return controller;
  }

  // 获取插入位置祖先 Block 元素, 非容器节点插入祖先为 Document、Block, 容器节点插入祖先为 Document、Block、Block...
  const startAncestors = document.getAncestors(selection.anchor.key);
  const lastText = fragment.getLastText();
  if (!lastText) {
    return controller;
  }

  const { start, end } = selection.sort(document);
  const { startText, endText } = value;
  const keys = document.getTexts().map((text) => text.key);

  // perform insert
  controller.command(insertFragmentAtRange, selection, fragment);

  // get the new values after insert
  ({ value } = controller);
  ({ document } = value);
  selection = value.selection;

  const updatedTexts = document.getTexts();
  const newTexts = updatedTexts.filter((n) => !keys.includes(n.key));
  const lastNewText = newTexts[newTexts.length - 1];

  // selection - take the selection back if it's removed (e.g. the last one block is deleted)
  if (!selection && updatedTexts.length) {
    const firstText = updatedTexts[0];
    const point = TextPoint.create({ key: firstText.key, offset: 0 });
    controller.command(
      select,
      Selection.create({ anchor: point, focus: point }),
    );
  }

  /**
   * 插入 fragment 后移动光标的位置：
   * 1. 首先判断本次插入操作是否有产生新的 text，如果没有产生新 text 节点，则为纯文字的插入，光标会自动校正，不需要处理
   * 2. 若产生了新的 text 节点，则先移动至 lastText 的 end 点
   * 3. 如果插入的过程中产生了对一段文字的后半段做 split 操作，则需要做偏移校正，校正的距离为插入前光标距离末尾的偏移量
   */
  if (lastNewText) {
    controller.command(moveToEndOfNode, lastNewText);
    const blockParent = document.getClosestBlock(lastNewText.key);
    const nextText = blockParent?.getNextText(lastNewText.key);
    // lastText 节点的 nextText 节点与插入 fragment 前的 endText 相等
    // 这种状况是由于在段落的行首插入一个 inline 元素不会触发 split 操作，这种情况不需要校正偏移量，直接移动至 next 的首位
    if (nextText && nextText.key === end.key) {
      controller.command(moveToStartOfNode, nextText);
    } else if (endText && end.isTextPoint()) {
      // 如果 nextText 节点不相等，则说明出现了文字截断，此时要做偏移位校正，校正时需要先从光标点位和字符串中取分割的文字串，然后再计算字数
      // 注意这里有个小问题：Point 的 offset 表示的是字符的偏移量，但是 moveBackward 方法是做的字偏移，所以要计算 Unicode 字
      // 关于 Unicode 的一个小问题：存在一种组合 Unicode 会导致字数计算有偏差，例如【👩‍ 🦱】，如果将空格删除，则两个 emoji 会合并，这种组合性的 emoji 在
      // 字数计算的时候会被拆分出来成 2 个字
      const subText = endText.text.substring(end.offset);
      const offsetToEnd = GraphemesBreaker.countBreaks(subText);

      const firstChildHasBlockChildren = fragment.nodes[0].hasBlockChildren();
      const startBlock = startText && document.getClosestBlock(startText.key) as Block;
      const isAtStartOfUnEmptyNode = startBlock && start.isAtStartOfNode(startBlock);
      if (firstChildHasBlockChildren && isAtStartOfUnEmptyNode) {
        // 不做光标移动
      } else {
        controller.command(moveBackward, offsetToEnd);
      }
      const subRoot = document.getAncestors(lastNewText.key)[1];

      // 插入 Void Block 节点时，光标移到 Block 节点外部
      if (
        // 根据祖先元素长度判断是否在容器节点内插入内容, 如果根节点下插入容器节点，插入后光标最后移出容器
        // 如果在容器内插入普通节点，光标保持在容器内部
        startAncestors.length <= 2 &&
        controller.query('isEdgeSelectable', subRoot) &&
        subRoot.getLastText() === lastNewText
      ) {
        controller.command(moveToStartOfNextBlock);
      }
    }
    if (controller.value.selection.isExpanded) {
      controller.command(moveToStart);
    }
  }
  return controller;
}

/**
 * 在指定的选区插入一个fragment
 * 1. 如果选区是expand，会先删除选区
 */
export function insertFragmentAtRange(
  controller: Controller,
  range: Selection,
  fragment: Document,
) {
  if (!fragment.nodes.length) {
    return controller;
  }
  return controller.withoutNormalizing(() => {
    range = deleteExpandedAtRange(controller, range);
    if (range.isCollapsed && range.anchor.isEdgePoint()) {
      controller.command(insertEmptyBlock, range.anchor);
      range = controller.value.selection;
    }

    fragment = fragment.mapDescendants((child) => child.regenerateKey());

    // NOTE: 这里调用 command key 试插件 onCommand 时间触发，主要用于图片的复制转存逻辑
    // TODO: 在 model 增加一个可以不计入 asl 的 attributes，用于存储 key
    controller.command('insertFragmentWithRealKeyAtRange', range, fragment);
  });
}

/**
 * 在指定的选区插入一个fragment
 * 1. fragment必须包含有效的无冲突的key
 */
export function insertFragmentWithRealKeyAtRange(
  controller: Controller,
  range: Selection,
  fragment: Document,
) {
  const { value } = controller;
  let { document } = value;

  // checking the start point
  const { start, end } = range.convertToTextPoints(document);
  let startText = document.getNode(start.key) as Node;
  let startBlock = document.getClosestBlock(startText.key) as Block;
  let startChild = startBlock.getFurthestAncestor(startText.key) as Element;
  const isAtStart = start.isAtStartOfNode(startBlock);
  const isAtEnd = end.isAtEndOfNode(startBlock);
  let parent = document.getParent(startBlock.key) as Element;
  const index = parent.nodes.indexOf(startBlock);

  const blocks = fragment.getBlocks();
  if (blocks.length === 0) {
    return controller;
  }

  const firstChild = fragment.nodes[0] as Block;
  const lastChild = fragment.nodes[fragment.nodes.length - 1] as Block;
  const firstBlock = blocks[0];
  const lastBlock = blocks[blocks.length - 1];

  const insertionNode = findInsertionNode(fragment, document, startBlock.key);

  // 设置用户数据
  controller.userData.mergeUserData(fragment);

  // If the new fragment only contains a void block, use `insertBlockAtRange`
  if (
    firstBlock === lastBlock &&
    controller.query('isVoid', firstBlock) &&
    !controller.query('isContainer', insertionNode)
  ) {
    controller.command(insertBlockAtRange, range, firstBlock);
    return controller;
  }

  // If the new fragment starts or ends with a single nested block,
  // e.g. a table, use `insertBlockAtRange`
  const firstChildHasBlockChildren = firstChild.hasBlockChildren();
  const lastChildHasBlockChildren = lastChild.hasBlockChildren();
  if (
    insertionNode === fragment &&
    (firstChildHasBlockChildren || lastChildHasBlockChildren)
  ) {
    // 如果插入的第一个节点是一个容器节点：
    //
    // Fragment:
    // ```tsx
    // <document>
    //   <block>
    //     <block>Hello</block>
    //     <block>World</block>
    //   </block>
    //   <block>Cangjie</block>
    // </document>
    // ```
    //
    // 插入到光标位置
    // ```tsx
    // <document>
    //   <block>Intro<cursor />123</block>
    //   <block>Command</block>
    // </document>
    // ```
    //
    // 期望是
    // ```tsx
    // <document>
    //   <block>Intro</block>
    //   <block>
    //     <block>Hello</block>
    //     <block>World</block>
    //   </block>
    //   <block>Cangjie</block>
    //   <block>123</block>
    //   <block>Command</block>
    // </document>
    // ```
    //
    if (firstChildHasBlockChildren) {
      // 非空节点插入内容，不需要进行 reverse
      const isAtStartOfUnEmptyNode = start.isAtStartOfNode(startBlock);
      const nodes = isAtStartOfUnEmptyNode ? fragment.nodes : [...fragment.nodes].reverse();
      nodes.forEach((node) => {
        controller.command(insertBlockAtRange, range, node as Block);
      });
      return controller;
    }

    // 如果插入的最后一个节点是一个容器节点：
    //
    // Fragment:
    // ```tsx
    // <document>
    //   <block>Cangjie</block>
    //   <block>
    //     <block>Hello</block>
    //     <block>World</block>
    //   </block>
    // </document>
    // ```
    //
    // 插入到光标位置
    // ```tsx
    // <document>
    //   <block>Intro<cursor />123</block>
    // </document>
    // ```
    //
    // 期望是
    // ```tsx
    // <document>
    //   <block>IntroCangjie</block>
    //   <block>
    //     <block>Hello</block>
    //     <block>World</block>
    //   </block>
    //   <block>123</block>
    // </document>
    // ```
    //
    if (lastChildHasBlockChildren) {
      // 插入最后一个节点，再在原位置插入剩余节点
      controller.command(insertBlockAtRange, range, lastChild);
      const rest = fragment.removeNode([fragment.nodes.length - 1]);
      if (rest.nodes.length) {
        controller.command(insertFragmentWithRealKeyAtRange, range, rest);
      }
    }
    return controller;
  }

  // If the first and last block aren't the same, we need to insert all of the
  // nodes after the insertion node's first block at the index.
  if (firstBlock !== lastBlock) {
    const lonelyParent = insertionNode.getFurthsest(
      firstBlock.key,
      (p) => (p as Block).nodes.length === 1,
    );
    const lonelyChild = lonelyParent || firstBlock;

    const startIndex = parent.nodes.indexOf(startBlock);
    const excludingLonelyChild = insertionNode.removeNode(
      insertionNode.assertPath(lonelyChild.key),
    );

    excludingLonelyChild.nodes.forEach((node, i) => {
      const newIndex = startIndex + i + 1;
      controller.command(insertNodeByKey, parent.key, newIndex, node);
    });
  }

  // Check if we need to split the node.
  if (start.offset !== 0) {
    controller.command(
      splitDescendantsByKey,
      startChild.key,
      start.key,
      start.offset,
    );
  }

  ({ document } = controller.value);
  startText = document.getNode(start.key) as Node;
  startBlock = document.getClosestBlock(start.key) as Block;
  parent = document.getParent(startBlock.key) as Element;
  startChild = startBlock.getFurthestAncestor(startText.key) as Element;

  if (firstBlock !== lastBlock) {
    const nextChild = isAtStart
      ? startChild
      : startBlock.getNextSibling(startChild.key);
    const nextNodes = nextChild
      ? listUtils.skipUntil(startBlock.nodes, (n) => n.key === nextChild.key)
      : [];
    const block = document.assertElement(lastBlock.key);

    if (!controller.query('isVoid', block)) {
      // 如果插入的最后一个节点是非 void block，则 split 后的 nodes 可以追加到 block 末尾
      // Fragment:
      // ```ts
      // <document>
      //   <block>Hello</block>
      //   <block>World</block>
      // </document>
      // ```
      //
      // 在如下光标位置插入:
      // ```ts
      // <document>
      //   <block>123<cursor />45</block>
      // </document>
      // ```
      //
      // 期望的插入结果是:
      // ```ts
      // <document>
      //   <block>123Hello</block>
      //   <block>World45</block>
      // </document>
      // ```
      const moveTo = block.nodes.length;
      nextNodes.forEach((node, i) => {
        const newIndex = moveTo + i;
        controller.command(moveNodeByKey, node.key, block.key, newIndex);
      });
    } else {
      // 如果插入的最后一个节点是 void block，则 split 后的 nodes 可以插入到 block 之后
      // Fragment:
      // ```ts
      // <document>
      //   <block>Hello</block>
      //   <void-block />
      // </document>
      // ```
      //
      // 在如下光标位置插入:
      // ```ts
      //   <document>
      //     <block>123<cursor />45</block>
      //   </document>
      // ```
      //
      // 期望的插入结果为:
      // ```ts
      //   <document>
      //     <block>123Hello</block>
      //     <void-block />
      //     <block>45</block>
      //   </document>
      // ```
      const insertAt =
        document.assertElement(parent.key).nodes.indexOf(block) + 1;
      nextNodes.forEach((node) => {
        controller.command(removeNodeByKey, node.key);
      });
      // 新插入的节点继承原 block 的属性
      const restBlock = startBlock
        .set('nodes', nextNodes)
        .regenerateKey() as Block;
      if (!isAtEnd) {
        controller.command(insertNodeByKey, parent.key, insertAt, restBlock);
      }
    }
  }

  if (isBlockEmpty(controller, startBlock)) {
    // 如果 start block 为空，则直接替换 start block 为待插入的 first block
    // 是否为空的断言需要插件实现 `isEmpty`，即由插件决定某个元素是否可以被判定为空，且被替换
    // Fragment:
    // ```ts
    //   <document>
    //     <block>Hello</block>
    //     <block>World</block>
    //   </document>
    // ```
    //
    // 在如下光标位置插入:
    // ```ts
    //   <document>
    //     <block><cursor /></block>
    //   </document>
    // ```
    //
    // 期望的插入结果是:
    // ```ts
    // <document>
    //   <block>Hello</block>
    //   <block>World</block>
    // </document>
    // ```
    controller
      .command(insertNodeByKey, parent.key, index, firstBlock)
      .command(removeNodeByKey, startBlock.key);
  } else {
    // 否则，保留 start block，并在 start block 的分裂位置插入 first block 的子孙
    // 1. first block is non-void
    // Fragment:
    // ```ts
    // <document>
    //   <block>Hello</block>
    //   <block>World</block>
    // </document>
    // ```
    //
    // 在如下光标位置插入:
    // ```ts
    // <document>
    //   <block>Cangjie<cursor /></block>
    // </document>
    // ```
    //
    // 期望的插入结果是:
    // ```ts
    // <document>
    //   <block>CangjieHello</block>
    //   <block>World</block>
    // </document>
    // ```
    //
    // 2. first block is void
    // Fragment:
    // ```ts
    // <document>
    //   <void-block />
    //   <block>World</block>
    // </document>
    // ```
    //
    // 在如下光标位置插入:
    // ```ts
    // <document>
    //   <block>Cangjie<cursor /></block>
    // </document>
    // ```
    //
    // 期望的插入结果是:
    // ```ts
    // <document>
    //   <block>Cangjie</block>
    //   <void-block />
    //   <block>World</block>
    // </document>
    // ```
    // 否则，保留 start block，并在 start block 的分裂位置插入 first block 的子孙
    const inlineChild = startBlock.getFurthestAncestor(
      startText.key,
    ) as BlockChild;
    const inlineIndex = startBlock.nodes.indexOf(inlineChild);
    if (controller.query('isVoid', firstBlock)) {
      const insertAt = parent.nodes.indexOf(startBlock) + 1;
      controller.command(insertNodeByKey, parent.key, insertAt, firstBlock);
    } else {
      firstBlock.nodes.forEach((inline, i) => {
        const o = start.offset === 0 ? 0 : 1;
        const newIndex = inlineIndex + i + o;
        controller.command(insertNodeByKey, startBlock.key, newIndex, inline);
      });
    }
  }
  return controller;
}
