export interface Subscription {
  unsubscribe: () => void;
}

export type Observer<T> = (data: T) => void;

interface Link<T> {
  next: Link<T> | null;
  prev: Link<T> | null;
  element: T;
}

export class Observable<T> {
  private linkHead: Link<Observer<T>> | null = null;

  public subscribe(observer: Observer<T>): Subscription {
    const link: Link<Observer<T>> = {
      next: this.linkHead,
      prev: null,
      element: observer,
    };
    if (this.linkHead) {
      this.linkHead.prev = link;
    }
    this.linkHead = link;
    return {
      unsubscribe: () => {
        if (link.next) {
          link.next.prev = link.prev
        }
        if (link.prev) {
          link.prev.next = link.next
        } else {
          this.linkHead = link.next
        }
      }
    }
  }

  protected onData(data: T) {
    let link = this.linkHead;
    while (link) {
      link.element(data);
      link = link.next;
    }
  }
}

export class Subject<T> extends Observable<T> {
  constructor() {
    super();
  }

  next(data: T) {
    this.onData(data);
  }
}


export class ReplaySubject<T> extends Observable<T> {
  private replays: T[] = []; 
  private readonly max: number; 

  constructor(max = 1) {
    super();
    this.max = max;
  }

  public subscribe(observer: Observer<T>): Subscription {
    const sub = super.subscribe(observer);
    this.replays.forEach(data => {
      observer(data);
    });
    return sub;
  }

  next(data: T) {
    this.onData(data);
    if (this.replays.length === this.max) {
      this.replays.shift();
    }
    this.replays.push(data);
  }
}
