/**
 * @packageDocumentation
 * @ignore
 */
 import RenderNode from '../interfaces/renderNode';

 export default function createRenderNodePlugin(
   renders: Record<string, RenderNode[]> = {},
 ) {
  // PERF: drop prototype
  const renderNodes: Record<string, RenderNode[]> = Object.assign(Object.create(null), renders);

  // @ts-ignore
  const renderNode: RenderNode = (props, controller, n) => {
    // @ts-ignore
    const renders = renderNodes[props.node?.type];
    if (!renders) {
      return n();
    }

    let prevArgs = [props, controller, n];
    let i = 0;

    function next(...as) {
      const currentArgs =
        as.length === 0 ? prevArgs : [...as, ...prevArgs.slice(as.length)];
      prevArgs = currentArgs;

      const fn = renders[i];
      if (!fn) {
        return null;
      }
      i += 1;
      return fn(currentArgs[0], controller, next);
    }

    return next();
  }

  return {
    models: [],
    renderNode,
  };
}
