import Controller from '../../controller';
import { ConvertToPluginCommands } from '../../interfaces/command';
import Node from '../../models/node';
import Element from '../../models/element';

export function setNodeUUID(
  controller: Controller,
  node: Element,
  uuid: string,
  uuidKey: string,
) {
  return controller.withoutSaving(() => {
    controller.command('setNodeByKey', node.key, {
      type: node.type,
      data: { ...node.data, [uuidKey]: uuid },
    });
  });
}

export function createGenerateUUID(gen: () => string, uuidKey: string) {
  return function generateUUID(controller: Controller, node: Node) {
    if (node.isElement() && !node.data[uuidKey]) {
      const uuid = gen();
      controller.command(setNodeUUID, node, uuid, uuidKey);
    }
    return controller;
  };
}

declare module '@ali/4ever-cangjie' {
  /** type combination for commands、queries、actions */
  interface PluginCommands
    extends ConvertToPluginCommands<{
      generateUUID: ReturnType<typeof createGenerateUUID>;
    }> {}
}
