// eslint-disable-next-line import/no-extraneous-dependencies
/**
 * @packageDocumentation
 * @ignore
 */
import * as React from 'react';
import Selection from '../../../models/selection';
import { CURSOR_WIDTH } from '../../../constants';
import Controller from '../../../controller';
import { PendingType } from '../../../interfaces/controller';

type CaretMode = 'placeholder' | 'caret';

const CURSOR_BLINK_ANIMATION_TIME = 530;

const CURSOR_STYLE: React.CSSProperties = {
  position: 'absolute',
  top: 0,
  left: 0,
  width: CURSOR_WIDTH,
  opacity: 1,
  zIndex: 1,
  pointerEvents: 'none',
  willChange: 'transform,height,opacity',
};

function useBlinkAnimation(
  cursorRef: React.RefObject<HTMLSpanElement>,
  pointStr: string,
  controller: Controller,
  // Placeholder Mode: disable the blink animation and add an opacity style
  mode?: CaretMode,
) {
  const isVisible = React.useRef(true);
  const timeout = React.useRef<number | null>(null);
  const prevPoint = React.useRef<string | null>(null);

  React.useLayoutEffect(() => {
    const caretNode = cursorRef.current;
    if (!caretNode || !pointStr) {
      return undefined;
    }

    if (mode === 'placeholder') {
      caretNode.style.opacity = '0.5';
      return undefined;
    }

    function toggleBackgroundColor() {
      if (caretNode) {
        // 输入过程中不闪烁
        if (controller.isPending && controller.pendingType === PendingType.input) {
          caretNode.style.opacity = '1';
        } else {
          const visible = prevPoint.current !== pointStr || isVisible.current;
          caretNode.style.opacity = visible ? '1' : '0';
          isVisible.current = !visible;
        }
      }
      timeout.current = window.setTimeout(
        toggleBackgroundColor,
        CURSOR_BLINK_ANIMATION_TIME,
      );
    }
    toggleBackgroundColor();
    prevPoint.current = pointStr;

    return () => {
      if (timeout.current) {
        clearTimeout(timeout.current);
      }
    };
  }, [pointStr, mode, controller]);
}

interface LocalCaretProps {
  controller: Controller;
  selection: Selection;
  color: string;
  position: {
    top: number;
    left: number;
    height: number;
  };
  style?: React.CSSProperties;
  mode?: CaretMode;
}

export default function LocalCaret(props: LocalCaretProps) {
  const ref = React.useRef<HTMLSpanElement>(null);

  const { position, color, style, mode, controller, selection } = props;

  /* https://aone.alibaba-inc.com/v2/bug/35876239# 《【0730闪会BugBash】左侧 edge selection 定位到高亮条外面去了》 */
  // 外部无法通过样式直接影响选区，需要通过 query 指定自定义偏移量
  const { left = 0, top = 0 } = React.useMemo(() => {
    if (selection.isExpanded) {
      return {};
    }
    return controller.query('getCaretOffset', selection) || {};
  }, [selection, controller]);

  const point = selection.anchor;
  const pointStr = selection.isCollapsed ? point.viewKey : '';
  const where = point.isTextPoint()
    ? point.offset + controller.value.composing.length
    : point.edge;
  const cursorStyle: React.CSSProperties = {
    ...CURSOR_STYLE,
    backgroundColor: color,
    height: position.height,
    ...(style || {}),
    transform: `translateX(${position.left + left}px) translateY(${position.top + top}px)`,
  };

  useBlinkAnimation(ref, pointStr, controller, mode);

  return (
    <span
      ref={ref}
      data-testid="cangjie-caret"
      data-cangjie-caret="true"
      data-cangjie-point={pointStr}
      key={pointStr}
      data-where={where}
      style={cursorStyle}
    />
  );
}
