/**
 * @packageDocumentation
 * @ignore
 */
import Controller from '../../controller/index';
import Selection from '../../models/selection';
import findDOMRange from './findDOMRange';
import findDOMNode, { findDOMNodeSafely } from './findDOMNode';
import { EdgePoint } from '../../models/point';
import { findCaretPosition, PointLocation } from './findCaretPosition';
import { CURSOR_WIDTH } from '../../constants';
import { IS_MOBILE } from '../environment';

// 避免 smooth 滚动的滚动距离阈值
// 参考 mac 下最大字号 72pt * 1.7 行高 = 163px 设置的 magic number
const MAX_LINE_HEIGHT = 150;

// 往上滚动时，底部预留额外距离。参考竞品 (G Docs)，移动端间距会大一些 (一行 + 4px)
const EXTRA_BOTTOM_OFFSET = IS_MOBILE ? 66 : 12;

export function findScrollableContainer(el, window) {
  if (!el) {
    return null;
  }

  let scroller;
  let parent = el.parentNode;
  while (!scroller) {
    if (!parent.parentNode) break;

    const style = window.getComputedStyle(parent);
    const { overflowY } = style;

    if (['auto', 'overlay', 'scroll'].includes(overflowY)) {
      scroller = parent;
      break;
    }

    parent = parent.parentNode;
  }

  // COMPAT: Because Chrome does not allow doucment.body.scrollTop, we're
  // assuming that window.scrollTo() should be used if the scrollable element
  // turns out to be document.body or document.documentElement. This will work
  // unless body is intentionally set to scrollable by restricting its height
  // (e.g. height: 100vh).
  if (!scroller) {
    return window.document.body;
  }

  return scroller;
}

export function getScrollableContainerInfo(scroller: Window | HTMLElement) {
  const isWindow =
    scroller === window ||
    scroller === window.document.body ||
    scroller === window.document.documentElement;

  // @ts-ignore
  const { innerHeight, visualViewport, _visualHeight } = window;

  let width;
  let height;
  let yOffset;
  let xOffset;
  let scrollerTop = 0;
  let scrollerLeft = 0;
  let scrollerBordersY = 0;
  let scrollerBordersX = 0;
  let scrollerPaddingTop = 0;
  let scrollerPaddingBottom = 0;
  let scrollerPaddingLeft = 0;
  let scrollerPaddingRight = 0;

  // iOS 13/14 Safari浏览器，去除键盘高度后的视口高度
  let viewportHeight: number = visualViewport ? visualViewport.height : innerHeight;
  // 底部工具条弹出时，下面区域视为 viewport 之外
  if (typeof _visualHeight === 'number' && _visualHeight > 0) {
    viewportHeight = _visualHeight;
  }

  if (isWindow) {
    // @ts-ignore
    const { innerWidth, pageYOffset, pageXOffset } = window;
    width = innerWidth;
    height = innerHeight;
    yOffset = pageYOffset;
    xOffset = pageXOffset;
  } else {
    const { offsetWidth, offsetHeight, scrollTop, scrollLeft } = scroller as HTMLElement;
    const {
      borderTopWidth,
      borderBottomWidth,
      borderLeftWidth,
      borderRightWidth,
      paddingTop,
      paddingBottom,
      paddingLeft,
      paddingRight,
    } = window.getComputedStyle(scroller as HTMLElement);

    const scrollerRect = (scroller as HTMLElement).getBoundingClientRect();
    width = offsetWidth;
    height = offsetHeight;
    scrollerTop = scrollerRect.top + parseInt(borderTopWidth, 10);
    scrollerLeft = scrollerRect.left + parseInt(borderLeftWidth, 10);

    scrollerBordersY =
      parseInt(borderTopWidth, 10) + parseInt(borderBottomWidth, 10);

    scrollerBordersX =
      parseInt(borderLeftWidth, 10) + parseInt(borderRightWidth, 10);

    scrollerPaddingTop = parseInt(paddingTop, 10);
    scrollerPaddingBottom = parseInt(paddingBottom, 10);
    scrollerPaddingLeft = parseInt(paddingLeft, 10);
    scrollerPaddingRight = parseInt(paddingRight, 10);
    yOffset = scrollTop;
    xOffset = scrollLeft;
  }

  return {
    isWindow,
    width,
    height,
    viewportHeight,
    scrollerTop,
    scrollerLeft,
    xOffset,
    yOffset,
    scrollerPaddingLeft,
    scrollerPaddingRight,
    scrollerPaddingTop,
    scrollerPaddingBottom,
    scrollerBordersX,
    scrollerBordersY,
  };
}

export function getDOMRectFromSelection(selection: Selection, content: HTMLElement, controller: Controller) {
  const collapsedSelection = selection.moveToFocus();
  if (!collapsedSelection) {
    return null;
  }

  const { anchor } = collapsedSelection;
  if (anchor.isEdgePoint()) {
    let node: Element | null = null;
    try {
      // eslint-disable-next-line react/no-find-dom-node
      node = findDOMNode(anchor.key);
    } catch (error) {
      // ignore
    }
    if (!node) {
      return null;
    }
    const domRect = node.getBoundingClientRect();
    const { top, height } = domRect;
    const left = anchor.edge === EdgePoint.BEFORE ? domRect.left : domRect.right;
    return typeof DOMRect !== 'undefined' ? new DOMRect(left, top, 1, height) : null;
  }

  let range: Range | null = null;
  try {
    range = findDOMRange(collapsedSelection, controller, content);
  } catch (e) {
    return null;
  }
  if (!range) {
    return null;
  }
  const cursorRect = range.getClientRects()[0];
  if (!cursorRect) {
    return null;
  }
  return cursorRect;
}

export function scrollToSelection(selection: Selection, content: HTMLElement, scroller: HTMLElement, controller: Controller) {
  const inline = controller.value.document.getClosestInline(selection.focus.key);
  let rect: PointLocation | undefined;
  // 光标在 inline 元素内部
  if (inline) {
    const inlineDOM = findDOMNodeSafely(inline.key);
    if (!inlineDOM) {
      return;
    }
    const inlineRect = inlineDOM.getBoundingClientRect();
    rect = {
      node: inlineDOM,
      clientLeft: inlineRect.left + inlineRect.width / 2,
      clientTop: inlineRect.top,
      height: inlineRect.height, // 以 inline 的尺寸为准
    };
  } else {
    rect = findCaretPosition(selection.focus, selection.isHanging ? 'start' : 'end', content, Infinity);
  }

  if (!rect) {
    return;
  }

  const {
    isWindow,
    width,
    height,
    viewportHeight,
    scrollerTop,
    scrollerLeft,
    xOffset,
    yOffset,
    scrollerPaddingLeft,
    scrollerPaddingRight,
    scrollerPaddingTop,
    scrollerPaddingBottom,
    scrollerBordersX,
    scrollerBordersY,
  } = getScrollableContainerInfo(scroller);

  const cursorTop = rect.clientTop + yOffset - scrollerTop;
  const cursorLeft = rect.clientLeft + xOffset - scrollerLeft;

  let x = xOffset;
  let y = yOffset;
  let inView = true;

  if (cursorLeft < xOffset) {
    // selection to the left of viewport
    x = cursorLeft - scrollerPaddingLeft;
    inView = false;
  } else if (
    cursorLeft + CURSOR_WIDTH + scrollerBordersX >
    xOffset + width
  ) {
    // selection to the right of viewport
    x = cursorLeft + scrollerBordersX + scrollerPaddingRight - width;
    inView = false;
  }

  if (cursorTop < yOffset) {
    // selection above viewport
    y = cursorTop - scrollerPaddingTop;
    inView = false;
  } else if (
    cursorTop + rect.height + scrollerBordersY >
    yOffset + height - EXTRA_BOTTOM_OFFSET
    // 此条件为被iOS键盘遮挡
    // iOS弹出键盘只会改变 visualViewport，不会影响其他任何地方
    || rect.clientTop + rect.height > viewportHeight - EXTRA_BOTTOM_OFFSET
  ) {
    // selection below viewport
    y = Math.max(
      cursorTop +
      scrollerBordersY +
      scrollerPaddingBottom +
      rect.height -
      height,
      // 此数值为避免被iOS键盘遮挡
      scrollerTop +
      cursorTop +
      rect.height -
      viewportHeight,
    );
    y += EXTRA_BOTTOM_OFFSET;
    inView = false;
  }

  // 对于大元素（超过 1/2 屏幕高度以上），特殊处理一下。
  const quaterHeight = height / 4;
  const isHugeElement = rect.height > quaterHeight * 2;
  // 如果大元素已经大部分在视图内（1/4 以上），就不要滚动了。
  const partInView =
    rect.clientTop < scrollerTop + quaterHeight * 3 &&
    rect.clientTop + rect.height > scrollerTop + quaterHeight;
  if (isHugeElement && partInView) {
    inView = true;
  }

  if (inView) {
    return;
  }

  const deltaY = Math.abs(y - scroller.scrollTop);

  (isWindow ? window : scroller).scrollTo({
    left: x,
    top: y,
    // 纵向滚动距离小于 Min(视口高度/4, 特定高度) 时，采用顺滑滚动
    //    > 一般用于输入文字导致的换行到视口外，然后滚动进视口
    // 否则滚动距离过大就直接跳转到指定位置，避免滚动动画时间过长，体感不好
    behavior: deltaY < Math.min(quaterHeight, MAX_LINE_HEIGHT) ? 'smooth' : 'auto',
  });
}
