import Selection from '../models/selection';

interface CangjieSelectDetail {
  selection: Selection;
  trigger?: string;
}

type CangjieSelectEvent = CustomEvent<CangjieSelectDetail>;

function CangjieSelectEvent(detail: CangjieSelectDetail) {
  return new CustomEvent('cangjieSelect', { detail });
}

export default CangjieSelectEvent;
