import { PendingConfigType, EnablePendingConfig } from '../interfaces/controller';

export default (config?: EnablePendingConfig): Record<PendingConfigType, boolean> => {
  let input = false;
  let selection = false;
  if (typeof config === 'boolean' || !config) {
    // @ts-ignore typing narrow error ????
    input = config || false;
    // @ts-ignore typing narrow error
    selection = config || false;
  } else {
    input = config.input || false;
    selection = config.selection || false;
  }
  return { input, selection };
};
