/**
 * @packageDocumentation
 * @module transferUtils
 */
import * as React from 'react';
import ClipboardManager from '@ali/clipboard-manager';
import Controller from '../controller/index';
import Document from '../models/document';
import CangjieClipboardEvent from '../events/cangjieClipboard';
import { MIME_TYPES } from '../constants';
import { isFileMimeType } from './getClipboardData';

function getTransferType(data) {
  if (data.fragment) {
    return 'fragment';
  }
  if (data.files && data.files.length) {
    return 'files';
  }
  if (data.html) {
    return 'html';
  }
  if (data.text) {
    return 'text';
  }
  return 'unknown';
}

export function getEventTransfer(
  event: React.ClipboardEvent | React.DragEvent | CangjieClipboardEvent,
  controller: Controller,
) {  
  const clipboardData =
    'dataTransfer' in event ? event.dataTransfer : event.clipboardData;
  if (!clipboardData) {
    return { files: [], fragment: null, html: '', text: '' };
  }

  const html = clipboardData.getData(MIME_TYPES.HTML);
  const text = clipboardData.getData(MIME_TYPES.TEXT) || '';
  const encodedFragment = clipboardData.getData(MIME_TYPES.FRAGMENT);
  let fragment: Document | null = null;
  if (encodedFragment) {
    try {
      fragment = decodeFragment(encodedFragment);
    } catch (e) {
      // do nothing
    }
  }
  if (!fragment && html && ~html.indexOf(` data-${controller.biz}-fragment="`)) {
    const FRAGMENT_MATCHER = new RegExp(
      ` data-${controller.biz}-fragment="([^\\s"]+)"`,
    );
    const matches = FRAGMENT_MATCHER.exec(html);
    // eslint-disable-next-line @typescript-eslint/no-unused-vars
    const [_, encoded] = matches || [];
    if (encoded) {
      fragment = decodeFragment(encoded);
    }
  }

  let files: File[] = [];
  try {
    if (clipboardData.items && clipboardData.items.length) {
      files = Array.from(clipboardData.items).reduce((fs, item) => {
        if (item.kind === 'file') {
          const file = item.getAsFile();
          if (file) {
            return [...fs, file];
          }
        }
        return fs;
      }, [] as File[]);
    } else if (clipboardData.files && clipboardData.files.length) {
      files = Array.from(clipboardData.files);
    }
  } catch (err) {
    if (clipboardData.files && clipboardData.files.length) {
      files = Array.from(clipboardData.files);
    }
  }

  const data = { files, fragment, html, text };
  return {
    ...data,
    type: getTransferType(data),
  };
}

/**
 * 编码 fragment
 * @param fragment
 */
export function encodeFragment(fragment: Document) {
  const string = JSON.stringify(fragment.toJSON());
  const encoded = window.btoa(encodeURIComponent(string));
  return encoded;
}

/**
 * 解码 fragment
 * @param text
 */
export function decodeFragment(text: string): Document {
  const decoded = decodeURIComponent(window.atob(text));
  const json = JSON.parse(decoded);
  const fragment = Document.fromJSON(json);
  return fragment;
}

let clip: ClipboardManager;

const createClip = () => {
  if (clip) return clip;
  const target = document.createElement('div');
  target.setAttribute('style', '-webkit-user-select: text !important');
  clip = new ClipboardManager({ target });
  return clip;
};
/**
 * 数据写入系统剪切板
 * @param event
 * @param data
 * @param callback
 */
function setClipboardData(
  event: CangjieClipboardEvent,
  data: Record<string, string>,
  callback?: () => void,
) {
  const isFileData = Object.keys(data).some((type) => type.startsWith('image'));
  if (event.nativeEvent?.clipboardData) {
    const dataEntries = Object.entries(data);
    dataEntries.forEach((item) => {
      const [mimeType, content] = item;
      if (!isFileMimeType(mimeType)) {
        event.nativeEvent?.clipboardData?.setData(mimeType, content);
      }
    });
    if (isFileData) {
      const clipManager = createClip();
      clipManager.writeFile(data, {
        succeeded: true,
        action: 'copy',
        text: '',
      }).finally(() => {
        if (callback) {
          callback();
        }
      });
    } else if (callback) {
      callback();
    }
  } else {
    const clipManager = createClip();
    clipManager.write(data, false);
    window.requestAnimationFrame(() => {
      if (callback) {
        callback();
      }
    });
  }
}

export function copyFragment(
  event: CangjieClipboardEvent,
  callback?: () => void,
) {
  const encodedFragment = event.clipboardData?.getData(MIME_TYPES.FRAGMENT);
  const text = event.clipboardData?.getData(MIME_TYPES.TEXT)
    || (encodedFragment && decodeFragment(encodedFragment).text)
    || '';
  const html = event.clipboardData?.getData(MIME_TYPES.HTML) || text;
  const data = {
    [MIME_TYPES.FRAGMENT]: encodedFragment,
    [MIME_TYPES.HTML]: html,
    [MIME_TYPES.TEXT]: text,
  };

  const imageTypes = event.clipboardData?.types.filter((type) =>
    type.startsWith('image'));
  imageTypes?.forEach((type) => {
    const imgData = event.clipboardData?.getData(type);
    if (imgData) {
      data[type] = imgData;
    }
  });
  setClipboardData(event, data, callback);
}