import { Commands, Block } from '../..';
import type { Schema } from '../../plugins/normalization';
import type { MoPlugin } from '../type/moPlugin';
import type Controller from '../../controller/index';
import { documentSchema } from '../plugins/document';

export function createSchema(plugins: MoPlugin[]): Schema {
  const s = plugins.reduce<Schema>((prevSchema, plugin) => {
    if (!plugin.schemas) {
      return prevSchema;
    }
    const schemas = Array.isArray(plugin.schemas) ? plugin.schemas : [plugin.schemas];
    schemas.forEach((schema) => {
      // document 是内部插件, 用到特殊定义，在内部处理
      const { type, isBlock, isVoid, isEdgeSelectable, schema: oldSchema } = schema;
      let val: any = {};
      if (['boolean', 'function'].includes(typeof isVoid)) {
        val.isVoid = isVoid;
      }
      if (typeof isEdgeSelectable === 'boolean') {
        val.isEdgeSelectable = isEdgeSelectable;
      }
      if (oldSchema) {
        val = {
          ...val,
          ...oldSchema,
        }
      }

      if (type === 'document') {
        prevSchema['document'] = {
          ...prevSchema['document'],
          ...val,
        };
      } else {
        prevSchema[(isBlock || isBlock === undefined) ? 'blocks' : 'inlines']![type] = val;
      }
    });

    return prevSchema;
  }, {
    ...documentSchema,
    blocks: {},
    inlines: {},
  });

  return s;
}

/**
 * 兼容原先 strictSchema 定义。新增的规则都在插件中指定，schema 和 strictSchema 都支持。
 * 
 * 1. document 下只能是已注册的 block type
 * 2. heading1-5, paragraph 下只能是已注册的 inline type
 * 3. table 的 schema 跟 @一辙 讨论了下， 考虑存量数据兼容，还是不在 table 里指定，保留在 strictSchema 中。
 */

export function createStrictSchema(plugins: MoPlugin[]): Schema {
  const schema = createSchema(plugins);
  const blockTypes = Object.keys(schema.blocks!);
  const inlineTypes = Object.keys(schema.inlines!);

  schema.document = {
    ...schema.document,
    nodes: [{ match: blockTypes.map(t => ({ type: t })), min: 1 }],
  };

  ['paragraph', 'heading-1', 'heading-2', 'heading-3', 'heading-4', 'heading-5', 'heading-6'].forEach((type) => {
    if (schema.blocks?.[type]) {
      schema.blocks[type] = {
        ...schema.blocks[type],
        nodes: [
          { match: [...inlineTypes.map(t => ({ type: t })), { klass: 'text' }] },
        ],
        normalize: (controller, error) => {
          const { code, child, node } = error;
          if (code === 'child_type_invalid' && blockTypes.includes(child.type)) {
            const parent = controller.value.document.getParent(node.key);
            if (parent) {
              const index = parent.nodes.indexOf(node.key);
              controller.command(Commands.moveNodeByKey, child.key, parent.key, index);
            }
          }
        },
      }
    }
  });

  // 指定 table 插件时的特殊处理
  if(schema.blocks?.['table']) {
    const extra = {
      'table': {
        isEdgeSelectable: true,
        nodes: [{ match: { type: 'table-row' }, min: 1 }],
      },
      'table-row': {
        nodes: [{ match: { type: 'table-cell' }, min: 1 }],
      },
      'table-cell': {
        nodes: [{ match: blockTypes.map((t) => ({ type: t })), min: 1 }],
        normalize: (controller: Controller, error) => {
          const { code, child, node } = error;
          if (code === 'child_type_invalid' && node.nodes.length === 1) {
            controller.command(Commands.removeNodeByKey, child.key)

            let defaultBlock = controller.query('getDefaultBlock', node.nodes);
            if (!defaultBlock) {
              if (!defaultBlock) {
                defaultBlock = Block.create({
                  type: Block.DEFAULT_TYPE
                });
              }
            }
            controller.command(
              Commands.insertNodeByKey,
              node.key,
              0,
              defaultBlock,
            );
          } else if (!node.nodes.length) {
            let defaultBlock = Block.create({
              type: 'paragraph',
            });
            if (!defaultBlock) {
              defaultBlock = Block.create({
                type: Block.DEFAULT_TYPE
              });
            }
            controller.command(
              Commands.insertNodeByKey,
              node.key,
              0,
              defaultBlock,
            );
          } else {
            controller.command(Commands.removeNodeByKey, child.key);
            const index = node.nodes.indexOf(child);
            for (let i = index + 1; i < node.nodes.length - 1; i++) {
              const c = node.nodes[i];
              if (!blockTypes.includes(c.type)) {
                controller.command(Commands.removeNodeByKey, c.key);
              }
            }
          }
        },
      },
    };
    Object.keys(extra).forEach((type => {
      schema.blocks![type] = {
        ...(schema.blocks![type] || {}),
        ...extra[type],
      }
    }));
  }

  return schema;
}
