const FieldsNotAffectSpace: Array<keyof ValueProperties> = [
  'composing',
  'isFocused',
  'decorations',
  'selection',
  'remoteSelections',
];

const OperationsNotAffectSpace: string[] = [
  OperationType.InsertText,
  OperationType.RemoveText,
  OperationType.SetMark,
  OperationType.AddMark,
  OperationType.RemoveMark,
  OperationType.SetSelection,
];

function isSetValueNotAffectSpace(key: keyof ValueProperties) {
  return FieldsNotAffectSpace.includes(key);
}

function isOperationNotAffectSpace(operation: Operation) {
  return OperationsNotAffectSpace.includes(operation.type);
}

export function getDirtyKeysFromOperation(value: Value, operation: Operation) {
  const {
    type,
    // @ts-ignore
    path,
    // @ts-ignore
    node,
  } = operation;
  const { document } = value;
  const opNode = node || document.getNodeByPath(path);
  const opNodeKey = opNode?.key;

  switch (type) {
    // PERF: Cangjie does not support normalizing `text.text` `leaf.marks`, just ignore changes for performance(~10ms in Huge Document story)
    // PERF: 🚫🚫🚫 No [...xxx]. Spread syntax will affect performance when huge node changed.
    // Ref: packages/cangjie/src/plugins/schema.ts
    // case 'add_mark':
    // case 'insert_text':
    // case 'remove_mark':
    // case 'remove_text':
    // case 'set_mark':
    case OperationType.SetNode: {
      const ancestors = document.getAncestorsByPath(path).map((n) => n?.key);
      ancestors.push(opNodeKey);
      return ancestors;
    }

    case OperationType.InsertNode: {
      const table = Text.isText(node)
        ? {}
        : (node as Element).getKeysToEdgesTable();
      const ancestors = document.getAncestorsByPath(path).map((n) => n?.key);
      ancestors.push(node.key);
      Array.prototype.push.apply(ancestors, Object.keys(table));
      return ancestors;
    }

    case OperationType.SplitNode: {
      const ancestors = document.getAncestorsByPath(path).map((n) => n?.key);
      // @ts-ignore
      const nextKey = document.getNextNodeByPath(path).key;
      ancestors.push(opNodeKey, nextKey);
      return ancestors;
    }

    case OperationType.MergeNode: {
      const ancestors = Path.ancestors(path);
      const previousPath = Path.decrement(path);
      ancestors.push(previousPath);
      return ancestors.map((p) => document.getNodeByPath(p)).map((n) => n?.key);
    }

    case OperationType.MoveNode: {
      // Path is more accurate than key when node moved.
      const { newPath } = operation as MoveNodeOperation;
      if (Path.isEqual(path, newPath)) {
        return [];
      }

      let parentPath = Path.parent(path);
      let newParentPath = Path.parent(newPath);

      // HACK: this clause only exists because the `move_path` logic isn't
      // consistent when it deals with siblings.
      if (!Path.isSibling(path, newPath)) {
        if (newParentPath.length && Path.isYounger(path, newPath)) {
          newParentPath = Path.decrement(newParentPath, 1, path.length - 1);
        }
        if (
          parentPath.length &&
          (Path.isYounger(newPath, path) || Path.isAncestor(newPath, path))
        ) {
          parentPath = Path.increment(parentPath, 1, newPath.length - 1);
        }
      }

      const oldAncestors = Path.ancestors(parentPath);
      const newAncestors = Path.ancestors(newParentPath);
      oldAncestors.push(parentPath);
      Array.prototype.push.apply(oldAncestors, newAncestors);
      oldAncestors.push(newParentPath);
      return oldAncestors
        .map((p) => document.getNodeByPath(p))
        .map((n) => n?.key);
    }

    case OperationType.RemoveNode: {
      // The node has been deleted, ancestors connot be getted by "document.getAncestors"
      const ancestors = Path.ancestors(path);
      return ancestors.map((p) => document.getNodeByPath(p)).map((n) => n?.key);
    }

    default: {
      return [];
    }
  }
}

/**
 * diff比较（严格：顺序改变算diff）
 * @param element
 * @param prevElement
 * @param curIndexKey
 * @param preIndexKey
 * @deprecated
 */
export function getDirtyKeysFromElementDiffStrictly(
  element: Node,
  prevElement: Node | null,
  curIndexKey?: string,
  preIndexKey?: string,
) {
  const dirtyKeys: string[] = [];
  if (element === prevElement && curIndexKey === preIndexKey) {
    return dirtyKeys;
  }
  dirtyKeys.push(element.key);
  if (!Element.isElement(element)) {
    return dirtyKeys;
  }
  if (!Element.isElement(prevElement)) {
    return Object.keys(element.getKeysToEdgesTable()).concat(element.key);
  }
  for (let i = 0; i < element.nodes.length; i += 1) {
    const child = element.nodes[i];
    const prevChild = prevElement.getNode(child.key);
    const preChildIndexKey = prevElement?.nodes[i]?.key;
    if (child !== prevChild || child.key !== preChildIndexKey) {
      dirtyKeys.push(
        ...getDirtyKeysFromElementDiffStrictly(
          child,
          prevChild,
          child.key,
          preChildIndexKey,
        ),
      );
    }
  }
  return dirtyKeys;
}

function getDirtyKeysFromElementDiff(element: Node, prevElement: Node | null) {
  const dirtyKeys: string[] = [];
  if (element === prevElement) {
    return dirtyKeys;
  }
  dirtyKeys.push(element.key);
  if (!Element.isElement(element)) {
    return dirtyKeys;
  }
  if (!Element.isElement(prevElement)) {
    return Object.keys(element.getKeysToEdgesTable()).concat(element.key);
  }
  for (let i = 0; i < element.nodes.length; i += 1) {
    const child = element.nodes[i];
    const prevChild = prevElement.getNode(child.key);
    if (child !== prevChild) {
      getDirtyKeysFromElementDiff(child, prevChild).forEach((key) =>
        dirtyKeys.push(key));
    }
  }
  return dirtyKeys;
}

interface ViewData {
  decorations: Decoration[];
  injections: Injection[];
}

const composingMark = Mark.create('cangjieComposing');

interface PerfStart {
  timestamp: number;
  type: PerfType;
  subtype: string;
  ignore?: boolean; // 是否需要忽略不计该数据
}

interface Perf {
  /** 输入开始的 timestamp */
  [PerfType.input]: PerfStart[];
  /** 输入法更新时的 timestamp */
  [PerfType.composing]: PerfStart[];
  /** 选区事件开始时的 timestamp */
  [PerfType.selection]: PerfStart[];
}

type ScrollType =
  | /** default: 跟随选区滚动 */
  'toSelection'
  /** 保持当前滚动位置稳定，一般是一次性的，消费过该数据后应置回 default 值 */
  | 'maintain';

export const defaultScrollType: ScrollType = 'toSelection';

interface Virtualize {
  /** 当前可见的元素集合 */
  visibles: string[];
  /** 当前正在调度的不可见节点模型计算 */
  invisibleCalculationId: null | number;
}

interface Flag {
  normalizing: boolean;
  pending: boolean;
  saving: boolean;
  /**
   * 编辑器 OP 跟踪
   */
  tracking: boolean;
  withCommand: boolean;
  destroyed: boolean;
  perf: Perf;
  /**
   * 页面滚动方式
   * BACKGROUND:
   * 1. 本地用户操作时，需要跟随选区滚动
   * 2. 协同数据应用后，需要保持本地状态稳定
   */
  scrollType: ScrollType;
  // 标记有关联关系的 OPs，解决 undo/redo 多次问题
  // 一般使可用 node.key 作为 relatedId
  opRelatedId: string | null;
}

export function insertComposingIntoLeaves(
  leaves: Leaf[],
  offset: number,
  composing: string,
  selection: Selection,
  decorations: Decoration[],
) {
  const [before, after] = Leaf.splitLeaves(leaves, offset);
  const beforeLastLeaf = before[before.length - 1];
  const beforeLastMarks = beforeLastLeaf ? beforeLastLeaf.marks : [];
  let marks: Mark[] | null = null;
  if (selection.marks) {
    const decorationsMarks = decorations.map(({ mark }) => mark);
    const activeDecorationsMarks = beforeLastMarks.filter((mark) =>
      decorationsMarks.some((m) => equal(m, mark)));
    marks = [...activeDecorationsMarks, ...selection.marks];
  } else {
    marks = beforeLastMarks;
  }

  const middle = Leaf.create({
    text: composing,
    marks: [...marks, composingMark],
  });
  const shouldConcatBefore = before.length > 1 || before[0]?.text;
  const shouldConcatAfter = after.length > 1 || after[0]?.text;
  return [
    ...(shouldConcatBefore ? before : []),
    middle,
    ...(shouldConcatAfter ? after : []),
  ];
}

