/**
 * @packageDocumentation
 * @ignore
 */
import React from 'react';
import RenderNodeProps from '../interfaces/renderNodeProps';
import Block from '../models/block';
import { useNodeStyle } from '../hooks/useStyle';
import { Selector } from '../constants';
import useGroupData from '../hooks/useGroupData';
import { isGroupCollapsed } from '../plugins/group/utils';
import { useUserData } from '../hooks/useUserData';

const blockStyle: React.CSSProperties = {
  position: 'relative',
};

const inlineStyle: React.CSSProperties = {
  ...blockStyle,
  display: 'inline-block',
  textIndent: 'initial',
  // 设置行高后，使void 节点的高度不变
  lineHeight: 'initial',
  maxWidth: '100%',
};

const voidStyle: React.CSSProperties = {
  ...inlineStyle,
};

const VoidInlineWrapper: React.FC<Omit<RenderNodeProps, 'children'>> = ({
  attributes,
  children,
}) => {
  return (
    <span
      data-cangjie-void
      style={voidStyle}
      {...attributes}
    >
      <span style={inlineStyle} >
        {children}
      </span>
    </span>
  );
};

const VoidBlockWrapper: React.FC<Omit<RenderNodeProps, 'children'>> = ({
  attributes,
  node,
  children,
  controller,
  visible,
}) => {
  const seletableAttrs = { [Selector.edgeSelectable]: true };
  // @ts-ignore
  const fold = Block.isBlock(node) ? (useUserData(node, 'fold') || false): false;
  const hidden = useGroupData<boolean>(
    node,
    (groups) => {
      return groups.some(
        (group) => isGroupCollapsed(group) && group.key !== node.key,
      );
    },
    fold,
  );
  // 合并虚拟化的 visible 与 flattenGroup 的 visible
  const finalVisible = visible !== false && !hidden;
  const nodeStyle = useNodeStyle(controller, node, finalVisible);

  return (
    <div
      data-cangjie-void
      data-block-uuid={(node as Block).data.uuid}
      style={nodeStyle}
      {...attributes}
      {...seletableAttrs}
    >
      {children}
    </div>
  );
};

const VoidWrapper: React.FC<Omit<RenderNodeProps, 'children'>> = (props) => {
  const isBlock = Block.isBlock(props.node);
  return (
    isBlock ? (
      <VoidBlockWrapper {...props} />
    ) : (
      <VoidInlineWrapper {...props} />
    )
  );
};

export default VoidWrapper;
