import Controller from '../../controller';
import { PendingType } from '../../interfaces/controller';
import { PendingPattern, PendingPredicate, PendingGetter } from '../../interfaces/pending';
import { OperationType } from '../../models/operation';

const isSetSelection = ({ operation }) => (
  operation && operation.type === OperationType.SetSelection
);

export function getSelectionData(controller: Controller) {
  const { document, selection } = controller.value;

  const blocks = (
    selection.isCollapsedAtEdge
      ? [document.getNode(selection.anchor.key)!]
      : document.getLeafBlocksAtRange(selection)
  );
  const isHideSelection = controller.query(
    'isHideSelection',
  )!;
  return { range: selection, blocksKeys: new Set(blocks.map((b) => b.key)), isHideSelection };
}

export const isSelect: PendingPredicate = (params) => {
  const { ops, controller } = params;

  if (ops.length === 0) {
    return false;
  }

  // 从 blur 状态开始选区（点选/拖选），会有个先 focus 再 set_selction 的过程，可以 pending
  return ops.every((op, index) => {
    if (index === 0) {
      return (op.key === 'isFocused' && controller.value.isFocused) || isSetSelection(op);
    }
    return isSetSelection(op);
  });
};

export const getPending: PendingGetter<null> = ({ controller }) => ({
  type: PendingType.selection,
  effect: () => {
    controller.selectionData$.next(getSelectionData(controller));
  },
});

const pattern: PendingPattern<null> = [
  isSelect,
  getPending,
];

export default pattern;
