/* eslint-disable no-dupe-class-members */
/**
 * @packageDocumentation
 * @module Models
 */
import equal from 'fast-deep-equal';
import isPlainObject from 'is-plain-object';
import { noop, once } from 'lodash-es';
import ImmutableRecord from './immutableRecord';
import Path from './path';
import Injection, { InjectionJSON } from './injection';
import Decoration, { DecorationJSON } from './decoration';
import Selection, { SelectionJSON, SupportPointName } from './selection';
import { TextPoint, EdgePoint } from './point';
import Node from './node';
import { ElementProperties, ElementTypeName } from './element';
import Document, { DocumentJSON } from './document';
import Block from './block';
import Inline from './inline';
import Text from './text';
import Mark, { MarkProperties } from './mark';
import Operation, {
  OperationType,
  AddMarkOperation,
  InsertNodeOperation,
  InsertTextOperation,
  MergeNodeOperation,
  MoveNodeOperation,
  RemoveMarkOperation,
  RemoveNodeOperation,
  RemoveTextOperation,
  SetMarkOperation,
  SetNodeOperation,
  SetSelectionOperation,
  SplitNodeOperation,
} from './operation';
import { Serializable, SerializeOptions } from '../interfaces/serializable';
import { mergeUserProperties } from './userData';

// eslint-disable-next-line no-console
const warning =
  typeof console !== 'undefined'
    ? once(console.warn.bind(console) || (() => {}))
    : noop;

function getDefaultSelection(document: Document) {
  const firstNode = document.getFirstNode();
  if (Text.isText(firstNode)) {
    const point = TextPoint.create({ key: firstNode.key, offset: 0 });
    return Selection.create({ anchor: point, focus: point });
  }
  const point = EdgePoint.create({ key: document.key, edge: EdgePoint.BEFORE });
  return Selection.create({ anchor: point, focus: point });
}

export interface ValueProperties {
  data?: Record<string, any>;
  document?: Document;
  selection?: Selection;
  remoteSelections?: Selection[];
  injections?: Injection[];
  decorations?: Decoration[];
  composing?: string;
  isFocused?: boolean;
}

export interface ValueJSON {
  data?: Record<string, any>;
  klass?: 'value';
  document?: DocumentJSON;
  selection?: SelectionJSON;
  remoteSelections?: SelectionJSON[];
  decorations?: DecorationJSON[];
  injections?: InjectionJSON[];
}

export default class Value extends ImmutableRecord
  implements Serializable<ValueJSON> {
  static create(props: Value | ValueProperties = {}): Value {
    if (Value.isValue(props)) {
      return props;
    }

    if (isPlainObject(props)) {
      const {
        data = {},
        document: doc = {},
        selection,
        remoteSelections = [],
        injections = [],
        decorations = [],
        composing = '',
        isFocused = false,
      } = props;
      const document = Document.create(doc);
      return new Value({
        data,
        document,
        selection: selection || getDefaultSelection(document),
        remoteSelections,
        injections,
        decorations,
        composing,
        isFocused,
      });
    }

    throw new Error(
      `\`Value.create\` only accepts objects or values, but you passed it: ${props}`,
    );
  }

  static fromJSON(json: ValueJSON) {
    const {
      document = {},
      injections,
      selection,
      remoteSelections,
      decorations,
    } = json;
    const documentValue = Document.fromJSON(document);
    return Value.create({
      ...json,
      injections: injections ? injections.map(Injection.fromJSON) : undefined,
      document: documentValue,
      selection: selection
        ? Selection.fromJSON(selection)
        : getDefaultSelection(documentValue),
      remoteSelections: remoteSelections
        ? remoteSelections.map(Selection.fromJSON)
        : undefined,
      decorations: decorations
        ? decorations.map(Decoration.fromJSON)
        : undefined,
    });
  }

  static isValue(maybeValue: any): maybeValue is Value {
    return maybeValue instanceof Value;
  }

  /**
   * value 的 injections 数组
   */
  public readonly injections: Injection[];

  /**
   * value 的 decoration 数组
   */
  public readonly decorations: Decoration[];

  /**
   * 是否是focus状态
   */
  public readonly isFocused: boolean;

  /**
   * value 的内存数据
   */
  public readonly data: Record<string, any>;

  /**
   * 根文档对象
   */
  public readonly document: Document;

  /**
   * 当前的选区
   */
  public readonly selection: Selection;

  /**
   * 当前的选区
   */
  public get rangeSelection() {
    if ('development' === process.env.NODE_ENV) {
      warning(
        '`Value.rangeSelection` is deprecated, please use `Value.selection`',
      );
    }
    return this.selection;
  }

  /**
   * 远程选区
   */
  public readonly remoteSelections: Selection[] = [];

  /**
   * 输入法 composing 但是没有确认的文字
   */
  public readonly composing: string = '';

  protected constructor(props: Required<ValueProperties>) {
    super();
    const {
      data,
      document,
      selection,
      remoteSelections,
      injections,
      decorations,
      composing,
      isFocused,
    } = props;
    this.data = { ...data };
    this.document = document;
    this.selection = selection;
    this.remoteSelections = remoteSelections;
    this.injections = injections;
    this.decorations = decorations;
    this.composing = composing;
    this.isFocused = isFocused;
    this.freeze();
  }

  /**
   * 获取当前选中的blocks
   */
  public get blocks() {
    return !this.selection
      ? []
      : this.document.getLeafBlocksAtRange(this.selection);
  }

  /**
   * 获取当前选中的inlines
   */
  public get inlines(): Inline[] {
    return !this.selection
      ? []
      : this.document.getLeafInlinesAtRange(this.selection);
  }

  private getElement(pointName: SupportPointName, type: 'block'): Block | null;

  private getElement(
    pointName: SupportPointName,
    type: 'inline',
  ): Inline | null;

  private getElement(pointName: SupportPointName, type: 'text'): Node | null;

  private getElement(pointName: SupportPointName, type: ElementTypeName) {
    const { document, selection } = this;
    if (!selection) {
      return null;
    }
    const point = selection.getPoint(pointName, document);
    if (type === 'block') {
      return document.getClosestBlockFromPoint(point.key);
    } else if (type === 'inline') {
      return document.getClosestInline(point.key);
    } else {
      return document.getNode(point.key);
    }
  }

  /**
   * 获取当前选中的anchor位置对应的block
   */
  public get anchorBlock() {
    return this.getElement('anchor', 'block');
  }

  /**
   * 获取当前选中的focus位置对应的block
   */
  public get focusBlock() {
    return this.getElement('focus', 'block');
  }

  /**
   * 获取当前选中的start位置对应的block
   */
  public get startBlock() {
    return this.getElement('start', 'block');
  }

  /**
   * 获取当前选中的end位置对应的block
   */
  public get endBlock() {
    return this.getElement('end', 'block');
  }

  /**
   * 获取当前选中的anchor位置对应的inline
   */
  public get anchorInline() {
    return this.getElement('anchor', 'inline');
  }

  /**
   * 获取当前选中的focus位置对应的inline
   */
  public get focusInline() {
    return this.getElement('focus', 'inline');
  }

  /**
   * 获取当前选中的start位置对应的inline
   */
  public get startInline() {
    return this.getElement('start', 'inline');
  }

  /**
   * 获取当前选中的end位置对应的inline
   */
  public get endInline() {
    return this.getElement('end', 'inline');
  }

  /**
   * 获取当前选中的anchor位置对应的text
   */
  public get anchorText() {
    return this.getElement('anchor', 'text');
  }

  /**
   * 获取当前选中的focus位置对应的text
   */
  public get focusText() {
    return this.getElement('focus', 'text');
  }

  /**
   * 获取当前选中的start位置对应的text
   */
  public get startText() {
    return this.getElement('start', 'text');
  }

  /**
   * 获取当前选中的end位置对应的text
   */
  public get endText() {
    return this.getElement('end', 'text');
  }

  /**
   * 获取当前选中位置对应的marks。
   * 如果用户设置了正在编辑的样式，返回用户的设置Marks；否则返回当前文字的Marks。
   */
  public get activeMarks() {
    if (!this.selection) {
      return [];
    }
    return (
      this.selection.marks ||
      this.document.getActiveMarksAtRange(this.selection)
    );
  }

  /**
   * 是否是blur状态
   */
  public get isBlurred() {
    return !this.isFocused;
  }

  /**
   * 获取当前选区对应的fragment
   */
  public get fragment() {
    const { selection, document } = this;
    return selection
      ? document.getFragmentAtRange(selection)
      : Document.create();
  }

  public merge(props: Partial<ValueProperties>) {
    return new Value({
      data: this.data,
      document: this.document,
      selection: this.selection,
      remoteSelections: this.remoteSelections,
      injections: this.injections,
      decorations: this.decorations,
      composing: this.composing,
      isFocused: this.isFocused,
      ...props,
    });
  }

  public set<T extends keyof ValueProperties>(
    key: T,
    value: Required<ValueProperties>[T],
  ) {
    return this.merge({
      [key]: value,
    });
  }

  public updatePoints(
    document: Document,
    map: (point: TextPoint) => TextPoint | null,
    removed?: Node,
  ) {
    const updateSelections = (selections, selection) => {
      if (!Selection.isSelection(selection)) {
        return [...selections, selection];
      }
      let { anchor, focus } = selection;
      if (anchor.isEdgePoint() || focus.isEdgePoint()) {
        ({ anchor, focus } = selection.convertToTextPoints(document));
      }
      const newAnchor = map(anchor);
      const newFocus = map(focus);
      if (!newAnchor || !newFocus) {
        return selections;
      }
      if (
        equal(newAnchor, anchor) &&
        equal(newFocus, focus) &&
        Selection.isValidSelection(document, selection)
      ) {
        // 如果选区没有任何变化，返回原选区即可
        return [...selections, selection];
      }
      return [
        ...selections,
        selection.merge({
          anchor: newAnchor,
          focus: newFocus,
        }),
      ];
    };

    // FIXME: 当 Document 内没有 Text 节点时，无法被 focus，等价于废档
    const selections = [this.selection].reduce<Selection[]>(
      updateSelections,
      [],
    );
    const remoteSelections = this.remoteSelections.reduce<Selection[]>(
      updateSelections,
      [],
    );

    const decorations = this.decorations.reduce<Decoration[]>((decs, dec) => {
      const { start, end } = dec;
      const newStart = map(start);
      const newEnd = map(end);
      if (!newStart || !newEnd) {
        return decs;
      }
      const startText = document.getNode(newStart.key);
      const endText = document.getNode(newEnd.key);
      if (!startText || !endText) {
        return decs;
      }

      if (
        removed &&
        Block.isBlock(removed) &&
        [start, end].every((n) => removed.getNode(n.key))
      ) {
        // 删除 Block 节点时, 内部包含的 decoration 清空掉
        return decs;
      } 

      if (equal(newStart, newEnd) && startText.text) {
        return decs;
      }
      return [
        ...decs,
        dec.merge({
          start: newStart,
          end: newEnd,
        }),
      ];
    }, []);
    return this.merge({
      selection: selections[0] || getDefaultSelection(document),
      remoteSelections,
      decorations,
    });
  }

  private addMark(path: Path, offset: number, length: number, mark: Mark) {
    let { document } = this;
    document = document.addMark(path, offset, length, mark);
    return this.set('document', document);
  }

  private insertNode(path: Path, node: Node) {
    let { document } = this;
    document = document.insertNode(path, node);
    return this.set('document', document);
  }

  private insertText(path: Path, offset: number, text: string, marks: Mark[]) {
    const { document: prevDoc } = this;
    const document = prevDoc.insertText(path, offset, text, marks);
    // node must be Text if .insertText works.
    const node = prevDoc.assertTextByPath(path);
    return this.updatePoints(document, (point) => {
      // 若在当前选区之前插入文本，则需要后移选区
      // insert 'e'
      //   │    cursor
      //   ▼     │
      //  HlloWor│ld
      //         │
      //
      //     │
      //     │ inserted
      //     ▼
      //
      //          │
      //  HelloWor│d
      //          │
      //        cursor
      if (node.key === point.key && offset <= point.offset) {
        return point.moveForward(text.length);
      }
      return point;
    }).set('document', document);
  }

  private mergeNode(path: Path) {
    const { document: prevDoc, injections: prevInject } = this;
    const document = prevDoc.mergeNode(path);
    const injections = prevInject.filter(({ key }) => {
      const injectTargetPath = prevDoc.getPath(key);
      return injectTargetPath && !Path.isEqual(injectTargetPath, path);
    });
    // prev & node could not be null if .mergeNode works.
    const prev = prevDoc.getPreviousSiblingByPath(path)!;
    const node = prevDoc.getNodeByPath(path)!;
    return this.updatePoints(document, (point) => {
      // 如果选区所在的文本被 merge，则需要将光标移动到合并后的文本，并且修正光标起点
      //     ┌─────────┐  cursor
      //     │         │ │
      // ┌───▼───┐ ┌───┴─┼────┐
      // │ Hello │ │ Worl│d   │
      // └───────┘ └─────┼────┘
      //                 │
      //        │
      //        │ merged
      //        │
      //        ▼  
      //
      //           │
      // ┌─────────┼──┐
      // │HelloWorl│d │
      // └─────────┼──┘
      //           │
      //          cursor
      if (Text.isText(node) && point.key === node.key) {
        return point.merge({
          key: prev.key,
          offset: prev.text.length + point.offset,
        });
      }
      return point;
    }).merge({ document, injections });
  }

  private moveNode(path: Path, newPath: Path) {
    if (equal(path, newPath)) {
      return this;
    }

    let { document } = this;
    document = document.moveNode(path, newPath);
    return this.set('document', document);
  }

  private removeMark(path: Path, offset: number, length: number, mark: Mark) {
    let { document } = this;
    document = document.removeMark(path, offset, length, mark);
    return this.set('document', document);
  }

  private removeNode(path: Path) {
    const { document: prevDoc, injections: prevInject } = this;
    const node = prevDoc.assertNodeByPath(path);
    const prev = prevDoc.getPreviousNodeByPath(path);
    const next = prevDoc.getNextNodeByPath(path);

    const document = prevDoc.removeNode(path);
    const injections = prevInject.filter(({ key }) => {
      const injectTargetPath = prevDoc.getPath(key);
      return injectTargetPath && !Path.isEqual(injectTargetPath, path);
    });

    // `document.nodes` is empty.
    if (!prev && !next) {
      return this.merge({
        document,
        selection: getDefaultSelection(document),
        remoteSelections: [],
        injections: [],
        decorations: [],
        composing: '',
      });
    }

    return this.updatePoints(document, (point) => {
      if (node.key === point.key || (node.isElement() && node.hasNode(point.key))) {
        // 若删除选区所在的节点或者其父节点，则要么移动选区到上一节点末，要么移动选区到下一节点开头
        //              cursor
        // ┌───────┐ ┌────┼───┐
        // │ Hello │ │ Wor│ld │
        // └───────┘ └────┼───┘
        //
        //          │
        //          │ removed
        //          ▼
        //
        // ┌──────┼┐
        // │ Hello││
        // └──────┼┘
        //      cursor
        if (prev) {
          const lastText = prev.getLastText();
          if (lastText) {
            return point.moveToEndOfNode(lastText);
          }
        }
        //       cursor
        //       │
        //  ┌────┼───┐ ┌───────┐
        //  │ Wor│ld │ │ Hello │
        //  └────┼───┘ └───────┘
        //       │

        //         │
        //         │ removed
        //         ▼
        //   │
        //  ┌┼──────┐
        //  ││Hello │
        //  └┼──────┘
        //   │
        // cursor

        if (next) {
          const firstText = next.getFirstText();
          if (firstText) {
            return point.moveToStartOfNode(firstText);
          }
        }
        return null;
      }
      return point;
    }, node).merge({ document, injections });
  }

  private removeText(path: Path, offset: number, text: string) {
    const { document: prevDoc } = this;
    const document = prevDoc.removeText(path, offset, text);
    // node must be Text if .insertText works.
    const node = prevDoc.assertTextByPath(path);
    const start = offset;
    const end = start + text.length;
    return this.updatePoints(document, (point) => {
      if (node.key === point.key) {
        // 若当前选区在删除文本之后，则需要前移选区
        //          cursor
        //   ┌────┐   │
        //  H│ello│Wor│d
        //   └────┘   │
        //
        //       │
        //       │      removed
        //       ▼
        //
        //      │
        //  HWor│d
        //      │
        //    cursor
        if (point.offset >= end) {
          return point.moveBackward(text.length);
        }
        // 若当前选区在删除文本起点之后，则前移选区到删除起点
        //   ┌────┐
        //  H│ello│World
        //   └────┘
        //      ──────  selection
        //
        //      │
        //      │      removed
        //      ▼
        //
        //   HWorld
        //    ──── selection
        if (point.offset > start) {
          return point.set('offset', start);
        }
      }
      return point;
    }).set('document', document);
  }

  private setMark(
    path: Path,
    offset: number,
    length: number,
    mark: Mark,
    properties: Partial<MarkProperties>,
  ) {
    let { document } = this;
    document = document.setMark(path, offset, length, mark, properties);
    return this.set('document', document);
  }

  private setNode(path: Path, properties: Partial<ElementProperties>) {
    let { document } = this;
    document = document.setNode(path, properties);
    return this.set('document', document);
  }

  /**
   * 拷贝出一个新value，并设置选区字段
   */
  public setSelection(selection: Selection) {
    return this.set('selection', selection);
  }

  private splitNode(
    path: Path,
    position: number,
    properties: Partial<ElementProperties>,
  ) {
    const { document: prevDoc } = this;
    const document = prevDoc.splitNode(path, position, properties);
    // node could not be null if .splitNode works.
    const node = prevDoc.getNodeByPath(path)!;
    return this.updatePoints(document, (point) => {
      // 如果在当前文本进行分裂，则需要调整选区到分裂后的文本
      //   position
      //       │    cursor
      //       │   │
      //  Hello│Wor│ld
      //       │   │
      //       │
      //
      //        │  split node
      //        ▼
      //
      //  Hello
      //  Wor│ld
      //    cursor
      if (
        Text.isText(node) &&
        point.key === node.key &&
        position <= point.offset
      ) {
        // next is just create by .splitNode
        const next = document.getNextSiblingByPath(path)!;
        return point.merge({
          key: next.key,
          offset: point.offset - position,
        });
      }
      return point;
    }).set('document', document);
  }

  /**
   * 应用operation，并返回一个新value
   */
  public applyOperation(operation: Operation) {
    let value: Value | null = null;
    const { type } = operation;
    if (type === OperationType.AddMark) {
      const { path, offset, length, mark } = operation as AddMarkOperation;
      value = this.addMark(path, offset, length, mark);
    } else if (type === OperationType.InsertNode) {
      const { path, node } = operation as InsertNodeOperation;
      value = this.insertNode(path, node);
    } else if (type === OperationType.InsertText) {
      const {
        path,
        offset,
        text,
        marks = [],
      } = operation as InsertTextOperation;
      value = this.insertText(path, offset, text, marks);
    } else if (type === OperationType.MergeNode) {
      const { path } = operation as MergeNodeOperation;
      value = this.mergeNode(path);
    } else if (type === OperationType.MoveNode) {
      const { path, newPath } = operation as MoveNodeOperation;
      value = this.moveNode(path, newPath);
    } else if (type === OperationType.RemoveMark) {
      const { path, offset, length, mark } = operation as RemoveMarkOperation;
      value = this.removeMark(path, offset, length, mark);
    } else if (type === OperationType.RemoveNode) {
      const { path } = operation as RemoveNodeOperation;
      value = this.removeNode(path);
    } else if (type === OperationType.RemoveText) {
      const { path, offset, text } = operation as RemoveTextOperation;
      value = this.removeText(path, offset, text);
    } else if (type === OperationType.SetMark) {
      const {
        path,
        offset,
        length,
        mark,
        properties,
      } = operation as SetMarkOperation;
      value = this.setMark(path, offset, length, mark, properties);
    } else if (type === OperationType.SetNode) {
      const { path } = operation as SetNodeOperation;
      const properties = mergeUserProperties(this, operation);
      value = this.setNode(path, properties);
    } else if (type === OperationType.SetSelection) {
      const { selection } = operation as SetSelectionOperation;
      value = this.setSelection(selection);
    } else if (type === OperationType.SplitNode) {
      const { path, position, properties } = operation as SplitNodeOperation;
      value = this.splitNode(path, position, properties);
    } else {
      throw new Error(`Unknown operation type: "${type}".`);
    }
    if (value.selection.anchor.key === value.document.key) {
      const selection = getDefaultSelection(value.document);
      return value.setSelection(selection);
    }
    return value;
  }

  public toJSON(options?: SerializeOptions) {
    const object: ValueJSON = {
      klass: 'value',
      document: this.document.toJSON(options),
    };
    if (options?.preserveDecorations) {
      object.decorations = this.decorations.map((decotation) => {
        const { start, end, mark } = decotation.toJSON(options);
        return {
          mark,
          start: {
            ...start,
            path: this.document.getPath(decotation.start.key),
          },
          end: {
            ...end,
            path: this.document.getPath(decotation.end.key),
          },
        };
      });
    }
    if (options?.preserveSelections) {
      const { selection } = this;
      const { anchor, focus } = selection.toJSON(options);
      object.selection = {
        anchor: {
          ...anchor,
          // @ts-ignore
          path: this.document.getPath(selection.anchor.key),
        },
        focus: {
          ...focus,
          // @ts-ignore
          path: this.document.getPath(selection.focus.key),
        },
      };
    }
    if (options?.preserveInjections) {
      object.injections = this.injections.map((injection) =>
        injection.toJSON(),
      );
    }
    return object;
  }
}
