import {
  JsonMLWithAttrs,
  JsonMLWithoutAttrs,
  JsonML,
} from '../type';
import createTextWithString from './createTextWithString';

export type {
  JsonMLWithAttrs,
  JsonMLWithoutAttrs,
  JsonML,
}

interface DescInputOption {
  maxParagraph?: number;
  ignore?: string[];
}

export function jsonMLGetDescription(asl: JsonML, options?: DescInputOption): JsonML {
  // 仅最外层会用到currParagraph
  let currParagraph: number | null = asl[0] === 'root' ? 0 : null;
  const children = jsonMLGetChildren(asl);
  const validChildren: JsonML[] = [];

  if (!Array.isArray(children)) {
    return asl;
  }

  for (const childAsl of children) {
    if (typeof currParagraph === 'number') {
      currParagraph++;
      if (options && typeof options.maxParagraph === 'number'
        && currParagraph > options.maxParagraph) {
        break;
      }
    }
    if (!(options && Array.isArray(options.ignore)
      && options.ignore.includes(childAsl[0]))) {
      validChildren.push(jsonMLGetDescription(childAsl, options));
    }
  }
  return [...jsonMLGetTagNameAndAttributes(asl), ...validChildren] as any as JsonML;
}

export function jsonMLGetTagName(elem: JsonML) {
  return elem[0] || '';
}

function isElement(maybeElem: any) {
  return (Array.isArray(maybeElem) && typeof maybeElem[0] === 'string')
    || typeof maybeElem === 'string';
}

function isAttributes(maybeAttrs: any) {
  return !!maybeAttrs && typeof maybeAttrs === 'object' && !Array.isArray(maybeAttrs);
}

export function jsonMLGetLastChild(node: JsonML): JsonML | null {
  const children = jsonMLGetChildren(node);
  return children.length ? children[children.length - 1] : null;
}

export function JsonMLHasAttributes(maybeElem: any): maybeElem is JsonMLWithAttrs {
  return isElement(maybeElem) && isAttributes(maybeElem[1]);
}

export function jsonMLGetAttributes<T = any>(elem: JsonML): T | null {
  return JsonMLHasAttributes(elem) ? elem[1] as unknown as T : null;
}

function jsonMLGetTagNameAndAttributes(elem: JsonML) {
  return (JsonMLHasAttributes(elem) ? elem.slice(0, 2) : elem.slice(0, 1)) as JsonML[];
}

export function jsonMLGetChildren(elem: JsonML): JsonML[] {
  return (JsonMLHasAttributes(elem) ? elem.slice(2) : elem.slice(1)) as JsonML[];
}

export function jsonMLAppend(elem: JsonML, ...children: JsonML[]): JsonML {
  return [...elem.slice(0), ...children.slice(0)] as unknown as JsonML;
}

export function jsonMLAppendAt(elem: JsonML, index: number, ...children: JsonML[]): JsonML {
  const meta = JsonMLHasAttributes(elem) ? elem.slice(0, 2) : elem.slice(0, 1);
  const ch = jsonMLGetChildren(elem);
  ch.splice(index, 0, ...children);
  return [...meta, ...ch] as unknown as JsonML;
}

export function jsonMLIsEmptyContent(content: JsonML): boolean {
  const children = jsonMLGetChildren(content);
  if (children.length > 1) {
    return false;
  }

  const child = children[0];
  if (!child) {
    return true;
  }

  if (jsonMLGetTagName(child) !== 'p') {
    return false;
  }

  const grandChildren = jsonMLGetChildren(child);
  if (grandChildren.length > 1) {
    return false;
  }

  const grandChild = grandChildren[0];
  const grandChildAttrs = jsonMLGetAttributes(grandChild);
  if (grandChildAttrs && grandChildAttrs['data-type'] !== 'text') {
    return false;
  }

  const grandGrandChildren = jsonMLGetChildren(grandChild);
  if (grandGrandChildren.length > 1) {
    return false;
  }

  const grandGrandChild = grandGrandChildren[0];
  if (grandGrandChild) {
    const grandGrandChildAttrs = jsonMLGetAttributes(grandGrandChild);
    if (grandGrandChildAttrs && grandGrandChildAttrs['data-type'] !== 'leaf') {
      return false;
    }

    if (jsonMLGetChildren(grandGrandChild).join('')) {
      return false;
    }
  }

  return true;
}

/**
 * 读取 asl 中的纯文本（leaf 节点）
 * @param jsonML
 */
export function jsonMLGetText(jsonML: JsonML): string {
  // 如果当前节点是叶子节点，则直接返回字符串
  const attrs = jsonMLGetAttributes(jsonML);
  if (attrs && attrs['data-type'] === 'leaf') {
    return jsonML[2] as string || '';
  }
  // 遍历孩子节点
  const children = jsonMLGetChildren(jsonML);
  return children.reduce((text, child) => {
    return text + jsonMLGetText(child);
  }, '');
}

export function jsonMLIsText(node: JsonML): boolean {
  return (
    jsonMLGetTagName(node) === 'span' && jsonMLGetAttributes(node)?.['data-type'] === 'text'
  );
}
