import Document from '../../../models/document';
import Selection from '../../../models/selection';
import { Selector } from '../../../constants';

/**
 * 从 cangjie-leaf 中获取 rects
 */
export function getRectsFromLeaf(
  element: HTMLElement,
  selection: Selection,
  document: Document,
): DOMRect[] {
  const { start, end } = selection.convertToTextPoints(document);
  const [keyStr, leafOffsetStr] =
    element.getAttribute(Selector.key)?.split(':') || [];
  const textNode = element.firstChild;
  if (!keyStr || !leafOffsetStr || !textNode) {
    return [];
  }

  // 检测 leaf 所在的 text 是否在选区内
  if (!document.isNodeInRange(keyStr, selection)) {
    return [];
  }

  const textLength = textNode.textContent?.length || 0;
  const leafOffset = parseInt(leafOffsetStr, 10);

  // 检测 leaf 是否在选区内
  if (keyStr === start.key && start.offset > leafOffset + textLength) {
    return [];
  }
  if (keyStr === end.key && end.offset < leafOffset) {
    return [];
  }

  // 是否是一半的 leaf
  let startOffset = -1;
  let endOffset = -1;
  if (
    keyStr === start.key &&
    start.offset >= leafOffset &&
    start.offset <= leafOffset + textLength
  ) {
    startOffset = start.offset - leafOffset;
  }
  if (
    keyStr === end.key &&
    end.offset >= leafOffset &&
    end.offset <= leafOffset + textLength
  ) {
    endOffset = end.offset - leafOffset;
  }
  if (startOffset >= 0 || endOffset >= 0) {
    try {
      const range = window.document.createRange();
      range.setStart(textNode, Math.max(startOffset, 0));
      range.setEnd(textNode, endOffset < 0 ? textLength : endOffset);
      return Array.from(range.getClientRects());
    } catch (error) {
      return [];
    }
  }

  // 到这里，肯定是完整的 leaf
  return Array.from(element.getClientRects());
}
