import Selection from '../../models/selection';
import findDOMRange from '../../utils/dom/findDOMRange';
import Controller from '../../controller/index';
import { findCaretPosition } from '../../utils/dom/findCaretPosition';
import { findDOMNodeSafely } from '../../utils/dom/findDOMNode';

const ARROW_SIZE = 10;
const POPUP_MIN_SPACE = 6;
const TOP_SAFE_SPACE = 42;

export type PopupPosition = {
  x: number; // client x
  y: number; // client y
  isTop: boolean; // is on top of the selection
  arrowOffset: number; // arrow offset x
};

function getSelectionRect(selection: Selection, controller: Controller) {
  let range: Range | null = null;
  try {
    // get the dom rectangles from the selction
    range = findDOMRange(selection, controller);
  } catch (_) {
    return null;
  }
  if (!range) {
    return null;
  }
  const domRects = Array.from(range.getClientRects());

  if (!domRects.length) {
    return null;
  }

  const { left, right, top, bottom } = domRects[0];
  // compute the rectangle area
  const rect = Array.from(domRects).reduce(
    (obj, item) => {
      obj.left = Math.min(item.left, obj.left);
      obj.top = Math.min(item.top, obj.top);
      obj.right = Math.max(item.right, obj.right);
      obj.bottom = Math.max(item.bottom, obj.bottom);
      return obj;
    },
    { left, right, top, bottom },
  );
  return rect;
}

function getCaretRect(selection: Selection, root?: Element) {
  const preferredEdge = selection.isHanging ? 'start' : 'end';
  const position = findCaretPosition(selection.anchor, preferredEdge, root);
  if (!position) {
    return null;
  }
  const { clientLeft: left, clientTop: top, height } = position;
  const right = left;
  const bottom = top + height;
  return {
    left,
    right,
    top,
    bottom,
  };
}

export const getPopupPosition = (
  selection: Selection,
  controller: Controller,
  popupWidth: number,
  popupHeight: number,
  defaultTop = true,
): PopupPosition | null => {
  const { document } = controller.value;
  const root = findDOMNodeSafely(document.key);
  const rect = selection.isCollapsed
    ? getCaretRect(selection, root)
    : getSelectionRect(selection, controller);
  if (!rect) {
    return null;
  }
  // default position is on top of selection
  let isTop = defaultTop;
  // move to bottom if the top is reaching out of screen
  if (rect.top - (popupHeight + POPUP_MIN_SPACE) < TOP_SAFE_SPACE) {
    isTop = false;
  }
  // special case when the user select very long text that exceed screen bottom
  if (rect.bottom + (popupHeight + POPUP_MIN_SPACE) > window.innerHeight) {
    isTop = true;
  }

  // default arrow is at the middle
  let arrowOffset = popupWidth / 2 - ARROW_SIZE / 2;
  let x = (rect.left + rect.right) / 2 - popupWidth / 2;
  let y = isTop
    ? rect.top - popupHeight - ARROW_SIZE
    : rect.bottom + ARROW_SIZE;

  // if popup is exceed the left/right edge, move it by the [offsetX] value
  let offsetX = 0;
  if (x < POPUP_MIN_SPACE) {
    offsetX = POPUP_MIN_SPACE - x;
  }
  if (x + popupWidth > window.innerWidth - POPUP_MIN_SPACE) {
    offsetX = window.innerWidth - x - popupWidth - POPUP_MIN_SPACE;
  }
  if (offsetX !== 0) {
    x += offsetX;
    arrowOffset -= offsetX;
  }

  arrowOffset = Math.min(
    popupWidth - ARROW_SIZE * 2,
    Math.max(ARROW_SIZE, arrowOffset),
  );

  y = Math.max(y, TOP_SAFE_SPACE);

  return { x, y, isTop, arrowOffset };
};
