import Value from '../models/value';
import CangjieDataTransfer from '../models/dataTransfer';
import { MIME_TYPES } from '../constants';
import { encodeFragment } from './transfer';
import Document from '../models/document';
import Element from '../models/element';
import { isEmptyObj, omitDeepBy } from './omitEmpty';
import Controller from '../controller';
import { UserData } from '../controller/userData';

export default function createClipboardDataFromSelection(controller: Controller) {
  const { value, uid } = controller;
  const { document, selection } = value;
  let fragment = document.getFragmentAtRange(selection); 
  fragment = withUserData(fragment, document.data?.userData, uid);
  const dataTransfer = CangjieDataTransfer.create();
  const text = fragment.nodes.reduce((acc, node) => {
    return `${acc}\n${node.text}`;
  }, '').slice(1);
  
  return dataTransfer
    .setData(MIME_TYPES.FRAGMENT, encodeFragment(fragment))
    .setData(MIME_TYPES.TEXT, text);
}

/**
 * 将节点的用户数据复制到 fragment 中
 * @param fragment 
 * @param userData 
 * @param uid 
 * @returns 
 */
export function withUserData (fragment: Document, userData?: UserData, uid?: string) {
  const originData = uid && userData?.[uid];
  if (originData) {
    const nextData = {};
    // 遍历 fragment 子节点，取出所有子节点的用户数据
    fragment.forEachDescendant((node) => {
      const uuid = Element.isElement(node) ? node.data?.uuid : null;
      if (uuid && originData?.[uuid]) {
        nextData[uuid] = originData?.[uuid];
      }
    })
    // 用户数据复制到 fragment 中
    if (Object.keys(nextData).length > 0) {
      return fragment.set('data', omitDeepBy({
        ...fragment.data,
        userData: { [uid]: nextData },
      }, isEmptyObj))
    }
  }
  return fragment;
}
