/**
 * @packageDocumentation
 * @ignore
 */
import equal from 'fast-deep-equal';

type EqualFunc<T> = (a: T, b: T) => boolean;

export default {
  add<T>(as: T[], b: T) {
    const index = as.findIndex(a => equal(a, b));
    const newAs = [...as];
    if (index !== -1) {
      newAs.splice(index, 1);
    }
    newAs.push(b);
    return newAs;
  },

  remove<T>(as: T[], b: T) {
    return as.filter(a => !equal(a, b));
  },

  has<T>(as: T[], b: T, isEqual: EqualFunc<T> = equal) {
    return as.some((a) => isEqual(a, b));
  },

  intersect<T>(as: T[], bs: T[]) {
    return as.filter((a => bs.some(b => equal(a, b))));
  },

  union<T>(as: T[], bs: T[], isEqual: EqualFunc<T> = equal) {
    return bs.reduce((merged, b) => {
      if (this.has(merged, b, isEqual)) {
        return merged;
      }
      merged.push(b);
      return merged;
    }, [...as]);
  },

  removeDuplicate<T>(as: T[]) {
    return as.filter((a, index) => 
      index === as.findIndex(b => equal(b, a))
    );
  },

  isSuperSet(as: any[], bs: any[]) {
    return bs.every(b => as.findIndex(a => equal(a, b)) !== -1)
  },
}
