// eslint-disable-next-line import/no-extraneous-dependencies
/**
 * @packageDocumentation
 * @ignore
 */
import * as React from 'react';
import ReactDOM from 'react-dom';
import useController from '../controller/useController';
import { TextPoint, EdgePoint } from '../models/point';
import Selection from '../models/selection';
import { useZoom, useZoomContainer } from '../hooks/useZoom';
import { IS_IOS, IS_IPAD, IS_MOBILE } from '../utils/environment';
import { findCaretPosition } from '../utils/dom/findCaretPosition';
import { pointAtDistance } from '../queries/selection';
import { Selector } from '../constants';

const SHOULD_FOLLOW_CARET = !IS_IOS || IS_IPAD;

const textAreaProps = IS_MOBILE ? {} : { spellCheck: false };

const commonStyle: React.CSSProperties = {
  position: 'fixed',
  overflow: 'hidden',
  width: 1,
  height: 1,
  lineHeight: 1,
  whiteSpace: 'pre-wrap',
  zIndex: -1,
  opacity: 0,
  background: 'transparent',
  color: 'transparent',
  outline: 'none',
  caretColor: 'transparent',
  willChange: 'left,top',
};

//@ts-ignore
if (process.env.NODE_ENV !== 'uitest') {
  // https://developer.mozilla.org/en-US/docs/Web/CSS/contain
  commonStyle.contain = 'strict';
}

const wrapperStyle: React.CSSProperties = SHOULD_FOLLOW_CARET ? commonStyle : {
  ...commonStyle,
  top: 0,
  left: -99,
  // 修复iOS we-web下输入文字时滚动问题
  // height: 'auto',
};

const textAreaStyle: React.CSSProperties = {
  fontSize: 'inherit',
  lineHeight: 1,
  padding: 0,
  border: 'none',

  // [IMPORTANT]
  // 1. iOS@<13 need to preserve `\n` to detect `moveUpward` & `moveDownward`,
  //    see: cangjie/src/hooks/useSelectionCalculator.ts
  // 2. Desktop need `nowrap` to prevent that IME positioning incorrectly
  whiteSpace: IS_IOS ? 'pre-wrap' : 'nowrap',

  // [IMPORTANT]
  // 1. Textarea will enlarge ancestors' rectangle when width is `auto`.
  // 2. `useInputDetector` will treat inputting as deleting if width is smaller than `1em`.
  width: '1em',
};

function usePointPosition(
  textareaWrapperRef: React.RefObject<HTMLDivElement>,
  point: EdgePoint | TextPoint | undefined,
  zoom: number,
) {
  const controller = useController();
  const container = useZoomContainer();
  React.useEffect(() => {
    if (!SHOULD_FOLLOW_CARET || !point) {
      return;
    }
    const textareaWrapper = textareaWrapperRef.current;
    if (!textareaWrapper) {
      return;
    }
    if (!container) {
      return;
    }
    const { isHanging } = controller.value.selection;
    const preferredEdge = isHanging ? 'start' : 'end';
    const rect = findCaretPosition(point, preferredEdge, container);
    if (!rect) {
      return;
    }
    const { clientLeft, clientTop } = rect;
    textareaWrapper.style.top = `${clientTop}px`;
    textareaWrapper.style.left = `${clientLeft}px`;
    textareaWrapper.style.fontSize = `${rect.height / zoom}px`;
  });
}

interface HiddenTextAreaProps
  extends React.TextareaHTMLAttributes<HTMLTextAreaElement> {
  selection: Selection;
}

const HiddenTextArea = React.forwardRef<
  HTMLTextAreaElement,
  HiddenTextAreaProps
>((props, ref) => {
  const { selection, ...rest } = props;
  const wrapperRef = React.createRef<HTMLDivElement>();
  const zoom = useZoom();
  const controller = useController();

  let { anchor: point } = selection;

  // if the target point is inside a void element, take the previous point
  const { document } = controller.value;
  const parentNode = document.getParent(point.key);
  if (parentNode && controller.query('isVoid', parentNode)) {
    point = controller.query(pointAtDistance, point, -1);
  }

  usePointPosition(wrapperRef, point, zoom);

  const children = (
    <div ref={wrapperRef} style={wrapperStyle} data-cangjie-hidden>
      <textarea
        {...rest}
        {...textAreaProps}
        ref={ref}
        rows={1}
        style={textAreaStyle}
        data-cangjie-input
        // https://aone.alibaba-inc.com/v2/project/585561/bug/45423783#
        // iOS 15+ 有时会出现输入英文时页面 crash 然后 reload
        // 从现象上看，若关闭了系统输入法的「自动改正」开关，就不会出现
        // 因此这里加个 Non-standard Attribute 做个防御
        autoCorrect="off"
      />
    </div>
  );
  return ReactDOM.createPortal(children, window.document.body);
});

export default HiddenTextArea;
