import * as React from 'react';
import Controller from '../../controller';
import Selection from '../../models/selection';
import Node from '../../models/node';
import RenderNodeProps from '../../interfaces/renderNodeProps';
import { useZoom } from '../../hooks/useZoom';
import { useSelectionData } from '../../hooks/useSelectionData';
import { useSelectingHots } from '../../hooks/useHots';
import * as domUtils from '../../utils/dom';
import useElementVisibility, { hasIntersectionObserver } from './useElementVisibility';

export enum LazyRenderStrategyType {
  LazyLoad = 'lazyload',
  Pruning = 'pruning',
}

interface LazyRenderStrategy {
  type?: LazyRenderStrategyType;
  forceVisible?: boolean;
  placeholder?: React.ReactElement | null;
  size?: {
    width: number;
    height: number;
  };
}

interface LazyLoadWrapperProps {
  node: Node;
  placeholder?: React.ReactElement | null;
  size?: {
    height: number;
    width?: number;
  };
  children: React.ReactElement;
}
function LazyLoadWrapper({ node, placeholder = null, size, children }: LazyLoadWrapperProps) {
  const placeholderRef = React.useRef<HTMLDivElement | null>(null);
  const [visible, unobserve] = useElementVisibility(placeholderRef);
  if (visible) {
    unobserve();
  }
  if (!visible) {
    return (
      <div
        ref={placeholderRef}
        data-cangjie-key={node.key}
        data-cangjie-lazy-load
        style={size}
      >
        {placeholder}
      </div>
    );
  }
  return children;
}

interface PruningManagerProps {
  controller: Controller;
  children: React.ReactElement;
}
function PruningManager({ controller, children }: PruningManagerProps) {
  const selectionUpdator = React.useCallback(
    ({ range }: { range: Selection }) => {
      if (range.isExpanded) {
        return;
      }
      const point = range.focus;
      const key = point.isTextPoint() ? `${point.key}:0` : point.key;
      const dom = domUtils.findDOMNodeSafely(key);
      if (dom) {
        showPrunedElements(dom);
      }
    },
    [controller],
  );

  const onHots = React.useCallback(() => {
    selectionUpdator({ range: controller.value.selection });
  }, [controller, selectionUpdator]);

  useSelectionData(controller.selectionData$, null, selectionUpdator, []);
  useSelectingHots(controller, onHots);

  return children;
}

interface PruningWrapperProps {
  controller: Controller;
  node: Node;
  isSelected: boolean;
  forceVisible: boolean;
  children: React.ReactElement;
}
function PruningWrapper(props: PruningWrapperProps) {
  const zoom = useZoom();
  const { controller, node, forceVisible, children } = props;
  const [isSelected, setIsSelected] = React.useState(props.isSelected);
  const placeholderRef = React.useRef<HTMLDivElement | null>(null);
  const [placeholderStyle, setPlaceholderStyle] = React.useState<React.CSSProperties | undefined>();
  const [isElementVisible] = useElementVisibility(placeholderRef, true);
  const visible = isElementVisible || isSelected || forceVisible;
  const prevVisible = React.useRef<boolean>(visible);

  const onHots = React.useCallback(() => {
    const { selection } = controller.value;
    setIsSelected(controller.view.isNodeInRange(node.key, selection));
  }, [controller, node.key]);

  useSelectionData(controller.selectionData$, null, onHots, []);
  useSelectingHots(controller, onHots);

  React.useLayoutEffect(() => {
    if (prevVisible.current === visible) {
      return;
    }

    if (prevVisible.current && !visible) {
      const placeholder = placeholderRef.current;
      if (!placeholder) {
        return;
      }
      const placeholderClientRect = placeholder.getBoundingClientRect();
      // HACK: `IntersectionObserver` cannot detect zero height element in Safari iframe.
      const height = placeholderClientRect.height || 1;
      // FIX: thrashing
      //   1. When placeholder is visible
      //   2. Remove `display: none` of placeholder
      //   3. Descendant's margin will push placeholder out of view
      // So placeholder will thrash if `margin-top` & `margin-bottom` is incorrect.
      const marginTop = placeholder.previousElementSibling
        ? placeholderClientRect.top - placeholder.previousElementSibling.getBoundingClientRect().bottom
        : 0;
      const marginBottom = placeholder.nextElementSibling
        ? placeholder.nextElementSibling.getBoundingClientRect().top - placeholderClientRect.bottom
        : 0;
      setPlaceholderStyle({
        height: height / zoom,
        marginTop: marginTop / zoom,
        marginBottom: marginBottom / zoom,
      });
    } else { // !prevVisible.current && visible
      setPlaceholderStyle(undefined);
    }
    prevVisible.current = visible;
  }, [visible, zoom]);

  return (
    <div
      ref={placeholderRef}
      data-cangjie-perf-pruning={!visible}
      style={placeholderStyle}
    >
      <div
        style={placeholderStyle && { display: 'none' }}
      >
        {children}
      </div>
    </div>
  );
}

const DATA_CANGJIE_PERF_PRUNING = 'data-cangjie-perf-pruning';
function showPrunedElements(target: Element) {
  const ancestorsWithPruning: Element[] = [];
  let ancestor: Element | null | undefined = target;
  while (ancestor) {
    if (ancestor.getAttribute(DATA_CANGJIE_PERF_PRUNING) === 'true') {
      ancestorsWithPruning.push(ancestor);
    }
    ancestor = ancestor.parentElement;
  }
  ancestorsWithPruning.forEach((node) => {
    if (node instanceof HTMLElement) {
      node.style.height = '';
      node.style.marginTop = '';
      node.style.marginBottom = '';
      node.setAttribute(DATA_CANGJIE_PERF_PRUNING, 'false');
    }
    const { firstElementChild } = node;
    if (firstElementChild instanceof HTMLElement) {
      firstElementChild.style.display = '';
    }
  });
}

interface PerfLazyRenderPluginConfig {
  lazyRenderStrategy?: (controller: Controller, node: Node) => LazyRenderStrategy;
}
function createPerfLazyRenderPlugin(config: PerfLazyRenderPluginConfig = {}) {
  if (!hasIntersectionObserver) {
    return {};
  }

  function renderEditable(_, controller: Controller, next) {
    return (
      <PruningManager controller={controller}>
        {next()}
      </PruningManager>
    );
  }

  function renderNode({ node, isSelected }: RenderNodeProps, controller: Controller, next) {
    const strategy = controller.query('lazyRenderStrategy', node) as LazyRenderStrategy || {};
    const { type = LazyRenderStrategyType.LazyLoad } = strategy;
    if (controller.enableVirtualize) {
      return next();
    }
    if (type === LazyRenderStrategyType.LazyLoad && (strategy.placeholder || strategy.size)) {
      const size = !strategy.size ? undefined : {
        width: strategy.size.width,
        // HACK: `IntersectionObserver` cannot detect zero height element in Safari iframe.
        height: strategy.size.height || 1,
      };
      return (
        <LazyLoadWrapper
          node={node}
          placeholder={strategy.placeholder}
          size={size}
        >
          {next()}
        </LazyLoadWrapper>
      );
    }
    if (type === LazyRenderStrategyType.Pruning) {
      return (
        <PruningWrapper
          controller={controller}
          node={node}
          isSelected={isSelected}
          forceVisible={strategy.forceVisible || false}
        >
          {next()}
        </PruningWrapper>
      );
    }
    return next();
  }

  if (config.lazyRenderStrategy) {
    return {
      queries: { lazyRenderStrategy: config.lazyRenderStrategy },
      renderEditable,
      renderNode,
    };
  }
  return {
    renderEditable,
    renderNode,
  };
}
createPerfLazyRenderPlugin.factory = (defaultConfig = {}) => (config = {}) =>
  createPerfLazyRenderPlugin({ ...defaultConfig, ...config });
createPerfLazyRenderPlugin.LazyRenderStrategyType = LazyRenderStrategyType;
createPerfLazyRenderPlugin.showPrunedElements = showPrunedElements;
createPerfLazyRenderPlugin.useElementVisibility = useElementVisibility;

export default createPerfLazyRenderPlugin;
