import { Node, environment } from '../../';
import { jsonMLAppend } from '../utils/jsonml';
import type { MoSerializationPlugin, JsonML } from '../type';
import { isElementValue } from '../utils';

function safeAppend(elem: JsonML, children: JsonML[]) {
  // BACKGROUND: Safari 下，`...children` 结果可能出现 null child
  if (environment.IS_SAFARI) {
    return (elem.slice(0).concat(children.slice(0)) as unknown) as JsonML;
  }
  return jsonMLAppend(elem, ...children);
}

export function createAsyncNodeToJsonML(plugins: MoSerializationPlugin[]) {
  const rules = plugins
    .map((plugin) => plugin.valueToJsonML!)
    .filter((rule) => !!rule);

  /**
   * 序列化 node to jsonML
   * @param node
   */
  async function nodeToJsonML(node: Node): Promise<JsonML> {
    const rule = rules.find((r) => r.match(node));
    if (!rule) {
      const { klass, type } = node.toJSON();
      throw new Error(
        `Cannot find any rule which match ${JSON.stringify({ klass, type })}`,
      );
    }

    const decorated = rule.decorate ? rule.decorate(node) : node;
    let jsonML: JsonML | undefined;
    if (rule.asyncConvert) {
      jsonML = await rule.asyncConvert(decorated);
    } else {
      jsonML = rule.convert(decorated);
    }

    if (!jsonML) {
      const { klass, type } = node.toJSON();
      throw new Error(
        `Invalid JsonML! JsonML is ${JSON.stringify({ klass, type })}`,
      );
    }

    // Only element value has nodes children
    if (isElementValue(decorated)) {
      const children = await Promise.all(decorated.nodes.map((child) => nodeToJsonML(child)));
      return safeAppend(jsonML, children);
    }

    return jsonML;
  }

  return nodeToJsonML;
}

export default function createNodeToJsonML(plugins: MoSerializationPlugin[]) {
  const rules = plugins
    .map((plugin) => plugin.valueToJsonML!)
    .filter((rule) => !!rule);

  /**
   * 序列化 node to jsonML
   * @param node
   */
  function nodeToJsonML(node: Node): JsonML {
    const rule = rules.find((r) => r.match(node));
    if (!rule) {
      const { klass, type } = node.toJSON();
      throw new Error(
        `Cannot find any rule which match ${JSON.stringify({ klass, type })}`,
      );
    }

    const decorated = rule.decorate ? rule.decorate(node) : node;
    const jsonML = rule.convert(decorated);

    if (!jsonML) {
      const { klass, type } = node.toJSON();
      throw new Error(
        `Invalid JsonML! JsonML is ${JSON.stringify({ klass, type })}`,
      );
    }

    // Only element value has nodes children
    if (isElementValue(decorated)) {
      const children = decorated.nodes.map((child) => nodeToJsonML(child));
      return safeAppend(jsonML, children);
    }

    return jsonML;
  }

  return nodeToJsonML;
}
