import type { Node } from '../../';
import { jsonMLGetChildren } from '../utils/jsonml';
import type { MoSerializationPlugin, JsonML } from '../type';
import { isElementValue } from '../utils';

export default function createJsonMLToNode(plugins: MoSerializationPlugin[]) {
  const rules = plugins
    .map((plugin) => plugin.jsonMLToValue!)
    .filter((rule) => !!rule);

  return function jsonMLToNode(jsonML: JsonML, parent?: JsonML): Node {
    const rule = rules.find((r) => r.match(jsonML, parent));
    if (!rule) {
      throw new Error(`Cannot find any rule which match ${JSON.stringify(jsonML, null, 2)}`);
    }

    let node = rule.convert(jsonML, parent);

    // Only element value has nodes children
    if (isElementValue(node)) {
      const children = jsonMLGetChildren(jsonML);
      node = node.merge({ nodes: children.map((c) => jsonMLToNode(c, jsonML)) });
    }

    node = rule.decorate ? rule.decorate(node) : node;

    return node;
  };
}
