import type { MarkProperties, default as Mark } from './../../../models/mark';
import type { JsonMLToValueRule, LeafRule, TextMark } from './../../type/rules';
import Text from '../../../models/text';
import {
  JsonML,
  jsonMLGetAttributes,
  jsonMLGetChildren,
  jsonMLIsText,
} from '../../utils/jsonml';

function attrsToMarks(attrs: TextMark, converts: Array<NonNullable<LeafRule['jsonMLAttrsToMarks']>>): Array<MarkProperties | Mark> {
  const handledKeys: Set<string> = new Set();
  const marks: Array<MarkProperties | Mark> = converts.reduce<Array<MarkProperties | Mark>>((prev, convert) => {
    const rst = convert(attrs);
    if (!rst) return [...prev];

    const { keys, marks } = rst;
    keys.forEach(k => handledKeys.add(k));
    return [
      ...prev,
      ...marks,
    ];
  }, []);

  const restKeys = Object.keys(attrs).filter(k => k !== 'data-type' && !handledKeys.has(k));

  restKeys.forEach((key) => {
    const data = attrs[key];
    let markData: string | object = data;
    const isObj = typeof data === 'object' && !!data && !Array.isArray(data);
    if (!isObj) {
      // not a object
      markData = { value: data };
    }
    marks.push({ type: key, data: markData as Record<string, any> });
  });

  return marks;
}

export function createJsonMLToValue(attrToMarks: Array<LeafRule['jsonMLAttrsToMarks']>): JsonMLToValueRule {
  const converts = attrToMarks.filter((f): f is NonNullable<LeafRule['jsonMLAttrsToMarks']> => !!f)

  const rule: JsonMLToValueRule = {
    name: 'text',
  
    match: (node: JsonML): boolean => {
      return jsonMLIsText(node);
    },
  
    convert: (node: JsonML): Text => {
      return Text.create({
        // @ts-ignore 
        leaves: jsonMLGetChildren(node).map((leaf: JsonML) => {
          const marks = attrsToMarks(jsonMLGetAttributes(leaf)! as TextMark, converts)
          return {
            text: String(jsonMLGetChildren(leaf)[0]),
            marks
          }
        }),
      });
    },
  }
  return rule;
}
