/* eslint-disable prefer-arrow-callback */
import { useEffect } from 'react';
import Controller from '../controller';
import { PendingType } from '../interfaces/controller';
import { PerfType } from '../interfaces/perf';
import Selection from '../models/selection';

function isDelayed(selection: Selection, controller: Controller) {
  return selection !== controller.value.selection;
}

function usePerf(controller: Controller, noTrack?: boolean) {
  /********************************************************************************************\
  * 正常绘制流程                                                                                 *
  *                 +---------+    +---------+   +---------+   +---------+   +----------+      *
  *  ---------------+ keydown +----+ render  +---+   rAF   +---+  paint  +---+setTimeout+----> *
  * Timeline        +---------+    +---------+   +---------+   +---------+   +----------+      *
  *                                                                                            *
  \********************************************************************************************/

  /*************************************************************************************************************\
  * 丢帧场景                                                                                                     *
  *                                +--------------+                                                             *
  *                          +---->| 未被绘制，丢帧 |                                                             *
  *                          |     +--------------+                                                             *
  *                          |                                                                                  *
  *            +--------------------------+                                                                     *
  *            | +---------+  +---------+ | +----------+  +---------+  +---------+ +---------+  +---------+     *
  *  ----------+-+ keydown +--+ render  +-+-+setTimeout+--+ keydown +--+ render  +-+   rAF   +--+  paint  +---> *
  * Timeline   | +---------+  +---------+ | +----------+  +---------+  +---------+ +---------+  +---------+     *
  *            +--------------------------+                                                                     *
  *                                                                                                             *
  \*************************************************************************************************************/
  useEffect(() => {
    if (noTrack) {
      return;
    }
    // 如果是 pending 期间的选区更新，进行输入统计
    const { pendingType } = controller;
    if (
      pendingType === PendingType.input ||
      pendingType === PendingType.selection
    ) {
      const { selection } = controller.value;
      let dropped = true;
      window.requestAnimationFrame(function preparePaint() {
        // 在下一次 repaint 发生前，若光标不再等于组件 update 时的光标，则认为丢帧
        dropped = isDelayed(selection, controller);
        const perfType =
          pendingType === PendingType.input
            ? controller.value.composing
              ? PerfType.composing
              : PerfType.input
            : PerfType.selection;
        controller.perfEnd(perfType, dropped);
      });
    }
  });
}

export default usePerf;
