/**
 * @packageDocumentation
 * @module domUtils
 */
import Controller from '../../controller/index';
import { TextPoint } from '../../models/point';
import { CANGJIE_EMPTY, Selector } from '../../constants';
import keyUtils from '../keyUtils';
import findClosestInContent from './findClosestInContent';

/**
 * 根据浏览器的节点和位置获取仓颉对应的 Point。
 */
function findPoint(
  nativeNode: Node | null,
  nativeOffset: number,
  controller: Controller,
) {
  if (!nativeNode) {
    return null;
  }

  const node =
    nativeNode instanceof Text
      ? nativeNode.parentElement
      : nativeNode;
  if (!(node instanceof Element)) {
    return null;
  }

  const { document } = controller.value;

  let leaf: Element | null;
  if (node.getAttribute(Selector.void)) {
    // 原本这里有一层「判断 node 内部是否存在 leaf 节点，不存在才继续」的逻辑
    // 由于 hetu 等插件内部可能嵌套了编辑器，且 key 不可控，可能与宿主重复，造成问题（选区异常 or 页面奔溃）
    // 因此这里去掉 `!isLeaf` 判断
    const key = node.getAttribute(Selector.key);
    if (!key) {
      return null;
    }
    const voidNode = document.getNode(key);
    if (!voidNode) {
      return null;
    }
    const text = voidNode.getFirstText();
    if (!text) {
      return null;
    }
    return TextPoint.create({
      key: text.key,
      offset: 0,
    });
  } else {
    leaf = node.getAttribute(`[${Selector.leaf}]`)
      ? node
      : findClosestInContent(node, `[${Selector.leaf}]`);
  }

  if (!leaf) {
    return null;
  }

  const [key, offset] = keyUtils.parse(
    leaf.getAttribute(Selector.key) || '',
  );

  const text = document.getNode(key);
  if (!text) {
    return null;
  }
  // NOTE: what if user data has just one `CANGJIE_EMPTY` character?
  const nativeText = leaf.textContent && leaf.textContent !== CANGJIE_EMPTY
    ? leaf.textContent.slice(0, nativeOffset)
    : '';
  return TextPoint.create({
    key,
    offset: offset + nativeText.length,
  });
}

export default findPoint;
