import React from 'react';
import Decoration from '../../models/decoration';
import Document from '../../models/document';
import Injection from '../../models/injection';

export interface RelativeRect extends Pick<DOMRect, 'top' | 'left' | 'width' | 'height'> {
  scrollLeft: number;
}

/**
 * 用于选区定位的依赖项
 *
 * @export
 * @interface SelectionContext
 */
export interface SelectionContext {
  contentRect?: DOMRect;
  // 选区相对定位的目标元素容器的 Rect，一般用于 scrollable 元素
  relativeRect?: RelativeRect;
  // 缩放比例（eg. 通过缩放工具栏可改变其值）
  zoom: number;
  // 纸张尺寸变化的标志（eg. 通过浏览器自带缩放功能可改变其值）
  resizeFlag: number;

  // 其他选区重渲的依赖项
  document: Document;
  injections: Injection[];
  decorations: Decoration[];
}

export const Context = React.createContext<SelectionContext | null>(null);

/**
 * @deprecated
 */
export function useSelectionContext(): SelectionContext {
  const context = React.useContext(Context);

  if (!context) {
    throw new Error('SelectionContext not found.');
  }

  return context;
}
