import { Selector } from "../../constants";
import { findDOMNodeSafely } from './findDOMNode';

/**
 * 查找指定节点下的选区容器
 * @param root 查询的根节点
 * @param layerFor 查找的指定容器的 selection-layer，若为空则不指定具体值
 */
export function getSelectionLayers(root = window.document.body, layerFor = '') {
  const selector = layerFor ? `[${Selector.selectionLayer}="${layerFor}"]` : `[${Selector.selectionLayer}]`;
  return root.querySelectorAll(selector) || [];
}

/**
 * 获取本地光标 DOM
 * @param root
 * @returns
 */
export function getCaretDom(root = window.document.body) {
  const layers = getSelectionLayers(root);
  for (let i = layers.length - 1; i >= 0; i--) {
    const layer = layers[i];
    const target = layer.shadowRoot
      ? layer.shadowRoot.querySelector('[data-cangjie-caret="true"]')
      : layer.querySelector('[data-cangjie-caret="true"]');

    if (target) {
      return target;
    }
  }
  return undefined;
}

/**
 * 查找归属于特定 data-cangjie-key 的容器 DOM 节点内的选区根节点
 * @param containerKey 指定容器 key
 * @returns
 */
export function findSelectionSlot(containerKey: string): HTMLDivElement | null | undefined {
  const containerDOM = findDOMNodeSafely(containerKey);
  try {
    const selectionLayer = getSelectionLayers(containerDOM, containerKey)[0];
    const wrapper = selectionLayer.shadowRoot
      ? selectionLayer.shadowRoot.querySelector('div')
      : selectionLayer.querySelector('div');
    return wrapper;
  } catch (e) {
    console.warn(`[Selection] Get selection slot dom for [${containerKey}] failed`, e);
  }
  return null;
}
