import * as React from 'react';
import { GROUP_COLLAPSED_KEY } from '../constants';
import useGroupData from '../hooks/useGroupData';
import RenderGroupProps from '../interfaces/renderGroupProps';
import Element from '../models/element';

interface WrapperProps {
  node: Element;
}

const GroupWrapper: React.FC<WrapperProps> = (props) => {
  const { children, node } = props;
  const isCollapsed = useGroupData(
    node,
    (groups) => {
      const group = groups.find((g) => g.key === node.key);
      return group?.data[GROUP_COLLAPSED_KEY];
    },
    false,
  );

  return React.cloneElement(children as React.ReactElement, {
    isCollapsed,
  });
};

export default GroupWrapper;
