/**
 * @packageDocumentation
 * @module Events
 */
import CangjieDataTransfer from '../models/dataTransfer';

type CangjieClipboardEventType = 'cangjieCopy' | 'cangjieCut' | 'cangjiePaste';

interface CangjieClipboardEventOption {
  nativeEvent?: ClipboardEvent;
  clipboardData?: DataTransfer;
}

interface CangjieClipboardEvent extends CustomEvent {
  readonly nativeEvent: ClipboardEvent;
  readonly clipboardData?: CangjieDataTransfer;
  setClipboardData(clipboardData: CangjieDataTransfer): CangjieClipboardEvent;
}

/**
 * 剪切板事件
 */
function CangjieClipboardEvent(
  type: CangjieClipboardEventType,
  { nativeEvent, clipboardData }: CangjieClipboardEventOption = {},
): CangjieClipboardEvent {
  const event = new CustomEvent(type);
  
  /**
   * https://bugs.webkit.org/show_bug.cgi?id=49739
   * webkit bug, safari 10 及以下会报 ERROR: Attempted to assign to readonly property.
   */
  if (clipboardData) {
    // @ts-ignore
    event.clipboardData = CangjieDataTransfer.create(clipboardData)
  }
  // @ts-ignore
  return Object.assign(event, {
    nativeEvent,
    setClipboardData(clipboardData: CangjieDataTransfer) {
      // @ts-ignore
      event.clipboardData = clipboardData;
      return Object.create(this, {
        clipboardData: {
          value: clipboardData,
        },
      });
    },
  });
}

export default CangjieClipboardEvent;
