import { v1 as uuidv1 } from 'uuid';
import { nodeMapToObject, normalizeSpacesInHTML } from '../../utils/parseHtml';
import type State from './State';
import type { ParserOptions } from '../../type';

interface IHtmlParserProps {
  onOpenTag: Function;
  onText: Function;
  onCloseTag: Function;
}

const MeasureStyle: any = {
  left: '-999px',
  position: 'absolute',
  visibility: 'hidden',
  whiteSpace: 'nowrap',
  zIndex: -999,
};

export const createIframe = () => {
  const iframe = document.createElement('iframe');
  for (const type in MeasureStyle) {
    iframe.style[type] = MeasureStyle[type];
  }
  // 开启沙盒模式，防止 xss 攻击
  iframe.setAttribute('sandbox', 'allow-same-origin');
  return iframe;
}

class DomParser {
  private readonly domParser = new DOMParser();

  private doc?: Document | null;

  private readonly skipTags = ['HEAD', 'STYLE'];

  private state?: State;

  private onOpenTag?: Function | null;

  private onText?: Function | null;

  private onCloseTag?: Function | null;

  private iframe = createIframe();

  init = (callback: IHtmlParserProps, options: ParserOptions) => {
    const { onOpenTag, onText, onCloseTag } = callback;
    this.onOpenTag = onOpenTag;
    this.onText = onText;
    this.onCloseTag = onCloseTag;
    this.state = options.state;
  };

  write = (chunk: string) => {
    document.body.appendChild(this.iframe!);
    this.doc = this.iframe!.contentDocument as Document;
    if (this.doc.body) {
      this.doc.body.innerHTML = chunk;
    }
    if (this.state) {
      this.state.htmlDom = this.doc.body;
    }
    this.traverse(this.doc.body);

    document.body.removeChild(this.iframe!);

  };

  end = () => {
    this.doc = null;
    this.onOpenTag = null;
    this.onText = null;
    this.onCloseTag = null;
  };

  private readonly traverse = (node: Node) => {
    if (!this.onText || !this.onOpenTag || !this.onCloseTag) {
      return;
    }
    if (node.nodeType === Node.TEXT_NODE) {
      if (node.textContent && normalizeSpacesInHTML(node.textContent)) {
        this.onText(node.textContent);
      }
      return;
    }
    if (
      [
        Node.ELEMENT_NODE,
        Node.DOCUMENT_NODE,
        Node.DOCUMENT_FRAGMENT_NODE,
      ].includes(node.nodeType)
    ) {
      const { tagName: originTagName, attributes } = node as HTMLElement;
      if (this.skipTags.includes(originTagName)) {
        return;
      }
      const traverseChildNodes = () => {
        Array.from(node.childNodes || []).forEach((child) => {
          this.traverse(child);
        });
      };
      if (originTagName) {
        const tagName = originTagName.toLowerCase();
        const attrs = nodeMapToObject(attributes);
        const element = node as HTMLElement;
        attrs.styleObj = window.getComputedStyle(element);
        const id = uuidv1();
        if (this.state) {
          this.state.currentDom = element;
        }
        this.onOpenTag(tagName, attrs, id);
        traverseChildNodes();
        this.onCloseTag(tagName, attrs, id);
      } else {
        traverseChildNodes();
      }
    }
  };
}

export default DomParser;
