import { Controller, Value, Schema } from '../../';
import { jsonMLGetChildren, jsonMLAppend } from '../utils/jsonml';
import createJsonMLToNode from './jsonMLToNode';
import { isDocumentValue } from '../utils';
import type { JsonMLToValueOptions, JsonML } from '../type';
import createEmptyParagraph from '../utils/createEmptyParagraph';

export default function createJsonMLToValue(
  jsonMLToNode: ReturnType<typeof createJsonMLToNode>,
  strictSchema: Schema,
) {
  /**
   * 序列化整个 ASL 文档
   * @param jsonML content ASL
   * @param options 序列化参数
   */
  function jsonMLToValue(jsonML: JsonML, options: JsonMLToValueOptions = {}): Value {
    const children = jsonMLGetChildren(jsonML);
    let newJsonML = jsonML;
    if (!(children && children.length > 0)) {
      newJsonML = jsonMLAppend(jsonML, createEmptyParagraph());
    }

    const document = jsonMLToNode(newJsonML);
    // 非 document 节点直接抛错
    if (!isDocumentValue(document)) {
      throw new Error(`Invalid jsonML, expect a document at the root of jsonML, but received ${JSON.stringify(jsonML, null, 2)}`);
    }

    const value = Value.create({
      document,
    });

    const { normalize = true, strict = false } = options;
    const toNormalize = normalize || strict;
    if (!toNormalize) return value;

    // 加载一个 scheme plugin 对 value 进行 normalize
    const schemaPlugin = strict ? [{ schema: strictSchema, models: [] }] : [];
    const controller = Controller.create({ value, plugins: schemaPlugin });
    return controller.value;
  }

  return jsonMLToValue;
}
