/**
 * @packageDocumentation
 * @ignore
 */
import Controller from '../controller/index';
import Query from '../interfaces/query';
import QueryEvent from '../interfaces/queryEvent';

export default function createQueriesPlugin(
  qrys: Record<string, Query> = {},
) {
  // PERF: drop prototype
  const queries = Object.assign(Object.create(null), qrys);
  function onQuery(qry: QueryEvent, controller: Controller, next) {
    const { type, args } = qry;
    const query = queries[type];
    if (!query) {
      return next();
    }
    const ret = query(controller, ...args);
    return ret === undefined ? next() : ret;
  }

  return {
    models: [],
    onQuery,
  };
}
