import { Text, Node, Inline, Block, Element, Document } from '../../';
import type { JsonML } from '../type';
import { jsonMLGetAttributes, jsonMLGetTagName } from './jsonml';

/* *************************************
 *             JsonML Type
 ************************************** */

export function isTextJsonML(node: JsonML): boolean {
  const tag = jsonMLGetTagName(node);
  const attrs = jsonMLGetAttributes(node);
  return tag === 'span' && !!attrs && attrs['data-type'] === 'text';
}

export function isInlineJsonML(node: JsonML): boolean {
  const tagName = jsonMLGetTagName(node);
  return tagName === 'a' || tagName === 'img';
}

/* *************************************
 *             Value Type
 ************************************** */

export function isTextValue(node: Node): node is Text {
  return Text.isText(node);
}

export function isElementValue(node: Node): node is Element {
  return Element.isElement(node);
}

export function isInlineValue(node: Node): node is Inline {
  return Inline.isInline(node);
}

export function isBlockValue(node: Node): node is Block {
  return Block.isBlock(node);
}

export function isDocumentValue(node: Node): node is Document {
  return Document.isDocument(node);
}
