import Controller from '../../../controller';
import Selection from '../../../models/selection';
import { SelectionWrapperMap } from '../types';

export function splitSelectionAndGetWrapper(
  controller: Controller,
  selection: Selection,
  globalRef: React.MutableRefObject<HTMLDivElement | null>
) {
  const wrappers: SelectionWrapperMap = controller.query('getSelectionContainers', {
    selection: selection,
    defaultWrapper: globalRef.current!
  }) || new Map();
  if (wrappers.size < 1) {
    wrappers.set(selection, { wrapper: globalRef.current!, originSelection: selection });
    return wrappers;
  }
  const res: SelectionWrapperMap = new Map();
  wrappers.forEach((v, k) => {
    res.set(k, { ...v, wrapper: v.wrapper || globalRef.current! });
  });
  return res;
}