import { createHtmlParser } from '@ali/cangjie-lib-for-tree-shaking';
import { v1 as uuidv1 } from 'uuid';
import { toStyleObject, normalizeSpacesInHTML } from '../../utils/parseHtml';
import type { ParserOptions } from '../../type';

interface TagMeta {
  name: string;
  attrs: object;
  id: string;
}

interface ParserCallbacks {
  onOpenTag: (name: string, attrs: object, id: string) => void;
  onText: (text: string) => void;
  onCloseTag: (name: string, attrs: object, id: string) => void;
}

export default class Parser {
  private readonly skipTags: string[] = ['head', 'style'];

  private isInSkipTag = false;

  private readonly stack: TagMeta[] = [];

  private callbacks?: ParserCallbacks;

  private parser?: ReturnType<typeof createHtmlParser>;

  init = (callbacks: ParserCallbacks, options: ParserOptions) => {
    this.callbacks = callbacks;

    this.parser = createHtmlParser(
      {
        onopentag: this.onOpenTag,
        ontext: this.onText,
        // @ts-ignore
        onclosetag: this.onCloseTag,
      },
      options,
    );
  };

  onOpenTag = (name: string, attrs: object): void => {
    if (this.skipTags.includes(name)) {
      this.isInSkipTag = true;
      return;
    }

    if (this.isInSkipTag) return;

    // @ts-ignore
    attrs.styleObj = toStyleObject(attrs.style);

    const id = uuidv1();
    this.callbacks?.onOpenTag(name, attrs, id);
    this.stack.push({ name, attrs, id });
  };

  onText = (text: string): void => {
    if (this.isInSkipTag || !normalizeSpacesInHTML(text)) return;
    this.callbacks?.onText(text);
  };

  onCloseTag = (name: string): void => {
    if (this.skipTags.includes(name)) {
      this.isInSkipTag = false;
      return;
    }

    if (this.isInSkipTag) return;

    const open = this.stack.pop();
    if (!open || open.name !== name) {
      throw new Error(`htmlparser2 doesn't close tag(${name}) correctly!`);
    }
    this.callbacks?.onCloseTag(name, open.attrs, open.id);
  };

  write(chunk: string): void {
    this.parser?.write(chunk);
  }

  end(): void {
    this.parser?.end();
  }
}
