import { cond } from 'lodash-es';
import { stubTrue } from 'lodash-es';
import { difference } from 'lodash-es';
import { OperationWithValue, PendingType } from '../interfaces/controller';
import Operation, {
  OperationType,
  InsertTextOperation,
  SetNodeOperation,
  InsertNodeOperation,
  MoveNodeOperation,
  NonSetSelectionOperation,
} from '../models/operation';
import Path from '../models/path';
import Document from '../models/document';
import Block from '../models/block';
import Node from '../models/node';
import Element from '../models/element';
import Selection from '../models/selection';
import Value from '../models/value';
import Controller from '../controller';
import { Hot } from '../interfaces/hot';

function isTextOp(operation: Operation): operation is InsertTextOperation {
  return [
    OperationType.InsertText,
    OperationType.RemoveText,
    OperationType.AddMark,
    OperationType.RemoveMark,
    OperationType.SetMark,
  ].includes(operation.type as OperationType);
}

function getHotAmongNodes(
  srcNodes: (Node | null | string)[],
  newDoc: Document,
): Hot[] {
  return srcNodes
    .map((srcNode) => {
      if (!srcNode) {
        return null;
      }
      // 如果操作节点在新的内容无法找到，则不继续查找其热区
      const node = newDoc.getNode(
        typeof srcNode === 'string' ? srcNode : srcNode.key,
      );
      if (!node) {
        return null;
      }
      return Block.isBlock(node)
        ? node
        : newDoc.getClosest(node.key, (node) => Block.isBlock(node));
    })
    .filter((node) => !!node)
    .map((node) => ({
      node: node as Block,
      path: newDoc.assertPath(node!.key),
    }));
}

function getHotsAtRange(
  range: Selection,
  afterChangeDoc: Document,
  ancestorPath: Path = [],
): Hot[] {
  let blocks: Block[] = [];
  if (Path.isRoot(ancestorPath)) {
    blocks = afterChangeDoc.getRootBlocksAtRange(range);
  } else {
    const ancestor = afterChangeDoc.assertNodeByPath(ancestorPath) as Element;
    const { start, end } = range.convertToTextPoints(afterChangeDoc);

    const startBlock = ancestor.getFurthsest(
      start.key,
      Block.isBlock,
    ) as Block | null;
    if (!startBlock) {
      return [];
    }
    if (start.key === end.key) {
      return [
        {
          node: startBlock,
          path: afterChangeDoc.assertPath(startBlock.key),
        },
      ];
    }

    const endBlock = ancestor.getFurthsest(end.key, Block.isBlock);
    if (!endBlock) {
      return [];
    }
    blocks = ancestor.nodes.slice(
      ancestor.nodes.indexOf(startBlock),
      ancestor.nodes.indexOf(endBlock) + 1,
    ) as Block[];
  }

  return blocks.map((node) => {
    return {
      node,
      path: afterChangeDoc.assertPath(node.key),
    };
  });
}

function getOpHots(
  op: OperationWithValue,
  controller: Controller,
): Hot[] | 'ignore' {
  const { operation, key, value, newValue } = op;
  const { document: beforeChangeDoc } = value;
  const { document: afterChangeDoc } = newValue;
  const { document: newDoc } = controller.value;

  /* 增量变更热区 */
  if (operation) {
    // 文本类型的操作，热区为当前文本所在容器
    if (isTextOp(operation)) {
      const { path } = operation;
      const text = beforeChangeDoc.assertNodeByPath(path);
      const newText = newDoc.getNode(text.key);
      return getHotAmongNodes([newText], newDoc);
    }
    // 节点子孙的变更：插入/删除/分裂/合并，热区为变更所在的节点
    else if (
      operation.type === OperationType.InsertNode ||
      operation.type === OperationType.RemoveNode ||
      operation.type === OperationType.SplitNode ||
      operation.type === OperationType.MergeNode
    ) {
      const { path } = operation as InsertNodeOperation;
      const parent = beforeChangeDoc.assertNodeByPath(Path.parent(path));
      return getHotAmongNodes([parent], newDoc);
    }
    // 设置节点
    // 1. 若改变节点内容
    //   a. 若变更不影响分组，则热区为被设置的节点
    //   b. 若变更影响分组，则热区设置为上游节点
    // 2. 若改变节点类型，可能影响上游分组，热区为设置节点的 parent
    else if (operation.type === OperationType.SetNode) {
      const {
        path,
        properties,
        prevProperties,
      } = operation as SetNodeOperation;
      if (
        properties.type !== prevProperties.type
      ) {
        return getHotAmongNodes(
          [beforeChangeDoc.assertNodeByPath(Path.parent(path))],
          newDoc,
        );
      }
      return getHotAmongNodes([beforeChangeDoc.assertNodeByPath(path)], newDoc);
    }
    // 移动节点，热区为：
    // 1. 起始节点的 parent
    // 2. 结束节点的 parent
    else if (operation.type === OperationType.MoveNode) {
      const { path, newPath } = operation as MoveNodeOperation;
      return getHotAmongNodes(
        [
          beforeChangeDoc.assertNodeByPath(Path.parent(path)),
          beforeChangeDoc.assertNodeByPath(Path.parent(newPath)),
        ],
        newDoc,
      );
    }
    // 设置选区，热区为跨越选区的所有最高层 block
    else if (operation.type === OperationType.SetSelection) {
      const { selection, prevSelection } = operation;
      return getHotsAtRange(prevSelection, afterChangeDoc).concat(
        getHotsAtRange(selection, afterChangeDoc),
      );
    }
  } else {
    /* 全量变更热区 */

    // 输入法的热区为输入法所在文本的容器
    if (key === 'composing') {
      return getHotAmongNodes([value.startText], newDoc);
    }

    // focus 状态不影响热区
    if (key === 'isFocused') {
      return 'ignore';
    }

    // injections 变更，找到变更前后的受影响的节点所在的热区
    if (key === 'injections') {
      return getHotAmongNodes(
        [
          ...difference(newValue.injections, value.injections),
          ...difference(value.injections, newValue.injections),
        ].map((inj) => inj.key),
        newDoc,
      );
    }

    // decorations 变更，找到变更前后受影响的区间所在的热区
    if (key === 'decorations') {
      return [
        ...difference(newValue.decorations, value.decorations),
        ...difference(value.decorations, newValue.decorations),
      ]
        .map((decoration) =>
          Selection.create({
            anchor: decoration.start,
            focus: decoration.end,
          }),
        )
        .reduce<Hot[]>((hots, range) => {
          hots.push(...getHotsAtRange(range, afterChangeDoc));
          return hots;
        }, []);
    }
  }

  return [];
}

/**
 * 如果在 hots 找到祖先或者自己，则不再添加
 * @param path
 * @returns
 */
function isExistedHot(path: Path, searchTable: Set<string>) {
  const ancestorsAndSelf = Path.ancestors(path).slice(1);
  ancestorsAndSelf.push(path);
  return ancestorsAndSelf.some((p) => searchTable.has(p.join(',')));
}

/**
 * 获得变更热区
 * @param srcOps
 * @param controller
 * @returns
 */
export function getHots(
  srcOps: OperationWithValue[],
  controller: Controller,
): Hot[] {
  try {
    const ops = srcOps.filter(
      ({ operation }) => !operation || operation.type !== 'set_selection',
    );

    // 所有热区
    const all: Hot[] = [];
    // 去重热区，仅保留最高级祖先
    const uniq: Hot[] = [];
    /**
     * 用于 hots 去重的查找表
     * ```
     * {
     *   '0,1,0': true,
     *   '1,0': true,
     * }
     * ```
     */
    const searchTable = new Set<string>();

    for (let i = 0; i < ops.length; i++) {
      const op = ops[i];
      const hots = getOpHots(op, controller);
      if (hots === 'ignore') {
        continue;
      }
      // 如果任意 op 未找到热区，即变更影响了全局，提前返回
      if (!hots.length) {
        return [];
      }

      all.push(...hots);
    }

    const {
      selection: prevSelection,
      document: prevDocument,
    } = srcOps[0].value;
    const { selection, document } = controller.value;

    // 选区发生变更时，计算热区
    if (prevSelection !== selection) {
      // 获得变更前选区所在的节点
      const prevSelectionIn = prevSelection.getClosestNode(prevDocument)!;
      const prevSelectionInPath = prevDocument.assertPath(prevSelectionIn.key);
      const selectionIn = selection.getClosestNode(document)!;
      const selectionInPath = document.assertPath(selectionIn.key);

      const ancestor = Path.common(prevSelectionInPath, selectionInPath);
      // 如果在同节点移动，热区仅放入新选区所在的节点
      if (Path.isEqual(prevSelectionInPath, selectionInPath)) {
        all.push({
          node: selectionIn,
          path: selectionInPath,
        });
      }
      // 如果是平层移动，放入老/新选区所在的节点
      else if (Path.isSibling(prevSelectionInPath, selectionInPath)) {
        all.push({
          node: prevSelectionIn,
          path: prevSelectionInPath,
        });
        all.push({
          node: selectionIn,
          path: selectionInPath,
        });
      }
      // 否则放入新老选区各自所在的，直到公共祖先的节点，保证节点子树每个节点的选中态都符合预期
      else {
        all.push(...getHotsAtRange(prevSelection, document, ancestor));
        all.push(...getHotsAtRange(selection, document, ancestor));
      }
    }

    all.sort((a, b) => a.path.length - b.path.length);
    all.forEach((hot) => {
      if (!isExistedHot(hot.path, searchTable)) {
        uniq.push(hot);
        searchTable.add(hot.path.join(','));
      }
    });

    return uniq;
  } catch (_e) {
    // 热区计算失败时，清空热区
    return [];
  }
}

interface FlushContext {
  operations: OperationWithValue[];
  value: Value;
}

const FIELD_SHOULD_NOT_BREAK_PENDING = [
  'decorations',
  'injections',
  'remoteSelections',
  'selection'
];

/**
 * 是否是文本输入
 * @param context
 * @returns
 */
export function isInputing(context: FlushContext) {
  const { operations, value } = context;
  if (value.selection.isExpanded) {
    return false;
  }
  const rest = operations.filter(operation => {
    return operation.operation || !FIELD_SHOULD_NOT_BREAK_PENDING.includes(operation.key as string) 
  });

  if (!rest.length) {
    return false;
  }

  // PERF: for-loop
  const opsLen = rest.length;
  let path: Path | null = null;
  for (let i = 0; i < opsLen; i++) {
    const { operation } = rest[i];
    if (!operation) {
      return false;
    }
    if (operation.type === 'set_selection') {
      continue;
    }
    const op = operation as NonSetSelectionOperation;
    if (!path) {
      path = op.path;
    }
    // 在同一个文本连续输入/删除时，可以 pending
    if (
      (op.type !== 'insert_text' && op.type !== 'remove_text') ||
      !Path.isEqual(path, op.path)
    ) {
      return false;
    }
  }
  return true;
}

/**
 * 是否是设置选区
 * @param context
 * @returns
 */
export function isSelecting(context: FlushContext) {
  return context.operations.every(({ operation, key }, index) => {
    if (index === 0 && !operation) {
      return key === 'isFocused' && context.value.isFocused;
    }

    return operation && operation.type === 'set_selection';
  });
}

/**
 * 是否在输入法
 * @param context
 * @returns
 */
export function isComposing(context: FlushContext) {
  const { composing } = context.value;
  return !!composing.length;
}

/**
 * 对于需要以动画速率运行的变更，周期性倾倒：
 * 1. 输入/删除文本
 * 2. 拖动选区
 *
 * ```plaintext
 *    +-------+       +-------+      +-------+     +-------+     +-------+
 *    |   h   |       |   e   |      |   l   |     |   l   |     |   o   |
 *    +-------+       +-------+      +-------+     +-------+     +-------+
 *        |               |              |             |             |
 *        v               v              v             v             v
 *    .-------.       .-------.      .-------.     .-------.     .-------.
 *   (  flush  )     (  flush  )    (  flush  )   (  flush  )   (  flush  )
 *    `-------'       `-------'      `-------'     `-------'     `-------'
 *
 *                                        |
 *                                        |
 *                                 debounced flush
 *                                        |
 *                                        v
 * + - - - - - - - - - - - - - - - - - - - - - - - - - - - - - - - - - - - +
 *    +-------+       +-------+      +-------+     +-------+     +-------+
 * |  |   h   |       |   e   |      |   l   |     |   l   |     |   o   | |
 *    +-------+       +-------+      +-------+     +-------+     +-------+
 * + - - - - - - - - - - - - - - - - - - - - - - - - - - - - - - - - - - - +
 *                                                                   |
 *                                                                   v
 *                                                               .-------.
 *                                                              (  flush  )
 *                                                               `-------'
 * ```
 * @param context
 * @returns
 */
export const shouldDebounceFlush = cond<FlushContext, PendingType | null>([
  [isSelecting, () => PendingType.selection],
  [isInputing, () => PendingType.input],
  [stubTrue, () => null],
]);

/**
 * 对于输入法等类型的临时变更，不进行 flush
 *
 * ```plaintext
 *  +-------+      +-------+     +-------+
 *  |   n   |      |   i   |     |   你   |
 *  +-------+      +-------+     +-------+
 *      |              |             |
 *      v              v             v
 *  .-------.      .-------.     .-------.
 * (  flush  )    (  flush  )   (  flush  )
 *  `-------'      `-------'     `-------'
 *
 *                      |
 *                      |
 *               suppress flush
 *                      |
 *                      v
 *                  +-------+
 *                  |   你   |
 *                  +-------+
 *                      |
 *                      v
 *                  .-------.
 *                 (  flush  )
 *                  `-------'
 * ```
 * @param context
 * @returns
 */
export const shouldSuppressFlush = cond<FlushContext, PendingType | null>([
  [isComposing, () => PendingType.input],
  [stubTrue, () => null],
]);
