import Controller from '../../controller';
import type { CaretAppearance } from '../../components/selection/types';

const cache = new WeakMap<Controller, { caret: CaretAppearance; wrapper: HTMLDivElement }>();

export const cacheLocalCaret = (controller: Controller, position: CaretAppearance, wrapper: HTMLDivElement) => {
  cache.set(controller, { caret: position, wrapper });
}

export const getLocalCaretPos = (controller: Controller) => {
  return cache.get(controller)?.caret;
}

export const getLocalCaretPosDetail = (controller: Controller) => {
  return cache.get(controller);
}