/* eslint-disable no-continue */
/* eslint-disable no-restricted-syntax */
/**
 * @packageDocumentation
 * @ignore
 */
import Controller from '../controller/index';
import * as Commands from '../commands/index';
import Node from '../models/node';
import Element from '../models/element';
import Document from '../models/document';
import Block from '../models/block';
import Inline from '../models/inline';
import Text from '../models/text';
import Mark from '../models/mark';

export interface Rule {
  match?: Rule | Rule[];
  klass?: string | ((type: string) => boolean);
  type?: string | ((type: string) => boolean);
  first?: Rule | Rule[];
  last?: Rule[];
  nodes?: Rule[];
  min?: number;
  max?: number;
  parent?: Rule[];
  previous?: Rule[];
  next?: Rule[];
  normalize?: (controller: Controller, error) => void;
}

const klassToIsType = {
  document: Document.isDocument,
  block: Block.isBlock,
  inline: Inline.isInline,
  text: Text.isText,
  mark: Mark.isMark,
};
function validateKlass(node: Node | Mark, rule: Rule) {
  if (!rule.klass) {
    return null;
  }
  // FIXME: remove `.toJSON()`
  if (typeof rule.klass === 'function' && rule.klass(node.toJSON().klass)) {
    return null;
  }
  const isType = klassToIsType[rule.klass as string];
  if (isType && isType(node)) {
    return null;
  }
  return { code: 'node_klass_invalid', rule, node };
}

function validateType(node: Block | Inline, rule: Rule) {
  if (!rule.type) {
    return null;
  }
  if (rule.type === node.type) {
    return null;
  }
  if (typeof rule.type === 'function' && rule.type(node.type)) {
    return null;
  }
  return { code: 'node_type_invalid', rule, node };
}

function validateFirst(node: Element, rule: Rule) {
  if (!rule.first) {
    return null;
  }
  const first = node.nodes[0];
  if (!first) {
    return null;
  }
  const error = validateRules(first, rule.first);
  if (!error) {
    return null;
  }
  return Object.assign({}, error, {
    rule,
    node,
    child: first,
    code: error.code.replace('node_', 'first_child_'),
  });
}

function validateLast(node: Element, rule: Rule) {
  if (!rule.last) {
    return null;
  }
  const last = node.nodes[node.nodes.length - 1];
  if (!last) {
    return null;
  }
  const error = validateRules(last, rule.last);
  if (!error) {
    return null;
  }
  // PERF
  return Object.assign({}, error, {
    rule,
    node,
    child: last,
    code: error.code.replace('node_', 'last_child_'),
  });
}

function validateParent(node: Element, child: Node, rules: Rule[]) {
  // PERF: For loop is faster than for of.
  const { length } = rules;
  for (let i = 0; i < length; i++) {
    const rule = rules[i];
    if (!rule.parent) {
      continue;
    }
    if (rule.match && !testRules(child, rule.match)) {
      continue;
    }
    const error = validateRules(node, rule.parent);
    if (!error) {
      continue;
    }
    // PERF
    return Object.assign({}, error, {
      rule,
      parent: node,
      node: child,
      code: error.code.replace('node_', 'parent_'),
    });
  }
  return null;
}

function validatePrevious(
  node: Element,
  child: Node,
  previous: Node | null,
  index: number,
  rules: Rule[],
) {
  if (!previous) {
    return null;
  }
  // PERF: For loop is faster than for of.
  const { length } = rules;
  for (let i = 0; i < length; i++) {
    const rule = rules[i];
    if (!rule.previous) {
      continue;
    }
    if (rule.match && !testRules(child, rule.match)) {
      continue;
    }
    const error = validateRules(previous, rule.previous);
    if (!error) {
      continue;
    }
    // PERF
    return Object.assign({}, error, {
      rule,
      node,
      child,
      index,
      previous,
      code: error.code.replace('node_', 'previous_sibling_'),
    });
  }
  return null;
}

function validateNext(
  node: Element,
  child: Node,
  next: Node | null,
  index: number,
  rules: Rule[],
) {
  if (!next) {
    return null;
  }
  // PERF: For loop is faster than for of.
  const { length } = rules;
  for (let i = 0; i < length; i++) {
    const rule = rules[i];
    if (!rule.next) {
      continue;
    }
    if (rule.match && !testRules(child, rule.match)) {
      continue;
    }
    const error = validateRules(next, rule.next);
    if (!error) {
      continue;
    }
    // PERF
    return Object.assign({}, error, {
      rule,
      node,
      child,
      index,
      next,
      code: error.code.replace('node_', 'next_sibling_'),
    });
  }
  return null;
}

function validateNodes(node: Element, rule: Rule, rules: Rule[] = []) {
  if (!node.nodes) {
    return null;
  }

  const children = node.nodes;
  const defs = rule.nodes ? [...rule.nodes] : [];

  let count = 0;
  let lastCount = 0;
  let min: number | null = null;
  let index = -1;
  let def: Rule | null = null;
  let max: number | null = null;
  let child: Node | null = null;
  let previous: Node | null = null;
  let next: Node | null = null;

  function nextDef() {
    if (defs.length === 0) {
      return false;
    }
    def = defs.shift() as Rule;
    lastCount = count;
    count = 0;
    min = def.min || null;
    max = def.max || null;
    return true;
  }

  function nextChild() {
    index += 1;
    previous = child;
    child = children[index] || null;
    next = children[index + 1] || null;
    if (!child) {
      return false;
    }
    lastCount = count;
    count += 1;
    return true;
  }

  function rewind() {
    if (index > 0) {
      index -= 1;
      count = lastCount;
    }
  }

  if (rule.nodes) {
    nextDef();
  }

  while (nextChild()) {
    const error =
      validateParent(node, child!, rules) ||
      validatePrevious(node, child!, previous, index, rules) ||
      validateNext(node, child!, next, index, rules);
    if (error) {
      return error;
    }
    if (rule.nodes) {
      if (!def) {
        return { code: 'child_unknown', rule, node, child, index };
      }
      if ((def as Rule).match) {
        // @ts-ignore
        const err = validateRules(child!, def.match);
        if (err) {
          // Since we want to report overflow on last matching child we don't
          // immediately check for count > max, but instead do so once we find
          // a child that doesn't match.
          if (max !== null && count - 1 > max) {
            rewind();
            return {
              code: 'child_max_invalid',
              rule,
              node,
              index,
              child: children[index],
              count,
              limit: max,
            };
          }

          const lastMin = min;
          // If there are more groups after this one then child might actually
          // be valid.
          if (nextDef()) {
            // If we've already satisfied the minimum for the current group,
            // then we can rewind and proceed to the next group.
            if (lastMin === null || lastCount - 1 >= lastMin) {
              index -= 1;
              continue;
            }

            // Otherwise we know that current value is underflowing. There are
            // three possible causes for this...

            // 1. There might just not be enough elements for current group, and
            // current child is in fact the first of the next group. If so, the
            // next def will not report errors, in which case we can rewind and
            // report an minimum error.
            // @ts-ignore
            if (validateRules(child!, def.match) === null) {
              rewind();
              return {
                code: 'child_min_invalid',
                rule,
                node,
                index,
                count: lastCount - 1,
                limit: lastMin,
              };
            }

            // 2. The current group is underflowing, but there is also an
            // invalid child before the next group.
            // 3. Or the current group is not underflowing but it appears so
            // because there's an invalid child between its members.
            // It's either the second or third case. If it's the second then
            // we could report an underflow, but presence of an invalid child
            // is arguably more important, so we report it first. It also lets
            // us avoid checking for which case exactly is it.
            // PERF
            return Object.assign({}, err, {
              rule,
              node,
              child,
              index,
              code: err.code.replace('node_', 'child_'),
            });
          }

          // Otherwise either we exhausted the last group, in which case it's
          // an unknown child, ...
          if (max != null && count > max) {
            return {
              code: 'child_unknown',
              rule,
              node,
              child,
              index,
            };
          }
          return Object.assign({}, err, {
            rule,
            node,
            child,
            index,
            code: err.code.replace('node_', 'child_'),
          });
        }
      }
    }
  }

  // Since we want to report overflow on last matching child we don't
  // immediately check for count > max, but do so after processing all nodes.
  if (max !== null && count > max) {
    return {
      code: 'child_max_invalid',
      rule,
      node,
      index: index - 1,
      count,
      child: children[index - 1],
      limit: max,
    };
  }
  if (rule.nodes) {
    do {
      if (min !== null && count < min) {
        return {
          code: 'child_min_invalid',
          rule,
          node,
          index,
          count,
          limit: min,
        };
      }
    } while (nextDef());
  }
  return null;
}

function validateRules(
  object: Node | Mark,
  rule: Rule | Rule[],
  rules?: Rule[],
  options: { every?: boolean } = {},
) {
  const { every = false } = options;
  if (Array.isArray(rule)) {
    const array = rule.length ? rule : [{}];

    let first = null;
    // PERF: For loop is faster than for of.
    const { length } = array;
    for (let i = 0; i < length; i++) {
      const r = array[i];
      const error = validateRules(object, r, rules);
      first = first || error;
      if (every && error) {
        return error;
      }
      if (!every && !error) {
        return null;
      }
    }
    return first;
  }

  return (
    validateKlass(object, rule) ||
    // @ts-ignore
    validateType(object, rule) ||
    // @ts-ignore
    validateFirst(object, rule) ||
    // @ts-ignore
    validateLast(object, rule) ||
    // @ts-ignore
    validateNodes(object, rule, rules)
  );
}

function testRules(object: Node | Mark, rules: Rule | Rule[]) {
  return !validateRules(object, rules);
}

function defaultNormalize(controller: Controller, error) {
  const { code, node, child, next, previous } = error;

  switch (code) {
    case 'child_max_invalid':
    case 'child_klass_invalid':
    case 'child_type_invalid':
    case 'child_unknown':
    case 'first_child_klass_invalid':
    case 'first_child_type_invalid':
    case 'last_child_klass_invalid':
    case 'last_child_type_invalid': {
      return Text.isText(child) &&
        Block.isBlock(node) &&
        node.nodes.length === 1
        ? controller.command(Commands.removeNodeByKey, node.key)
        : controller.command(Commands.removeNodeByKey, child.key);
    }

    case 'previous_sibling_klass_invalid':
    case 'previous_sibling_type_invalid': {
      return Text.isText(previous) &&
        Block.isBlock(node) &&
        node.nodes.length === 1
        ? controller.command(Commands.removeNodeByKey, node.key)
        : controller.command(Commands.removeNodeByKey, previous.key);
    }

    case 'next_sibling_klass_invalid':
    case 'next_sibling_type_invalid': {
      return Text.isText(next) && Block.isBlock(node) && node.nodes.length === 1
        ? controller.command(Commands.removeNodeByKey, node.key)
        : controller.command(Commands.removeNodeByKey, next.key);
    }

    case 'child_min_invalid':
    case 'parent_klass_invalid':
    case 'parent_type_invalid': {
      return Document.isDocument(node)
        ? node.nodes.forEach((n) => controller.command(Commands.removeNodeByKey, n.key))
        : controller.command(Commands.removeNodeByKey, node.key);
    }

    default: {
      return controller.command(Commands.removeNodeByKey, node.key);
    }
  }
}

interface ElementRule extends Omit<Rule, 'match'> {
  isVoid?: boolean | ((controller: Controller, node: Node) => boolean);
  isEdgeSelectable?: boolean;
  /**
   * Inline 内边缘是否可选中
   */
  isSideSelectable?: boolean;
}

function isOnlyContainsVoidAndEdgeSelectable(rule: ElementRule) {
  return !Object.keys(rule)
    .filter(key => key !== 'isVoid' && key !== 'isEdgeSelectable')
    .length;
}

interface MarkRule extends Omit<Rule, 'match'> {
  isAtomic?: boolean;
}

function isOnlyContainsAtomic(rule: MarkRule) {
  return !Object.keys(rule)
    .filter(key => key !== 'isAtomic')
    .length;
}

export interface Schema {
  rules?: Rule[];
  document?: Omit<Rule, 'match'>;
  blocks?: Record<string, ElementRule>;
  inlines?: Record<string, ElementRule>;
  marks?: Record<string, MarkRule>;
}

export default function createNormalizationPlugin(schema: Schema) {
  const {
    rules = [],
    document,
    blocks = {},
    inlines = {},
    marks = {},
  } = schema;

  const schemaRules = [...rules];
  if (document) {
    schemaRules.push({
      match: [{ klass: 'document' }],
      ...document,
    });
  }
  Object.keys(blocks)
    .filter((type) => !isOnlyContainsVoidAndEdgeSelectable(blocks[type]))
    .forEach((type) => {
      schemaRules.push({
        match: [{ klass: 'block', type }],
        ...blocks[type],
      });
    });
  Object.keys(inlines)
    .filter((type) => !isOnlyContainsVoidAndEdgeSelectable(inlines[type]))
    .forEach((type) => {
      schemaRules.push({
        match: [{ klass: 'inline', type }],
        ...inlines[type],
      });
    });
  Object.keys(marks)
    .filter((type) => isOnlyContainsAtomic(marks[type]))
    .forEach((type) => {
      schemaRules.push({
        match: [{ klass: 'mark', type }],
        ...marks[type],
      });
    });

  // Check if a `mark` is atomic
  const isAtomicDict: Record<string, boolean> = Object.create(null);
  Object.keys(marks).forEach(type => {
    if ('isAtomic' in marks[type]) {
      isAtomicDict[type] = marks[type].isAtomic!;
    }
  });
  function isAtomic(_, mark): boolean | undefined {
    return isAtomicDict[mark.type];
  }

  const isVoidDict = {
    block: Object.create(null),
    inline: Object.create(null),
  };
  const isEdgeSelectableDict = {
    block: Object.create(null),
    inline: Object.create(null),
  };
  const isSideSelectableDict = Object.create(null);
  Object.keys(blocks).forEach(type => {
    if ('isVoid' in blocks[type]) {
      isVoidDict.block[type] = blocks[type].isVoid;
    }
    if ('isEdgeSelectable' in blocks[type]) {
      isEdgeSelectableDict.block[type] = blocks[type].isEdgeSelectable;
    }
  });
  Object.keys(inlines).forEach(type => {
    if ('isVoid' in inlines[type]) {
      isVoidDict.inline[type] = inlines[type].isVoid;
    }
    if ('isEdgeSelectable' in inlines[type]) {
      isEdgeSelectableDict.inline[type] = inlines[type].isEdgeSelectable;
    }
    if ('isSideSelectable' in inlines[type]) {
      isSideSelectableDict[type] = inlines[type].isSideSelectable;
    }
  });
  // Check if a `node` is void.
  function isVoid(controller: Controller, node: Node): boolean | undefined {
    let klass = '';
    if (Block.isBlock(node)) {
      klass = 'block';
    }
    if (Inline.isInline(node)) {
      klass = 'inline';
    }
    if (!klass) {
      return undefined;
    }
    const predict = isVoidDict[klass][(node as Element).type];
    return typeof predict === 'function'
      ? predict(controller, node)
      : predict;
  }

  // Check if the side of `node` is selectable
  function isSideSelectable(controller: Controller, node: Node): boolean | undefined {
    if (Inline.isInline(node)) {
      return isSideSelectableDict[node.type];
    }
    return undefined;
  }

  // Check if a `node` is selectable.
  function isEdgeSelectable(_, node: Node): boolean | undefined {
    let klass = '';
    if (Block.isBlock(node)) {
      klass = 'block';
    }
    if (Inline.isInline(node)) {
      klass = 'inline';
    }
    if (!klass) {
      return undefined;
    }
    return isEdgeSelectableDict[klass][(node as Element).type];
  }

  function validateNode(node: Node, controller: Controller, next) {
    const matches = schemaRules.filter(
      (r) => r.match && testRules(node, r.match),
    );
    const error = validateRules(node, matches, schemaRules, { every: true });
    if (!error) {
      return next();
    }
    return error;
  }

  function normalizeNode(node: Node, controller: Controller, next) {
    const error = validateNode(node, controller, () => null);
    if (!error) {
      return next();
    }
    return () => {
      const { rule } = error;
      const { document: $document } = controller.value;
      if (rule.normalize) {
        rule.normalize(controller, error);
      }
      if (controller.value.document === $document) {
        defaultNormalize(controller, error);
      }
    };
  }

  return {
    models: [],
    queries: { isAtomic, isVoid, isEdgeSelectable, isSideSelectable },
    validateNode,
    normalizeNode,
  };
}
