/**
 * @packageDocumentation
 * @module Models
 */

import { MAX_HISTORY_SIZE } from '../constants';
import ImmutableRecord from './immutableRecord';
import Operation, { OperationType } from './operation';
import Path from './path';

const BATCH_UNDO_MERGE_TIME = 1000;

function shouldClear(op: Operation) {
  return op.type !== OperationType.SetSelection;
}

function shouldMerge(op: Operation, prevOp: Operation) {
  if (!prevOp) return false;

  return (
    (op.type === OperationType.InsertText &&
      prevOp.type === OperationType.InsertText &&
      op.offset === prevOp.offset + prevOp.text.length &&
      Path.isEqual(op.path, prevOp.path)) ||
    (op.type === OperationType.RemoveText &&
      prevOp.type === OperationType.RemoveText &&
      op.offset + op.text.length === prevOp.offset &&
      Path.isEqual(op.path, prevOp.path))
  );
}

function shouldOverwrite(op: Operation, prevOp: Operation | undefined) {
  return prevOp && op.type === OperationType.SetSelection && prevOp.type === OperationType.SetSelection;
}

export interface HistoryProperties {
  redos: Operation[][];
  undos: Operation[][];
}

export default class History extends ImmutableRecord {
  static create(params?: { maxHistorySize?: number }) {
    return new History(params);
  }

  private constructor(props: Partial<HistoryProperties & {
    lastUndoCreatedTime: number;
    maxHistorySize?: number;
  }> = {}) {
    super();
    const {
      redos = [],
      undos = [],
      lastUndoCreatedTime = 0,
      maxHistorySize = MAX_HISTORY_SIZE,
    } = props;
    this.redos = redos;
    this.undos = undos.slice(-maxHistorySize);
    this.lastUndoCreatedTime = lastUndoCreatedTime;
    this.maxHistorySize = maxHistorySize;
    this.freeze();
  }

  public readonly redos: Operation[][];

  public readonly undos: Operation[][];

  private readonly lastUndoCreatedTime: number = 0;

  private readonly maxHistorySize: number;

  public merge(props: Partial<HistoryProperties>) {
    return new History({
      redos: this.redos,
      undos: this.undos,
      maxHistorySize: this.maxHistorySize,
      ...props,
      lastUndoCreatedTime: this.lastUndoCreatedTime,
    });
  }

  public set<T extends keyof HistoryProperties>(
    key: T,
    value: Required<HistoryProperties>[T],
  ) {
    return this.merge({
      [key]: value,
    });
  }

  public moveLastRedoRecordToUndos() {
    const { redos } = this;
    if (!redos.length) {
      return this;
    }
    const newRedos = redos.slice(0, -1);
    const lastRedoRecord = redos[redos.length - 1];
    const newUndos = [...this.undos, lastRedoRecord];
    return this.merge({
      undos: newUndos,
      redos: newRedos,
    });
  }

  public moveLastUndoRecordToRedos(undoRecord?: Operation[]) {
    const { undos } = this;
    if (!undos.length) {
      return this;
    }

    const newUndos = undos.slice(0, -1);
    const lastUndoRecord = undoRecord || undos[undos.length - 1];
    const newRedos = [...this.redos, lastUndoRecord];
    return this.merge({
      undos: newUndos,
      redos: newRedos,
    });
  }

  public mergeToLastUndoIfNeed(operation: Operation, forceMerge: boolean) {
    let undos = [...this.undos];
    let lastBatch = undos[undos.length - 1];
    const lastOp = lastBatch && lastBatch[lastBatch.length - 1];
    const overwrite = shouldOverwrite(operation, lastOp);
    const merge = !!lastBatch
      && (this.lastUndoCreatedTime === 0 || Date.now() - this.lastUndoCreatedTime <= BATCH_UNDO_MERGE_TIME)
      && (shouldMerge(operation, lastOp) || overwrite);

    if (lastBatch && (merge || forceMerge)) {
      if (overwrite) {
        lastBatch = lastBatch.slice(0, -1);
      }
      lastBatch = [...lastBatch, operation];
      undos = [...undos.slice(0, -1), lastBatch];
    } else {
      const batch = [operation];
      undos = [...undos, batch];
    }

    return this.merge({
      undos,
      redos: shouldClear(operation) ? [] : this.redos,
    });
  }
}
