import { omit, union } from 'lodash-es';
import { isEmptyObj, omitDeepBy } from '../utils/omitEmpty';
import { SetNodeOperation } from './operation';
import Value from './value';

export function mergeUserProperties (
  value: Value,
  operation: SetNodeOperation
) {
  const { path, properties, context } = operation;
  // 将当前 OP 中用户数据与 value 上的合并
  // 避免协同编辑下，不同用户/不同段落 OP 数据相互覆盖
  const matched = context?.match(/userdata\/(.*)\/(.*)/);
  const targetData = properties?.data?.userData || {};
  if (matched && path?.length === 0 && targetData) {
    let mergedData = value.document.data?.userData || {};
    // context 上携带此次修改用户数据的 uid 和块 uuid
    const mUid = matched[1];
    let uids: string[] = [];
    if (mUid === '*') {
      uids = union(Object.keys(mergedData), Object.keys(targetData));
    } else if (mUid) {
      uids = [mUid];
    }
    const uuids = matched[2].split(',');
    uids.forEach((uid) => {
      uuids.forEach((uuid) => {
        const target = targetData?.[uid]?.[uuid];
        if (uuid) {
          if (target === undefined) {
            // 删除数据
            mergedData = {
              ...mergedData,
              [uid]: omit(mergedData?.[uid], uuid)
            };
          } else {
            // 用 op 中 uuid 对应数据覆盖 document 上对应的
            mergedData = {
              ...mergedData,
              [uid]: {
                ...mergedData?.[uid],
                [uuid]: target,
              }
            }
          }
        } 
      })
    })
    return {
      ...properties,
      data: omitDeepBy({
        ...properties.data,
        userData: mergedData
      }, isEmptyObj),
    };
  }
  return properties;
}