import * as React from 'react';
import { jsonMLGetTagName, jsonMLGetAttributes, jsonMLGetChildren, jsonMLIsText } from '../../utils/jsonml';
import type { JsonML } from '../../type/jsonml';
import type { JsonMLToHTMLRule, LeafRule, TextMark } from '../../type';

/**
 * 在 Word 的 CCP 导出规范中，文档中的空白符（Simple space）在 text/html 中会全部替换成 &nbsp;（码点 160）
 * 在导出 text/plain 中，则会保留 Simple space 的码点（32）
 */
const NON_BREAKING_SPACE_CODE = 160;

function normalizeText(text: string): string {
  text = text.replace(/ /g, String.fromCharCode(NON_BREAKING_SPACE_CODE));
  return text;
}

function createLeafToElement(jsonMLAttrsToStyles: Array<NonNullable<LeafRule['jsonMLAttrsToStyle']>>) {
  return (leaf: JsonML, key: number[]): React.ReactElement => {
    const attrs: TextMark = jsonMLGetAttributes(leaf) || {};
    const style: React.CSSProperties = jsonMLAttrsToStyles.reduce((prev, attrToStyle) => {
      return attrToStyle({
        ...attrs,
      }, {
        ...prev
      });
    }, {});

    // dataType 不需要透出给插件处理，兼容一下旧的 inlineCode
    const dataType = (attrs as any).inlineCode ? 'inlineCode' : 'text';

    const text = normalizeText(jsonMLGetChildren(leaf)[0] as unknown as string);

    return (
      <span data-type={dataType} style={style} key={key.toString()}>
        {text}
      </span>
    );
  }
}

export function createJsonMLToHTML(jsonMLAttrsToStyles: Array<LeafRule['jsonMLAttrsToStyle']>): JsonMLToHTMLRule {
  const leafToElement = createLeafToElement(jsonMLAttrsToStyles.filter((f): f is NonNullable<LeafRule['jsonMLAttrsToStyle']> => !!f));

  return {
    name: 'text',

    match: (node: JsonML): boolean => {
      return jsonMLIsText(node);
    },

    convert: (node: JsonML, key: number[]): React.ReactElement => {
      const children = jsonMLGetChildren(node).map((leaf, index) =>
        leafToElement(leaf, [...key, index]),
      );
      return <React.Fragment>{children}</React.Fragment>;
    },
  };
}
