/**
 * @packageDocumentation
 * @module domUtils
 */
import getElementByCangjieKey from './getElementByCangjieKey';

/**
 * 根据节点的 key 查找对应的 DOM 节点。如果找不到，就返回 null
 */
export function findDOMNodeSafely(
  key: string,
  root: Window | Element = window,
) {
  const node =
    root !== window
      ? getElementByCangjieKey(key, root as Element)
      : getElementByCangjieKey(key, root.document);
  return node || undefined;
}

/**
 * 根据节点的 key 查找对应的 DOM 节点。如果找不到，就 throw error。
 * 类似的 API：domUtils.findDOMNodeSafely
 */
function findDOMNode(key: string, root: Window | Element = window) {
  const node = findDOMNodeSafely(key, root);
  if (!node) {
    throw new Error(
      `Unable to find a DOM node for "${key}". This is often because of forgetting to add \`props.attributes\` to a custom component.`,
    );
  }
  return node;
}

export default findDOMNode;
