/**
 * @packageDocumentation
 * @ignore
 */
import { EdgePoint, TextPoint } from '..';
import { Selector } from '../constants';
import findDOMLeaves from './dom/findDOMLeaves';

const RE_RGB = /^rgba?\((.+)\)/i;
const ALPHA_THRESHOLD = 0.5;
const LUMINANCE_THRESHOLD = 0.5;

type RGBATuple = [number, number, number, number];

function isCoordInsideDOMRect(x: number, y: number, rect: DOMRect) {
  const { left, right, top, bottom } = rect;
  return x >= left && x <= right && y >= top && y <= bottom;
}

/**
 * @deprecated
 * 获取当前元素的显示背景色。
 * 从此元素的 parent 依次向上，知道找到一个设置背景色的 element 为止。
 * 如果一直没找到，则返回白色。
 */
export function getBackgroundColor(at: Element, caretContainer: Element | null): RGBATuple {
  let element: Element | null = caretContainer;

  // check if it's inside a caret contaienr
  if (caretContainer) {
    const overlayBlocks = caretContainer.querySelectorAll(`[${Selector.nonSelect}]`);
    const { left, width, top, height } = at.getBoundingClientRect();
    const x = left + width / 2;
    const y = top + height / 2;
    const block = Array.from(overlayBlocks).find(b => {
      return isCoordInsideDOMRect(x, y, b.getBoundingClientRect());
    });
    if (block) {
      element = block as HTMLElement;
    }
  }

  // default get background color logic
  while (element) {
    const color = window.getComputedStyle(element).backgroundColor;
    const rgba = rgbaFromString(color);
    if (rgba && rgba[3] > ALPHA_THRESHOLD && element.getBoundingClientRect().width > 0) {
      return rgba;
    }
    element = element.parentElement;
  }
  return [255, 255, 255, 1];
}

export function getBgColor(targetBlock: HTMLElement, at: { top: number, left: number, height: number }, point: TextPoint | EdgePoint): RGBATuple {
  let element: HTMLElement | null = targetBlock;
  if (point.isTextPoint()) {
    const leaves = targetBlock.querySelectorAll<HTMLElement>(`[${Selector.key}^="${point.key}:"]`);
    const target = Array.from(leaves).find(b => {
      return isCoordInsideDOMRect(at.left, at.top + at.height / 2, b.getBoundingClientRect());
    });
    if (target) {
      element = target;
    }
  }

  while (element) {
    const color = window.getComputedStyle(element).backgroundColor;
    const rgba = rgbaFromString(color);
    if (rgba && rgba[3] > ALPHA_THRESHOLD && element.getBoundingClientRect().width > 0) {
      return rgba;
    }
    element = element.parentElement;
  }
  return [255, 255, 255, 1];
}

/**
 * rgba 字符串转为 rgba 数组。
 * 'rgba(0,0,0,0.88)' => [0,0,0,0.88]
 * 'RGB(0,0,0)' => [0,0,0,1]
 */
function rgbaFromString(str: string): RGBATuple | null {
  const rgb = RE_RGB.exec(str) || [];
  const rgbs = (rgb[1] || '').split(',');
  if (rgbs.length < 3) {
    return null;
  } else if (rgbs.length === 3) {
    rgbs.push('1');
  }
  const rgba: RGBATuple = [0, 0, 0, 0];
  rgba[0] = parseInt(rgbs[0], 10);
  rgba[1] = parseInt(rgbs[1], 10);
  rgba[2] = parseInt(rgbs[2], 10);
  rgba[3] = parseFloat(rgbs[3]);
  return rgba;
}

/**
 * 获取相对的 luminance。
 * https://www.w3.org/TR/2008/REC-WCAG20-20081211/#relativeluminancedef
 */
function getLuminance(rgba: RGBATuple): number {
  const a: number[] = [];
  for (let i = 0; i < 3; i++) {
    const x = rgba[i] / 255;
    a[i] = x <= 0.03928 ? x / 12.92 : Math.pow((x + 0.055) / 1.055, 2.4);
  }
  return 0.2126 * a[0] + 0.7152 * a[1] + 0.0722 * a[2];
}

/**
 * 根据背景色，获取光标的颜色。
 */
export function getCaretColor(backgroundColor: RGBATuple) {
  const luminance = getLuminance(backgroundColor);
  return luminance > LUMINANCE_THRESHOLD ? 'black' : 'white';
}
