/**
 * @packageDocumentation
 * @ignore
 */
import * as React from 'react';
import ReactDOM from 'react-dom';

import Selection from '../../models/selection';
import Controller from '../../controller/index';
import { useScroll } from '../../hooks/useScroll';
import { ContextMenuItem } from '../../interfaces/contextMenu';
import { getPopupPosition, PopupPosition } from './popupPosition';
import { useSelectionData } from '../../hooks/useSelectionData';
import { Z_INDEX_CONTEXTMENU } from '../../constants';
import { IS_MOBILE } from '../../utils/environment';

const ITEM_HEIGHT = 54;
const POPUP_HEIGHT = ITEM_HEIGHT + 8 * 2;
const POPUP_BOTTOM_EXTRA_HEIGHT = 20;

export type ContextMenuProps = {
  selection: Selection;
  controller: Controller;
  hidePopup?: (controller: Controller) => void;
};

const wrapperStyle: React.CSSProperties = {
  position: 'fixed',
  zIndex: Z_INDEX_CONTEXTMENU,
  backgroundColor: '#585A5C',
  padding: '8px 4px',
  borderRadius: '8px',
  display: 'flex',
  flexDirection: 'row',
  opacity: '0',
  transition: 'opacity 0.3s',
};

const itemStyle: React.CSSProperties = {
  height: `${ITEM_HEIGHT}px`,
  lineHeight: '16px',
  color: 'white',
  minWidth: '56px',
  width: '56px',
  fontSize: '10px',
  display: 'flex',
  alignItems: 'center',
  justifyContent: 'center',
  flexDirection: 'column',
  whiteSpace: 'nowrap',
  overflow: 'hidden',
};

const iconStyle: React.CSSProperties = {
  width: '24px',
  height: '24px',
  marginBottom: '2px',
};

const arrowStyle: React.CSSProperties = {
  position: 'absolute',
  bottom: '-4px',
  width: '10px',
  height: '10px',
  backgroundColor: '#585A5C',
  transform: 'rotate(45deg)',
};

const arrowStyleTop: React.CSSProperties = {
  ...arrowStyle,
  bottom: 'inherit',
  top: '-4px',
};
function ContextMenu(props: ContextMenuProps) {
  const { selection, controller, hidePopup } = props;

  const [position, setPosition] = React.useState<PopupPosition | null>(null);
  const rootRef = React.useRef<HTMLDivElement>(null);

  const updatePosition = () => {
    if (rootRef.current) {
      const { width } = rootRef.current.getBoundingClientRect();
      const pos = getPopupPosition(
        selection,
        controller,
        width,
        POPUP_HEIGHT,
        IS_MOBILE,
      );
      setPosition(pos);
    }
  };
  React.useEffect(updatePosition, [selection]);

  const setNodeVisible = React.useCallback((visible) => {
    const node = rootRef.current;
    if (selection.isCollapsed) {
      hidePopup && hidePopup(controller)
    } else if (node) {
      node.style.opacity = visible ? '1' : '0';
    }
  }, [selection, hidePopup]);

  useSelectionData(
    controller.selectionData$,
    null,
    ({ range }) => {
      if (range.isExpanded && selection.isCollapsed) {
        setNodeVisible(false);
      }
    },
    [],
  );

  useScroll({
    onStart: () => {
      setNodeVisible(false);
    },
    onEnd: () => {
      updatePosition();
      setNodeVisible(true);
    },
  });

  let buttons = controller
    .run('contextMenu')
    .filter((b) => Boolean(b.options?.mobile));
  buttons = buttons.sort((a, b) => {
    const groupA = a.options?.group || 0;
    const groupB = b.options?.group || 0;
    return groupA - groupB;
  });

  const styleForWrapper = position
    ? {
        ...wrapperStyle,
        left: position.x,
        top: position.isTop
          ? position.y
          : position.y + POPUP_BOTTOM_EXTRA_HEIGHT,
        opacity: 1,
      }
    : wrapperStyle;

  const handleAction = (button: ContextMenuItem) => {
    return (event: React.MouseEvent | TouchEvent | React.TouchEvent) => {
      event.stopPropagation();
      event.preventDefault();
      controller.run('onAction', button.action);
    };
  };

  const availableButtons = buttons.filter((btn) => {
    if (btn.options && btn.options.disable) {
      return false;
    }
    return true;
  });

  // 如果可用的 buttons 数为 0，则不渲染
  if (availableButtons.length <= 0) {
    return null;
  }

  const styleForArrow = position
    ? {
        ...(position.isTop ? arrowStyle : arrowStyleTop),
        left: position.arrowOffset,
      }
    : arrowStyle;

  return (
    <div
      data-role="mobileContextMenu"
      ref={rootRef}
      className="cangjieContextMenu"
      style={styleForWrapper}
    >
      {availableButtons.map((button, index) => {
        const { name, role, icon: Icon } = button;
        return (
          <div
            key={name}
            data-role={role || ''}
            onMouseDown={handleAction(button)}
            onTouchEnd={handleAction(button)}
            style={itemStyle}
          >
            {typeof Icon !== 'function' && (
              <img
                style={iconStyle}
                src={
                  Icon ||
                  'https://gw.alicdn.com/imgextra/i4/O1CN011xit6C1NdBJNUvlF9_!!6000000001592-55-tps-24-24.svg'
                }
              />
            )}
            {typeof Icon === 'function' && <Icon />}
            <div>{name}</div>
          </div>
        );
      })}
      <span style={styleForArrow} />
    </div>
  );
}

export default (props: ContextMenuProps) =>
  ReactDOM.createPortal(<ContextMenu {...props} />, document.body);
