import * as React from 'react';
import useCangjieKeyBindingRef from '../hooks/useCangjieKeyBindingRef';

export interface LeafComponentProps {
  leafKey: string;
  style?: React.CSSProperties;
}

const LeafComponent: React.FC<LeafComponentProps> = (props) => {
  const { leafKey, style, children } = props;
  const ref = useCangjieKeyBindingRef<HTMLSpanElement>(leafKey);

  return (
    <span
      ref={ref}
      data-cangjie-key={leafKey}
      data-cangjie-leaf
      data-cangjie-mark={props['data-cangjie-mark']}
      style={style}
      data-testid={leafKey}
    >
      {children}
    </span>
  );
};

export default LeafComponent;
