import type { Value } from '../../';
import type { JsonML } from '../type';
import createNodeToJsonML, { createAsyncNodeToJsonML } from './nodeToJsonML';


export function createAsyncValueToJsonML(asyncNodeToJsonML: ReturnType<typeof createAsyncNodeToJsonML>) {
  /**
   * 序列化 value to ASL
   * @param value
   */
  async function valueToJsonML(value: Value) {
    return asyncNodeToJsonML(value.document);
  }

  return valueToJsonML;
}

export default function createValueToJsonML(nodeToJsonML: ReturnType<typeof createNodeToJsonML>) {
  /**
   * 序列化 value to ASL
   * @param value
   */
  function valueToJsonML(value: Value): JsonML {
    return nodeToJsonML(value.document);
  }

  return valueToJsonML;
}
