/**
 * @packageDocumentation
 * @module Events
 */
interface CangjieInputDetail {
  type: string;
  data: string | null;
};

interface CangjieInputEvent extends CustomEvent<CangjieInputDetail> {}

function CangjieInputEvent(detail: CangjieInputDetail) {
  return new CustomEvent('cangjieInput', { detail });
}

export default CangjieInputEvent;
