import { Injection, Controller, Node } from '../';
import { isEmpty, omit } from 'lodash-es';

export function setInjection (controller: Controller, node: Node, data) {
  const { injections } = controller.value;
  const curInjection = injections.find(inj => inj.key === node.key) || {} as Injection;
  const injection = Injection.create({
    key: node.key,
    data: {
      ...(curInjection.data || {}),
      ...data,
    },
  });

  controller.setInjections([
    ...injections.filter(inj => inj.key !== node.key),
    injection,
  ]);
};

export function deleteInjection (controller: Controller, node: Node, keys: string[]) {
  const { injections } = controller.value;
  const curInjection = injections.find(inj => inj.key === node.key) || {} as Injection;
  const nextData = omit(curInjection.data, keys)
  const nextInjections = injections.filter(inj => inj.key !== node.key)
  if (!isEmpty(nextData)) {
    const injection = Injection.create({
      key: node.key,
      data: nextData,
    });
    nextInjections.push(injection);
  }

  controller.setInjections(nextInjections);
};
