import { isEqual, omit } from 'lodash-es';
import * as Commands from '../commands/index';
import Element from '../models/element';
import Node from '../models/node';
import Controller from './index';
import { Subject } from '../models/observable';
import Operation from '../models/operation';
import Document from '../models/document';
import { isEmptyObj, omitDeepBy } from '../utils/omitEmpty';
import { setInjection, deleteInjection } from '../utils/setInjection';

type uid = string
type uuid = string
export type UserData = Record<
  uid, 
  Record<uuid, UserDataContent>
>

export interface UserDataContent {}

export default class UserDataManager {
  public update$ = new Subject<{ uuid: string }>();

  private controller: Controller;

  private localData: UserData = {};

  constructor(controller: Controller) {
    this.controller = controller;
  }

  public get <T extends keyof UserDataContent>(
    node: Node | null,
    key: T,
  ): UserDataContent[T] | undefined {
    if (Element.isElement(node)) {
      const { uid } = this.controller;
      const uuid: string | undefined = node.data?.uuid;
      const nData = node.data[key];
      if (uid && uuid) {
        const { userData } = this.controller.value.document.data;
        const uData = userData?.[uid]?.[uuid]?.[key];
        const lData = this.localData?.[uid]?.[uuid]?.[key];
        return lData ?? uData ?? nData;
      }
      return nData;
    }
    return undefined;
  }

  public set <T extends keyof UserDataContent>(
    node: Node | null,
    properties: Record<T, UserDataContent[T]>,
  ) {
    if (Element.isElement(node)) {
      const { uid, value } = this.controller;
      const { document } = value;
      const { userData }= document.data;
      const uuid: string | undefined = node.data?.uuid;
      if (uid && uuid) {
        // 有 uid 和 uuid，设置用户数据
        const data = omitDeepBy({
          ...document.data,
          userData: this.mergeProperties(
            userData,
            uid,
            uuid,
            properties,
          ),
        }, isEmptyObj);
        if (data.userData !== document.data.userData) {
          return this.controller.withOpContext(`userdata/${uid}/${uuid}`, () => {
            this.controller.command(Commands.setNodeByKey, document.key, { data });
          });
        }
      } else {
        // 设置 node.data 数据
        const n = document.getNode(node?.key);
        if (n && Element.isElement(n)) {
          const data = {
            ...n.data,
            ...properties,
          };
          if (!isEqual(data, n.data)) {
            return this.controller.withOpContext(`userdata`, () => {
              this.controller.command(Commands.setNodeByKey, n.key, { data });
            });
          }
        }
      }
    }
    return this.controller;
  }

  public setLocal <T extends keyof UserDataContent>(
    node: Node | null,
    properties: Record<T, UserDataContent[T]>,
  ) {
    if (Element.isElement(node)) {
      const { uid } = this.controller;
      const uuid: string | undefined = node.data?.uuid;
      if (uid && uuid) {
        this.localData = this.mergeProperties(
          this.localData,
          uid,
          uuid,
          properties,
        );
        this.update$.next({ uuid });
      } else {
        setInjection(this.controller, node, properties);
      }
    }
    return this.controller;
  }

  public delete <T extends keyof UserDataContent>(
    node: Node | null,
    keys: T[],
  ) {
    if (Element.isElement(node)) {
      const uuid: string | undefined = node.data?.uuid;
      const { uid, value } = this.controller;
      const { document } = value;
      const { userData }= document.data;
      if (uid && uuid) {
        const data = omitDeepBy({
          ...document.data,
          userData: this.deleteProperties(
            userData,
            uid,
            uuid,
            keys,
          ),
        }, isEmptyObj);
        if (data.userData !== document.data.userData) {
          return this.controller.withOpContext(`userdata/${uid}/${uuid}`, () => {
            this.controller.command(Commands.setNodeByKey, document.key, { data });
          });
        }
      } else {
        // 删除 node.data 数据
        const n = document.getNode(node?.key);
        if (n && Element.isElement(n)) {
          const data = omit(n.data, keys)
          if (!isEqual(data, n.data)) {
            return this.controller.withOpContext(`userdata`, () => {
              this.controller.command(Commands.setNodeByKey, n.key, { data });
            });
          }
        }
      }
    }
    return this.controller;
  }

  public deleteLocal <T extends keyof UserDataContent>(
    node: Node | null,
    keys: T[],
  ) {
    if (Element.isElement(node)) {
      const { uid } = this.controller;
      const uuid: string | undefined = node.data?.uuid;
      if (uid && uuid) {
        this.localData = this.deleteProperties(
          this.localData,
          uid,
          uuid,
          keys,
        )
        this.update$.next({ uuid });
      } else {
        deleteInjection(this.controller, node, keys);
      }
    }
    return this.controller;
  }

  public flush = (ops: Operation[]) => {
    this.updateNodes(ops);
    this.refreshData(ops);
  }

  public mergeUserData = (target: Document) => {
    const { uid, value } = this.controller;
    const { document } = value;
    const { userData }= document.data;
    const { userData: targetData } = target.data;
    if (uid && targetData?.[uid]) {
      const uuids = Object.keys(targetData[uid]);
      if (uuids.length > 0) {
        const data = omitDeepBy({
          ...document.data,
          userData: {
            ...userData || {},
            [uid]: {
              ...userData?.[uid] || {},
              ...targetData[uid],
            }
          }
        }, isEmptyObj)
        if (data.userData !== document.data.userData) {
          return this.controller.withOpContext(`userdata/${uid}/${uuids.join(',')}`, () => {
            this.controller.command(Commands.setNodeByKey, document.key, { data });
          });
        }
      }
    }
    return this.controller;
  }

  private refreshData (ops: Operation[]) {
    // 根据 OP 类型，刷新 document.data 上数据
    const { value } = this.controller;
    const { document } = value;
    const { userData: originData }= document.data;
    const uuids: string[] = [];
    const uids = Object.keys(originData || {});
    let userData = originData;

    ops.forEach((op) => {
      // op 是删除节点，将 document.data 中对应数据也删除
      if (
        op.type === 'remove_node' &&
        Element.isElement(op.node) &&
        !/merge_node|split_node/.test(op.context || '')
      ) {
        // 遍历节点及子节点
        const iterator = (node) => {
          const uuid = (node as any).data?.uuid;
          if (Element.isElement(node) && uuid) {
            uids.forEach((uid) => {
              userData = this.deleteData(userData, uid, uuid);
            })
            if (userData !== originData) {
              uuids.push(uuid);
            }
          }
        }
        iterator(op.node);
        op.node.forEachDescendant(iterator);
      }
    });

    // 批量更新
    if (uuids.length) {
      const data = omitDeepBy({ ...document.data, userData }, isEmptyObj);
      this.controller.withOpContext(`userdata/*/${uuids.join(',')}`, () => {
        this.controller.command(Commands.setNodeByKey, document.key, { data });
      });
    }
  }

  private updateNodes (ops: Operation[]) {
    ops.forEach((op) => {
      // update$.next 刷新节点视图
      const { context } = op;
      if (op.type === 'set_node') {
        const matched = context?.match(/userdata\/(.*)\/(.*)/);
        if (matched) {
          // context 上携带此次修改用户数据的 uid 和块 uuid
          const uid = matched[1];
          const uuids = matched[2].split(',');
          if (uid === this.controller.uid || uid === '*') {
            uuids.forEach((uuid) => uuid && this.update$.next({ uuid }));
          }
        }
      }
    })
  }

  private mergeProperties (
    data: UserData | undefined,
    uid: string,
    uuid: string,
    properties: Record<string, any>,
  ): UserData {
    return {
      ...data || {},
      [uid]: {
        ...data?.[uid] || {},
        [uuid]: {
          ...data?.[uid]?.[uuid] || {},
          ...properties,
        }
      }
    };
  }

  private deleteData (
    data: UserData | undefined,
    uid: string,
    uuid: string,
  ): UserData {
    if (data?.[uid]?.[uuid] !== undefined) {
      // 删除对应 uuid 下所有数据
      return {
        ...data,
        [uid]: omit(data?.[uid], uuid)
      }; 
    }
    return data || {};
  }

  private deleteProperties (
    data: UserData | undefined,
    uid: string,
    uuid: string,
    keys: string[],
  ): UserData {
    if (typeof data?.[uid]?.[uuid] === 'object') {
      // 对应 block uuid 下有数据，去除相应 keys 属性
      return {
        ...data,
        [uid]: {
          ...data?.[uid],
          [uuid]: omit(data?.[uid]?.[uuid], keys),
        },
      }; 
    }
    return data || {};
  }
}
