/**
 * @packageDocumentation
 * @ignore
 */
import Controller from '../controller/index';
import { ControllerConfigs } from '../interfaces/controller';
import History from '../models/history';
import Operation, { OperationType, SetSelectionOperation } from '../models/operation';
import Document from '../models/document';
import Selection, { SelectionJSON } from '../models/selection';
import hotkeys from '../utils/hotkeys';

const SUPRESS_WITHOUT_SAVING_ERROR = { __SECRET_INTERNALS_DO_NOT_USE_OR_YOU_WILL_BE_FIRED: true };

function deserializeSelection(document: Document, selection: SelectionJSON) {
  const { anchor, focus } = selection;
  return Selection.fromJSON({
    anchor: {
      ...anchor,
      // @ts-ignore
      key: anchor.path ? document.assertNodeByPath(anchor.path).key : anchor.key,
    },
    focus: {
      ...focus,
      // @ts-ignore
      key: focus.path ? document.assertNodeByPath(focus.path).key : focus.key,
    },
  });
}

function deserializeOperation(document: Document, operation: Operation) {
  if (operation.type !== OperationType.SetSelection) {
    return operation;
  }
  const selection = deserializeSelection(document, operation.selection);
  const prevSelection = deserializeSelection(document, operation.prevSelection);
  return {
    ...operation,
    selection,
    prevSelection,
  } as SetSelectionOperation;
}

function serializeSelection(document: Document, selection: Selection) {
  const { anchor, focus } = selection;
  return {
    anchor: {
      ...anchor.toJSON(),
      path: document.getPath(anchor.key),
    },
    focus: {
      ...focus.toJSON(),
      path: document.getPath(focus.key),
    },
  };
}

export default function createHistoryPlugin(configs?: ControllerConfigs) {
  const maxHistorySize = configs?.history?.maxHistorySize;

  function redo(controller: Controller) {
    const { value } = controller;
    const history: History = value.data.history || History.create({ maxHistorySize });
    const { redos } = history;
    if (!redos.length) {
      return controller;
    }

    const batch = redos[redos.length - 1];
    controller.withoutNormalizing(() => {
      controller.withoutSaving(() => {
        batch.forEach(op => {
          controller.applyOperation(deserializeOperation(controller.value.document, op));
        });
      }, SUPRESS_WITHOUT_SAVING_ERROR);
    });
    const newHistory = history.moveLastRedoRecordToUndos();
    return controller.setData({
      ...value.data,
      history: newHistory,
    });
  }

  function undo(controller: Controller) {
    const { value } = controller;
    const history: History = value.data.history || History.create({ maxHistorySize });
    const { undos } = history;
    if (!undos.length) {
      return controller;
    }

    const undoRecord: Operation[] = [];
    controller.withoutNormalizing(() => {
      controller.withoutSaving(() => {
        const reversed = [...undos[undos.length - 1]].reverse();
        reversed.forEach(op => {
          undoRecord.unshift(undoOperation(controller, op));
          controller.applyOperation(deserializeOperation(controller.value.document, Operation.invert(op)));
        });
      }, SUPRESS_WITHOUT_SAVING_ERROR);
    });
    const newHistory = history.moveLastUndoRecordToRedos(undoRecord);
    return controller.setData({
      ...value.data,
      history: newHistory,
    });
  }

  function undoOperation(controller: Controller, operation: Operation) {
    if (operation.type === OperationType.InsertNode) {
      const { document } = controller.value;
      return {
        ...operation,
        node: document.assertNodeByPath(operation.path),
      };
    }
    return operation;
  }

  function onConstruct(controller: Controller, next) {
    // @ts-ignore
    const privateApplyOperation = controller.privateApplyOperation;
    // @ts-ignore
    controller.privateApplyOperation = (op: Operation) => {
      // @ts-ignore
      if (controller.flag.saving) {
        const { value, operations } = controller;
        const { document, data } = value;

        // @ts-ignore
        const operation: Operation = op.type === OperationType.SetSelection
          ? {
            ...op,
            selection: serializeSelection(document, op.selection),
            prevSelection: serializeSelection(document, op.prevSelection),
          }
          : op;

        const history: History = data.history || History.create({ maxHistorySize });
        const newHistory = history.mergeToLastUndoIfNeed(operation, operations.length !== 0);
        const newValue = value.set('data', {
          ...value.data,
          history: newHistory,
        });
        controller.setValue(newValue, {
          clearOperations: false,
          normalize: false,
          clearHots: false,
        });
      }
      return privateApplyOperation.call(controller, op);
    };
    return next();
  }

  function onKeyDown(event: React.KeyboardEvent, controller: Controller, next) {
    if (hotkeys.isRedo(event as any)) {
      event.preventDefault();
      return controller.withoutPending((ctrl) => {
        return ctrl.command('redo');
      });
    }
    if (hotkeys.isUndo(event as any)) {
      event.preventDefault();
      return controller.withoutPending((ctrl) => {
        return ctrl.command('undo');
      });
    }
    return next();
  }

  return {
    commands: {
      redo,
      undo,
    },
    onConstruct,
    onKeyDown,
    models: [],
  };
}
