import React from 'react';
import equal from 'fast-deep-equal';
import { debounce } from 'lodash-es';
import Controller from '../controller';
import Selection from '../models/selection';
import { TextPoint } from '../models/point';
import { scrollToSelection } from '../utils/dom';
import { IS_MOBILE } from '../utils/environment';
import { isScrolling } from '../utils/dom/outsideScroller';
import { pointAtStartOfNode, pointAtEndOfNode } from '../queries';

const DEBOUNCE_TIME_SCROLL_BY_SELECTION = 60;

export function useScrollBySelection(
  scrollableContainerRef: React.MutableRefObject<HTMLDivElement | null>,
  contentRef: React.MutableRefObject<HTMLDivElement | null>,
  controller: Controller,
) {
  const { selection, composing } = controller.value;
  const scrollDepsRef = React.useRef<{ selection: Selection | null; composing: string }>({ selection: null, composing: '' });

  const scrollBySelection = React.useMemo(
    () => debounce((targetSelection: Selection, targetComposing: string) => {
      if (
        (targetSelection.isExpanded || !controller.readOnly) && // 拖选时 or 编辑态下的光标 可触发滚动
        controller.value.isFocused &&
        contentRef.current &&
        scrollableContainerRef.current &&
        (!equal(targetSelection, scrollDepsRef.current.selection) || targetComposing !== scrollDepsRef.current.composing)
      ) {
        scrollDepsRef.current = {
          selection: targetSelection,
          composing,
        };

        // 移动端：选区 Expand 时候，没必要每次渲染都进行 scroll，不然会导致无法跨段落选择
        if (IS_MOBILE && targetSelection.isExpanded) {
          return;
        }

        // 表格设置的选区，禁止 scroll
        if (targetSelection.data.isByTable) {
          return;
        }

        if (isScrolling(controller)) {
          return;
        }

        // 全选状态下，就不要scroll了。
        const { document: latestDocument, selection: latestSelection } = controller.value;
        const { start, end } = latestSelection.sort(latestDocument);
        const startPoint = controller.query(pointAtStartOfNode, latestDocument);
        const endPoint = controller.query(pointAtEndOfNode, latestDocument);

        const skipScroll = equal(start, startPoint) && equal(end, endPoint);
        if (skipScroll) {
          return;
        }

        const target = (
          composing
            ? Selection.create({
              ...targetSelection,
              focus: TextPoint.create({
                ...targetSelection.focus,
                offset: (targetSelection.focus as TextPoint).offset + targetComposing.length,
              }),
            })
            : targetSelection
        );

        scrollToSelection(
          target,
          contentRef.current,
          scrollableContainerRef.current,
          controller,
        );
      }
    }, DEBOUNCE_TIME_SCROLL_BY_SELECTION),
    [contentRef, scrollableContainerRef, controller],
  );

  // 根据选区变化滚动页面
  React.useEffect(() => {
    // 排除渲染后需要滚回至原位置而非选区所在位置的情况
    // BACKGROUND: 协同编辑时，若协同者在同一段落的本地选区之前编辑，会导致本地选区变化
    if (controller.shouldScrollToSelection) {
      scrollBySelection(selection, composing);
    }
    return () => {
      scrollBySelection.cancel();
    };
    // 这里不能加 document 依赖，因为可能会导致多人协同的时候，异常滚动。
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [controller, selection, composing]);
}
