import { omit } from 'lodash-es';
import Controller from '.';
import { GROUP_COLLAPSED_KEY } from '../constants';
import { OperationWithValue } from '../interfaces/controller';
import { Group, GroupWithKey } from '../interfaces/group';
import Element from '../models/element';
import { ReplaySubject } from '../models/observable';
import Operation, { OperationType } from '../models/operation';
import Node from '../models/node';

type Groups = Record<string, Group>;

/**
 * 模型分组管理
 */
class GroupManager {
  private groups: {
    current: Groups;
    prev: Groups;
  } = {
    current: {},
    prev: {},
  };

  public groups$ = new ReplaySubject<typeof this.groups['current']>();

  private controller: Controller;

  private CACHE = {
    NODE_GROUP: new Map<string, string>(),
  };

  constructor(controller: Controller) {
    this.controller = controller;
  }

  /**
   * 清理 group manager 内存占用
   */
  public cleanup() {
    this.groups = { current: {}, prev: {} };
    this.CACHE.NODE_GROUP.clear();
  }

  /**
   * 获得指定 key 的分组
   * @param key
   * @returns
   */
  public getGroup(key: string) {
    return this.groups.current[key];
  }

  /**
   * 获得所有分组
   * @returns
   */
  public getGroups() {
    return this.groups.current;
  }

  /**
   * 向订阅节点通知最新的分组信息
   * @param force
   * @returns
   */
  public notifyNewGroups(force = false): this {
    if (force || this.groups.current !== this.groups.prev) {
      this.groups.prev = this.groups.current;
      this.groups$.next(this.groups.current);
    }
    return this;
  }

  /**
   * 设置分组数据
   * @param key
   * @param data
   * @returns
   */
  public setGroupData<T = {}>(key: string, data: T) {
    const group = this.getGroup(key);
    this.groups.current = {
      ...this.groups.current,
      [key]: {
        ...group,
        data: {
          ...data,
          [GROUP_COLLAPSED_KEY]: group.data[GROUP_COLLAPSED_KEY],
        },
      },
    };
    this.notifyNewGroups();
    return this;
  }

  /**
   * 融合分组数据
   * @param key
   * @param data
   * @returns
   */
  public mergeGroupData<T = {}>(key: string, data: Partial<T>) {
    const group = this.getGroup(key);
    this.groups.current = {
      ...this.groups.current,
      [key]: { ...group, data: { ...group.data, ...data } },
    };
    this.notifyNewGroups();
    return this;
  }

  /**
   * 生成全文所有的 Groups
   */
  public generateAllGroups(): this {
    this.controller.value.document.forEachDescendant((node) => {
      if (node.isElement()) {
        this.generateGroup(node);
      }
    });
    return this;
  }

  public initGroups(groups: Groups): this {
    if (groups) {
      this.groups.current = groups;
      this.groups.prev = groups;
    }
    return this;
  }

  /**
   * 获得节点所在的分组序列
   * @param node
   * @returns
   */
  public getNodeGroups(node: Element): GroupWithKey[] {
    const { document } = this.controller.value;
    const groups: GroupWithKey[] = [];
    const elementGroup = this.getGroup(node.key);
    if (elementGroup) {
      groups.push({ key: node.key, ...elementGroup });
    }
    let groupKey = this.CACHE.NODE_GROUP.get(node.key);
    while (groupKey !== undefined) {
      const group = this.getGroup(groupKey);
      if (group) {
        groups.push({ key: groupKey, ...group });
      }
      let groupElement = document.getNode(groupKey);
      if (!groupElement) {
        break;
      }

      groupKey = this.CACHE.NODE_GROUP.get(groupElement.key);
      while (
        groupKey === undefined &&
        groupElement &&
        groupElement !== document
      ) {
        const parent = document.assertParent(groupElement.key);
        groupKey = this.CACHE.NODE_GROUP.get(parent.key);
        groupElement = parent;
      }
    }
    return groups;
  }

  /**
   * 针对特殊变更，重新分组
   * @param operationWithValue
   */
  public regenerateGroup(
    operationWithValue: OperationWithValue & { operation: Operation },
  ): this {
    const { operation, value, newValue } = operationWithValue;
    if (
      operation.type === OperationType.RemoveNode &&
      operation.node.isElement()
    ) {
      const prevGroup = this.getGroup(operation.node.key);
      // 节点删除后，对应删除其分组
      this.removeGroup(operation.node.key);
      // 从删除节点前一个节点开始重新分组
      this.regenerateGroupFrom(
        value.document.getPreviousSibling(operation.node.key),
        prevGroup?.data[GROUP_COLLAPSED_KEY] || false,
      );
    } else if (
      operation.type === OperationType.InsertNode &&
      operation.node.isElement()
    ) {
      const node = newValue.document.assertNodeByPath(operation.path);
      const prevNode = newValue.document.getPreviousSibling(node.key);
      const prevGroup = prevNode && this.getGroup(prevNode.key);
      // 从当前重新节点进行分组
      this.regenerateGroupFrom(
        operation.node,
        prevGroup?.data[GROUP_COLLAPSED_KEY] || false,
      );
      // 对新插入节点的子节点进行分组
      operation.node.forEachDescendant((n) => {
        if (n.isElement()) {
          this.generateGroup(n, prevGroup?.data[GROUP_COLLAPSED_KEY] || false);
        }
      });
    } else if (operation.type === OperationType.SetNode) {
      // 删除节点原来的分组
      const node = newValue.document.assertNodeByPath(operation.path);
      const oldNode = value.document.assertNodeByPath(operation.path);
      const prevGroup = this.getGroup(node.key);
      const prevOldGroup = this.getGroup(oldNode.key);
      this.removeGroup(node.key);
      // 从当前节点开始，重新分组
      this.regenerateGroupFrom(
        node,
        prevGroup?.data[GROUP_COLLAPSED_KEY] || false,
      );
    } else if (operation.type === OperationType.MergeNode) {
      const node = value.document.assertNodeByPath(operation.path);
      const prevGroup = this.getGroup(node.key);
      if (node.isElement()) {
        // 删除节点所在分组
        this.removeGroup(node.key);
        // 从合并到的节点开始重新分组
        this.regenerateGroupFrom(
          newValue.document.getPreviousSiblingByPath(operation.path),
          prevGroup?.data[GROUP_COLLAPSED_KEY] || false
        );
      }
    } else if (operation.type === OperationType.SplitNode) {
      const node = value.document.assertNodeByPath(operation.path);
      if (node.isElement()) {
        const prevGroup = this.getGroup(node.key);
        // 删除节点所在分组
        this.removeGroup(node.key);
        // 从合并到的节点开始重新分组
        this.regenerateGroupFrom(
          newValue.document.getNextSiblingByPath(operation.path),
          prevGroup?.data[GROUP_COLLAPSED_KEY] || false
        );
      }
    } else if (operation.type === OperationType.MoveNode) {
      // 从移动起点开始重新分组
      const node = value.document.assertNodeByPath(operation.path);
      this.regenerateGroupFrom(value.document.getPreviousSibling(node.key));
      // 从移动终点开始重新分组
      this.regenerateGroupFrom(node);
    }
    return this;
  }

  private removeGroup(key: string | null) {
    if (!key) {
      return this;
    }
    this.groups.current = omit(this.groups.current, key);
    this.CACHE.NODE_GROUP.delete(key);
    return this;
  }

  private generateGroup(element: Element, isCollapsed = false) {
    const group = this.controller.run('generateGroup', element);
    const currentGroup = this.getGroup(element.key);
    // 如果分组已经不存在，则分组列表中丢弃该分组
    if (!group && currentGroup) {
      this.groups.current = omit(this.groups.current, element.key);
    }
    // 若产生了新的分组，则添加该分组
    else if (group) {
      // 如果分组类型变更，则新建分组
      if (currentGroup?.type !== group.type) {
        this.groups.current = {
          ...this.groups.current,
          [element.key]: {
            ...group,
            data: { ...group.data, [GROUP_COLLAPSED_KEY]: isCollapsed },
          },
        };
      }
      const nodesInGroup = this.getNodesInGroup(
        element,
        this.getGroup(element.key),
      );
      nodesInGroup.forEach((n) => {
        this.CACHE.NODE_GROUP.set(n.key, element.key);
      });
    }
    return this;
  }

  private getNodesInGroup(node: Element, group: Group) {
    const { document } = this.controller.value;
    let next = document.getNextSibling(node.key);
    const nodes: Element[] = [];
    const groups = [group];
    let closestGroup: Group | undefined = group;
    while (next && next.isElement() && closestGroup) {
      const isElementInGroup = this.controller.query(
        'isElementInGroup',
        next,
        closestGroup,
      );
      if (isElementInGroup !== false) {
        if (closestGroup === group) {
          nodes.push(next);
        }
      } else {
        groups.pop();
        closestGroup = groups[groups.length - 1];
        continue;
      }

      const maybeGroup = this.getGroup(next.key);
      if (maybeGroup) {
        groups.push(maybeGroup);
        closestGroup = maybeGroup;
      }

      next = document.getNextSibling(next.key);
    }
    return nodes;
  }

  private regenerateGroupFrom(node: Node | null, isCollapsed = false): this {
    if (!node || !node.isElement()) {
      return this;
    }

    const { document } = this.controller.value;
    // 对变更节点所在的前序分组进行重新分组
    while (node) {
      this.generateGroup(node as Element, isCollapsed);
      node = document.getPreviousSibling(node.key);
    }
    return this;
  }
}

export default GroupManager;
