/* eslint-disable guard-for-in */
/* eslint-disable @typescript-eslint/member-ordering */
import DomParser, { createIframe } from './DomParser';
import State from './State';
import type { MoSerializationPlugin, MoHtmlParser, JsonML } from '../../type';

type Event = 'onOpenTag' | 'onText' | 'onCloseTag';
const MeasureStyle: any = {
  left: '-999px',
  position: 'absolute',
  visibility: 'hidden',
  whiteSpace: 'nowrap',
  zIndex: -999,
};

export default class Deserializer {
  private readonly plugins: MoSerializationPlugin[];
  private readonly htmlFrom: string;
  private readonly parser: MoHtmlParser;
  private iframe = createIframe();

  constructor(options: Record<string, any>) {
    this.plugins = options.plugins;
    this.htmlFrom = options.htmlFrom;
    this.parser = options.parser || new DomParser();
  }

  private isDomParser() {
    return this.parser instanceof DomParser
  }

  deserialize(html: string, configs: Record<string, any> = {}): JsonML {
    const state = new State({ });

    if (!this.isDomParser()) {
      document.body.appendChild(this.iframe!);
      const { body: htmlDom } = this.iframe!.contentDocument as Document;
      if (htmlDom) {
        htmlDom.innerHTML = html;
      }
      state.htmlDom = htmlDom;
    }    

    let rules = this.plugins
      .map((plugin) => {
        const { htmlToJsonML } = plugin;
        if (htmlToJsonML) {
          return typeof htmlToJsonML === 'function'
            ? htmlToJsonML(this.htmlFrom)
            : htmlToJsonML;
        }
        return null;
      })
      .filter((rule) => !!rule);

    rules = rules.map((rule) => {
      if (!!rule!.name && rule!.name in configs) {
        rule = {
          ...rule,
          ...configs[rule!.name],
        };
      }
      return rule;
    });

    function onEvent(event: Event, ...args: any[]): void {
      for (let i = 0; i < rules.length; i += 1) {
        const rule = rules[i]![event];
        if (!rule) {
          continue; // eslint-disable-line no-continue
        }
        // @ts-ignore
        const ok = rule(state, ...args);
        if (ok) break;
      }
    }

    this.parser.init(
      {
        onOpenTag(...args: any[]) {
          onEvent('onOpenTag', ...args);
        },
        onText(...args: any[]) {
          onEvent('onText', ...args);
        },
        onCloseTag(...args: any[]) {
          onEvent('onCloseTag', ...args);
        },
      },
      {
        decodeEntities: true,
        state,
      },
    );
    this.parser.write(html);
    this.parser.end();
    if (!this.isDomParser()) {
      document.body.removeChild(this.iframe!);
    }
    return state.getJsonML();
  }
}
