/**
 * @packageDocumentation
 * @module Models
 */
import isPlainObject from 'is-plain-object';
import { Serializable, SerializeOptions } from '../interfaces/serializable';
import ImmutableRecord from './immutableRecord';
import { TextPointJSON, TextPoint } from './point';
import Mark, { MarkJSON } from './mark';

export interface DecorationProperties {
  start: TextPoint;
  end: TextPoint;
  mark: Mark;
}

export interface DecorationJSON {
  start: TextPointJSON;
  end: TextPointJSON;
  mark: MarkJSON;
}

export default class Decoration extends ImmutableRecord
  implements Serializable<DecorationJSON> {
  static create(props: Decoration | DecorationProperties): Decoration {
    if (Decoration.isDecoration(props)) {
      return props;
    }

    if (isPlainObject(props)) {
      const { start, end, mark } = props;
      return new Decoration({
        start: TextPoint.create(start),
        end: TextPoint.create(end),
        mark: Mark.create(mark),
      });
    }

    throw new Error(
      `\`Decoration.create\` only accepts objects or decorations, but you passed it: ${props}`,
    );
  }

  static fromJSON(json: DecorationJSON) {
    const { start, end, mark } = json;
    return Decoration.create({
      start: TextPoint.fromJSON(start),
      end: TextPoint.fromJSON(end),
      mark: Mark.fromJSON(mark),
    });
  }

  static isDecoration(maybeDecoration: any): maybeDecoration is Decoration {
    return maybeDecoration instanceof Decoration;
  }

  /**
   * Decoration的start point
   */
  public readonly start: TextPoint;

  /**
   * Decoration的end point
   */
  public readonly end: TextPoint;

  /**
   * Decoration的mark
   */
  public readonly mark: Mark;

  private constructor(props: DecorationProperties) {
    super();
    const { start, end, mark } = props;
    this.start = start;
    this.end = end;
    this.mark = mark;
    this.freeze();
  }

  public merge(props: Partial<DecorationProperties>) {
    return new Decoration({
      start: this.start,
      end: this.end,
      mark: this.mark,
      ...props,
    });
  }

  public set<T extends keyof DecorationProperties>(
    key: T,
    value: DecorationProperties[T],
  ) {
    return this.merge({
      [key]: value,
    });
  }

  public toJSON(options?: SerializeOptions) {
    return {
      start: this.start.toJSON(options),
      end: this.end.toJSON(options),
      mark: this.mark.toJSON(),
    };
  }
}
