import type { Attributes, LeafRule, TextMark, ValueToJsonMLRule } from './../../type/rules';
import Text from '../../../models/text';
import type Node from '../../../models/node';
import type Leaf from '../../../models/leaf';
import type Mark from '../../../models/mark';
import type { JsonML } from '../../utils/jsonml';

export function marksToAttrs(marks: Mark[], converts: Array<NonNullable<LeafRule['markToJsonMLAttrs']>>): Attributes {
  return marks.reduce((attrs: Attributes, mark: Mark) => {
    let textMark: TextMark | undefined;
    for (let i = 0, len = converts.length; i < len; i++) {
      const m = converts[i](mark);
      if (m) {
        textMark = m;
        break;
      }
    }
    if (!textMark) {
      textMark = { [mark.type]: mark.data };
    }

    return {
      ...attrs,
      ...textMark,
    }
  }, {} as Attributes);
}

export function createValueToJsonML(markToAttrs: Array<LeafRule['markToJsonMLAttrs']>): ValueToJsonMLRule {
  const converts = markToAttrs.filter((f): f is NonNullable<LeafRule['markToJsonMLAttrs']> => !!f)

  const rule = {
    match: (node: Node): boolean => {
      return Text.isText(node);
    },
  
    convert: (node: Text): JsonML => {
      return [
        'span',
        { 'data-type': 'text' },
        // @ts-ignore
        ...node.leaves.reduce((acc: JsonML[], leaf: Leaf) => ([
          ...acc,
          [
            'span',
            {
              ...marksToAttrs(leaf.marks!, converts),
              'data-type': 'leaf',
            },
            leaf.text,
          ],
        ]), []),
      ];
    },
  }
  return rule;
}
