import * as React from 'react';

export const hasIntersectionObserver = process.env.NODE_ENV === 'test'
  || (typeof window !== 'undefined' && 'IntersectionObserver' in window);

type IntersectionChangeHandler = (entry: IntersectionObserverEntry) => void;

class IntersectionObserver {
  private static instance: IntersectionObserver | null = null;
  public static getInstance() {
    if (!IntersectionObserver.instance) {
      IntersectionObserver.instance = new IntersectionObserver();
    }
    return IntersectionObserver.instance;
  }

  private elementToHandler: WeakMap<Element, IntersectionChangeHandler>;
  private nativeIO: globalThis.IntersectionObserver | null;
  private constructor() {
    this.elementToHandler = new WeakMap();
    this.nativeIO = hasIntersectionObserver
      ? new window.IntersectionObserver(this.handleIntersectionChange)
      : null;
  }
  private handleIntersectionChange = (entries: IntersectionObserverEntry[]) => {
    entries.forEach(entry => {
      const handler = this.elementToHandler.get(entry.target);
      if (handler) {
        handler(entry);
      }
    });
  }

  public observe(element: Element, handler: IntersectionChangeHandler) {
    this.elementToHandler.set(element, handler);
    this.nativeIO?.observe(element);
  }
  public unobserve(element: Element) {
    this.elementToHandler.delete(element);
    this.nativeIO?.unobserve(element);
  }
}

export default function useElementVisibility(
  elementRef: React.MutableRefObject<HTMLElement | null>,
  defaultVisible: boolean = false,
): [boolean, () => void] {
  const [visible, setVisible] = React.useState(defaultVisible);
  const unobserve = () => {
    if (elementRef.current) {
      IntersectionObserver.getInstance().unobserve(elementRef.current);
    }
  };
  React.useLayoutEffect(() => {
    if (elementRef.current) {
      IntersectionObserver.getInstance().observe(
        elementRef.current,
        (entry: IntersectionObserverEntry) => setVisible(entry.isIntersecting),
      );
      return unobserve;
    }
    return undefined;
  }, []);
  return [visible, unobserve];
}