import { isPlainObject, isEmpty, omitBy, isArray, forOwn, isObject } from 'lodash-es';

export function isEmptyObj (value) {
  if (isPlainObject(value) || isArray(value)) {
    return isEmpty(value)
  }
  return false
}

export function omitEmptyObj (obj: object) {
  return omitBy(obj, isEmptyObj);
}

export function omitDeepBy (obj: object, predicate: (o) => boolean) {
  let nextObj = obj;
  forOwn(obj, (value, key) => {
    if (isObject(value)) {
      nextObj = {
        ...nextObj,
        [key]: omitDeepBy(value, predicate)
      }
    }
  })
  return omitBy(nextObj, predicate);
}
