import { Selector } from '../../constants';
import getElementByCangjieKey from './getElementByCangjieKey';

function findDOMLeaves(key: string, root: Window | Element = window) {
  const element = root instanceof Element ? root : root.document;
  const firstLeaf = getElementByCangjieKey(`${key}:0`, element);
  const leafBlock = firstLeaf?.closest(`[${Selector.leafBlock}]`);
  return leafBlock ? Array.from(leafBlock.querySelectorAll<HTMLElement>(`[${Selector.key}^="${key}:"]`)) : [];
}

export default findDOMLeaves;