import * as React from 'react';
import Controller from '../controller';
import RenderNodeProps from '../interfaces/renderNodeProps';
import Node from '../models/node';
import { Subject } from '../models/observable';

interface FirstRenderContextValue {
  animationFrameSubject: Subject<undefined>;
  delayRenderSlices: Set<string>;
}
const FirstRenderContext =
  React.createContext<FirstRenderContextValue | null>(null);

interface FirstRenderManagerProps {
  controller: Controller;
  isFirstRenderSlice: (node: Node) => boolean;
  firstFrameSize: number;
  children: React.ReactNode;
}
function FirstRenderManager({
  controller,
  isFirstRenderSlice,
  firstFrameSize,
  children,
}: FirstRenderManagerProps) {
  const animationFrameSubject = React.useMemo(
    () => new Subject<undefined>(),
    [],
  );

  const delayRenderSlices = React.useMemo(() => {
    const delays = new Set<string>();

    let remained = firstFrameSize;
    const document = controller.view;
    document.forEachDescendant((node) => {
      if (remained <= 0 && isFirstRenderSlice(node)) {
        delays.add(node.key);
      }
      remained -= 1;
    });

    return delays;
  }, []);

  const context = React.useMemo(
    () => ({
      animationFrameSubject,
      delayRenderSlices,
    }),
    [animationFrameSubject, delayRenderSlices],
  );

  React.useEffect(() => {
    let raf: number | null = null;
    function loop() {
      if (!delayRenderSlices.size) {
        return;
      }
      raf = window.requestAnimationFrame(() => {
        context.animationFrameSubject.next(undefined);
        loop();
      });
    }
    loop();
    return () => {
      if (raf) {
        window.cancelAnimationFrame(raf);
      }
    };
  }, []);

  return (
    <FirstRenderContext.Provider value={context}>
      {children}
    </FirstRenderContext.Provider>
  );
}

function useShouldMount(node: Node) {
  const context = React.useContext(FirstRenderContext);
  if (!context) {
    throw new Error(
      "The `useShouldMount` hook must be used inside the <FirstRenderManager> component's context.",
    );
  }

  const [shouldMount, setShouldMount] = React.useState(
    !context.delayRenderSlices.has(node.key),
  );

  React.useEffect(() => {
    if (shouldMount) {
      return undefined;
    }
    const subscription = context.animationFrameSubject.subscribe(() => {
      subscription.unsubscribe();
      context.delayRenderSlices.delete(node.key);
      setShouldMount(true);
    });
    return subscription.unsubscribe;
  }, []);

  return shouldMount;
}

interface FirstRenderSliceProps {
  node: Node;
  children: React.ReactElement;
}
function FirstRenderSlice({ node, children }: FirstRenderSliceProps) {
  const shouldMount = useShouldMount(node);
  return shouldMount ? children : null;
}

interface PerfFirstRenderPluginConfig {
  isFirstRenderSlice: (node: Node) => boolean;
  firstFrameSize?: number;
}
function createPerfFirstRenderPlugin(config: PerfFirstRenderPluginConfig) {
  const { isFirstRenderSlice, firstFrameSize = 1000 } = config;

  function renderEditable(_, controller: Controller, next) {
    return (
      <FirstRenderManager
        controller={controller}
        isFirstRenderSlice={isFirstRenderSlice}
        firstFrameSize={firstFrameSize}
      >
        {next()}
      </FirstRenderManager>
    );
  }

  function renderNode(props: RenderNodeProps, _, next) {
    const { node } = props;
    if (!isFirstRenderSlice(node)) {
      return next();
    }
    return <FirstRenderSlice node={node}>{next()}</FirstRenderSlice>;
  }

  return {
    renderEditable,
    renderNode,
  };
}
createPerfFirstRenderPlugin.factory =
  (defaultConfig: PerfFirstRenderPluginConfig) =>
  (config: Partial<PerfFirstRenderPluginConfig>) =>
    createPerfFirstRenderPlugin({ ...defaultConfig, ...config });

export default createPerfFirstRenderPlugin;
