/**
 * @packageDocumentation
 * @ignore
 */
import * as React from 'react';
import { debounce, noop } from 'lodash-es';
import { ControllerContext } from './index';
import Value from '../models/value';
import useHots from '../hooks/useHots';

const isSupportProxy = typeof Proxy !== 'undefined';

interface UseControllerOptions {
  /** 最大延迟渲染周期，指定这个参数用来控制消费了 `useController` hook 的组件，跟随编辑器内容渲染的频度 */
  maxPending?: number;
}

/**
 * 获得 Controller 实例，如果组件严格期望在编辑器内容变更后，不进行重渲，仅消费 Controller 实例，请使用 `useControllerStatic`
 * @param options 
 * @returns 
 */
export default function useController(options?: UseControllerOptions) {
  const { maxPending = 0 } = options || {};
  const context = React.useContext(ControllerContext);
  if (!context) {
    throw new Error(
      "The `useController` hook must be used inside the <Cangjie.Provider> component's context.",
    );
  }

  const isRendering = React.useRef(true);
  isRendering.current = true;
  React.useLayoutEffect(() => {
    isRendering.current = false;
  });

  const [, forceRender] = React.useReducer((s) => s + 1, 0);
  const hasConsumedValue = React.useRef(false);
  const lastConsumedValue = React.useRef<Value | null>(null);
  const controller = React.useMemo(() =>
    !isSupportProxy
      ? context.controller
      : new Proxy(context.controller, {
          get(ctrl, prop) {
            if (prop === 'value' && isRendering.current) {
              hasConsumedValue.current = true;
              lastConsumedValue.current = ctrl[prop];
            }
            return ctrl[prop];
          },
        }),
    [context.controller],
  );

  const debouncedRerender = React.useMemo(() => {
    if (Number.isFinite(maxPending)) {
       return debounce(forceRender, maxPending);
    }
    return noop;
  }, [maxPending]);


  React.useEffect(() => {
    const sub = context.subject.subscribe(() => {
      if (
        (!isSupportProxy || hasConsumedValue.current)
          && context.controller.value !== lastConsumedValue.current
      ) {
        forceRender();
      }
    });
    return sub.unsubscribe;
  }, [context]);

  useHots(controller, debouncedRerender);

  return controller;
}
