import { noop } from 'lodash-es';
import { useState, useEffect } from 'react';
import useControllerStatic from '../controller/useControllerStatic';
import { UserDataContent } from '../controller/userData';
import Element from '../models/element';

/**
 * 获取用户数据
 * @param node 
 * @param key 
 * @returns 
 */
export function useUserData<T extends keyof UserDataContent>(
  node: Element,
  key: T,
) {
  const controller = useControllerStatic();
  const { uid } = controller;
  const initValue = controller.userData.get(node, key);
  const { uuid } = node.data;
  const [data, setData] = useState(initValue);

  useEffect(() => {
    if (uid && uuid) {
      // 订阅用户数据 update$ 更新（uid 和 uuid 存在的情况，存储在 document.data）
      const sub = controller.userData.update$.subscribe((event) => {
        const { uuid: euuid } = event;
        if (euuid === uuid || euuid === '*') {
          // 同一个节点，用户数据发生改变
          const cNode = controller.value.document.getNode(node.key.split('-')[0]);
          if (cNode && Element.isElement(cNode)) {
            const nextData = controller.userData.get(cNode, key);
            setData(nextData);
          }
        }
      });
      return () => sub.unsubscribe();
    } else {
      // 观察节点 node 变化（uid 或 uuid 不存在的情况，存储在 node.data）
      const nextData = node.data?.[key];
      if (nextData !== data) {
        setData(nextData);
      }
      return noop;
    }
  }, [controller, node, uuid, uid, key, data]);

  return data;
}
