/**
 * @packageDocumentation
 * @ignore
 */
import { IS_DEV } from '../utils/environment';
import deepFreeze from '../utils/deepFreeze';

export default abstract class ImmutableRecord {
  protected freeze() {
    if (IS_DEV) {
      deepFreeze(this);
    }
  }

  abstract merge(props: Partial<object>): ImmutableRecord;

  abstract set<K extends keyof object>(
    key: K,
    value: object[K],
  ): ImmutableRecord;
}
