/**
 * @packageDocumentation
 * @module Models
 */
import isPlainObject from 'is-plain-object';
import Element, { ElementProperties } from './element';
import Node from './node';
import Block, { BlockJSON } from './block';
import Inline from './inline';
import Text from './text';
import Mark from './mark';
import Path from './path';
import Selection from './selection';
import Controller from '../controller/index';
import { SerializeOptions } from '../interfaces/serializable';
import Data from '../interfaces/data';
import setUtils from '../utils/setUtils';
import keyUtils from '../utils/keyUtils';
import { EdgePoint } from './point';

export interface DocumentProperties extends ElementProperties {
  nodes?: Block[];
}

export interface DocumentJSON {
  klass?: 'document';
  key?: string;
  data?: Data;
  nodes?: BlockJSON[];
}

export default class Document extends Element {
  static create(props: Document | DocumentProperties = {}): Document {
    if (Document.isDocument(props)) {
      return props;
    }

    if (isPlainObject(props)) {
      const {
        key = keyUtils.create(),
        data = {},
        nodes = [],
      } = props;
      return new Document({
        key,
        data,
        nodes,
      });
    }

    throw new Error(
      `\`Document.create\` only accepts objects or documents, but you passed it: ${props}`,
    );
  }

  static fromJSON(json: DocumentJSON) {
    return Document.create({
      ...json,
      nodes: json.nodes ? json.nodes.map(Block.fromJSON) : [],
    });
  }

  static isDocument(maybeDocument: any): maybeDocument is Document {
    return maybeDocument instanceof Document;
  }

  protected constructor(props: Required<DocumentProperties>) {
    super(props);
    const { nodes } = props;
    this.nodes = nodes;
  }

  public readonly type: string = 'document';

  public readonly nodes: Block[];

  public merge(props: Partial<DocumentProperties>) {
    return new Document({
      key: this.key,
      data: this.data,
      nodes: this.nodes,
      ...props,
    });
  }

  public set<T extends keyof DocumentProperties>(
    key: T,
    value: Required<DocumentProperties>[T],
  ) {
    return this.merge({
      [key]: value,
    });
  }

  /**
   * 获取selection内对应的所有marks
   */
  public getMarksAtRange(range: Selection) {
    if (range.isCollapsed && range.anchor.isEdgePoint()) {
      return [];
    }
    const { start, end } = range.convertToTextPoints(this);
    if (range.isCollapsed) {
      return this.getMarksAtPosition(start.key, start.offset);
    }

    if (start.key === end.key) {
      const startText = this.getNode(start.key);
      return Text.isText(startText)
        ? startText.getMarksBetweenOffsets(start.offset, end.offset)
        : [];
    }

    const texts = this.getTextsBetweenPositions(start.key, end.key);
    return texts.reduce<Mark[]>((marks, text) => {
      if (text.key === start.key) {
        return setUtils.union(
          marks,
          text.getMarksBetweenOffsets(start.offset, text.text.length),
        );
      }
      if (text.key === end.key) {
        return setUtils.union(
          marks,
          text.getMarksBetweenOffsets(0, end.offset),
        );
      }
      return setUtils.union(marks, text.getMarks());
    }, []);
  }

  /**
   * 获取selection内对应的所有公共marks
   */
  public getActiveMarksAtRange(range: Selection) {
    if (range.isCollapsed && range.anchor.isEdgePoint()) {
      return [];
    }
    if (!Selection.isValidSelection(this, range)) {
      return [];
    }
    const { start, end } = range.convertToTextPoints(this);
    if (range.isCollapsed) {
      return this.getMarksAtPosition(start.key, start.offset);
    }

    const startNode = this.getNode(start.key);
    let startText = startNode?.isText() ? startNode : null;
    if (!startText) {
      return [];
    }

    let startKey = start.key;
    let startOffset = start.offset;
    let endKey = end.key;
    let endOffset = end.offset;

    if (startKey === endKey) {
      return startText.getActiveMarksBetweenOffsets(startOffset, endOffset);
    }

    while (startKey !== endKey && endOffset === 0) {
      const endText = this.getPreviousText(endKey);
      if (!endText) {
        return [];
      }
      endKey = endText.key;
      endOffset = endText.text.length;
    }

    while (startKey !== endKey && startOffset === startText.text.length) {
      startText = this.getNextText(startKey);
      if (!startText) {
        return [];
      }
      startKey = startText.key;
      startOffset = 0;
    }

    const startMarks = startText.getActiveMarksBetweenOffsets(
      startOffset,
      startText.text.length,
    );
    if (startMarks.length === 0) return [];
    const endText = this.getNode(endKey);
    const endMarks = endText?.isText()
      ? endText.getActiveMarksBetweenOffsets(0, endOffset)
      : [];
    let marks = setUtils.intersect(startMarks, endMarks);
    // If marks is already empty, the active marks is empty
    if (marks.length === 0) return marks;

    if (startKey === endKey) return marks;

    let text = this.getNextText(startKey);

    while (text && text.key !== endKey) {
      if (text.text.length !== 0) {
        marks = setUtils.intersect(marks, text.getActiveMarks());
        if (marks.length === 0) return [];
      }

      text = this.getNextText(text.key);
    }
    return marks;
  }

  public getMarksAtPosition(key: string, offset: number) {
    const text = this.getNode(key);
    const currentMarks = !text?.isText() ? [] : text.getMarksAtIndex(offset);
    if (offset !== 0) {
      return currentMarks;
    }

    const closestBlock = this.getClosestBlock(key);
    if (!closestBlock || closestBlock.text === '') {
      // Insert mark for empty block; the empty block are often created by split node or add marks in a range including empty blocks
      return currentMarks;
    }

    const previous = this.getPreviousText(key);
    if (previous && closestBlock.getNode(previous.key)) {
      return previous.getMarksAtIndex(previous.text.length);
    }

    return currentMarks;
  }

  /**
   * 获取插入点对应的marks
   * 如果选区是collapse的，返回插入位置的marks;
   * 如果插入位置在新的text节点，则尝试获取同一block的前一个text对应的mark；
   * 如果选区是expand，则返回start位置对应的marks
   */
  public getInsertMarksAtRange(range: Selection) {
    if (range.isCollapsed && range.anchor.isEdgePoint()) {
      return [];
    }
    const { start } = range.convertToTextPoints(this);
    if (range.isCollapsed) {
      return this.getMarksAtPosition(start.key, start.offset);
    }

    const text = this.getNode(start.key);
    return !text?.isText() ? [] : text.getMarksAtIndex(start.offset + 1);
  }

  public getNodesAtRange(range?: Selection) {
    if (!range) {
      return [];
    }
    const doc = this;
    const start = range.getStart(doc);
    const end = range.getEnd(doc);

    const startPath = this.getPath(start.key)!;
    const endPath = this.getPath(end.key)!;

    const startIndex = startPath[0];
    const endIndex = endPath[0];
    const startBlock = this.nodes[startIndex];
    const endBlock = this.nodes[endIndex];

    const result: Node[] = [];

    function push(node: Node) {
      result.push(node);
      if (Block.isBlock(node) || Inline.isInline(node)) {
        node.nodes.forEach(push);
      }
    }

    function pushEdgeNode(node: Node) {
      const path = doc.getPath(node.key)!;

      if (
        Path.isEqual(path, startPath) ||
        Path.isAncestor(path, startPath) ||
        Path.isAncestor(startPath, path) ||
        Path.isEqual(path, endPath) ||
        Path.isAncestor(path, endPath) ||
        Path.isAncestor(endPath, path) ||
        (!range!.isCollapsed &&
          Path.isAfter(path, startPath) &&
          Path.isBefore(path, endPath))
      ) {
        result.push(node);
        if (Block.isBlock(node) || Inline.isInline(node)) {
          node.nodes.forEach(pushEdgeNode);
        }
      }
    }

    // Push the nodes in start path
    result.push(startBlock);
    startBlock.nodes.forEach(pushEdgeNode);

    if (startIndex === endIndex) {
      return result;
    }

    // Push the nodes between start and end
    this.nodes.slice(startIndex + 1, endIndex).forEach((node) => {
      push(node);
    });

    // Push the nodes in end path
    result.push(endBlock);
    endBlock.nodes.forEach(pushEdgeNode);
    return result;
  }

  /**
   * 获取最近的支持 edge 选择的 block 节点。
   * 如果 key 对应的节点满足条件，会直接返回此节点。
   */
  public getClosestSelectableBlock(key: string, controller: Controller) {
    let ans = this.getAncestors(key);
    const n = this.getNode(key);
    if (n?.isElement()) {
      ans.push(n);
    }
    ans = ans.reverse();
    for (const node of ans) {
      if (controller.query('isEdgeSelectable', node)) {
        return node;
      } else if (node.isBlock() && controller.query('isVoid', node)) {
        return node;
      }
    }
    return null;
  }

  /**
   * 获取 rootKey 节点下距离 key 最远的支持 edge 选择的 block 节点。
   * 如果 key 对应的节点满足条件，也会返回此节点。
   */
  public getFurthestSelectableBlock(key: string, rootKey: string, controller: Controller) {
    const ans = this.getAncestors(key);
    const n = this.getNode(key);
    if (n?.isElement()) {
      ans.push(n);
    }
    const rootIndex = ans.findIndex((node) => node.key === rootKey);
    if (rootIndex === -1) {
      return null;
    }
    for (let i = rootIndex; i < ans.length; i++) {
      const node = ans[i];
      if (controller.query('isEdgeSelectable', node)) {
        return node;
      } else if (node.isBlock() && controller.query('isVoid', node)) {
        return node;
      }
    }
    return null;
  }

  /**
   * 获取最近的Void父节点
   */
  public getClosestVoid(key: string, controller: Controller) {
    const path = this.getPath(key);
    if (!path) {
      return null;
    }

    return this.getClosestVoidByPath(path, controller);
  }

  /**
   * 获取最近的Void父节点
   */
  private getClosestVoidByPath(path: Path, controller: Controller) {
    return this.getClosestByPath(path, (node) => {
      return controller.query('isVoid', node) as boolean;
    });
  }

  /**
   * 是否有一个Void父节点
   */
  public hasVoidParent(key: string, controller: Controller) {
    const path = this.getPath(key);
    if (!path) {
      return false;
    }
    return this.hasVoidParentByPath(path, controller);
  }

  private hasVoidParentByPath(path: Path, controller: Controller) {
    const closest = this.getClosestVoidByPath(path, controller);
    return !!closest;
  }

  /**
   * 获取最顶层的一个Void节点
   */
  public getFurthsestVoid(key: string, controller: Controller) {
    return this.getFurthsest(
      key,
      (node) => controller.query('isVoid', node) as boolean,
    );
  }

  /**
   * 根据选区，获取一个fragment
   */
  public getFragmentAtRange(range: Selection, controller?: Controller) {
    const { start, end } = range.convertToTextPoints(this);
    const startTextPath = this.getPath(start.key);
    const endTextPath = this.getPath(end.key);
    if (!startTextPath || !endTextPath) {
      return Document.create();
    }
    const startVoid =
      controller && this.getClosestVoidByPath(startTextPath, controller);
    const endVoid =
      controller && this.getClosestVoidByPath(endTextPath, controller);
    const startPath = startVoid
      ? this.assertPath(startVoid.key)
      : startTextPath;
    const endPath = endVoid ? this.assertPath(endVoid.key) : endTextPath;

    let node = this;
    let targetPath = endPath;
    let targetPosition = endVoid ? endVoid.nodes.length : end.offset;
    let mode = 'end';
    while (targetPath.length) {
      const index = targetPath[targetPath.length - 1];
      node = node.splitNode(targetPath, targetPosition);
      targetPath = Path.parent(targetPath);
      targetPosition = index + 1;

      if (!targetPath.length && mode === 'end') {
        targetPath = startPath;
        targetPosition = startVoid ? 0 : start.offset;
        mode = 'start';
      }
    }

    const startIndex = startPath[0] + 1;
    const endIndex = endPath[0] + 2;
    const nodes = node.nodes.slice(startIndex, endIndex);
    // @ts-ignore
    return Document.create({ nodes });
  }

  /**
   * 获取特定区间的所有叶子block
   */
  public getLeafBlocksAtRange(range: Selection): Block[] {
    const { start, end } = range.convertToTextPoints(this);
    const startBlock = this.getClosestBlockFromPoint(start.key);

    // PERF: the most common case is when the range is in a single block node,
    // where we can avoid a lot of iterating of the tree.
    if (start.key === end.key) {
      return [startBlock!];
    }

    const endBlock = this.getClosestBlockFromPoint(end.key);
    const blocks = this.getBlocks();
    const startIndex = blocks.indexOf(startBlock!);
    const endIndex = blocks.indexOf(endBlock!);
    return blocks.slice(startIndex, endIndex + 1);
  }

  /**
   * 循环获取document下的所有叶子blocks节点
   */
  public getBlocks() {
    return Block.getBlocks(this.nodes);
  }

  /**
   * 循环获取document下的所有叶子blocks节点
   */
  public getBlocksByType(type: string) {
    return Block.getBlocksByType(this.nodes, type);
  }

  /**
   * 循环获取document下的特定区间内的全部blocks节点
   */
  public getRootBlocksAtRange(range?: Selection): Block[] {
    if (!range) {
      return [];
    }
    const { start, end } = range.convertToTextPoints(this);

    const startBlock = this.getFurthsestBlock(start.key);
    if (!startBlock) {
      return [];
    }
    if (start.key === end.key) {
      return [startBlock];
    }

    const endBlock = this.getFurthsestBlock(end.key);
    if (!endBlock) {
      return [];
    }
    return this.nodes.slice(
      this.nodes.indexOf(startBlock),
      this.nodes.indexOf(endBlock) + 1,
    );
  }

  /**
   * 判断节点是否在Selection之内
   */
  public isNodeInRange(key: string, range: Selection) {
    const path = this.getPath(key);
    if (!path) {
      return false;
    }
    return this.isNodeInRangeByPath(path, range);
  }

  /**
   * 判断节点是否在Selection之内
   */
  public isNodeInRangeByPath(path: Path, range: Selection) {
    if (!range || range.isCollapsedAtEdge) {
      return false;
    }
    const { start, end } = range.sort(this);
    const startPath = start.getPathIn(this);
    const endPath = end.getPathIn(this);

    // we cannot trust the selection
    if (!startPath || !endPath) {
      return false;
    }

    // edge cases
    if (
      start.isEdgePoint() &&
      start.edge === EdgePoint.AFTER &&
      (Path.isEqual(startPath, path) || Path.isAncestor(startPath, path))
    ) {
      return false;
    }
    if (
      end.isEdgePoint() &&
      end.edge === EdgePoint.BEFORE &&
      (Path.isEqual(endPath, path) || Path.isAncestor(endPath, path))
    ) {
      return false;
    }

    const isBeforeStart = Path.compare(path, startPath) === -1;
    const isAfterEnd = Path.compare(path, endPath) === 1;
    return !isBeforeStart && !isAfterEnd;
  }

  /**
   * 获取Selection内所有的Text节点
   */
  public getTextsAtRange(range: Selection): Text[] {
    const { start, end } = range.convertToTextPoints(this);
    return this.getTextsBetweenPositions(start.key, end.key);
  }

  /**
   * 获取Selection内所有的叶子Inline节点
   */
  public getLeafInlinesAtRange(range: Selection): Inline[] {
    const array = this.getTextsAtRange(range)
      .map((text) => this.getClosestInline(text.key))
      .filter((exists): exists is Inline => Boolean(exists));

    return array;
  }

  /**
   * 获取Selection内的所有最远Inline节点
   */
  public getRootInlinesAtRange(range?: Selection): Inline[] {
    if (!range) {
      return [];
    }

    const array = this.getTextsAtRange(range)
      .map((text) => this.getFurthsestInline(text.key))
      /* eslint-disable @typescript-eslint/indent */
      .reduce<Inline[]>((inlines, inline) => {
        if (inline && !inlines.includes(inline)) {
          inlines.push(inline);
        }
        return inlines;
      }, []);
    /* eslint-enable @typescript-eslint/indent */

    return array;
  }

  public toJSON(options?: SerializeOptions) {
    const object: DocumentJSON = {
      klass: 'document',
      data: { ...this.data },
      nodes: this.nodes.map((node) => node.toJSON(options)),
    };

    if (options?.preserveKeys) {
      object.key = this.key;
    }

    return object;
  }
}
