/**
 * @packageDocumentation
 * @ignore
 */
import Controller from '../controller/index';
import * as Commands from '../commands/index';
import CangjieClipboardEvent from '../events/cangjieClipboard';
import CangjieSelectEvent from '../events/cangjieSelect';
import CangjieDataTransfer from '../models/dataTransfer';
import { ContextMenuItem } from '../interfaces/contextMenu';
import { defaultLocale, ContextMenuLocale } from '../locale/contextMenu';
import { ControllerConfigs } from '../interfaces/controller';
import createClipboardDataFromSelection from '../utils/createClipboardDataFromSelection';
import { IS_IOS, IS_MOBILE } from '../utils/environment';
import { getClipboardData, readClipboardDataFromHtml } from '../utils/getClipboardData';
import { Copy, Cut, Paste, Select, SelectAll } from '../components/mobile/icons';

export const ACTION_COPY = Symbol('ACTION_COPY');
export const ACTION_COPY_FAIL = Symbol('ACTION_COPY_FAIL');
export const ACTION_CUT = Symbol('ACTION_CUT');
export const ACTION_CUT_FAIL = Symbol('ACTION_CUT_FAIL');
export const ACTION_PASTE = Symbol('ACTION_PASTE');
export const ACTION_PASTE_FAIL = Symbol('ACTION_PASTE_FAIL');
export const ACTION_SELECT = Symbol('ACTION_SELECT');
export const ACTION_SELECT_ALL = Symbol('ACTION_SELECT_ALL');


function execCommandPolyfill(command: string) {
  const maybeHiddenTextArea = window.document.activeElement;
  let hiddenTextAreaOriginValue = '';
  let hiddenTextAreaOriginStart = 0;
  let hiddenTextAreaOriginEnd = 0;
  if (maybeHiddenTextArea instanceof HTMLTextAreaElement) {
    hiddenTextAreaOriginValue = maybeHiddenTextArea.value;
    hiddenTextAreaOriginStart = maybeHiddenTextArea.selectionStart;
    hiddenTextAreaOriginEnd = maybeHiddenTextArea.selectionEnd;
    // NOTE: `execCommand` will not trigger onCopy & onCut on Safari if selection is collapsed.
    maybeHiddenTextArea.value = ' ';
    maybeHiddenTextArea.selectionStart = 0;
    maybeHiddenTextArea.selectionEnd = maybeHiddenTextArea.value.length;
  }
  window.document.execCommand(command);
  if (maybeHiddenTextArea instanceof HTMLTextAreaElement) {
    maybeHiddenTextArea.value = hiddenTextAreaOriginValue;
    maybeHiddenTextArea.selectionStart = hiddenTextAreaOriginStart;
    maybeHiddenTextArea.selectionEnd = hiddenTextAreaOriginEnd;
  }
}

function execCommand(command: string, controller: Controller) {
  if (controller.value.isBlurred) {
    // 手动调用 flush, 触发 仓颉 HiddenTextarea 聚焦
    controller.command(Commands.focus).flush();
    requestAnimationFrame(() => {
      execCommandPolyfill(command);
    });
  } else {
    execCommandPolyfill(command);
  }
}

const pasteMenu = (
  locale: ContextMenuLocale,
): ContextMenuItem => {
  return {
    name: locale.paste,
    action: {
      type: ACTION_PASTE,
    },
    key: 'paste',
    role: 'paste',
    icon: Paste,
    options: {
      hotKey: 'mod+v',
      mobile: true,
    },
  };
};
// [defaultLocale] is the same as [configs.plugins.contextMenu]
function contextMenu(configs?: ControllerConfigs) {
  const locale = configs?.plugins?.contextMenu || defaultLocale;
  return function (
    controller: Controller,
    next: () => ContextMenuItem[] | null,
  ) {
    const menus: ContextMenuItem[] = [];
    const { readOnly, canCopy } = controller;
    const { selection, document } = controller.value;
    const isReadMode = Boolean(readOnly || controller.query('isQuickPreview'));
    const hasVoidParent =
      selection?.isCollapsed &&
      document.hasVoidParent(selection.anchor.key, controller);
    if (hasVoidParent) {
      // 图片等暂不支持ContextMenu
    } else if (selection?.isExpanded) {
      const items: ContextMenuItem[] = [
        {
          name: locale.cut,
          action: {
            type: ACTION_CUT,
          },
          key: 'cut',
          role: 'cut',
          icon: Cut,
          options: {
            hotKey: 'mod+x',
            mobile: true,
            disable: isReadMode,
          },
        },
        {
          name: locale.copy,
          action: {
            type: ACTION_COPY,
          },
          key: 'copy',
          role: 'copy',
          icon: Copy,
          options: {
            disable: isReadMode && !canCopy,
            hotKey: 'mod+c',
            mobile: true,
          },
        },
      ];
      if (!isReadMode) {
        items.push(pasteMenu(locale));
      }
      menus.push(...items);
    } else if (selection?.isCollapsed && !selection.data.isByTable) {
      menus.push(
        {
          name: locale.select,
          action: {
            type: ACTION_SELECT,
          },
          key: 'select',
          icon: Select,
          options: {
            mobile: true,
            group: 10,
          },
        },
        {
          name: locale.selectAll,
          action: {
            type: ACTION_SELECT_ALL,
          },
          key: 'select-all',
          icon: SelectAll,
          options: {
            hotKey: 'mod+a',
            mobile: true,
            group: 10,
          },
        },
      );
      menus.push(
        {
          name: locale.cut,
          action: {
            type: ACTION_CUT,
          },
          key: 'cut',
          role: 'cut',
          options: {
            hotKey: 'mod+x',
            disable: true,
          },
        },
        {
          name: locale.copy,
          action: {
            type: ACTION_COPY,
          },
          key: 'copy',
          role: 'copy',
          options: {
            hotKey: 'mod+c',
            disable: true,
          },
        },
      );
      if (!isReadMode) {
        menus.push(pasteMenu(locale));
      }
    }
    const others = next() || [];
    return [...menus, ...others];
  };
}

function handleAction(action, controller: Controller, next) {
  const { type } = action;

  if (type === ACTION_COPY) {
    if (IS_MOBILE) {
      // 移动端 document.activeElement 非 hiddenTextarea, 无法通过 execCommand 触发 copy 事件
      const event = CangjieClipboardEvent('cangjieCopy');
      try {
        controller.run('onCangjieCopy', event.setClipboardData(createClipboardDataFromSelection(controller)));
      } catch (error) {
        controller.run('onAction', { type: ACTION_COPY_FAIL, payload: { error } });
        throw (error);
      }
    } else {
      execCommand('copy', controller);
    }
  } else if (type === ACTION_CUT) {
    if (IS_MOBILE) {
      const event = CangjieClipboardEvent('cangjieCut');
      try {
        controller.run('onCangjieCut', event.setClipboardData(createClipboardDataFromSelection(controller)));
      } catch (error) {
        controller.run('onAction', { type: ACTION_CUT_FAIL, payload: { error } });
        throw (error);
      }
    } else {
      execCommand('cut', controller);
    }
  } else if (type === ACTION_PASTE) {
    const event = CangjieClipboardEvent('cangjiePaste');
    if (IS_MOBILE) {
      const customEvent = event.setClipboardData(CangjieDataTransfer.create());
      const html = event.clipboardData?.getData('text/html');
      if (html) {
        const customData = readClipboardDataFromHtml(html);
        customData.forEach(([dataType, data]) => {
          customEvent.clipboardData?.setData(dataType, data);
        });
      }
      try {
        controller.run('onCangjiePaste', customEvent);
      } catch (error) {
        controller.run('onAction', { type: ACTION_PASTE_FAIL, payload: { isShortcut: false, error } });
        throw (error);
      }
    } else {
      const isPasteEnable = window.document.execCommand('paste');
      if (!isPasteEnable) {
        getClipboardData().then((clipboardData) => {
          controller.run('onCangjiePaste', event.setClipboardData(clipboardData));
        }, (error) => {
          controller.run('onAction', { type: ACTION_PASTE_FAIL, payload: { isShortcut: false, error } });
          console.error(error);
        });
      }
    }
  } else if (type === ACTION_SELECT) {
    // Select
    const { selection } = controller.value;
    selection &&
      controller.command(Commands.selectWordAtPoint, selection.anchor);
  } else if (type === ACTION_SELECT_ALL) {
    // Select All
    const { document, selection } = controller.value;
    selection &&
      controller.run(
        'onCangjieSelect',
        CangjieSelectEvent({
          selection: selection.moveToRangeOfNode(document, controller),
        }),
      );
  }
  return next();
}
export default (configs?: ControllerConfigs) => ({
  contextMenu: contextMenu(configs),
  onAction: handleAction,
  models: [],
});
