/**
 * @packageDocumentation
 * @module Serializer
 */
import type { Value, Schema } from '../../';
import type { MoSerializationPlugin as Plugin, MoHtmlParser, Mo, MoPlugin, BaseMo, HtmlMo, TextMo, MarkdownMo, AsyncMo, JsonML } from '../type';
import createJsonMLToValue from './jsonMLToValue';
import createValueToJsonML, { createAsyncValueToJsonML } from './valueToJsonML';
import createJsonMLToNode from './jsonMLToNode';
import createNodeToJsonML, { createAsyncNodeToJsonML } from './nodeToJsonML';
import createHtmlToJsonML from './htmlToJsonML';
import createJsonMLToHTML from './jsonMLToHTML';
import createTextToJsonML from './textToJsonML';
import createNodeToText from './nodeToText';
import createValueToText from './valueToText';
import createMarkdownToHTML from './markdownToHTML';
import createHtmlToMarkdown from './htmlToMarkdown';
import { createSchema } from '../schema/createSchema';
import { createSerializerPlugin as documentFactory } from '../plugins/document';
import { createSerializerPlugin as textFactory } from '../plugins/text';


function createBase(p: Plugin[], schema: Schema): BaseMo {
  const plugins = [documentFactory(), textFactory(p), ...p];
  const jsonMLToNode = createJsonMLToNode(plugins);
  const jsonMLToValue = createJsonMLToValue(jsonMLToNode, schema);
  const nodeToJsonML = createNodeToJsonML(plugins);
  const valueToJsonML = createValueToJsonML(nodeToJsonML);

  return {
    // 反序列化 ASL
    jsonMLToValue,
    // 序列化 ASL
    valueToJsonML,

    // 主要用于 operation 序列化、反序列化
    jsonMLToNode,
    nodeToJsonML,
  };
}

function createText(p: Plugin[], base: BaseMo): TextMo {
  const plugins = [textFactory(p), ...p, documentFactory()];

  const beforeValueToText = plugins
    .map((p) => p.valueToText?.hooks?.before)
    .filter((p) => !!p)
    .reduce((acc, p) => (value: Value) => p!(acc!(value)), (value: Value) => value);

  const textToJsonML = createTextToJsonML(plugins);
  const nodeToText = createNodeToText(plugins);
  const valueToText = createValueToText(nodeToText, beforeValueToText);

  return {
    // 纯文本粘贴
    textToJsonML,
    textToValue: function textToValue(text: string): Value {
      return base.jsonMLToValue(textToJsonML(text));
    },

    // 纯文本导出
    valueToText,
  }
}

function createHtml(p: Plugin[], base: BaseMo, parser?: MoHtmlParser): HtmlMo {
  const plugins = [textFactory(p), ...p, documentFactory()];
  
  const htmlToJsonML = createHtmlToJsonML(plugins, parser);
  const jsonMLToHTML = createJsonMLToHTML(plugins);

  return {
    // 富文本粘贴
    htmlToJsonML,
    htmlToValue: function htmlToValue(html: string): Value {
      return base.jsonMLToValue(htmlToJsonML(html), { strict: true });
    },

    // 输出 HTML
    jsonMLToHTML,
    valueToHTML: function valueToHTML(value: Value): string {
      return jsonMLToHTML(base.valueToJsonML(value));
    },
  };
}

function createAsync(p: Plugin[]): AsyncMo {
  const plugins = [textFactory(p), ...p, documentFactory()];
  const asyncNodeToJsonML = createAsyncNodeToJsonML(plugins);
  const asyncValueToJsonML = createAsyncValueToJsonML(asyncNodeToJsonML);
  return {
    asyncValueToJsonML,
  };
}

function createMarkdown(p: Plugin[], base: BaseMo, hs: HtmlMo): MarkdownMo {
  const plugins = [textFactory(p), ...p, documentFactory()];
  const markdownToJsonML = async (markdown: string) => {
    const markdownToHTML = await createMarkdownToHTML(plugins);
    const html = markdownToHTML(markdown);
    return hs.htmlToJsonML(html);
  }


  const jsonMLToMarkdown = async(jsonML: JsonML) => {
    const jsonMLToHTML = createJsonMLToHTML(plugins);
    const htmlToMarkdown = await createHtmlToMarkdown(plugins);
    return htmlToMarkdown(jsonMLToHTML(jsonML));
  }

  return {
    // markdown 粘贴
    markdownToJsonML,
    markdownToValue: async (markdown: string) => {
      const jsonML = await markdownToJsonML(markdown);
      return base.jsonMLToValue(jsonML);
    },
    jsonMLToMarkdown,
  }
}

function create(
  plugins: Plugin[], schema: Schema, parser?: MoHtmlParser,
): Mo {
  const baseSerialzer = createBase(plugins, schema);
  const textSerializer = createText(plugins, baseSerialzer);
  const htmlSerializer = createHtml(plugins, baseSerialzer, parser);
  const mdSerializer = createMarkdown(plugins, baseSerialzer, htmlSerializer);
  const asyncSerializer = createAsync(plugins);

  return {
    ...baseSerialzer,
    ...textSerializer,
    ...htmlSerializer,
    ...mdSerializer,
    ...asyncSerializer,
  }
}

function getSchemaAndPluginsFromMoPlugins(
  plugins: MoPlugin[],
) {
  const schema = createSchema(plugins);
  const serializerPlugins = plugins.map(p => p.serializer).filter((s): s is Plugin => !!s);

  return {
    schema, serializerPlugins,
  }
}

/**
 * @deprecated create serializer with `serializations plugins`
 * @returns serializer
 */
export const createSerializerOld = create;

/**
 * create serializer only support jsonMl <=> value
 * @param plugins mo plugins
 * @returns serializer
 */
export function createBaseSerializer(
  plugins: MoPlugin[],
) {
  const { schema, serializerPlugins } = getSchemaAndPluginsFromMoPlugins(plugins);
  return createBase(serializerPlugins, schema);
}

/**
 * create serializer with jsonMl、html、text、markdown
 * @param plugins mo plugins
 * @param parser optional html parser
 * @returns serializer
 */
export function createSerializer(
  plugins: MoPlugin[], parser?: MoHtmlParser,
) {
  const { schema, serializerPlugins } = getSchemaAndPluginsFromMoPlugins(plugins);
  return create(serializerPlugins, schema, parser);
}
