/**
 * @packageDocumentation
 * @module Models
 */

/**
 * Models中存在循环引用，需要保证子类import在前，父类import在后
 * 依赖：Block -> Inline -> Element -> Text -> Node
 * import顺序： Inline -> Element -> Text -> Node
 */
import isPlainObject from 'is-plain-object';
import Inline, { InlineJSON } from './inline';
import Element, { ElementProperties } from './element';
import Text, { TextJSON } from './text';
import Node from './node';
import { SerializeOptions } from '../interfaces/serializable';
import Data from '../interfaces/data';
import keyUtils from '../utils/keyUtils';

export type BlockChild = Block | Inline | Text;

export type BlockChildren = BlockChild[];

export type BlockChildrenJSON = BlockJSON[] | Array<InlineJSON | TextJSON>;

export interface BlockProperties<D = Data, N extends Node[] = BlockChildren> extends ElementProperties<D, N> {
  type: string;
  nodes?: N;
}

export interface BlockJSON<T = Data, N = BlockChildrenJSON> {
  klass: 'block';
  key?: string;
  type: string;
  data?: T;
  nodes?: N;
}

export default class Block<D = Data, N extends Node[] = BlockChildren> extends Element<D, N> {
  static readonly DEFAULT_TYPE = 'paragraph';

  static create<SD = Data, SN extends BlockChildren = BlockChildren>(
    properties: Block<SD, SN> | BlockProperties<SD, SN> | string,
  ) {
    if (Block.isBlock(properties)) {
      return properties;
    }

    const props: BlockProperties<SD, SN> = typeof properties === 'string' ? { type: properties } : properties;

    if (isPlainObject(props)) {
      const {
        type,
        key = keyUtils.create(),
        data,
        nodes = [Text.create()] as SN,
      } = props;

      if (typeof type !== 'string') {
        throw new Error('`Block.create` requires a `type` string.');
      }

      return new Block<SD, SN>({
        type,
        key,
        data: data as SD,
        nodes,
      });
    }

    throw new Error(
      `\`Block.create\` only accepts objects, string or blocks, but you passed it: ${props}`,
    );
  }

  static childFromJSON(node: BlockJSON | InlineJSON | TextJSON) {
    const { klass } = node;
    if (klass === 'block') {
      return Block.fromJSON(node as BlockJSON);
    }
    return Inline.childFromJSON(node as InlineJSON | TextJSON);
  }

  static fromJSON(json: BlockJSON) {
    const { nodes } = json;
    return Block.create({
      ...json,
      // @ts-ignore
      nodes: nodes ? nodes.map(Block.childFromJSON) : nodes,
    });
  }

  static isBlock(maybeBlock: any): maybeBlock is Block {
    return maybeBlock instanceof Block;
  }

  static isBlocks(maybeBlocks: any[]): maybeBlocks is Block[] {
    return maybeBlocks.every(Block.isBlock);
  }

  /**
   * 是否是叶子 block
   */
  static isLeafBlock(maybeLeafBlock: any): boolean {
    const { nodes } = maybeLeafBlock;

    if (!this.isBlock(maybeLeafBlock)) {
      return false;
    }

    if (!nodes.length) {
      return true;
    }

    const first = nodes[0];
    return !this.isBlock(first);
  }

  /**
   * 循环获取所有的叶子 block 节点
   */
  public static getBlocks(nodes: Node[]): Block[] {
    return nodes.reduce((array, child) => {
      if (!Block.isBlock(child)) {
        return array;
      }
      if (!Block.isLeafBlock(child)) {
        return array.concat(child.getBlocks());
      }
      array.push(child);

      return array;
    }, [] as Block[]);
  }

  /**
   * 循环获取所有的满足指定 type 的叶子 block 节点
   */
  public static getBlocksByType(nodes: Node[], type: string): Block[] {
    return nodes.reduce((array: Block[], node: Node) => {
      if (!Block.isBlock(node)) {
        return array;
      } else if (Block.isLeafBlock(node) && node.type === type) {
        array.push(node as Block);
        return array;
      } else {
        return array.concat(node.getBlocksByType(type));
      }
    }, []);
  }

  protected constructor(props: Required<BlockProperties<D, N>>) {
    super(props);
    const { type } = props;
    this.type = type;
    this.freeze();
  }

  public readonly type: string;

  public merge(props: Partial<BlockProperties<D, N>>) {
    return new Block<D, N>({
      type: this.type,
      key: this.key,
      data: this.data,
      nodes: this.nodes,
      ...props,
    });
  }

  public set<T extends keyof BlockProperties<D, N>>(
    key: T,
    value: Required<BlockProperties<D, N>>[T],
  ) {
    return this.merge({
      [key]: value,
    });
  }

  /**
   * 自身的 nodes 中，是否有 block 类型的节点
   */
  public hasBlockChildren() {
    return this.nodes.some((b) => Block.isBlock(b));
  }

  /**
   * 获取自身树下所有的叶子 block 节点
   */
  public getBlocks(): Block[] {
    return Block.getBlocks(this.nodes);
  }

  /**
   * 循环获取所有的满足指定 type 的叶子 block 节点
   */
  public getBlocksByType(type: string): Block[] {
    return Block.getBlocksByType(this.nodes, type);
  }

  public toJSON(options?: SerializeOptions) {
    const object: BlockJSON = {
      klass: 'block',
      type: this.type,
      data: { ...this.data },
      nodes: this.nodes.map((node) => node.toJSON(options)),
    };

    if (options?.preserveKeys) {
      object.key = this.key;
    }
    return object;
  }
}
