/**
 * @packageDocumentation
 * @ignore
 */
import Controller from '../controller/index';
import * as Commands from '../commands/index';
import Node from '../models/node';
import Document from '../models/document';
import Block from '../models/block';
import Inline from '../models/inline';
import Text from '../models/text';

function isInlineOrText(node: Node) {
  return Inline.isInline(node) || Text.isText(node);
}

function normalizeNode(node: Node, controller: Controller, next) {
  if (Document.isDocument(node)) {
    // Only allow block nodes in documents.
    const length = node.nodes.length;
    for (let i = 0; i < length; i += 1) {
      const child = node.nodes[i];
      if (!Block.isBlock(child)) {
        return () => controller.command(Commands.removeNodeByKey, (child as Node)!.key);
      }
    }
  } else if (Block.isBlock(node) || Inline.isInline(node)) {
    const length = node.nodes.length;
    // Ensure that block and inline nodes have at least one text child.
    if (length < 1) {
      return () => controller.command(Commands.insertNodeByKey, node.key, 0, Text.create());
    }

    // 1. Only allow block nodes or inline and text nodes in blocks.
    // 2. Only allow inline and text nodes in inlines.
    const predicate = Block.isBlock(node) && Block.isBlock(node.nodes[0])
      ? Block.isBlock
      : isInlineOrText;
    for (let i = 0; i < length; i += 1) {
      const child = node.nodes[i];
      if (!predicate(child)) {
        return () => controller.command(Commands.removeNodeByKey, child.key);
      }

      // Ensure that inline nodes are surrounded by text nodes.
      if (i === 0 && Inline.isInline(child)) {
        return () => controller.command(Commands.insertNodeByKey, node.key, 0, Text.create());
      }
      const next = node.nodes[i + 1];
      if (Inline.isInline(child)) {
        if (!Text.isText(next)) {
          return () => controller.command(Commands.insertNodeByKey, node.key, i + 1, Text.create());
        }
      } else if (Text.isText(child)) {
        // Merge adjacent text nodes.
        if (Text.isText(next)) {
          return () => controller.command(Commands.mergeNodeByKey, next.key);
        }
      }
    }
  }
  return next();
}

export default function createCoreSchemaPlugin() {
  return {
    normalizeNode,
  };
}
