import { useEffect, useCallback } from 'react';
import { noop } from 'lodash-es';
import { Observable, Observer } from '../models/observable';
import Path from '../models/path';
import Node from '../models/node';
import Document from '../models/document';
import Block from '../models/block';
import Decoration from '../models/decoration';
import { TextPoint } from '../models/point';
import { InputData } from '../interfaces/input';
import Controller from '../controller';
import { replaceNodeIntoElement } from '../utils/replaceNodeIntoElement';

export interface PendingElements {
  document: Document;
  node: Node;
}

export function useInputPending<T extends InputData>(
  elements: PendingElements,
  nodePath: Path,
  controller: Controller,
  enablePending: boolean | undefined,
  pending$: Observable<T>,
  updateElements: (elements: PendingElements) => void,
) {
  const { node, document } = elements;
  // 根据最新 node，使用 toView 局部重算 view node
  const pendingHandler: Observer<T> = useCallback((data) => {
    const { startPath: textPath, startOffset, key: textKey } = data;
    // 判断当前编辑的 text 节点是否在 node 中
    const oldViewText = Block.isBlock(node) ? node.getNode(textKey) : null;
    if (Block.isBlock(node) && oldViewText) {
      // 获取最新 text 和缓存的 view data
      const text = controller.value.document.getNodeByPath(textPath);
      if (text && oldViewText) {
        const viewData = controller.getViewData(oldViewText);
        const { injections } = viewData;
        // 推算出新的 decorations
        const decorations = viewData.decorations.slice(0);
        decorations.forEach((d, i) => {
          if (Decoration.isDecoration(d)) {
            const { key } = text;
            const { start, end } = d;
            if (start.key === key && end.key === key) {
              const delta = text.text.length - oldViewText.text.length;
              if (delta && startOffset <= start.offset) {
                // 在 decoration 前面增删文字
                const tmp = d.set(
                  'start',
                  TextPoint.create({ key, offset: start.offset + delta }),
                );
                decorations[i] = tmp.set(
                  'end',
                  TextPoint.create({ key, offset: end.offset + delta }),
                );
              } else if (delta && startOffset <= end.offset) {
                // 在 decoration 里面增删文字
                decorations[i] = d.set(
                  'end',
                  TextPoint.create({ key, offset: end.offset + delta }),
                );
              }
            }
          }
        });

        const viewText = controller.toView(text, decorations, injections);
        // 替换 view node 中的 text 节点
        let rPath: Path | null = textPath.slice(nodePath.length);
        if (node.getNodeByPath(rPath)?.key !== viewText.key) {
          rPath = node.getPath(viewText.key);
        }
        const viewNode = replaceNodeIntoElement(
          node,
          rPath,
          viewText,
        );
        // 替换 view document 中的 node 节点
        let dPath: Path | null = nodePath;
        if (document.getNodeByPath(dPath)?.key !== viewNode.key) {
          dPath = document.getPath(viewNode.key);
        }
        const documentView = replaceNodeIntoElement(document, dPath, viewNode) as Document;
        updateElements({
          node: viewNode,
          document: documentView,
        });
      }
    }
  }, [controller, document, node, nodePath, updateElements]);

  useEffect(() => {
    // 只在 leaf block 监听 pending 流
    if (Block.isLeafBlock(node) && enablePending) {
      const sub = pending$.subscribe(pendingHandler);
      return () => sub.unsubscribe();
    }
    return noop;
  }, [
    pending$,
    pendingHandler,
    node,
    enablePending,
  ]);
}
