/**
 * @packageDocumentation
 * @ignore
 */
import { isKeyHotkey } from 'is-hotkey';
import { IS_MAC } from './environment';
import React from 'react';

const HOTKEYS = {
  bold: 'mod+b',
  compose: [
    'left',
    'right',
    'down',
    'up',
    'space',
    'shift?+backspace',
    'shift?+enter',
    'shift?+delete',
    'shift?+tab',
  ],
  moveBackward: 'left',
  moveForward: 'right',
  moveUpward: 'up',
  moveDownward: 'down',
  deleteBackward: 'shift?+backspace',
  deleteForward: 'shift?+delete',
  extendBackward: 'shift+left',
  extendForward: 'shift+right',
  extendUpward: 'shift+up',
  extendDownward: 'shift+down',
  italic: 'mod+i',
  splitBlock: 'shift?+enter',
  undo: 'mod+z',
  space: 'space',
  enter: 'enter',
  tab: 'tab',
  copy: 'mod+c',
  cut: 'mod+x',
  paste: 'mod+v',
};

// https://support.apple.com/en-us/HT201236
const APPLE_HOTKEYS = {
  selectAll: 'cmd+a',
  moveBackward: 'ctrl+b',
  moveForward: 'ctrl+f',
  moveUpward: 'ctrl+p',
  moveDownward: 'ctrl+n',
  moveWordBackward: 'opt+left',
  moveWordForward: 'opt+right',
  moveLineBackward: ['cmd+left', 'home'],
  moveLineForward: ['cmd+right', 'end'],
  moveBlockBackward: ['opt+up', 'ctrl+a'],
  moveBlockForward: ['opt+down', 'ctrl+e'],
  moveToDocumentStart: 'cmd+up',
  moveToDocumentEnd: 'cmd+down',
  deleteBackward: ['ctrl+backspace'],
  deleteForward: ['ctrl+delete', 'ctrl+d', 'fn+delete'],
  deleteLineBackward: 'cmd+shift?+backspace',
  deleteLineForward: ['cmd+shift?+delete', 'ctrl+k'],
  deleteWordBackward: 'opt+shift?+backspace',
  deleteWordForward: 'opt+shift?+delete',
  extendWordBackward: 'opt+shift+left',
  extendWordForward: 'opt+shift+right',
  extendLineBackward: 'cmd+shift+left',
  extendLineForward: 'cmd+shift+right',
  extendBlockBackward: ['opt+shift+up', 'ctrl+shift+a'],
  extendBlockForward: ['opt+shift+down', 'ctrl+shift+e'],
  extendToDocumentStart: ['cmd+shift+up', 'fn+left'],
  extendToDocumentEnd: ['cmd+shift+down', 'fn+right'],
  splitBlock: 'ctrl+o',
  redo: 'cmd+shift+z',
  transposeCharacter: 'ctrl+t',
};

// https://support.microsoft.com/en-us/help/12445/windows-keyboard-shortcuts
const WINDOWS_HOTKEYS = {
  moveWordBackward: 'ctrl+left',
  moveWordForward: 'ctrl+right',
  moveLineBackward: 'home',
  moveLineForward: 'end',
  moveBlockBackward: 'ctrl+up',
  moveBlockForward: 'ctrl+down',
  moveToDocumentStart: 'ctrl+home',
  moveToDocumentEnd: 'ctrl+end',
  extendWordBackward: 'ctrl+shift+left',
  extendWordForward: 'ctrl+shift+right',
  extendLineBackward: 'shift+home',
  extendLineForward: 'shift+end',
  extendBlockBackward: 'ctrl+shift+up',
  extendBlockForward: 'ctrl+shift+down',
  selectAll: 'ctrl+a',
  deleteWordBackward: 'ctrl+shift?+backspace',
  deleteWordForward: 'ctrl+shift?+delete',
  redo: ['ctrl+y', 'ctrl+shift+z'],
};

const create = (key: string) => {
  const generic = HOTKEYS[key];
  const apple = APPLE_HOTKEYS[key];
  const windows = WINDOWS_HOTKEYS[key];
  const isGeneric = generic && isKeyHotkey(generic);
  const isApple = apple && isKeyHotkey(apple);
  const isWindows = windows && isKeyHotkey(windows);

  return (event: React.KeyboardEvent | KeyboardEvent) => {
    if (isGeneric && isGeneric(event)) {
      return true;
    }
    if (IS_MAC && isApple && isApple(event)) {
      return true;
    }
    if (!IS_MAC && isWindows && isWindows(event)) {
      return true;
    }
    return false;
  };
};

export default {
  isBold: create('bold'),
  isCompose: create('compose'),
  isSelectAll: create('selectAll'),
  isMoveBackward: create('moveBackward'),
  isMoveForward: create('moveForward'),
  isMoveUpward: create('moveUpward'),
  isMoveDownward: create('moveDownward'),
  isDeleteBackward: create('deleteBackward'),
  isDeleteForward: create('deleteForward'),
  isDeleteLineBackward: create('deleteLineBackward'),
  isDeleteLineForward: create('deleteLineForward'),
  isDeleteWordBackward: create('deleteWordBackward'),
  isDeleteWordForward: create('deleteWordForward'),
  isExtendBackward: create('extendBackward'),
  isExtendForward: create('extendForward'),
  isExtendUpward: create('extendUpward'),
  isExtendDownward: create('extendDownward'),
  isExtendWordBackward: create('extendWordBackward'),
  isExtendWordForward: create('extendWordForward'),
  isExtendLineBackward: create('extendLineBackward'),
  isExtendLineForward: create('extendLineForward'),
  isExtendBlockBackward: create('extendBlockBackward'),
  isExtendBlockForward: create('extendBlockForward'),
  isExtendToDocumentStart: create('extendToDocumentStart'),
  isExtendToDocumentEnd: create('extendToDocumentEnd'),
  isItalic: create('italic'),
  isMoveWordBackward: create('moveWordBackward'),
  isMoveWordForward: create('moveWordForward'),
  isMoveLineBackward: create('moveLineBackward'),
  isMoveLineForward: create('moveLineForward'),
  isMoveBlockBackward: create('moveBlockBackward'),
  isMoveBlockForward: create('moveBlockForward'),
  isMoveToDocumentStart: create('moveToDocumentStart'),
  isMoveToDocumentEnd: create('moveToDocumentEnd'),
  isRedo: create('redo'),
  isSplitBlock: create('splitBlock'),
  isTransposeCharacter: create('transposeCharacter'),
  isUndo: create('undo'),
  isSpace: create('space'),
  isEnter: create('enter'),
  isCopy: create('copy'),
  isCut: create('cut'),
  isPaste: create('paste'),
};
