import { merge } from 'lodash-es';
import { getTraverseRootsByDOMPath } from './dom';

export interface TraverseNodeContext {
  scrollableKey?: string;
  gapLabel?: string;
  /**
   * 排版模式下，不同 line 内的 span 可能重叠，需要增加 lineId 标识以避免被识别为同一行
   */
  lineId?: string;
  /**
   * 当前节点下叶子节点(leaf or void)是否只有一个
   */
  onlyLeafChild?: boolean;
}

export type TraverseNodeHandler = (decesdant: HTMLElement, context?: TraverseNodeContext) => {
  stopTraverse: boolean;
  context?: TraverseNodeContext;
};

interface TraverseOptions {
  /**
   * start block DOM 的 Path：自顶向下排列
   */
  startPath: HTMLElement[];
  /**
   * end block DOM 的 Path：自顶向下排列
   */
  endPath: HTMLElement[];
  /**
   * 当前节点的 DOM Path
   */
  rootPath: HTMLElement[];
  /**
   * 是否已遍历到 start block DOM
   */
  isStarted: boolean;
  /**
   * 是否已遍历到 end block DOM
   */
  isEnded: boolean;
}

export function traverseDOMTree(
  root: HTMLElement,
  callback: TraverseNodeHandler,
  parentContext: TraverseNodeContext = {},
  options: TraverseOptions,
) {
  let { isStarted, isEnded } = options;
  const { stopTraverse, context } = callback(root, parentContext);
  if (stopTraverse) {
    return { isStarted, isEnded };
  }
  const childContext = merge({ ...parentContext }, context);
  const { startPath, endPath, rootPath } = options;
  for (const ele of root.children) {
    const path = rootPath.concat(ele as HTMLElement);
    const [,, commonStartPath] = getTraverseRootsByDOMPath(startPath, path);
    const [,, commonEndPath] = getTraverseRootsByDOMPath(endPath, path);

    // 未遍历到 start dom 且在遍历 start dom 的祖先节点
    if (!isStarted && commonStartPath.length === path.length) {
      const started = commonStartPath.length === startPath.length;
      const newOpt = traverseDOMTree(
        ele as HTMLElement,
        callback,
        childContext,
        {
          startPath,
          endPath,
          rootPath: path,
          isStarted: started,
          isEnded,
        },
      );
      isStarted = newOpt.isStarted;
      isEnded = newOpt.isEnded;
    } else if (isStarted && (!isEnded || commonEndPath.length === endPath.length)) {
      // 已到过 start dom 且未遍历完 end dom tree
      const ended = commonEndPath.length === endPath.length;
      const newOpt = traverseDOMTree(
        ele as HTMLElement,
        callback,
        childContext,
        {
          startPath,
          endPath,
          rootPath: path,
          isStarted,
          isEnded: ended,
        },
      );
      isStarted = newOpt.isStarted;
      isEnded = newOpt.isEnded;
    } else if (isEnded && commonEndPath.length < endPath.length){
      // 已遍历完 end dom tree
      break;
    }
  }
  return { isStarted, isEnded };
}

/**
 * 情况一：startAncestor, endAncestor 为相同节点
 * startAncestor/endAncestor/commonPath
 *                / \
 *               /   \
 *             void  block
 *                     /\
 *                    /  \
 *                 leaf  leaf
 *
 * 情况二：startAncestor, endAncestor 为兄弟节点
 *             commonPath
 *             /    |    \
 *            /     |     \
 *  startAncestor  ...  endAncestor
 *     / \               /    \
 *    /   \             /      \
 * void    block      block     block
 *          /\         /\        /\
 *         /  \       /  \      /  \
 *      leaf  leaf leaf  leaf leaf leaf
 * (startPath)              (endPath)
 *
 * 情况三：startAncestor, endAncestor 为兄弟节点，且 startPath = startAncestor or endPath = endAncestor
 *             commonPath
 *             /    |    \
 *            /     |     \
 *  startAncestor  ...  endAncestor
 * (startPath/void)       /    \
 *                       /      \
 *                     block     block
 *                      / \       / \
 *                     /   \     /   \
 *                   leaf  leaf leaf leaf
 *                                 (endPath)
 */
export function traverseDOMForest(
  startPath: HTMLElement[],
  endPath: HTMLElement[],
  callback: TraverseNodeHandler,
) {
  const [startAncestor, endAncestor, commonPath] = getTraverseRootsByDOMPath(startPath, endPath);

  let root: HTMLElement | null = commonPath.length === startPath.length ? startPath[startPath.length - 1] : startAncestor;
  let { isStarted, isEnded } = {
    isStarted: commonPath.length === startPath.length,
    isEnded: commonPath.length === endPath.length,
  };
  while (root) {
    const opt = traverseDOMTree(
      root,
      callback,
      {},
      {
        startPath,
        endPath,
        rootPath: [...commonPath, root],
        isStarted: isStarted || (commonPath.length + 1 === startPath.length && root === startPath[startPath.length - 1]),
        isEnded : isEnded || (commonPath.length + 1 === endPath.length && root === endPath[endPath.length - 1]),
      },
    );
    isStarted = opt.isStarted;
    isEnded = opt.isEnded;
    if (root === endAncestor) {
      break;
    }
    root = root.nextElementSibling as (HTMLElement | null);
  }
}
