import Document from "../../../models/document";
import type { JsonML } from "../../type";
import { jsonMLGetAttributes, jsonMLGetTagName } from "../../utils/jsonml";

export default {
  name: 'document',

  match(node: JsonML): boolean {
    return jsonMLGetTagName(node) === 'root';
  },

  convert(node: JsonML): Document {
    return Document.create({
      data: jsonMLGetAttributes(node) || {},
    });
  },
};
